<template>
    <div class='partnerMager-input'>
        <i>*</i>
        <span>{{textName}}</span>
        <el-input v-model="input" :class="[this.error ? 'check-error' : '']" :placeholder="defaultName == undefined ? '请输入内容' : defaultName" @change='setValue'  :maxlength='maxlength'></el-input>
        <p v-if = 'this.error'>{{errorPropt}}</p>
    </div>
</template>


<script>
export default {
    props:['textName', 'defaultName', 'maxlength', 'defaultValue','error','errorPropt'],
	data () {
		return {
            input:this.defaultValue
		}
	},
	methods:{
       setValue(){
           this.$emit('setValue', this.input)
       }
    },
    watch:{
        defaultValue(val){
            this.input = val;
        }
    }
}
</script>


