<template>
<div class="abTable-warp">
    <div class="table abTable-hearder">
        <div class="table-tr">
            <div class="table-td">
                <div class="sub-table">
                    <div class="sub-table-tr">
                        <div style="width: 3%;" class="table-th">&nbsp;</div>
                        <div style="width: 5%;" class="table-th">序号</div>
                        <div style="width: 12%;" class="table-th">手机号/认证名称</div>
                        <div style="width: 10%;" class="table-th">客诉流水号</div>
                        <div style="width: 15%;" class="table-th">订单号</div>
                        <div style="width: 10%;" class="table-th">产品名称</div>
                        <div style="width: 15%;" class="table-th">异常原因</div>
                        <div style="width: 5%;" class="table-th">状态</div>
                        <div style="width: 8%;" class="table-th">创建人</div>
                        <div style="width: 13%;" class="table-th">创建时间</div>
                        <div style="width: 4%;" class="table-th"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div v-if="listData.length > 0" v-for="(item,index) in listData">
        <div class="table abTable-content">
            <div class="table-tr">
                <div class="table-td">
                    <div class="sub-table">
                        <div class="sub-table-tr">
                            <div class="sub-table-td" style="width: 3%;">
                                <el-checkbox v-model="checkbox[index]" @change="checkChange(index,item)"></el-checkbox>
                            </div>
                            <div class="sub-table-td" style="width: 5%;">{{index + pageSize*pageNumber +1}}</div>
                            <div class="sub-table-td" style="width: 12%;">{{item.fphone}}<br/>{{item.certificateName}}</div>
                            <div class="sub-table-td" style="width: 10%;">{{item.fcscptjournalid}}</div>
                            <div class="sub-table-td" style="width: 15%;">{{item.forderid}}</div>
                            <div class="sub-table-td" style="width: 10%;">{{item.fgroupgoodname}}</div>
                            <div class="sub-table-td" style="width: 15%;" :title="item.fexceptionreason">{{item.fexceptionreason}}</div>
                            <div class="sub-table-td" style="width: 5%;">
                                <span v-if="item.fstatus ==0 ">待处理</span>
                                <span v-if="item.fstatus ==1 ">退款中</span>
                                <span v-if="item.fstatus ==2 ">已补数</span>
                                <span v-if="item.fstatus ==3 ">已退款</span>
                                <span v-if="item.fstatus ==4 ">已驳回</span>
                                <span v-if="item.fstatus ==5 ">已关闭</span>
                                <span v-if="item.fstatus ==6 ">已处理</span>
                            </div>
                            <div class="sub-table-td" style="width: 8%;" :title="item.fcreator">{{item.fcreator}}</div>
                            <div class="sub-table-td" style="width: 13%;"><span v-if="item.fcreatetime">{{item.fcreatetime.substring(0,item.fcreatetime.length -2)}}</span> </div>
                            <div class="sub-table-td" style="width: 4%;"><span v-if="item.fcsoperatingtime" class="abTable-unfoldBtn" :class="{'unfoldActive':isfold[index]}" @click="unfold(index)"></span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="table ab-table-unfoldContent" v-show="isfold[index]" v-if="item.fcsoperatingtime">
            <div class="table-tr">
                <div class="table-td">
                    <div class="sub-table">
                        <div class="sub-table-tr">
                            <div class="table-th" style="width: 10%;">类型</div>
                            <div class="table-th" style="width: 20%;">处理方式</div>
                            <div class="table-th" style="width: 10%;">数量/金额</div>
                            <div class="table-th" style="width: 30%;">异常原因/沟通记录/财务说明</div>
                            <div class="table-th" style="width: 10%;">操作人</div>
                            <div class="table-th" style="width: 20%;">操作时间</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="table-tr " v-if="item.fcsoperatingtime">
                <div class="table-td">
                    <div class="sub-table">
                        <div class="sub-table-tr">
                            <div class="sub-table-td" style="width: 10%;">客服</div>
                            <div class="sub-table-td" style="width: 20%;">
                                <span v-if="item.fcshandlingtype == 1">退款</span>
                                <span v-if="item.fcshandlingtype == 2">补数</span>
                            </div>
                            <div class="sub-table-td" style="width: 10%;">
                                {{item.fpatchamount}}<span v-if="item.fpatchamount">片</span>&nbsp;
                            </div>
                            <div class="sub-table-td" style="width: 30%;">
                                <span :title="item.fcscommrecord">{{item.fcscommrecord}}</span>
                            </div>
                            <div class="sub-table-td" style="width: 10%;">{{item.fcsoperator}}</div>
                            <div class="sub-table-td" style="width: 20%;">{{item.fcsoperatingtime.substring(0,item.fcsoperatingtime.length -2)}}</div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="table-tr" v-if="item.ffinoperatingtime">
                <div class="table-td">
                    <div class="sub-table">
                        <div class="sub-table-tr">
                            <div class="sub-table-td" style="width: 10%;">财务</div>
                            <div class="sub-table-td" style="width: 20%;"><span v-if="item.ffinhandlingresult == 1">退款</span>
                                <span v-if="item.ffinhandlingresult == 2">驳回</span></div>
                            <div class="sub-table-td" style="width: 10%;">{{item.frefundamount}}元</div>
                            <div class="sub-table-td" style="width: 30%;">{{item.ffincomments}}</div>
                            <div class="sub-table-td" style="width: 10%;">{{item.ffinoperator}}</div>
                            <div class="sub-table-td" style="width: 20%;">{{item.ffinoperatingtime.substring(0,item.ffinoperatingtime.length -2)}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div  v-if="listData.length == 0" class="abTable-noData">
        暂无数据！
    </div>
</div>
</template>
<script>
/**
 * Created by qxl on 2017/9/11.
 */
export default {
    props: ['listData', 'pageNumber', 'pageSize'],
    data() {
        return {
            isfold: [

            ],
            checkbox: [

            ]
        }
    },
    methods: {
        unfold(index) {
            this.$set(this.isfold, index, !this.isfold[index]);
        },
        checkChange(index, item) {
            for (var i = 0; i < this.checkbox.length; i++) {
                if (i === index) {
                    continue
                }
                this.$set(this.checkbox, i, false);
            }
            if (this.checkbox[index]) {
                this.$emit('checkChange', item)
            } else {
                this.$emit('checkChange', false)
            }
        }
    },
    watch: {
        listData: {
            handler(curVal) {
                this.isfold = [];
                this.checkbox = [];
                this.$emit('checkChange', false);
                for (var i = 0; i < curVal.length; i++) {
                    this.isfold.push(false);
                    this.checkbox.push(false);
                }
            },
            deep: true
        }
    }
}
</script>
<style>
.abTable-warp {
    border: 1px solid #8f8f8f;
}

.abTable-warp .abTable-hearder {
    background: #e7ebef;
    border-bottom: 1px solid #d8d8d8;
}
.abTable-content{
    background: #f3f5fa;
    border-bottom: 1px solid #d8d8d8;
}

.ab-table-unfoldContent {
    border-bottom: 1px solid #d8d8d8;
}
.abTable-unfoldBtn {
    cursor: pointer;
    background: url("../../../../../static/images/publicImages/upIcon.png") no-repeat;
    display: inline-block;
    width: 24px;
    height: 12px;
}

.abTable-warp .unfoldActive {
    background: url("../../../../../static/images/publicImages/downIcon.png") no-repeat;
}

.abTable-noData {
    height: 100px;
    text-align: center;
    line-height: 100px;
}

.abTable-warp .table {
    display: table;
    width: 100%;
    border-collapse: collapse;
    font-size: 14px;
}

.abTable-warp .table-tr {
    display: table-row;
    height: 30px;
}

.abTable-warp .table-th {
    display: table-cell;
    font-weight: bold;
    height: 100%;
    text-align: center;
    vertical-align: middle;
}

.abTable-warp .table-td {
    display: table-cell;
    height: 100%;
    padding: 20px 0px;
}

.abTable-warp .sub-table {
    width: 100%;
    height: 100%;
    display: table;
}

.abTable-warp .sub-table-tr {
    display: table-row;
    height: 100%;
}

.abTable-warp .sub-table-td {
    word-break: break-all;
    display: table-cell;
    height: 100%;
    text-align: center;
    vertical-align: middle;
}
.abTable-warp .ab-table-unfoldContent .table-td{
    padding: 10px 0px;
}
</style>



