function number(value, callback) {
    if(!/^\d+$/.test(value)) {
        callback(new Error('请输入整数'));
    }
    else {
        callback();
    }
}
function float(value, callback) {
    if(!/^\d+(\.\d+)?$/.test(value)) {
        callback(new Error('请输入小数'));
    }
    else {
        callback();
    }
}
/**
 *整数
 */
const isNumber = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    number(value, callback)
};
/**
 * 小数
 */
const isFloat = (rule, value, callback) => {
    if(value === '') {
        if(rule.required) {
            callback(new Error('必填项'));
        }
        else {
            callback();
            return
        }
    }
    float(value, callback)
};
/**
 * 限制位数
 */
function doneLenFloat(value, callback, index) {
    let v = parseInt(value) + ''
    if(!/^\d+(\.\d+)?$/.test(value)) {
        callback(new Error('请输入小数'));
    }
    else if(v.length > index) {
        callback(new Error(`限制${index}位`));
    }
    else {
        callback();
    }
}
//位数
const isLenFloatTwo = (rule, value, callback) => {
    if(value === '') {
        if(rule.required) {
            callback(new Error('必填项'));
        }
        else {
            callback();
            return
        }
    }
    doneLenFloat(value, callback, 2)
};

/**
 * 必填项银行卡号
 */
const isBankcard = (rule, value, callback) => {
    if(value === '') {
        callback(new Error('必填项'));
        return
    }
    number(value, callback)
};
/**
 * 传真
 */
const isFax = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    if( !/^(\d{3,4}(-|\s))?\d{7,8}$/.test(value)) {
        callback(new Error('请输入正确的传真号'));
    }
    else {
        callback();
    }
};
/**
 * 网址
 */
const isWebSite = (rule, value, callback) => {
    if(value === '') {
        callback();
        return
    }
    if( !/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/.test(value)) {
        callback(new Error('请输入正确的网址'));
    }
    else {
        callback();
    }
};

const rules = {
    registercapital: [  //注册资本
        { validator: isNumber, trigger: 'blur' }
    ],
    annualsales: [      //年销售额
        { validator: isNumber, trigger: 'blur' }
    ],
    enterprisenumber: [     //企业人数
        { validator: isNumber, trigger: 'blur' }
    ],
    plantarea: [     //厂房面积
        { validator: isNumber, trigger: 'blur' }
    ],
    linenumber: [     //生产线数量
        { validator: isNumber, trigger: 'blur' }
    ],
    linedoor: [     //生产线门幅
        { validator: isNumber, trigger: 'blur' }
    ],
    productquantity: [     //产品数量
        { validator: isNumber, trigger: 'blur' }
    ],
    carnumber: [     //车辆数量
        { validator: isNumber, trigger: 'blur' }
    ],
    managenumber: [     //管理人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    rdnumber: [     //研发人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    fmassnumber: [     //质量人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    salesnumber: [     //销售人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    logisticsnumber: [     //物流人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    onelinenumber: [     //一线生产人员数量
        { validator: isNumber, trigger: 'blur' }
    ],
    linespeed:  [     //生产线平均车速
        { validator: isNumber, trigger: 'blur' }
    ],
    avgcapacity: [     //日均产能
        { validator: isNumber, trigger: 'blur' }
    ],
    maxcapacity: [     //日最大产能
        // { validator: isLenFloatTen, trigger: 'blur' }
        { validator: isNumber, trigger: 'blur' }
    ],


    opentime: [     //开机时长
        { validator: isLenFloatTwo, trigger: 'blur' }
    ],


    bankcard: [         //银行卡号
        { validator: isBankcard, required: true, trigger: 'blur' }
    ],
    bank: [             //开户银行
        { required: true, message: '必填项', trigger: 'blur' }
    ],
    accountholder: [       //开户人
        { required: true, message: '必填项', trigger: 'blur' }
    ],
    configuration: [   //订单分发配置
        { required: true, message: '必填项', trigger: 'change' }
    ],


    fax: [      //传真
        { validator: isFax, trigger: 'blur' }
    ],
    website: [  //网址
        { validator: isWebSite, trigger: 'blur' }
    ],

}

export default rules



