<template>
    <div id="commodity_sell">
        <tHeader>在售商品</tHeader>
        <div class="main">
            <div class="select">
                <span>商品分类:</span>&nbsp;&nbsp;
                <el-select v-model="one" clearable filterable  placeholder="请选择" @change="doneOneClass" @clear="clearOneClass">
                    <el-option
                        v-for="(item, index) in oneList"
                        :key="index"
                        :label="item.catName"
                        :value="item.catId">
                    </el-option>
                </el-select>
                <i style="width: 13px; background: #8c8c8c; height: 1px; display: inline-block; margin-bottom: 5px"></i>
                <el-select v-model="two" clearable filterable  placeholder="请选择" @change="doneTwoClass" @clear="clearTwoClass">
                    <el-option
                        v-for="(item, index) in twoList"
                        :key="index"
                        :label="item.catName"
                        :value="item.catId">
                    </el-option>
                </el-select>
                <i style="width: 13px; background: #8c8c8c; height: 1px; display: inline-block; margin-bottom: 5px"></i>
                <el-select v-model="three" clearable filterable  placeholder="请选择">
                    <el-option
                        v-for="(item, index) in threeList"
                        :key="index"
                        :label="item.catName"
                        :value="item.catId">
                    </el-option>
                </el-select>
                <span style="margin-left: 30px">
                    商品名称:<el-input v-model.trim="commodity_name" placeholder="请输入商品名称" style="width: 250px; margin: 0 30px 0 12px"></el-input>
                    <el-button type="primary" icon="search" @click="search">查询</el-button>
                </span>
            </div>
            <el-button type="primary" @click="save" style="margin: 0 0 21px 10px;">保存设置</el-button>
            <div class="q_table">
                <el-table
                    :data="tableData"
                    border
                    v-loading="loading"
                    :row-class-name="tableRowClassName"
                    style="width: 100%">
                    <el-table-column
                        type="index"
                        label="序号"
                        align="center"
                        width="62">
                    </el-table-column>
                    <el-table-column
                        label="商品图片"
                        align="center"
                        width="178">
                        <template scope="scope">
                            <img class="commodity_img" :src="httpBase + scope.row.url">
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="goodName"
                        label="商品名称"
                        align="center"
                        width="156">
                    </el-table-column>
                    <el-table-column
                        label="商品分类"
                        align="center"
                        min-width="186">
                        <template scope="scope">
                            {{ scope.row.goodTypel1Name }}>{{ scope.row.goodTypel2Name }}>{{ scope.row.goodTypeName }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="商品促销价格(元)"
                        align="center"
                        width="140">
                        <template scope="scope">
                            <span v-if="scope.row.minProPrice == scope.row.maxProPrice">{{ scope.row.minProPrice }}</span>
                            <span v-else>{{ scope.row.minProPrice }} - {{ scope.row.maxProPrice }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="积分最高抵用金额(元)"
                        align="center"
                        width="155">
                        <template scope="scope">
                            <span v-if="scope.row.minUsedIntegral == scope.row.maxUsedIntegral">{{ scope.row.minUsedIntegral }}</span>
                            <span v-else>{{ scope.row.minUsedIntegral }} - {{ scope.row.maxUsedIntegral }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="actualAmount"
                        label="累计兑换"
                        align="center"
                        width="102">
                    </el-table-column>
                    <el-table-column
                        label="商品顺序"
                        align="center"
                        width="150">
                        <template scope="scope">
                            <div class="turn">
                                <el-input-number v-model="scope.row.order" size="small" :debounce="0" @change="handleChange" :min="1" :max="999999" style="width: 110px;"></el-input-number>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="设为热门推荐"
                        align="center"
                        width="127">
                        <template scope="scope">
                            <el-checkbox v-model="scope.row.isRecommend"></el-checkbox>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="operator"
                        label="操作人"
                        align="center"
                        width="102">
                    </el-table-column>
                    <el-table-column
                        prop="updateTime"
                        label="操作时间"
                        align="center"
                        min-width="110">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="prchase-list">
            <Paging
                :showPageArry="showPageArry"
                :showPagelist="showPagelist"
                :listLength="listLength"
                :currentPage="currentPage"
                @changePage="changePage"
                @changSize="changSize">
            </Paging>
        </div>
    </div>
</template>

<script>
    import { IMGPATH } from '@/util/constant'
    export default {
        components: {
            tHeader: () => import('@/components/public/tHeader.vue'),
            Paging: () => import('@/components/public/paging')
        },
        data() {
            return {
                httpBase: IMGPATH,
                loading: true,
                one: '',
                two: '',
                three: '',
                commodity_name: '',
                oneList: [],
                twoList: [],
                threeList: [],
                tableData: [],
                //分页
                showPageArry: [25, 50, 100],     //每页显示个数选择器的选项设置
                showPagelist: 25,                   //每页显示默认15
                listLength: 0,                      //总数
                currentPage: 1                      //选择页
            }
        },
        created() {
            this.initClassify()
            this.searchGoodsOnSalePage()
        },
        methods: {
            //下拉
            doneOneClass(value) {
                this.two = ''
                this.three = ''
                this.threeList = []
                for(let item of this.oneList) {
                    if(value === item.catId) {
                        this.twoList = item.children
                        return
                    }
                }
            },
            doneTwoClass(value) {
                this.three = ''
                for(let item of this.twoList) {
                    if(value === item.catId) {
                        this.threeList = item.children
                        return
                    }
                }
            },
            //清空
            clearOneClass() {
                this.twoList = []
                this.threeList = []
            },
            clearTwoClass() {
                this.threeList = []
            },
            search() {
                this.searchGoodsOnSalePage()
            },
            save() {
                this.$confirm('您是否确定保存?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(!this.tableData.length) {
                        this.$message({
                            message: '请确保有保存的数据',
                            duration: 1000,
                            type: 'warning'
                        });
                        return false
                    }
                    let list = [...this.tableData]
                    for(let item of list) {
                        if(item.isRecommend === true) {
                            item.isRecommend = 1
                        }
                        else {
                            item.isRecommend = 0
                        }
                    }
                    this.$http.post('/productController/saveGoodsOnSale.do', list).then( res => {
                        if(res.data.success) {
                            this.$message({
                                message: '保存成功',
                                duration: 1000,
                                type: 'success'
                            });
                            this.one = ''
                            this.two = ''
                            this.three = ''
                            this.currentPage = 1
                            this.showPagelist = 25
                            this.searchGoodsOnSalePage()
                        }
                        else {
                            this.$message({
                                message: '保存失败',
                                duration: 1000,
                                type: 'error'
                            });
                        }
                    }).catch(error => {
                        this.$message({
                            message: '保存失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })
                }).catch(() => {});
            },
            //table
            handleChange(value) {

            },
            //分页
            changePage(page) {
                this.currentPage = page
                this.searchGoodsOnSalePage()
            },
            changSize(page) {
                this.showPagelist = page
                this.currentPage = 1
                this.searchGoodsOnSalePage()
            },
            //斑马线
            tableRowClassName(row, index) {
                if (index%2 === 1) {
                    return 'active-row';
                }
                return '';
            },

            initClassify() {
                this.$http.post('/productController/initClassify.do').then( res => {
                    if(res.data.success) {
                        this.oneList = res.data.data.classifyList
                    }
                }).catch( error => {
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
            },
            searchGoodsOnSalePage() {
                this.loading = true
                this.$http({
                    url: '/productController/searchGoodsOnSalePage.do',
                    method: 'post',
                    params: {
                        goodName: this.commodity_name,
                        goodTypel2: this.two,
                        goodTypel1: this.one,
                        goodTypeId: this.three,
                        page: this.currentPage,
                        size: this.showPagelist
                    }
                }).then( res => {
                    if(res.data.success) {
                        this.tableData = res.data.data.goodsOnSaleList.content
                        this.listLength = res.data.data.goodsOnSaleList.totalSize
                        this.filterTableData()
                    }
                    else {
                        this.loading = false
                        this.$message({
                            message: '获取数据失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.loading = false
                    this.$message({
                        type: 'error',
                        duration: 1000,
                        message: '错误'
                    });
                    console.log(error)
                })
            },
            filterTableData() {
                for(let item of this.tableData) {
                    if(item.isRecommend == 1) {
                        item.isRecommend = true
                    }
                    else {
                        item.isRecommend = false
                    }
                }
            }
        },
        watch: {
            tableData(newValue) {
                this.loading = false
            }
        }
    }
</script>

<style lang="scss" scoped>
    #commodity_sell {
        padding: 30px 30px 138px;
        .main {
            background: #fff;
            border-width: 0 1px 2px 1px;
            border-style: solid;
            border-color: #ededed;
            border-bottom-color: #e2e2e2;
            border-radius: 0 0 6px 6px;
            padding-bottom: 13px;
            .select {
                padding: 24px 8px 29px 10px;
                .el-select {
                    width: 150px;
                }
                button {
                    width: 110px;
                }
            }
            .q_table {
                padding: 0 10px;
                box-sizing: border-box;
                .commodity_img {
                    width: 130px;
                    height: 120px;
                    border-radius: 3px;
                    display: block;
                    margin: 15px auto;
                }
            }
        }
        .prchase-list .el-pagination  {
            top: 0;
            margin-top: 18px;
            margin-bottom: 77px;
        }
    }
</style>



