<template>
	<div class="partnerManage">
		<tHeader>认证审核信息</tHeader>
		  <div class='partnerManage-Img'>
			<el-dialog :visible.sync="dialogFormVisible" :show-close="false" @open='openDialog' @close='closeDialog' :modal-append-to-body='false'>
			 <img :src="selectImg" class='partnerManage-Img-css'>
			</el-dialog>
		  </div>
		<div class="partManage-bg partManage-position personal-step">
            <personal-left :step01='step01'></personal-left>
			<personalright 
			     :stringArry='personalArry'
				 @getText='getText'
				 :image01='IDcardJust'
				 :image02='IDcardBack'
				 :imgClass='imgClass'
				 :classImg='classImg'
				 @showImg='showImg'
				 :defalt='defalt'
				 :pass='pass'
				 :noPass='noPass'
				 :returnBtn='returnBtn'
				 @completed='completed'
				 @setNum = 'getRadio'
				 :isEdit='isEdit'
				 @goBack='goBack'
				 >
				<div class='personal-write'>
					<div class='persoanl-div persoanl-sex'>
						<span>性别</span>
						<el-radio class="sex" v-model="sex" label="1" :disabled='isDisabled'>男</el-radio>
						<el-radio class="sex" v-model="sex" label="2" :disabled='isDisabled'>女</el-radio>
					</div>
					<div class='persoanl-nation'>
						<span>民族</span>
						<el-input v-model='nation' :disabled='isDisabled'></el-input>
					</div>
					<div class='persoanl-div'>
						<span>出生</span>
						<div class="block">
							<el-date-picker v-model="birthTime" type="date" placeholder="选择日期" :editable="false" :disabled='isDisabled'></el-date-picker>
						</div>
					</div>
					<div class='persoanl-div personal-expiration'>
						<span>有效期限</span>
						<div class="block">
							<el-date-picker v-model="term" type="date" placeholder="选择日期" :editable="false" :disabled='isDisabled'></el-date-picker>
						</div>
					</div>
					<div class='persoanl-address'>
						<span >住址</span>
						<el-input class='personal-longInput' v-model='address' :disabled='isDisabled'></el-input>
					</div>
				</div>
			</personalright>
		</div>
	</div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import personalright from './personalRight'
import personalLeft from './personalLeft'
import {getTimes} from '@/util/common.js'
import {VERSION} from '@/util/constant.js' 
export default {
	props:['getInitData'],
	created(){
	   let data = this.getInitData;
	   let type = data.num;
	   this.personalArry[0].information = data.fname;
	   this.personalArry[1].information = data.fidNumber;
       this.id = data.fid;
	   this.userId = data.fuserId;
	   var imgData = {
			version:VERSION,	
			userId:data.fuserId,
			pictures:[{
				frelativeId:data.fid,
				ftype:10
			},
			{
				frelativeId:data.fid,
				ftype:11
			}]
		}
	   this.getInitImg(imgData);
	   //查看
	   if(type === '-1'){
		   this.defalt = false;
		   this.pass = true;
		   this.returnBtn = true;
		   this.isDisabled = true;
	   }
	   //修改
	   if(type === '-2'){
		   this.defalt = false;
		   this.pass = true;
		   this.isEdit = true;
	   }
	   //查看赋值
	   if(type !== 0){
          this.address = data.faddress;
		  this.nation = data.fnation;
		  this.birthTime = data.fbirthday;
		  this.term = data.fvalidDate;
		  this.sex = data.fsex + '';
	   }
	},
	data() {
		return {
			isDisabled:false,
			userId:'',                 //用户ID
			id:'',                     //个人认证ID
			address:'',               //地址
			nation:'',                //民族
			term:'',                  //有效期限
			birthTime:'',             //出生日期
			sex:'1',                  //性别
			radio:'1',                //通过/驳回 1/2
			rejectReason:'',          //驳回理由
			personalArry:[
                {string:'真实姓名:', information:''},
				{string:'身份证号码:', information:''}
			],
			IDcardJust:'',
			IDcardBack:'',
			imgClass:'porsonal-IDimg',
			classImg:'porsonal-IDimg',
			dialogFormVisible:false,   //显示放大图片
			selectImg:'',
			defalt:true,               //默认审核状态
			pass:false,                //通过状态
			noPass:false,              //未通过状态
			returnBtn:false,           //查看时，显示返回按钮
			isEdit:false,			   //是否修改界面
			step01:'身份证正/反面',
		}
	},
	components: {
		tHeader,
		personalright,
		personalLeft
	},
	methods: {
	   //获取驳回理由
       getText(data){
		  this.rejectReason = data;
	   },
	   //放大图片
       showImg(imgNumber){
		   this.selectImg = this.IDcardBack
		   if(imgNumber === '1'){
			  this.selectImg = this.IDcardJust
		   }
           this.dialogFormVisible = true
	   },
	   //审核完成
	   completed(){
		   var personalData={
              fsex:this.sex,
			  fnation:this.nation,
			  fbirthday:this.birthTime == ''  ? '' : getTimes(this.birthTime),
			  faddress:this.address,
			  fvalidDate:this.term == '' ? '' : getTimes(this.term),
			  frejectReason:this.rejectReason,
			  fid:this.id,
              fuserId:this.userId,
			  fverifier:this.$store.getters.doneAdmin.uname //审核人取登录的账号
		   }
		   let data = {
              version:VERSION,
			  operationType:this.radio,
			  certificationType:'1',
			  personCertification:personalData
		   }
		   if(this.radio === '1'){
				if(this.nation === '' || this.faddress === '' || this.birthTime === '' || this.term === ''){
					this.$message({
						message: '所填字段不能为空!',
						type: 'error'
					});
				    return 
			    }
		   }
		   if(this.radio === '2' && this.rejectReason === ''){
			   this.$message({
					message: '请输入驳回原因',
					type: 'error'
				});
			   	return 
		   }
		   if(this.radio === '2' && this.rejectReason.length < 5){
				this.$message({
					message: '驳回理由不得少于5个字',
					type: 'error'
				});
				return
		   }
		   this.$http.post('/outerUser/userAudit.do', data).then(res => {
                if(res.data.success){
				   this.$message({
					   message: '审核成功',
					   type: 'success'
				   });
                   this.$emit('goBack');
				}
            }).catch(function (error) {
                console.log(error);
            });
	   },
	   //获取通过或者驳回
	   getRadio(num){
          this.radio = num
	   },
	   //获取图片
       getInitImg(data){
            this.$http.post('/outerUser/getPicture.do', data).then(res => {
                if (res.data.success) {
                      this.IDcardJust = res.data.data[0].furl;
					  if(res.data.data[1]){
                        this.IDcardBack = res.data.data[1].furl
					  }
                }
            }).catch(function (error) {
                console.log(error);
            });
		},
		//返回列表
		goBack(){
			this.$emit('goBack');
		},
		openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
	}
}
</script>
    

<style>

</style>


