<template>
    <div class="partnerManage">
        <tHeader>纸板团购入口位管理</tHeader>
        <div class='common-bg'>
            <regional-choice
              @getPlease = 'getPlease'
            >
            </regional-choice>
            <table-carouse 
            :tableData='imgTable'
            :pleaseCodes='pleaseCode'
            :fbooths='fbooth'
            @saveUpImg='saveUpImg'
            :buyDetals='buyDetal'
            ></table-carouse>
        </div>
    </div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import tableCarouse from './tebleCarouse'
import regionalChoice from '@/components/public/regionalChoice'
import {VERSION, IMGPATH} from '@/util/constant.js'
export default {
    created(){
        //初始化获取团购列表，获取之后在初始化轮播图
        this.getListGrpup();
    },
	data () {
		return {
            pleaseCode:3303,
            imgTable:[],
            fbooth:4,
            buyDetal:''
		}
	},
	methods:{
        //获取市的code码
        getPlease(please){
           this.pleaseCode = please.code;
           this.getListGrpup();
        },
        //查询市对应的轮播图
        findImg(getData){
            this.$http.post('/banner/loadBanner.do', getData).then(res => {
                if (res.data.success) {
                    this.imgTable = res.data.data;
                    if(this.imgTable.length == 0){
                        this.init();
                        return
                    }
                    for(let i=0; i<this.imgTable.length; i++){
                         this.imgTable[i].number = i + 1;
                         this.imgTable[i].fimgpath = IMGPATH + this.imgTable[i].fimgpath;
                         if(this.imgTable[i].fjumpposition == 1){
                             this.imgTable[i].linkType = '普通网页'
                         }
                         if(this.imgTable[i].fjumpposition == 3){
                             this.imgTable[i].linkType = '商城列表'
                         }
                         if(this.imgTable[i].fjumpposition == 2 && this.imgTable[i].ffunctiontype == 2){
                                this.imgTable[i].fname = 'APP首页团购爆款特价列表入口';
                                 this.imgTable[i].linkType = '团购列表-爆款特价列表';
                         }
                         if(this.imgTable[i].fjumpposition == 2 && this.imgTable[i].ffunctiontype == 0){
                                this.imgTable[i].fname = 'APP首页团购全部产品列表入口';
                                this.imgTable[i].linkType = '团购列表-全部列表';
                         }
                         if(this.imgTable[i].fjumpposition == 2 && this.imgTable[i].ffunctiontype == 1){
                                this.imgTable[i].fname = 'APP首页团购限量抢购列表入口';
                                this.imgTable[i].linkType = '团购列表-限量抢购列表';
                         }
                         if(this.imgTable[i].fjumpposition == 4){
                             for(let i=0; i<this.buyDetal.length; i++){
                                 if(this.imgTable[3].fproductid === this.buyDetal[i].id){
                                       this.imgTable[3].linkType = '团购商品详情-' + this.buyDetal[i].regionName
                                 }
                                 if(this.imgTable[4].fproductid === this.buyDetal[i].id){
                                       this.imgTable[4].linkType = '团购商品详情-' + this.buyDetal[i].regionName
                                 }
                                 if(this.imgTable[5].fproductid === this.buyDetal[i].id){
                                       this.imgTable[5].linkType = '团购商品详情-' + this.buyDetal[i].regionName
                                 }
                             }
                             this.imgTable[3].fname = 'APP首页团购产品1'
                             this.imgTable[4].fname = 'APP首页团购产品2'
                             this.imgTable[5].fname = 'APP首页团购产品3'
                         }
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //刷新界面
        saveUpImg(){
           var upImg={ 
             farea:this.pleaseCode, 
             fclient:2, 
             fbooth:4, 
             fkeyarea:this.pleaseCode + ''
           }
           //获取初始化浙江温州轮播图
           this.findImg(upImg);
        },
        //获取团购列表
        getListGrpup(){
           let data ={
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fgroupareaid:this.pleaseCode,
                pagesize:1000,
                pagenum:0
            }
            this.$http.post('/product/loadListsForAddres.do', data).then(res => {
               if(res.data.success){
                    console.log('获取团购列表成功')
                    this.buyDetal = res.data.data;
                    for(let i=0; i<this.buyDetal.length; i++){
                        this.buyDetal[i].regionName = this.buyDetal[i].fproductname;
                        this.buyDetal[i].id = this.buyDetal[i].fid;
                    }
                    if(this.buyDetal.length == 0){
                        this.buyDetal = [
                            {regionName:'无团购产品',  id:''}
                        ]
                    }
               }
               //初始化轮播图
               this.saveUpImg();
           }).catch(function (error) {
                console.log(error);
           });
        },
        //初始化广告位
        init(){
            let data ={
                fbooth:4,
                fkeyarea:this.pleaseCode,
                farea:this.pleaseCode
            }
            this.$http.post('/banner/initializeAdPosition.do', data).then(res => {
                if (res.data.success) {
                    //初始化轮播图
                    this.saveUpImg();
                }
            }).catch(function (error) {
                console.log(error);
            });
        }
    },
    components:{
        tHeader,
        tableCarouse,
        regionalChoice
    }  
}
</script>


