import { IMGPATH } from '@/util/constant'
export default function (_this) {
    _this.$http({
        url: '/productController/editGoods.do',
        method: 'post',
        params: {
            goodId: _this.goodId
        }
    }).then( res => {
        if(res.data.success) {
            let $all = res.data.data
            //商品分类名称
            _this.$http({
                url: '/productController/initClassify.do',
                method: 'post'
            }).then( res => {
                if(res.data.success) {
                    _this.classifyList = res.data.data.classifyList
                    _this.allSortValueList = [$all.goodsDetail.goodTypel1, $all.goodsDetail.goodTypel2, $all.goodsDetail.goodTypeId]
                }
                else {
                    _this.$message({
                        message: '获取分类信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                _this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
            //商品名称
            _this.name = $all.goodsDetail.goodName
            //商品标签
            _this.$http.post('/productController/initLabel.do').then( res => {
                if(res.data.success) {
                    _this.allTags = res.data.data.labelList
                    let dynamicTags = []
                    for(let item of _this.allTags) { //为了选择的标签不能选择
                        item.disabled = false
                        for(let list of $all.goodsLabelList) {
                            if(item.id === list.labelId) {
                                item.disabled = true
                                dynamicTags.push(item)
                                break
                            }
                        }
                    }
                    _this.dynamicTags = dynamicTags
                }
                else {
                    _this.$message({
                        message: '获取标签信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                _this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
            //发货类型
            _this.$http({
                url: '/productController/shipType.do',
                method: 'post',
            }).then( res => {
                if(res.data.success) {
                    _this.shipTypeMapList = res.data.data.shipTypeMapList
                    _this.shipType = $all.goodsDetail.shipType
                }
                else {
                    _this.$message({
                        message: '获取发货类型信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                _this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
            //商品属性
            _this.attrs = $all.goodsAttrList
            //商品规格和商品销售信息table
            _this.$http.post('/productController/initSpec.do').then( res => {
                if(res.data.success) {
                    _this.specList = res.data.data.specList
                    for(let item of $all.goodsSpecList) {
                        _this.checkedNorms.push(item.specId)
                        for(let list of _this.specList) {
                            if(item.specId === list.id) {
                                _this.checkedNormsNames.push(list.specName)
                                _this.norms.push({
                                    specId: item.specId,
                                    specName: list.specName,
                                    specValues: item.specValues
                                })
                                break
                            }
                        }
                    }
                    _this.copyQuotesInfo = $all.goodsDetail.specPriceList
                    if(_this.norms.length !== 0) {
                        for(let item of _this.copyQuotesInfo.specPrices) {
                            if(item.url === '') {
                                item.baseUrl = ''
                            }
                            else {
                                item.baseUrl = IMGPATH + item.url
                            }
                            if(item.isExist === 1) {
                                item.isExist = true
                            }
                            else {
                                item.isExist = false
                            }
                            if(item.url !== '') {
                                item.tag = 1            //做个标记 如果是后端返回的 图片加个标记
                            }
                        }
                    }
                    else {
                        _this.norms = []        //如果为空 强制重复赋一个[]，促使触发watch监听
                    }
                }
                else {
                    _this.$message({
                        message: '获取商品规格信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                _this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
            //商品主图和详情图
            for(let item of $all.goodsPicList) {
                let dict = {
                    url: IMGPATH + item.url,
                    fileurl: item.url,
                    id: item.id,
                    fileid: item.picId,
                    tag: 1                  //做个标记 如果是后端返回的 图片加个标记
                }
                if(item.type === 1) {
                    _this.fileMainList.push(dict)
                }
                if(item.type === 2) {
                    _this.fileDetailList.push(dict)
                }
            }
        }
        else {
            _this.$message({
                message: '数据获取失败',
                duration: 1000,
                type: 'error'
            });
        }
    })
}



