<template>
    <div id="rechargeAudit-warp">
        <div class="rechargeAudit-warp-padding">
            <div class="rechargeAudit-radius">
                <tHeader>充值审核</tHeader>
                <div class="rechargeAudit-content">
                    <ul class="rechargeAudit-nav clearfix">
                        <li v-for="(item,index) in navList">
                            <span :class="{active:item.navState}"  @click="tab(index)">{{item.navContent}}</span>
                        </li>
                    </ul>
                    <div>
                        <audit-query @query="query"></audit-query>
                    </div>
                    <div class="rechargeAudit-btn-warp">
                        <span @click="audit">审核</span>
                    </div>
                    <div class="rechargeAudit-tableWarp">
                        <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  height="700" @select="selectList">
                            <el-table-column type="selection">
                            </el-table-column>
                            <el-table-column  label="序号" width="70" align="center">
                                <template scope="scope">
                                    <span>{{scope.$index + size*pageNo+1}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column property="fphone" label="手机号" align="center" width="120">
                            </el-table-column>
                            <el-table-column property="customerName" label="认证名" align="center" width="120">>
                            </el-table-column>
                            <el-table-column label="状态" align="center" width="120">
                                <template scope="scope">
                                    <span  v-show="scope.row.fstatus == 0" class="userManage-disableBtn">未审核</span>
                                    <span  v-show="scope.row.fstatus == 1" class="userManage-disableBtn">已审核</span>
                                    <span  v-show="scope.row.fstatus == 2" class="userManage-enableBtn">已驳回</span>
                                </template>
                            </el-table-column>
                            <el-table-column property="forderType" label="类别" align="center" width="120">
                                <template scope="scope">
                                    <span  v-show="scope.row.forderType == 1" class="userManage-disableBtn">充值</span>
                                    <span  v-show="scope.row.forderType == 2" class="userManage-enableBtn">扣款</span>
                                </template>
                            </el-table-column>
                            <el-table-column property="famount" label="金额(元)" align="center" width="120">
                            </el-table-column>
                            <el-table-column label="资金类型" align="center" width="120">
                                 <template scope="scope">
                                     <span  v-show="scope.row.ffundType == 1" >现金</span>
                                    <span  v-show="scope.row.ffundType == 2" >承兑</span>
                                    <span  v-show="scope.row.ffundType == 3">电汇</span>
                                     <span  v-show="scope.row.ffundType == 4" >积分</span>
                                    <span  v-show="scope.row.ffundType == 0" >其他</span>
                                </template>
                            </el-table-column>
                            <el-table-column property="fremark" label="备注" align="center" width="250">
                            </el-table-column>
                            <el-table-column property="foperator" label="申请人" align="center" width="120">
                            </el-table-column>
                            <el-table-column  label="申请时间" align="center" width="180">
                                <template scope="scope">
                                    <span v-if="scope.row.foperatingTime">{{scope.row.foperatingTime.substring(0,scope.row.foperatingTime.length -2)}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column property="fauditExplain" label="审核说明" align="center" width="180">
                            </el-table-column>
                            <el-table-column property="fauditor" label="审核人" align="center" width="120">
                            </el-table-column>
                            <el-table-column label="审核时间" align="center" width="180">
                                <template scope="scope">
                                    <span v-if="scope.row.fauditTime">{{scope.row.fauditTime.substring(0,scope.row.fauditTime.length -2)}}</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <div class="rechargeAudit-btnMasker"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="rechargeAudit-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false"
                           @current-change="handleCurrentChange"
                           :current-page="pageNo +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
        <audit-window @rejectSure="rejectSure" :currentRow="currentRow" @passSure="passSure" :auditWindowShow="auditWindowShow" @auditWindowClose="auditClose"></audit-window>
        <div class="rechargeAudit-masker" v-show="mask"></div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import auditWindow from './auditWindow.vue'
    import auditQuery from './auditQuery.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        components:{
            tHeader,
            auditWindow,
            auditQuery
        },
        created(){
          this.listInit(false);
        },
        data(){
            return {
                currentRow:[],
                //遮罩层
                mask:false,
                 //扣款的window
                auditWindowShow:false,
                //导航的列表
                navList:[
                    {navContent:'已审核', navState:false},
                    {navContent:'未审核', navState:false},
                    {navContent:'已驳回', navState:false},
                    {navContent:'全部', navState:true}
                ],
                //类型
                type:'',
                //类型容器
                typeList:[
                    {
                        type:1,
                        value:2
                    }
                ],
                phone:'', //手机号
                endTime:'', // 结束时间
                startTime:'', //开始时间
                total:0, //总数
                size:15, //条数
                pageNo:0, // 当前页,
                orderType: '', //订单类别
                status: null, // 审核状态
                userId:'', //用户id
                tabIndex:3, // 导航的索引
                tableData:[] // 列表容器
            }
        },
        methods:{
            selectList(obj){
                if(obj.length !== 0){
                    if(obj.length > 1){
                        obj[0] = obj[1];
                        obj = obj.splice(1,1);
                    }
                    this.currentRow = obj;
                }else{
                    this.currentRow = [];
                }
            },
            audit(){
                if(this.currentRow.length == 0){
                    this.msg('请选择记录!', 'error');
                }else if(this.currentRow[0].fstatus != 0){
                    this.msg('请选择未审核的记录!', 'error');
                }else{
                    this.auditWindowShow = true;
                    this.mask = true;
                }
            },
            auditClose(){
                this.auditWindowShow = false;
                this.mask = false;
            },
            passSure(){
                this.msg('通过成功！', 'success');
                this.auditWindowShow = false;
                this.mask = false;
                this.currentRow = [];
                this.listInit(true)
            },
            rejectSure(){
                this.msg('驳回成功！', 'success');
                this.auditWindowShow = false;
                this.mask = false;
                this.currentRow = [];
                this.listInit(true)
            },
            // 一页多少条变化
            handleSizeChange(size){
                this.size = size;
                this.listInit(true);
            },
            //页码变化
            handleCurrentChange(page){
                this.pageNo = page - 1;
                this.listInit(false);
            },
            // 导航条变化
            tab(index){
                if(this.tabIndex == index){
                    return;
                }
                for(var i=0; i<this.navList.length; i++){
                    this.navList[i].navState = false;
                }
                this.navList[index].navState = true;
                switch (index) {
                    case 0:
                        this.status = 1;
                        break;
                    case 1:
                        this.status = 0;
                        break;
                    case 2:
                        this.status = 2;
                        break;
                    default:
                        this.status = null
                        break;
                }
                this.tabIndex = index;
                this.currentRow = [];
                this.listInit(true);
            },
            //查询
            query(queryJson){
                this.endTime = queryJson.endTime;
                this.startTime = queryJson.startTime;
                this.orderType = queryJson.orderType;
                this.phone = queryJson.phone;
                this.listInit(true);
            },
            // 查询的数据
            queryData(){
                return {
                    startTime:this.startTime == ''?null:this.startTime,
                    endTime:this.endTime == '' ?null:this.endTime,
                    orderType:this.orderType ==''?null:parseFloat(this.orderType),
                    status:this.status,
                    pageSize:this.size,
                    pageNo:this.pageNo,
                    version:VERSION,
                    phone:this.phone == ''?null:this.phone
                }
            },
            // 列表初始化
            listInit(sizechange){
                this.$http.post('/rechargeOrder/auditList.do',this.queryData()).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = parseFloat(res.data.total);
                        if(sizechange){
                            this.pageNo = 0;
                        }
                    }
                })
            },
            // 消息
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            }
        }
    }
</script>

<style>
    #rechargeAudit-warp{
        width: 100%;
        height:100%;
        padding-bottom:250px;
    }
    .rechargeAudit-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .rechargeAudit-radius{
        background: #fff;
        border-radius:8px;
        padding-bottom:100px;
    }
    .rechargeAudit-content{
        padding:0px 10px;
    }
    .rechargeAudit-tableWarp{
        position: relative;
    }
    .rechargeAudit-btnMasker{
        width: 30px;
        height: 30px;
        position: absolute;
        top:5px;
        left: 10px;
        z-index: 10000;
    }
    .rechargeAudit-nav{
        height:50px;
        font-size:16px;
        border-bottom:2px solid #b0b0b0;
        padding-left:10px;
    }
    .rechargeAudit-nav li{
        float:left;
        height:50px;
        line-height:50px;
        padding:0px 22px;
    }
    .rechargeAudit-nav li span{
        cursor: pointer;
    }
    .rechargeAudit-nav li span.active{
        color:#38acff;
    }
    /*
        分页
    */
    .rechargeAudit-pagination{
        text-align: center;
    }
    .rechargeAudit-pagination .el-pagination button,
    .rechargeAudit-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .rechargeAudit-pagination .el-pager li.btn-quicknext,
    .rechargeAudit-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .rechargeAudit-pagination .el-pagination .el-select .el-input input,
    .rechargeAudit-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .rechargeAudit-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .rechargeAudit-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 3000;
    }
    .rechargeAudit-btn-warp{
        padding:20px 0px;
    }
    .rechargeAudit-btn-warp span{
        width:90px;
        height:30px ;
        text-align: center;
        cursor:pointer;
        line-height: 30px;
        display: inline-block;
        border-radius: 4px;
        margin: 0px 10px;
        background:#38acff;
        color:#fff;
    }
</style>



