<template>
    <div id="commodity_all">
        <tHeader>全部商品</tHeader>
        <div class="main">
            <el-menu default-active="1" class="el-menu-demo" mode="horizontal">
                <el-menu-item index="1" @click="doneOnOffStatus(2)">全部</el-menu-item>
                <el-menu-item index="2" @click="doneOnOffStatus(1)">已上架</el-menu-item>
                <el-menu-item index="3" @click="doneOnOffStatus(0)">未上架</el-menu-item>
            </el-menu>
            <div class="btn_groups">
                <el-button type="primary" @click="add"><i class="fa fa-plus-circle"></i>新增</el-button>
                <!--<el-button type="primary" style="width: 130px;"><i class="fa fa-plus-circle"></i>复制并新增</el-button>-->
                <el-button type="primary" @click="doneUp"><i class="fa fa-arrow-up"></i>上架</el-button>
                <el-button type="primary" @click="doneDown"><i class="fa fa-arrow-down"></i>下架</el-button>
                <el-button type="primary" @click="doneRemove"><i class="fa fa-trash-o"></i>删除</el-button>
                <span>
                    商品标签:
                    <el-select v-model="tag_name" clearable filterable placeholder="请选择" style="width: 250px; margin-right: 15px;">
                        <el-option
                            v-for="item in tagsData"
                            :key="item.id"
                            :label="item.labelName"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </span>
                <span>
                    商品名称: <el-input v-model.trim="commodity_name" placeholder="请输入商品名称" style="width: 250px; margin-right: 15px;"></el-input>
                    <el-button type="primary" icon="search" @click="search">查询</el-button>
                </span>
            </div>
            <div class="q_table">
                <el-table
                    ref="multipleTable"
                    :data="tableData"
                    border
                    v-loading="loading"
                    :row-class-name="tableRowClassName"
                    @selection-change="handleSelectionChange"
                    style="width: 100%">
                    <el-table-column
                        type="selection"
                        align="center"
                        width="55">
                    </el-table-column>
                    <el-table-column
                        type="index"
                        label="序号"
                        align="center"
                        width="62">
                    </el-table-column>
                    <el-table-column
                        label="商品图片"
                        align="center"
                        width="178">
                        <template scope="scope">
                            <img class="commodity_img" :src="httpBase + scope.row.url">
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="goodName"
                        label="商品名称"
                        align="center"
                        width="156">
                    </el-table-column>
                    <el-table-column
                        label="商品分类"
                        align="center"
                        min-width="186">
                        <template scope="scope">
                            {{ scope.row.goodTypel1Name }}>{{ scope.row.goodTypel2Name }}>{{ scope.row.goodTypeName }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="状态"
                        align="center"
                        width="100">
                        <template scope="scope">
                            {{ scope.row.onOffStatus == 1 ? '已上架' : '未上架' }}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="商品促销价格(元)"
                        align="center"
                        width="153">
                        <template scope="scope">
                            <span v-if="scope.row.minProPrice == scope.row.maxProPrice">{{ scope.row.minProPrice }}</span>
                            <span v-else>{{ scope.row.minProPrice }} - {{ scope.row.maxProPrice }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="积分最高抵用金额(元)"
                        align="center"
                        width="155">
                        <template scope="scope">
                            <span v-if="scope.row.minUsedIntegral == scope.row.maxUsedIntegral">{{ scope.row.minUsedIntegral }}</span>
                            <span v-else>{{ scope.row.minUsedIntegral }} - {{ scope.row.maxUsedIntegral }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="actualAmount"
                        label="累计兑换"
                        align="center"
                        width="102">
                    </el-table-column>
                    <el-table-column
                        prop="operator"
                        label="操作人"
                        align="center"
                        width="102">
                    </el-table-column>
                    <el-table-column
                        prop="updateTime"
                        label="操作时间"
                        align="center"
                        min-width="110">
                    </el-table-column>
                    <el-table-column
                        prop="time"
                        label="操作"
                        align="center"
                        width="198">
                        <template scope="scope">
                            <div v-if="scope.row.onOffStatus == 1">
                                <el-button
                                    size="small"
                                    style="width: 70px;"
                                    @click="commodityDown(scope.$index, scope.row)">
                                    下架
                                </el-button>
                            </div>
                            <div v-else>
                                <el-button
                                    size="small"
                                    style="width: 70px;"
                                    @click="commodityUp(scope.$index, scope.row)">
                                    上架
                                </el-button>
                                <el-button
                                    size="small"
                                    style="width: 70px; margin-left: 4px"
                                    @click="commodityRevise(scope.$index, scope.row)">
                                    修改
                                </el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="prchase-list">
            <Paging
                :showPageArry="showPageArry"
                :showPagelist="showPagelist"
                :listLength="listLength"
                :currentPage="currentPage"
                @changePage="changePage"
                @changSize="changSize">
            </Paging>
        </div>
    </div>
</template>

<script>
    import { IMGPATH } from '@/util/constant'
    export default {
        components: {
            tHeader: () => import('@/components/public/tHeader.vue'),
            Paging: () => import('@/components/public/paging')
        },
        data() {
            return {
                httpBase: IMGPATH,
                loading: true,
                tag_name: '',
                commodity_name: '',
                status: 2,
                onOffDeleteStatus: '',              //0下架，1上架, 2删除
                multipleSelection: [],              //复选框数据
                tagsData: [],                       //标签数据
                tableData: [],
                showPageArry: [15, 30, 45, 60],     //每页显示个数选择器的选项设置
                showPagelist: 15,                   //每页显示默认15
                listLength: 0,                      //总页数
                currentPage: 1                      //选择页
            }
        },
        created() {
            this.$http.post('/productController/initLabel.do').then( res => {
                if(res.data.success) {
                    this.tagsData = res.data.data.labelList
                }
                else {
                    this.$message({
                        message: '标签数据加载失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                this.$message({
                    message: '标签数据加载报错',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
            this.getTableData()
        },
        computed: {
            admin() {
                return this.$store.getters.doneAdmin
            }
        },
        methods: {
            add() {
                this.$confirm('您是否确定新增商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$router.push({name: 'commodity_add', query: { goodId: '' }})
                }).catch(() => {});
            },
            //商品上下架
            doneOnOffStatus(index) {
                this.currentPage = 1
                this.status = index
                this.getTableData()
            },
            commodityUp(index, row) {
                this.$confirm('您是否确定上架商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.multipleSelection = []
                    this.multipleSelection.push(row)
                    this.onOffDeleteStatus = 1
                    this.getOnOffStatus()
                }).catch(() => {});
            },
            commodityDown(index, row) {
                this.$confirm('您是否确定下架商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.multipleSelection = []
                    this.multipleSelection.push(row)
                    this.onOffDeleteStatus = 0
                    this.getOnOffStatus()
                }).catch(() => {});
            },
            commodityRevise(index, row) {
                this.$confirm('您是否确定修改商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$router.push({name: 'commodity_add', query: { goodId: row.goodId }})
                }).catch(() => {});
            },
            //查询,
            search() {
                this.currentPage = 1
                this.getTableData()
            },
            //复选框
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            //上下架删
            doneUp() {
                this.$confirm('您是否确定上架商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.onOffDeleteStatus = 1
                    this.getOnOffStatus()
                }).catch(() => {});
            },
            doneDown() {
                this.$confirm('您是否确定下架商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.onOffDeleteStatus = 0
                    this.getOnOffStatus()
                }).catch(() => {});
            },
            doneRemove() {
                this.$confirm('您是否确定删除商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.onOffDeleteStatus = 2
                    this.getOnOffStatus()
                }).catch(() => {});
            },
            //分页
            changePage(page) {
                this.currentPage = page
                this.getTableData()
            },
            changSize(page) {
                this.showPagelist = page
                this.currentPage = 1
                this.getTableData()
            },
            //斑马线
            tableRowClassName(row, index) {
                if (index%2 === 1) {
                    return 'active-row';
                }
                return '';
            },
            //获取数据函数
            getTableData() {
                this.loading = true
                this.$http({
                    url: '/productController/searchAllGoodsPage.do',
                    method: 'post',
                    params: {
                        goodName: this.commodity_name,
                        status: this.status,
                        current: this.currentPage,
                        size: this.showPagelist,
                        labelId: this.tag_name
                    }
                }).then( res => {
                    if(res.data.success) {
                        this.listLength = res.data.data.allGoodsPageList.totalSize
                        this.tableData = res.data.data.allGoodsPageList.content
                    }
                    else {
                        this.loading = false
                        this.$message({
                            message: '获取数据失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.loading = false
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
            },
            getOnOffStatus() {
                if(this.multipleSelection.length == 0) {
                    this.$message({
                        message: '请选择您要操作的商品信息',
                        duration: 1000,
                        type: 'info'
                    });
                    return
                }
                let list = []
                if(this.onOffDeleteStatus == 2) {   //删除
                    list = this.multipleSelection.map((item, index) => {
                        return item.goodId
                    })
                }
                else {                              //上下架
                    for(let item of this.multipleSelection) {
                        if(item.onOffStatus != this.onOffDeleteStatus) {
                            list.push(item.goodId)
                        }
                    }
                }
                this.$http({
                    url: '/productController/editGoodsStatus.do',
                    method: 'post',
                    params: {
                        goodIds: list,
                        status: this.onOffDeleteStatus,
                        userName: this.admin.uname,
                        userId: this.admin.id
                    }
                }).then( res => {
                    if(res.data.success) {
                        this.$message({
                            message: '恭喜你，操作成功',
                            duration: 1000,
                            type: 'success'
                        });
                        this.multipleSelection = []
                        this.$refs.multipleTable.clearSelection();
                        this.getTableData()
                    }
                    else {
                        this.multipleSelection = []
                        this.$refs.multipleTable.clearSelection();
                        if(res.data.code === 200002) {
                            this.$message({
                                message: '当前选择的所有商品已为此状态。',
                                duration: 1000,
                                type: 'info'
                            });
                        }
                        else if(res.data.code === 200005) {     //多选删除 不允许
                            this.$message({
                                message: res.data.msg,
                                duration: 1000,
                                type: 'warning'
                            });
                        }
                        else {
                            this.$message({
                                message: '您选择的商品操作失败，请重新操作',
                                duration: 1000,
                                type: 'warning'
                            });
                        }
                    }
                }).catch( error => {
                    this.multipleSelection = []
                    this.$refs.multipleTable.clearSelection();
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
            }
        },
        watch: {
            tableData(newValue) {
                this.loading = false
            }
        }
    }
</script>

<style lang="scss" scoped>
    @media screen and (max-width: 1500px) {
        #commodity_all .btn_groups .el-input {
            width: 140px !important;
        }
        #commodity_all .btn_groups .el-select {
            width: 140px !important;
        }
    }
    @media screen and (max-width: 1400px) {
        #commodity_all .btn_groups button {
            margin-right: 10px !important;
        }
    }
    #commodity_all {
        padding: 30px 30px 44px;
        .main {
            background: #fff;
            border-width: 0 1px 2px 1px;
            border-style: solid;
            border-color: #ededed;
            border-bottom-color: #e2e2e2;
            border-radius: 0 0 6px 6px;
            padding-bottom: 13px;
            /*elementui的修改*/
            .el-menu {
                box-shadow: 0 3px 10px 2px #eeeeee;
                padding-left: 30px;
                background: #fff;
                .el-menu-item {
                    font-size: 16px;
                }
            }
            .el-menu--horizontal .el-menu-item:hover {
                background: #fff;
            }
            .btn_groups {
                height: 82px;
                padding-top: 25px;
                padding-left: 30px;
                box-sizing: border-box;
                button {
                    width: 110px;
                    margin-right: 20px;
                    i {
                        margin-right: 10px;
                    }
                }
            }
            .q_table {
                padding: 0 10px;
                box-sizing: border-box;
                .commodity_img {
                    width: 130px;
                    height: 120px;
                    border-radius: 3px;
                    display: block;
                    margin: 15px auto;
                }
            }
        }
        .prchase-list .el-pagination  {
            top: 0;
            margin-top: 18px;
            margin-bottom: 77px;
        }
    }
</style>



