<template>
    <div class='carouse-region-select'>
        <carouse-select 
        :region='region' 
        :textName='regionName'
        @getSelect='getRegion'
        :regionName='regiondefault'
        :isDisableds='isDisabled'
        placeholder='请选择省'
        >
        </carouse-select>
        <span class='carouse-x'>——</span>
        <carouse-select 
        :region='place' 
        @getSelect='getPlease'
        :regionName='pleaseName'
        :isDisableds='isDisabled'
        placeholder='请选择市'
        >
        </carouse-select>
    </div>
</template>


<script>
import carouseSelect from './select'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    props:['defaultRegion', 'defaultPlease', 'parterCode', 'keyArea','isDisableds', 'regionNameDf', 'isProduce'],
    created(){
            //获取行政区域
            if(this.parterCode == -1){
                this.$http.post('/partners/findPartnersProvince.do').then(res => {
                    if(res.data.success){
                       var region = res.data.data;
                       for(let i=0; i<region.length; i++){
                            region[i].regionName = region[i].oprovince;
                            region[i].regionName = clippingProvice(region[i].regionName);
                        }
                        this.region = region;
                    }
                 }).catch(function (error) {
                    console.log(error);
                 });
                return 
            }
            //获取全部销售区域
            if(this.parterCode == -2){
                this.$http.post('/partnersArea/findAllProvince.do').then(res => {
                    if (res.data.success) {
                        var region = res.data.data;
                        for(let i=0; i<region.length; i++){
                            region[i].regionName = region[i].provinceName;
                            region[i].regionName = clippingProvice(region[i].regionName);
                        }
                        this.region = region;
                    }
                }).catch(function (error) {
                    console.log(error);
                });
                return
            }
            //用于根据合作方ID获取销售区域
            if(this.parterCode !== undefined){
                return
            }else{
                //初始化获取已添加省份,获取已添加区域
                this.$http.post('/address/findProvince.do ', { code: "0", status: "1" }).then(res => {
                    if (res.data.success) {
                        var region = res.data.data;
                        for(let i=0; i<region.length; i++){
                            region[i].regionName = region[i].fprovincename;
                            region[i].regionName = clippingProvice(region[i].regionName);
                        }
                        this.region = region;
                    }
                }).catch(function (error) {
                    console.log(error);
                });
                if(!this.isProduce){
                    var initCity={
                        status:'1',
                        code:33
                    }
                    this.findCity(initCity);
                }
                
            }
            
    },
	data () {
		return {
			region:[],
            place:[],
            regionName:this.regionNameDf === undefined ? '区域:' : this.regionNameDf,
            regiondefault:this.defaultRegion === undefined ? '浙江' : this.defaultRegion,
            pleaseName:this.defaultPlease === undefined ? '温州' : this.defaultPlease,
            pleaseCode:'',
            key:'',
            isDisabled:this.isDisableds
		}
	},
	methods:{
        //获取省
        getRegion(region){
           //根据行政区域获取省
           if(this.parterCode == -1){
               for(let i=0; i<this.region.length; i++){
                    if(region === this.region[i].regionName){
                        var proviece = this.region[i].oprovince;
                    }
               }
               this.finCityProvice(proviece);
               this.regiondefault = region;
               let proviceName ={
                   name:region,
                   fullName:proviece
               }
               this.pleaseName = '';
               this.$emit('getRegionProvice', proviceName);
               return
           }
           //根据销售区域省查询市
           if(this.parterCode == -2){
               for(let i=0; i<this.region.length; i++){
                    if(region === this.region[i].regionName){
                         this.searchSaleCity(this.region[i].provinceCode)
                    }
               }
               this.pleaseName = '';
               this.$emit('getRegionSale', region);
               return
           }
           //获取销售区域省
           if(this.parterCode !== undefined){
                for(let i=0; i<this.region.length; i++){
                     if(region == this.region[i].regionName){
                        this.searchCitySale(this.region[i].provinceCode, this.region[i].pid);
                     }
                }
                this.$emit('getRegionSale', region);
                return;
           }
           var getId={};
           for(let i=0; i<this.region.length; i++){
               if(region === this.region[i].regionName){
                  getId.code = this.region[i].fprovincecode;
               }
           }
           getId.status = '1';
           this.regiondefault = region;
           this.findCity(getId);
           this.pleaseName = ''; 
           let proviceData={
               code:getId,
               name:region
           }
           this.$emit('getRegion', proviceData);
        },
        //获取市
        getPlease(please){
            //根据行政区域省获取市
            if(this.parterCode == -1){
                for(let i=0; i<this.place.length; i++){
                    if(please === this.place[i].regionName){
                        var cityName = this.place[i].ocity;
                    }
                }
                this.pleaseName = please;
                //行政区域市
                this.$emit('getPleaseProvice', cityName);
                return
           }
           //销售区域市
           if(this.parterCode == -2){
                for(let i=0; i<this.place.length; i++){
                    if(please === this.place[i].regionName){
                        var cityName = this.place[i].cityName;
                        var code = this.place[i].cityCode;
                    }
                }
                let data = {
                    name: cityName,
                    saleCode:code
                }
                this.pleaseName = please;
                this.$emit('getSalePlease', data);
                return
           }
           if(this.parterCode !== undefined){
               for(let i=0; i<this.place.length; i++){
                    if(please === this.place[i].regionName){
                        var citycode = this.place[i].cityCode;
                    }
                }
                let data = {
                    name:please,
                    saleCode:citycode
                }
                //销售区域市
                this.$emit('getPleaseSale', data);
               return
           }
           var pleaseCode
           for(let i=0; i<this.place.length; i++){
               if(please === this.place[i].regionName){
                  pleaseCode = this.place[i].fcitycode;
                  var cityName = this.place[i].fcityname;
               }
           }
           let pleaseData ={
               code:pleaseCode,
               name:cityName
           }
           this.pleaseName = please;
           //服务区域市
           this.$emit('getPlease', pleaseData);
        },
        //根据服务区域省份名称获取市
        findCity(getId){
            this.$http.post('/address/findCity.do', getId).then(res => {
                if(res.data.success) {
                    for(let i=0; i<res.data.data.length; i++){
                        res.data.data[i].regionName = res.data.data[i].fcityname;
                        res.data.data[i].regionName = clippingCity(res.data.data[i].regionName);
                    }
                    this.place = res.data.data;
                }
           }).catch(function (error) {
                console.log(error);
           });
        },
        //根据行政区域省份名称获取市
        finCityProvice(name){
            this.$http.post('/partners/findPartnersCity.do', {province:name}).then(res => {
                if(res.data.success) {
                    for(let i=0; i<res.data.data.length; i++){
                        res.data.data[i].regionName = res.data.data[i].ocity;
                        res.data.data[i].regionName = clippingCity(res.data.data[i].regionName);
                    }
                    this.place = res.data.data;
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查询合作方销售区域省
        searchSale(oid, key){
            if(key == ''){
                return
            }
            this.$http.post('/partnersArea/findOptionByPartner.do', {oid:oid,keyarea:key}).then(res => {
                if(res.data.success){
                   console.log('获取销售区域省成功')
                   var region = res.data.data;
                        this.place = [];
                        for(let i=0; i<region.length; i++){
                            region[i].regionName = region[i].provinceName;
                            region[i].regionName = clippingProvice(region[i].regionName);
                        }
                        this.region = region;
                }
            }).catch(function (error) {
                 console.log(error);
            });
        },
        //根据合作方销售区域省查询销售区域市
        searchCitySale(code,pid){
            let data = {
                pid:pid,
                keyarea:this.key,
                provincecode:code
            }
            this.$http.post('/partnersArea/findOptionByProvince.do', data).then(res => {
               if(res.data.success){
                   console.log('获取销售区域市成功');
                   for(let i=0; i<res.data.data.length; i++){
                        res.data.data[i].regionName = res.data.data[i].cityName;
                        res.data.data[i].regionName = clippingCity(res.data.data[i].regionName);
                    }
                    this.place = res.data.data;
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //根据销售区域省查询市
        searchSaleCity(code){
          this.$http.post('/partnersArea/findCityByProvince.do', { 'provinceCode' : code }).then(res => {
               if(res.data.success){
                   console.log('获取销售区域市成功');
                   for(let i=0; i<res.data.data.length; i++){
                        res.data.data[i].regionName = res.data.data[i].cityName;
                        res.data.data[i].regionName = clippingCity(res.data.data[i].regionName);
                    }
                    this.place = res.data.data;
               }
           }).catch(function (error) {
                 console.log(error);
           });
        }
    },
    watch:{
        //获取合作方Pid用于查询销售区域
        parterCode(oid){
            this.pleaseCode = oid;
        },
        //获取合作方拆分键用于查询销售区域
        keyArea(data){
            if(data == undefined){
                return
            }
            this.key = data;
            this.searchSale(this.pleaseCode, data)
        },
        //是否禁用
        isDisableds(val){
            this.isDisabled = val;
        },
        //默认省名称
        defaultRegion(val){
            this.regiondefault = val;
            this.getRegion(val);
        },
        //默认市名称
        defaultPlease(val){
            this.pleaseName = val;
        }
    },
    components:{
        carouseSelect
    }
}
</script>


