export default {
    /**
     * 因为存在一些bug 做层级的控制, 打开Dialog 对话框菜单层级降低， 关闭层级提高
     * 1.但是对于Dialog 对话框  一定要写上内置的关闭close和打开open事件(必填)，并调用下面方法
     * 2.因为弹框中不存在modal-append-to-body可以将遮罩层设置在Dialog节点里的属性，所以:modal-append-to-body=false(必填),遮罩层节点设置在Dialog父节点中
     * this.$store.commit('openZindexModel')
     * this.$store.commit('closeZindexModel')
     */
    openZindexModel() {
        let header = document.getElementById('header')
        let sideMenu = document.getElementById('menu')
        header.style.zIndex = 1000
        sideMenu.style.zIndex = 1000
    },
    closeZindexModel() {
        let header = document.getElementById('header')
        let sideMenu = document.getElementById('menu')
        header.style.zIndex = 3000
        sideMenu.style.zIndex = 3000
    }
}



