<template>
    <div class="partnerManage">
        <tHeader>商品管理</tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <regional-choice
                    @getSalePlease='getPlease'
                    @getRegionSale='getRegion'
                    :parterCode='-2'
                    :defaultPlease='defaultPlease'
                    :defaultRegion='defaultRegion'
                    regionNameDf='销售区域:'
                >
                
                </regional-choice>
                <div class='parter-select-top'>
                    <partner
                        @getSelect='getSelectParter'
                        :codeName='codeName'
                        :partName='partnerName'
                        :isCommday='true'
                    ></partner>
                </div>
                <div class='partner-select'>
                    <carouse-select
                        textName='生效营销方案类型:'
                        :region='region'
                        @getSelect='getSelect'
                    ></carouse-select>
                </div>
                <div class='commodity-btn'>
                    <el-button type="primary" icon="search" @click='search'>搜索</el-button>
                </div>
                <div class='commodity-updata-btn'>
                    <el-button type="primary" @click='updataSetting'>更新设置</el-button>
                </div>
            </div>
            <div class='partner-table'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="序号" width="60">
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="产品销售区域" width="120">
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="合作方名称" width="150">
                    </el-table-column>
                    <el-table-column prop="fproductname" label="产品名称" width="150">
                    </el-table-column>
                    <el-table-column prop="type" label="产品状态" >
                    </el-table-column>
                    <el-table-column prop="moneyType" label="生效营销方案类型" width="150">
                    </el-table-column>
                    <el-table-column label="产品顺序" width="150">
                        <template scope="scope">
                            <div class='commodity-icon'>
                                <i class='el-icon-minus' @click='reduceNum(scope.row.forder, scope.row)'></i>
                                <input class='commodity-number' v-model="scope.row.forder" maxlength="4"/>
                                <i class='el-icon-plus' @click='addNum(scope.row.forder, scope.row)'></i>
                            </div>
                        </template>  
                    </el-table-column>
                    <el-table-column label="名称显示" width="170">
                        <template scope="scope">
                            <el-checkbox v-model="scope.row.checked" @change='showPartName(scope.row)'>显示合作方名称</el-checkbox>
                        </template>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="操作人" >
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="操作时间" width="150">
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import {VERSION} from '@/util/constant.js' 
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fsupplierid =  this.admin.ucompany;   //合作方id
        // //非东京账号登录
        // if(this.admin.ucompany != 100){
        //    this.defaultRegion = clippingProvice(this.admin.oprovince);
        //    this.defaultPlease = clippingCity(this.admin.ocity);
        //    this.partnerName =  this.admin.oname;      //合作方名称
        //    this.search();
        // }
        //区域拆分键
        this.fkeyarea = this.admin.ocode;
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},              //登录信息
            region:[
                {regionName:'全部', value:-1},
                {regionName:'限量团购', value:1},
                {regionName:'限时团购', value:2}
            ],
            fgroupareaid:'',       //服务区域
            fkeyarea:'',            //区域拆分键
            fsupplierid:'',        //合作方ID
            codeName:'',           //合作方查询市
            fgrouptype:'',         //营销方案类型
            tableData:[],
            typeArry:{
                '0':'待上架',
                '1':'审核中',
                '2':'已通过',
                '3':'已驳回'
            }
		}
	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //区域名称code
        getPlease(please){
            this.fgroupareaid = please.saleCode;
            this.codeName = please.name;
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
        },
        //合作方选择
        getSelectParter(data){
            this.fsupplierid = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //加一
        addNum(num, data){
            data.forder = Number(num) + 1;
            if(data.forder > 9999){
                data.forder = 9999;
            }
        },
        //减一
        reduceNum(num, data){
            data.forder = Number(num) - 1;
            if(data.forder < 0){
                data.forder = 0;
            }
        },
        //选择营销方案类型
        getSelect(name){
           for(let i=0; i<this.region.length; i++){
               if(name == this.region[i].regionName){
                   this.fgrouptype = this.region[i].value;
               }
            }
            if(this.fgrouptype == -1){
                this.fgrouptype = '';
            }
        },
        //查询列表
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    var region = res.data.data;
                    for(let i=0; i < region.length; i++){
                        region[i].number = i + 1;
                        region[i].type = this.typeArry[region[i].fonoffstatus];
                        region[i].moneyType = region[i].fgrouptype == 1 ? '限量团购' : region[i].fgrouptype ==  2 ? '限时团购' : '/';
                        region[i].checked = region[i].fisdiplaysn == 0 ? false : true;
                    }
                    this.tableData = region;
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //搜索
        search(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.fsupplierid,
                fonoffstatus:'2',
                fgroupareaid:this.fgroupareaid,
                pagenum:0,
                pagesize:1000,
                fgrouptype:this.fgrouptype
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '请选择区域合作方！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.searchList(data);
        },
        //更新设置
        updataSetting(){
            var fid=[];
            var forder=[];
            var fisdiplaysn = [];
            for(let i=0; i<this.tableData.length; i++){
                fid.push(this.tableData[i].fid);
                forder.push(this.tableData[i].forder);
                fisdiplaysn.push(this.tableData[i].fisdiplaysn);
                if(!Number(this.tableData[i].forder) && this.tableData[i].forder != 0){
                    this.$message({
                        message: '产品顺序请填写0-9999整数！',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:fid.join('#'),
                forders:forder.join('#'),
                fisdiplaysn:fisdiplaysn.join('#')
            }
            if(data.fid == '' && data.forders == '' && data.fisdiplaysn == ''){
                this.$message({
                    message: '请修改列表数据！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.$http.post('/product/updateForder.do', data).then(res => {
                  if(res.data.success){
                        this.$message({
                            message: '更新设置成功！',
                            type: 'success',
                            duration: 2000
                        });
                        this.search();
                  }else{
                      this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                      });
                  }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //是否显示合作方名称
        showPartName(data){
            if(data.checked){
                data.fisdiplaysn = '1'
            }else{
                data.fisdiplaysn = '0'
            }
        }
    },
    computed:{
        
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner
    }  
}
</script>


