<template>
	<div id="order">
		<div class="order__main">
            <tHeader>订单管理</tHeader>
            <div class="order__main__search">
                <ul class="clearfix">
                    <li>
                        <span style="color: #ff4949;">*</span><regional-choice :parterCode='-1' :isProduce="true" :isDisableds="isDisableds" :defaultRegion="defaultRegion" :defaultPlease="defaultPlease" @getRegionProvice="getRegion" @getPleaseProvice="getPlease"></regional-choice>
                    </li>
                    <li style="margin-left: 25px;">
                        <span style="color: #ff4949;">*</span>合&nbsp;&nbsp;作&nbsp;&nbsp;方:
                        <el-select v-model="partnerIndex" :disabled="isDisableds" @change="handleUnlock(partnerIndex)" placeholder="请选择" class="media-partner-width">
                            <el-option
                                v-for="(item, index) in partnerTable"
                                :key="index"
                                :label="item.oname"
                                :value="index">
                            </el-option>
                        </el-select>
                    </li>
                </ul>
                <ul class="clearfix">
					<li class="dayTime">
						周&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;期 :
                        <button class="dayBtn" :style="isToday ? actived : ''" @click="doneToday">当日</button>
                        <button class="dayBtn" :style="isYesterday ? actived : ''" @click="doneYesterday">昨日</button>
					</li>
					<li>
						订单状态 :
						<el-select v-model="search.fstatus" placeholder="请选择" clearable style="width: 150px;">
							<el-option
							v-for="item in orderState"
							:key="item.value"
							:label="item.label"
							:value="item.value">
							</el-option>
						</el-select>
					</li>
					<li>
						客户名称 :
						<el-input v-model.trim="search.custName" placeholder="请输入客户名称" style="width: 150px;"></el-input>
					</li>
				</ul>
				<ul class="clearfix">
					<li style="margin-right: 30px">
						订&nbsp;单&nbsp;号&nbsp; :
						<el-input v-model.trim="search.fchildorderid" placeholder="请输入订单号" style="width: 150px;"></el-input>
					</li>
					<li style="margin-right: 30px">
						产品名称 :
						<el-input v-model.trim="search.fgroupgoodname" placeholder="请输入产品名称" style="width: 300px;"></el-input>
					</li>
					<li>
						规格 :
						<el-input v-model.trim="search.fboxlength" placeholder="长" style="width: 60px;" @blur="toFloat('fboxlength', search.fboxlength, 1)"></el-input>
						*
						<el-input v-model.trim="search.fboxwidth" placeholder="宽" style="width: 60px;" @blur="toFloat('fboxwidth', search.fboxwidth, 1)"></el-input>
						*
						<el-input v-model.trim="search.fboxheight" placeholder="高" style="width: 60px;" @blur="toFloat('fboxheight',search.fboxheight, 1)"></el-input>
					</li>
				</ul>
				<ul class="clearfix">
					<li>
						下单时间 :
						<el-date-picker
						v-model="search.orderStartTime"
						type="datetime"
						placeholder="请输入下单时间起始日"
						:picker-options="pickerOptions"
                        :clearable="false"
						class="date-picker-width"
                        @change="changeTime"
                        >
						</el-date-picker>&nbsp;至&nbsp;
					</li>
					<li style="margin-right:55px;">
						<el-date-picker
						v-model="search.orderEndTime"
						type="datetime"
						placeholder="请输入下单时间截止日"
						:picker-options="pickerOptions"
                        :clearable="false"
						class="date-picker-width"
                        @change="changeTime"
                        >
						</el-date-picker>
					</li>
					<li>
						交期 :
						<el-date-picker
						v-model="search.deliveryStartTime"
						type="datetime"
						placeholder="请输入交期起始日"
						:picker-options="pickerOptionsOther"
                        :clearable="false"
						class="date-picker-width"
                        >
						</el-date-picker>&nbsp;至&nbsp;
					</li>
					<li style="margin-right:30px;">
						<el-date-picker
						v-model="search.deliveryEndTime"
						type="datetime"
						placeholder="请输入交期截止日"
						:picker-options="pickerOptionsOther"
                        :clearable="false"
						class="date-picker-width"
                        >
						</el-date-picker>
					</li>
					<li>
						<el-button type="primary" icon="search" @click="searchFilter" style="width: 110px; margin-top: -2px;">查询</el-button>
					</li>
				</ul>
                <ul>
                    <el-button style="width: 240px;" type="primary" :disabled="btnAuth" @click='allOrderHandout'>订单分发</el-button>
                    <el-button  type="primary" @click="exportExcel">导出excel</el-button>
                </ul>
			</div>
			<div class="order__main__table">
                <el-table
                    v-loading="tableLoading"
                    ref="multipleTable"
                    :data="tableData"
                    border
                    tooltip-effect="dark"
                    style="width: 100%"
                    @selection-change="handleSelectionChange"
                    :row-class-name="tableRowClassName">
                    <el-table-column
                        align="center"
                        type="selection"
                        width="59">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        type="index"
                        label="序号"
                        width="60">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fchildorderid"
                        label="订单号"
                        show-overflow-tooltip
                        width="130">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="交期"
                        show-overflow-tooltip
                        width="153">
                        <template scope="scope">{{ scope.row.fdeliveryString }}</template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="下单时间"
                        show-overflow-tooltip
                        width="153">
                        <template scope="scope">{{ scope.row.fcreatetimeString }}</template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fstatus"
                        label="状态"
                        show-overflow-tooltip
                        width="96">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fgroupgoodname"
                        label="产品名称"
                        show-overflow-tooltip
                        width="118">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmaterialname"
                        label="材料名称"
                        show-overflow-tooltip
                        width="118">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famount"
                        :render-header="numLabel"
                        show-overflow-tooltip
                        width="56">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famountpiece"
                        :render-header="sheetLabel"
                        show-overflow-tooltip
                        width="56">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fseries"
                        label="连做方式"
                        show-overflow-tooltip
                        width="72">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxmodel"
                        label="箱型"
                        show-overflow-tooltip
                        width="60">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxlength"
                        label="长"
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxwidth"
                        label="宽"
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fboxheight"
                        label="高"
                        show-overflow-tooltip
                        width="65">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmateriallength"
                        :render-header="blankingLongLabel"
                        show-overflow-tooltip
                        width="66">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fmaterialwidth"
                        :render-header="blankingWidthLabel"
                        show-overflow-tooltip
                        width="66">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fstavetype"
                        label="压线方式"
                        show-overflow-tooltip
                        width="85">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fhlineformula"
                        label="横压公式"
                        show-overflow-tooltip
                        width="123">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fvlineformula"
                        label="纵压公式"
                        show-overflow-tooltip
                        width="175">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fhline"
                        label="横压"
                        show-overflow-tooltip
                        width="183">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fvline"
                        label="纵压"
                        show-overflow-tooltip
                        width="212">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fproductarea"
                        :render-header="areaLabel"
                        show-overflow-tooltip
                        width="92">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="funitprice"
                        :render-header="priceLabel"
                        show-overflow-tooltip
                        width="98">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="famountprice"
                        :render-header="moneyLabel"
                        show-overflow-tooltip
                        width="73">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fcodeprovince"
                        label="区域"
                        show-overflow-tooltip
                        width="198">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="manufacturerName"
                        label="合作方名称"
                        show-overflow-tooltip
                        width="202">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fpusername"
                        label="客户名称"
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fconsignee"
                        label="联系人"
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="fcontactway"
                        label="联系方式"
                        show-overflow-tooltip
                        width="120">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="faddressdetail"
                        label="地址"
                        show-overflow-tooltip
                        width="282">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="foperator"
                        label="操作人"
                        width="100">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="操作时间"
                        width="153">
                        <template scope="scope">{{ scope.row.foperatetimeString }}</template>
                    </el-table-column>
                    <el-table-column label="操作" align="center" fixed="right" width="105" class="is-show">
                        <!--<el-table-column
                            align="center"
                            label="分发订单"
                            width="104"
                        >
                            <template scope="scope">
                                <el-button type="text" size="small" @click="handleIssue(scope.$index, scope.row)" :disabled="scope.row.orderOut">分发订单</el-button>
                            </template>
                        </el-table-column>-->
                        <el-table-column
                            align="center"
                            label="取消订单"
                            width="104"
                            >
                            <template scope="scope">
                                <el-button type="text" size="small" @click="handleCancel(scope.$index, scope.row)" :disabled="scope.row.orderCancel">取消订单</el-button>
                            </template>
                        </el-table-column>
                    </el-table-column>
                </el-table>
			</div>
            <el-dialog title="提示" :visible.sync="dialogTableVisible" class="cancelDialog" :modal-append-to-body=false @close="$store.commit('closeZindexModel')" @open="$store.commit('openZindexModel')">
                <h4 class="title"><i class="el-icon-warning" style="color: #fe9702; font-size: 34px;"></i><span>亲爱哒,您确认取消订单吗?</span></h4>
                <div class="content">
                    <p>本单消费金额<span style="color: #fec996; font-size: 16px;">{{ cancel.famountprice }}</span>元,赠送积分<span style="color: #fec996; font-size: 16px">{{ cancel.fgiveintegral }}</span>分</p>
                    <p>此订单的主订单详情如下:</p>
                    <p><span style="color: #b6b6b6;">主订单号</span>：&nbsp;{{ cancel.forderId }}</p>
                    <p><span style="color: #b6b6b6;">下单日期</span>：&nbsp;{{ cancel.fcreatetimeString }}</p>
                    <p>
                        <span style="color: #b6b6b6;">支付方式</span>：&nbsp;
                        <i v-if="balanceToggle">余额支付<span style="color: #e84449;">¥{{ cancel.fbalance }}</span><span v-if="cancel.fpaytype != 0">+</span></i>
                        <i v-if="paymentToggle">{{ paymentName }}<span style="color: #e84449;">¥{{ cancel.fthirdpay }}</span></i>
                    </p>
                    <p><span style="color: #b6b6b6;">商品数量</span>：&nbsp;共<span style="color: #e84449;">{{ cancel.productCount }}</span>件商品</p>
                    <p><span style="color: #b6b6b6;">实付金额</span>：&nbsp;<span style="color: #e84449;">¥{{ cancel.ftotalprice }}</span></p>
                    <div style="margin-top: 16px;">
                        取消后将退回金额<el-input v-model.trim="backMoney" style="width: 100px;" :maxlength="14" @blur="doneBackMoney"></el-input>元,
                        收回积分<el-input v-model.trim="backIntegral" style="width: 100px;" :maxlength="14" @blur="doneBackIntegral"></el-input>分
                    </div>
                </div>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogTableVisible = false">取 消</el-button>
                    <el-button type="primary" @click="sureOrder">确 定</el-button>
                </div>
            </el-dialog>
		</div>
        <div class="order__current-page">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="pageSize"
                layout="total, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
	</div>
</template>

<script>
	import tHeader from '@/components/public/tHeader.vue'
    import regionalChoice from '@/components/public/regionalChoice.vue'
    import { get3MonthBefor } from '@/util/common.js'
	export default {
		name: 'order',
		data() {
			return {
                actived: {
                    color: '#1d90e6',
                    borderColor: '#1d90e6',
                },
                isToday: true,
                isYesterday: false,
                tableLoading: false,
                dialogTableVisible: false,
                defaultRegion: '',
                defaultPlease: '',
                balanceToggle: false,       //余额支付
                paymentToggle: false,       //支付方式显示
                paymentName: '',            //支付名字
                //有无接口控制
                isDisableds: false,
                btnAuth: true,
                orderBtnAuth: '',

		        orderState: [
                    {
                        value: '-1',
                        label: '全部'
                    },
			        {
			          value: '1',
			          label: '待付款'
			        },
                    {
			          value: '2',
			          label: '已付款'
			        },
                    {
			          value: '3',
			          label: '已分发'
			        },
                    {
                        value: '4',
                        label: '已入库'
                    },
                    {
                        value: '5',
                        label: '已出库'
                    },
                    {
                        value: '6',
                        label: '已收货'
                    },
                    {
                        value: '7',
                        label: '已取消'
                    }
		        ],
                partnerTable: [],

                partnerIndex: '',

                search: {
                    fkeyarea: '',                             //合作方拆分键
                    manufacturerName: '',                   //合作商 名称
                    fprovince: '',                            //省
                    fcity: '',                                 //市
                    fmanufacturer: '',                        //订单列表上合作方的id

                    "fstatus":'',                             //订单状态
                    "custName":"",                            //客户名称
                    "fchildorderid":"",                      //订 单 号
                    "fgroupgoodname":"",                     //产品名称
                    "fboxlength":'',                          //长
                    "fboxwidth":'',                           //宽
                    "fboxheight":'',                          //高
                    "orderStartTime": new Date(new Date().setHours(0, 0, 0)),  //订单开始时间     这里的日历时间插件有个问题，如果可操作删除，删了不是为空而是undefined，所以不给他可删除的操作
                    "orderEndTime": new Date(),                             //订单结束时间
                    "deliveryStartTime": "",                 //交期开始时间
                    "deliveryEndTime": ""                    //交期结束时间
                },
                cancel: {},
                sureOrderMsg: {},                       //取消订单时传的参数
                backMoney: '',                          //返回金额
                backIntegral: '',                       //返回积分

                //前3个月disabled
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerOptionsOther: {
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor()
                    }
                },

                tableData: [],                      //table数据
                multipleSelection: [],          //复选框选中的行信息

                currentPage: 1,                  //前往第几页
                pageSize: 15,                    //每页显示条目个数
                total: 0,                         //总条数
			}
		},
		components: {
			tHeader,
            regionalChoice
		},
        created() {
            if(this.admin.ucompany != '100') { //非东经
                this.isPartner()
            }
        },
        computed: {
		    admin() {
		        return this.$store.getters.doneAdmin
            }
        },
        mounted() {
		    let area = document.querySelector('.carouse-select>span')
            area.innerHTML = '区&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;域 :'
        },
		methods: {
		    //合作方，非东经
            isPartner() {
                this.isDisableds = true
                this.defaultRegion = this.admin.oprovince
                this.defaultPlease = this.admin.ocity
                this.partnerIndex = this.admin.oname

                this.search.fkeyarea = this.admin.ocode
                this.search.manufacturerName = this.admin.oname
                this.search.fprovince = this.admin.oprovince
                this.search.fcity = this.admin.ocity
                this.search.fmanufacturer = this.admin.ucompany

                this.$http.post('/partners/findPartnersById.do', {"oid": this.admin.ucompany, "ocode": this.admin.ocode}).then(res => {
                    if(res.data.success) {
                        this.orderBtnAuth = res.data.data.configuration
                        if(this.orderBtnAuth == 1) {   //1为有接口 2为无接口
                            this.btnAuth = true
                        }
                        else {
                            this.btnAuth = false
                        }
                    }
                    else {
                        this.$message.error('获取合作方信息失败');
                    }
                }).catch(error => {
                    this.$message.error('获取合作方信息失败');
                    console.log(error)
                })
            },
		    //模糊查询根据后台参数过滤
            filtersParameter() {
                let childOrderModel = {...this.search}
                if(childOrderModel.fstatus === '') {
                    childOrderModel.fstatus = -1
                }
                if(childOrderModel.fboxlength === '') {
                    childOrderModel.fboxlength = -1
                }
                if(childOrderModel.fboxwidth === '') {
                    childOrderModel.fboxwidth = -1
                }
                if(childOrderModel.fboxheight === '') {
                    childOrderModel.fboxheight = -1
                }
                if(childOrderModel.orderStartTime != '') childOrderModel.orderStartTime = formatDate(new Date(childOrderModel.orderStartTime))
                if(childOrderModel.orderEndTime != '') childOrderModel.orderEndTime = formatDate(new Date(childOrderModel.orderEndTime))
                if(childOrderModel.deliveryStartTime != '') childOrderModel.deliveryStartTime = formatDate(new Date(childOrderModel.deliveryStartTime))
                if(childOrderModel.deliveryEndTime != '') childOrderModel.deliveryEndTime = formatDate(new Date(childOrderModel.deliveryEndTime))
                return childOrderModel
            },
		    //获取合作方
            getPartnerList() {
                this.$http.post('/partners/findOption.do', {city: this.search.fcity}).then( res => {
                    if(res.data.success) {
                        this.partnerTable = res.data.data
                    }
                }).catch( error => {
                    console.log('订单管理合作方获取:' +  error)
                })
            },
		    //订单管理请求参数filters 获得订单
            getOrderList() {
                this.tableLoading = true
                let search = {
                    version: '1.0',
                    pageNum: this.currentPage,
                    pageSize: this.pageSize,
                    childOrderModel: this.filtersParameter()
                }
                this.$http.post('/order/load.do', search).then( res => {
                    if(res.data.success) {
                        if(!res.data.data.length) {
                            this.$message({
                                message: '在该条件下没有数据',
                                duration: 1000,
                                type: 'info'
                            });
                            this.tableLoading = false
                        }
                        this.tableData = res.data.data
                        this.total = res.data.totalCount
                        for(let item of  this.tableData) {
                            switch(item.fstatus) {
                                case 1:
                                    item.fstatus = '待付款'
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                case 2:
                                    item.fstatus = '已付款'
                                    if(this.admin.ucompany == '100') {  //东经
                                        item.orderOut = true
                                        item.orderCancel = false
                                    }
                                    else {  //合作方
                                        if(this.orderBtnAuth == 1) { //合作方有接口
                                            item.orderOut = true
                                            item.orderCancel = false
                                        }
                                        else {      //合作方无接口
                                            item.orderOut = false
                                            item.orderCancel = false
                                        }
                                    }
                                    break
                                case 3:
                                    item.fstatus = '已分发'
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 4:
                                    item.fstatus = '已入库'
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 5:
                                    item.fstatus = '已出库'
                                    item.orderOut = true
                                    item.orderCancel = false
                                    break
                                case 6:
                                    item.fstatus = '已收货'
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                case 7:
                                    item.fstatus = '已取消'
                                    item.orderOut = true
                                    item.orderCancel = true
                                    break
                                default:
                                    item.fstatus = '状态有误'
                                    item.orderOut = true
                                    item.orderCancel = true
                            }
                        }
                        for(let item of  this.tableData) {
                            switch(item.fboxmodel) {
                                case '1':
                                    item.fboxmodel = '普通箱型'
                                    break
                                case '2':
                                    item.fboxmodel = '全包箱型'
                                    break
                                case '3':
                                    item.fboxmodel = '半包箱型'
                                    break
                                case '4':
                                    item.fboxmodel = '有底无盖箱型'
                                    break
                                case '5':
                                    item.fboxmodel = '有盖无底箱型'
                                    break
                                case '6':
                                    item.fboxmodel = '天地盖箱型'
                                    break
                                default:
                                    item.fboxmodel = '其他箱型'
                            }
                        }
                    }
                    else {
                        this.tableLoading = false
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '错误',
                        message: '获得订单列表失败'
                    });
                    this.tableLoading = false
                    console.log('获得订单列表' + error)
                })
            },
            //分发订单方法
            distributeOrder(childOrderIdArr){
                let parameter = {
                    "version": "1.0",
                    "fmanufacturer": this.search.fmanufacturer,
                    "operator": '',                         //操作人的id   登录得时候获得
                    "fkeyarea": this.search.fkeyarea,
                    "childOrderIds": childOrderIdArr
                }
                this.$http.post('/order/distributionOrder.do', parameter).then( res => {
                    if(res.data.success) {
                        this.$message({
                            message: '订单分发成功',
                            duration: 1000,
                            type: 'success'
                        });
                        this.getOrderList()
                    }
                    else {
                        this.$message({
                            message: '分发订单失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '错误',
                        message: '操作失败'
                    });
                    console.log(error)
                })
            },
		    //选择地区
            getRegion(region) {
                this.search.fprovince = region.name
            },
            getPlease(please) {
                this.partnerIndex = ''
                if(please.name != '') {
                    this.search.fcity = please
                    this.getPartnerList()
                }
            },
            //当选中合作方时
            handleUnlock(index) {
                try {
                    this.search.fmanufacturer = this.partnerTable[index].id
                     this.search.manufacturerName = this.partnerTable[index].oname
                     this.search.fkeyarea = this.partnerTable[index].ocode
                }
                catch(e) {}
            },
            /**
             * 当日 昨日
             */
            doneToday() {
                let start = new Date()
                let end = new Date()
                start = start.setHours(0, 0, 0)
                this.search.orderStartTime =  start
                this.search.orderEndTime =  end
                this.$nextTick(function () {
                    this.isToday = true
                    this.isYesterday = false
                })
            },
            doneYesterday() {
                let time = new Date()
                time = new Date(time.getTime()-8.64e7)
                let start = time.setHours(0, 0, 0)
                let end = time.setHours(23, 59, 59)
                this.search.orderStartTime =  start
                this.search.orderEndTime =  end
                this.$nextTick(function () {
                    this.isToday = false
                    this.isYesterday = true
                })
            },
            changeTime() {
                this.isToday = false
                this.isYesterday = false
            },
            toFloat(name, value, num) {
                if(value === '') return
                if(/^\d+(\.\d+)?$/.test(value)) {
                    this.search[name] = (+value).toFixed(num)
                }
                else {
                    this.search[name] = ''
                    this.$message({
                        message: '格式必须为数字',
                        duration: 1000,
                        type: 'warning'
                    });
                }
            },
            doneToFixed(key, index) {
                let num = this[key]
                if(num === '') return
                let v = parseInt(this[key]) + ''
                if(!/^\d+(\.\d+)?$/.test(num)) {
                    this[key] = ''
                    this.$message({
                        message: '请输入数字',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else if(v.length > 10) {
                    this[key] = ''
                    this.$message({
                        message: '不能超过10位',
                        duration: 1000,
                        type: 'info'
                    });
                }
                else {
                    if(index === 0) {
                        this[key] = num
                    }
                    else {
                        this[key] = (+num).toFixed(index)
                    }
                }
            },
            doneBackMoney() {
                this.doneToFixed('backMoney', 2)
            },
            doneBackIntegral() {
                this.doneToFixed('backIntegral', 0)
            },
            //设定斑马线
            tableRowClassName(row, index) {
                if (index%2 === 1) {
                    return 'active-row';
                }
                return '';
            },
            //当选择任何一个复选框按钮时触发 将这行数据添加到multipleSelection中
            handleSelectionChange(row) {
                this.multipleSelection = row;
            },
            //设置表头样式
            numLabel(h, { column, $index }) {
                return h('div',[
                    '只数',
                    //h('p', '(只)')
                ])
            },
            sheetLabel(h, { column, $index }) {
                return h('div',[
                    '片数',
                    //h('p', '(片)')
                ])
            },
            blankingLongLabel(h, { column, $index }) {
                return h('div',[
                    '落料长',
                    //h('p', '(cm)')
                ])
            },
            blankingWidthLabel(h, { column, $index }) {
                return h('div',[
                    '落料宽',
                    //h('p', '(cm)')
                ])
            },
            areaLabel(h, { column, $index }) {
                return h('div',[
                    '面积',
                    //h('p', '(平方厘米)')
                ])
            },
            priceLabel(h, { column, $index }) {
                return h('div',[
                    '单价',
                    //h('p', '(元/平方米)')
                ])
            },
            moneyLabel(h, { column, $index }) {
                return h('div',[
                    '金额',
                    //h('p', '(元)')
                ])
            },

            //查询 分发订单，导出excel,撤销分发，取消分发  点击触发事件
            searchFilter() {
                if(this.admin.ucompany != '100') {
                    if(this.orderBtnAuth === '') {
                        return
                    }
                }
                if(this.partnerIndex !== '') {
                    this.getOrderList()
                }
                else {
                    this.$message({
                        message: '缺少必填项',
                        duration: 1000,
                        type: 'warning'
                    });
                }

            },
            allOrderHandout() {
                if(!this.tableData.length) {
                    this.$message({
                        message: '请确保有数据',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                let text = ''
                if(this.multipleSelection.length) {
                    text = '此操作将分发订单, 是否继续?'
                }
                else {
                    text = '您将对所有已付款状态订单进行订单分发, 是否继续?'
                }
                this.$confirm(text, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let childOrderIdArr = []
                    if(this.multipleSelection.length) {
                        //如果复选框中只能对已付款的订单进行分发
                        for(let item of this.multipleSelection) {
                            if(item.fstatus != '已付款') {
                                this.$message({
                                    message: '只能对已付款的订单进行分发',
                                    duration: 1000,
                                    type: 'warning'
                                });
                                return
                            }
                        }
                        for(let item of this.multipleSelection) {
                            if(item.fstatus == '已付款') {
                                childOrderIdArr.push({"childOrderId": item.fchildorderid})
                            }
                        }
                    }
                    this.distributeOrder(childOrderIdArr)
                })
            },
            exportExcel() {
                if(!this.tableData.length) {
                    this.$message({
                        message: '请确保有数据',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.$confirm('此操作将导出excel, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = []
                    if(this.multipleSelection.length) {
                        for(let item of this.multipleSelection) {
                            ids.push(item.fchildorderid)
                        }
                    }
                    let childOrderModel = {ids, ...this.filtersParameter()}
                    let parameter = {
                        version: '1.0',
                        flag: 1,
                        childOrderModel
                    }
                    this.$http.post('/order/exportExcel.do', parameter).then( res => {
                        if(res.data.success) {
                            location.href = res.data.data.furl
                        }
                        else {
                            this.$message({
                                message: '导出excel失败',
                                duration: 1000,
                                type: 'error'
                            });
                        }
                    }).catch( error => {
                        this.$notify.error({
                            title: '错误',
                            message: '操作失败'
                        });
                        console.log(error)
                    })
                })
            },
            handleIssue(index, row) {
                this.$confirm('此操作将分发订单, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.distributeOrder([{"childOrderId": row.fchildorderid}])
                })
            },
            handleCancel(index, row) {
                //取消订单确定时参数
                this.sureOrderMsg = {
                    version: '1.0',
                    childOrderId: row.fchildorderid,              //子订单号
                    operator: '',                                   //操作人的id   登录得时候获得
                    fkeyarea: row.fgokeyarea,                       //客户拆分键fgokeyarea
                    fpartnerFkeyarea: row.fkeyarea,                //客户拆分键fgokeyarea
                    userId: row.fpuserid,                           //子订单上的客户id
                    keyArea: row.fgokeyarea,                        //子订单上的客户拆分键
                    amount: '',                                     //取消退款的金额
                    forderId: row.fchildorderid,                  //子订单号
                    fuserid: row.fpuserid,                           //子订单上的客户id
                    fintegeral: '',                                  //取消退款时扣除的积分
                    fpartnerid: row.fmanufacturer,                  //子订单上的合作方id
                    forderid: row.fchildorderid,                    //子订单号
                    fmanufacturerkeyarea: row.fkeyarea,             //取子订单号拆分件
                    fmktplanchangeid: row.fmarktingplanid,           //子订单变更id
                    fsalevolume: row.fproductarea,                   //子订单的面积
                    fmanufacturer: row.fmanufacturer               //子订单上的合作方id
                }

                let parameter = {
                    "version": "1.0",
                    "childOrderId": row.fchildorderid,
                    "fkeyarea": row.fkeyarea
                }
                this.cancel = {}
                this.$http.post('/order/cancelOrderInfo.do', parameter).then( res => {
                    if(res.data.success) {
                        this.cancel = res.data.data
                        this.backMoney = res.data.data.famountprice
                        this.backIntegral = res.data.data.fgiveintegral
                        /**
                         * 支付方式
                         * 0余额支付
                         * 1微信
                         * 2支付宝
                         * 3银行卡
                         * 10微信+余额
                         * 20支付宝+余额
                         * 30银行卡+余额
                         */
                        if(this.cancel.fpaytype === 0) {
                            this.balanceToggle = true
                            this.paymentToggle = false
                            return
                        }
                        if(this.cancel.fpaytype === 1) {
                            this.paymentName = '微信支付'
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 2) {
                            this.paymentName = '支付宝支付'
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 3) {
                            this.paymentName = '银行卡支付'
                            this.balanceToggle = false
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 10) {
                            this.paymentName = '微信支付'
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 20) {
                            this.paymentName = '支付宝支付'
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                        if(this.cancel.fpaytype === 30) {
                            this.paymentName = '银行卡支付'
                            this.balanceToggle = true
                            this.paymentToggle = true
                            return
                        }
                    }
                    else {
                        this.$message({
                            message: '数据获取失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '错误',
                        message: '操作失败'
                    });
                    console.log(error)
                })
                this.dialogTableVisible = true
            },
            sureOrder() {
                if(this.backMoney === '' || this.backIntegral === '') {
                    this.$message({
                        message: '请确保退回金额和收回积分有值',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.sureOrderMsg.amount = this.backMoney
                this.sureOrderMsg.fintegeral = this.backIntegral
                this.$http.post('/order/cancelOrder.do', this.sureOrderMsg).then( res=> {
                    if(res.data.success) {
                        this.$message({
                            message: '取消订单成功',
                            duration: 1000,
                            type: 'success'
                        });
                        this.dialogTableVisible = false
                        this.getOrderList()
                    }
                    else {
                        this.$message({
                            message: '取消订单失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$notify.error({
                        title: '错误',
                        message: '操作失败'
                    });
                    console.log(error)
                })
            },
            //分页事件
            handleCurrentChange(page) {     //点击页数触发
                this.getOrderList()
            }
		},
		watch: {
            tableData() {
                this.tableLoading = false
            }
        }
	}
    function formatDate(now) {
        let year=now.getFullYear();
        let month=now.getMonth()+1;
        let date=now.getDate();
        let hour=now.getHours();
        let minute=now.getMinutes();
        let second=now.getSeconds();
        let arr = [month, date, hour, minute, second]
        if(month<10) {
            month = '0' + month
        }
        if(date<10) {
            date = '0' + date
        }
        if(hour<10) {
            hour = '0' + hour
        }
        if(minute<10) {
            minute = '0' + minute
        }
        if(second<10) {
            second = '0' + second
        }
        return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
    }
</script>

<style>
	@import '../../../assets/style/order.css';
</style>



