<template>
    <div id="menu" :style="{width: activeWidth, overflow: getBarsState ? 'hidden' : 'visible'}">
        <div class="innerMenu" :style="{'overflow-y': getBarsState ? 'scroll' : 'visible'}" v-if="reset">
            <div class="bars-toggle" @click="barsBtnToggle">
                <i class="fa fa-bars" :class="[getBarsState ? 'xicon' : 'yicon']"></i>
            </div>
            <el-menu :default-active="newActiveIndex" class="el-menu-vertical-demo" :unique-opened="true" :collapse="!getBarsState">
                <el-menu-component  v-for="(item, index) in treeModel" :key="index" :tree="item" :get-bars-state="getBarsState"></el-menu-component>
            </el-menu>
        </div>
	</div>
</template>

<script>
    import elMenuComponent from './MenuList.vue'
	export default {
		data() {
			return {
                reset: true,                     //重置
			}
		},
        props: ['treeModel', 'activeIndex'],
		computed: {
            //设置menu的width
            activeWidth() {
                return this.$store.state.home.menuWidth
            },
            //得到伸缩开关状态
            getBarsState() {
                return this.$store.state.home.barsState
            },
            //对于3级以上的菜单路由的显示
            newActiveIndex() {
                if(this.$route.path.split('/').length > 4) {    //["", "home", "operation", "partner"]  可以坚持到6级菜单
                    return this.$route.path.split('/')[3]
                }
                else {
                    return this.activeIndex
                }
            }
		},
		components: {
            elMenuComponent,
		},
		methods: {
            barsBtnToggle() {
                this.$store.commit('changeMenuWidth')
            },
		},
        watch: {
            '$route' (to, from) {
               /* setTimeout(() => {
                    this.activeIndex = this.$store.state.menu.activeChildMenu
                    console.log(this.activeIndex)
                })*/
            },
            //默认跳转到第一个有权限的子菜单，监听子菜单更新,重新渲染
            treeModel(ary){
                this.reset = false;
                setTimeout(() => {
                    this.reset = true;
                })
                for(var i=0; i<ary.length; i++){
                    //权限不在子菜单
                    if(ary[i].auth && ary[i].childrenNode.length == 0){
                        this.$router.push({name: ary[i].menu.url});
                        setTimeout(()=> {
                            this.activeIndex = ary[i].menu.url;
                        }, 1);
                        return
                    }
                    //权限在子菜单
                    if(ary[i].auth && ary[i].childrenNode.length > 0){
                        for(let l=0; l<ary[i].childrenNode.length; l++){
                            if(ary[i].childrenNode[l].auth && ary[i].childrenNode[l].childrenNode.length == 0){
                                this.$router.push({name: ary[i].childrenNode[l].menu.url});
                                setTimeout(()=> {
                                    this.activeIndex = ary[i].childrenNode[l].menu.url;
                                }, 1);
                                return
                            }
                        }
                    }
                }
            }
        }
	}
</script>

<style lang="scss">
	#menu {
        width: 180px;
        transition: .3s width ease-in-out,.3s padding-left ease-in-out,.3s padding-right ease-in-out;
        background: #333743;
        min-height: 100%;
        position: fixed;
        top: 50px;
        .el-menu-item.is-active {
            color: #20a0ff;
        }
        .bars-toggle {
            height: 30px;
            background: #42475b;
            text-align: center;
            line-height: 30px;
            font-size: 16px;
            color: #fff;
            cursor: pointer;
            .fa-bars {
                transition: all .1s;
            }
            .xicon {
                transform: rotate(0deg);
            }
            .yicon {
                transform: rotate(90deg);
            }
        }
        .innerMenu {
            /*overflow-y: scroll;*/
            min-height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
        }
        /*修改element-ui内置样式*/
        .el-menu {
            background: #333743;
        }
        .el-menu-item, .el-submenu__title {
            color: #d0d1d3;
        }
        .el-menu-item:hover {
            background: #38adff;
            color: #fff;
        }
        .el-submenu .el-menu {
            background: #333743;
        }
        .el-submenu .el-menu-item:hover, .el-submenu__title:hover {
            background: #38adff;
            color: #fff;
        }
        .el-menu-vertical-demo:not(.el-menu--collapse) {
            box-sizing: border-box;
            width: 180px;
            padding-bottom:100px;
        }
	}
</style>



