<template>
    <div class="window-warp" v-show="windowShow">
        <h3>提示</h3>
        <span class="window-closeBtn" @click="windowClose()"></span>
        <p class="window-info">{{info}}</p>
        <div class="window-btnGroup">
            <button class="window-cancelBtn" @click="windowClose()">取消</button>
            <button class="window-yesBtn" @click="sure()">确定</button>
        </div>
    </div>
</template>
<script>
    export default{
        props:['info','windowShow'],
        methods: {
            sure(){
                this.$emit('sure');
            },
            windowClose(){
                this.$emit('windowClose');
            }
        }
    }
</script>
<style>
    .window-warp{
        width:422px;
        height: 180px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 100000;
    }
    .window-warp h3{
        font-size: 20px;
        line-height: 56px;
        padding-left: 20px;
    }
    .window-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .window-info{
        margin-top: 16px;
        height: 36px;
        padding-left:87px;
        line-height: 36px;
        background: url(../../../../../static/images/publicImages/info.png) no-repeat 30px 0;
    }
    .window-btnGroup{
        position: absolute;
        bottom: 20px;
        right: 20px;
    }
    .window-btnGroup button{
        width:80px;
        height: 34px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        cursor:pointer;
        outline: none;
    }
    .window-btnGroup .window-cancelBtn{
         color:#666;
         border:1px solid #c6c6c6;
         background: #fff;
     }
    .window-btnGroup .window-yesBtn{
        margin-left: 18px;
    }
</style>



