<template>
    <div id="senListTable">
        <el-table border ref="singleTable" :data="listData" highlight-current-row  style="width: 100%"   height="680">
            <el-table-column  label="序号" width="70" align="center">
                <template scope="scope">
                    <span>{{scope.$index + size*(currentPage - 1)+1}}</span>
                </template>
            </el-table-column>
            <el-table-column property="fsupplierarea" label="区域" align="center">
            </el-table-column>
            <el-table-column property="fsuppliername" label="合作方" align="center">
            </el-table-column>
            <el-table-column property="fgrouparea" label="产品销售区域" align="center">
            </el-table-column>
            <el-table-column property="fproductname" label="产品名称" align="center">
            </el-table-column>
            <el-table-column property="name"  align="center" :render-header="moneyIntergral" width="400">
                    <template scope="scope">
                        付款金额 <input type="text" class="text" :disabled="errorList[scope.$index].editSaveStatus" v-model="data[scope.$index].cellPayment" :class="{errorInput:errorList[scope.$index].money}"  @input="moneyNumCheck(scope.$index)" > 元：赠送 <input type="text"  :disabled="errorList[scope.$index].editSaveStatus" :class="{errorInput:errorList[scope.$index].intergral}" @input="intergralNumCheck(scope.$index)" class="text" v-model="data[scope.$index].cellIntegral">积分
                    </template>
            </el-table-column>
            <el-table-column property="name"  align="center" label="操作" width="100">
                    <template scope="scope">
                        <span class="editSave" @click="editSave(scope.$index)">{{errorList[scope.$index].editSaveStatus?'修改':'保存'}}</span>
                    </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default{
        props:['errorList','currentPage','size','data'],
        data(){
            return{

            }
        },
        computed: {
          listData: function () {
            return this.data;
          }
        },
        methods: {
            // 付款金额 和积分数量 布局
            moneyIntergral(h, { column, $index }) {
                //标题布局要用
                return h('span', [
                    '付款金额',
                    h('span', '积分数量')
                ])
            },
            //输入款检验
            moneyNumCheck(index){
                //this.listData[index].cellPayment = this.listData[index].cellPayment.replace(/\D+|^0/g,'');
                this.listData[index].cellPayment = this.listData[index].cellPayment.replace(/\D+/g,'');
                if(this.listData[index].cellPayment.charAt(0) === '0'
                &&this.listData[index].cellPayment.charAt(1) !== ''){
                   this.listData[index].cellPayment = 0;
                }
            },
            intergralNumCheck(index){
                this.listData[index].cellIntegral = this.listData[index].cellIntegral.replace(/\D+/g,'');
                if(this.listData[index].cellIntegral.charAt(0) === '0'
                &&this.listData[index].cellIntegral.charAt(1) !== ''){
                   this.listData[index].cellIntegral = 0;
                }
            },
            // 保存
            editSave(index){
                this.editSaveStatus = !this.editSaveStatus;
                this.$emit('editSave',index);
            }
        }
    }
</script>
<style>
    #senListTable th:nth-child(6){
        text-align: inherit;
    }
    #senListTable th:nth-child(6) .cell > span{
        padding-left: 125px;
    }
    #senListTable th:nth-child(6) .cell > span > span{
        padding-left: 78px;
    }
    #senListTable .text{
        border: 1px solid #c6c6c6;
        width: 78px;
        height:22px;
    }
    #senListTable .errorInput{
        border-color:#f57777;
        box-shadow:0px 0px 3px red ;
    }
    #senListTable input{
        text-align: center;
    }
    #senListTable .editSave{
        cursor:pointer;
        color: #20a0ff;
    }
    #senListTable div.cell{
        padding: 0;
    }
</style>



