<template>
    <div class='carouse-close-propt'>
        <el-dialog title="提示" :visible.sync="dialogVisibles" :show-close='false' @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
            <img src="../../../static/images/publicImages/close.png" class='carouse-close-iconClose' @click='closeVisible'>
            <img src='../../../static/images/propt.png' class='carouse-close-icon'>
            <span class='carouse-close-delete' :title='text'>{{text}}</span>
            <div :class="[needCancel ? 'carouse-close-btn' : 'carouse-close-btn-one']">
                <el-button @click='closeVisible' v-if='needCancel'>取消</el-button>
                <el-button type="primary" @click='sure'>确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    props:['dialogVisible', 'text', 'needCancel'],
	data () {
		return {
            dialogVisibles:this.dialogVisible
		}
	},
    methods:{
        closeVisible(){
            this.$emit('closeVisible')
        },
        sure(){
            this.$emit('sure')
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    watch:{
        dialogVisible(val){
             this.dialogVisibles = val;
        }
    }
}
</script>



