import axios from 'axios'
import { MessageBox } from 'element-ui';
import qs from 'qs'

import store from '@/store'

//对axios的配置
axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.baseURL = '/djcrm';   //配置根接口地址

//POST传参序列化(添加请求拦截器)
axios.interceptors.request.use((config) => {
	//确保一个网页只登录一个用户
    if(config.url != '/djcrm/inneruser/handleoncetoken.do') {
        if(localStorage.getItem('admin') == null) {
            // localStorage.clear();
            // location.href = store.state.base.loginIp
            return
        }
        if(store.getters.doneAdmin.uname != JSON.parse(localStorage.getItem('admin')).uname) {
            MessageBox.alert('请刷新页面或重新登录,建议一个页面只登录一个用户', '提示', {
                confirmButtonText: '确定',
                type: 'error',
                callback: action => {
                    // localStorage.clear();
                    // location.href = store.state.base.loginIp
                }
            })
        }
    }
    return config;
},(error) =>{
    return Promise.reject(error);
});

//返回状态判断(添加响应拦截器)
axios.interceptors.response.use((res) =>{
	//对响应数据做些事
    if(res.data.code == -10001) {
        MessageBox.alert('您已长时间未操作系统，请重新登录', '提示', {
            confirmButtonText: '确定',
            type: 'error',
            callback: action => {
                localStorage.clear();
                location.href = store.state.base.loginIp
            }
        })
    }
    if(res.data.code == -10002) {
        MessageBox.alert('抱歉,您无该权限，请于相关人员联系', '提示', {
            confirmButtonText: '确定',
            type: 'warning',
            callback: action => {

            }
        })
    }
    return res
}, (error) => {
    return error;
});

export default axios



