<template>
    <div class="widthdrawCash-queryList-warp">
        <ul class="clearfix">
            <li>
                类别：
                <el-select  v-model="timetype" placeholder="请选择" clearable>
                    <el-option v-for="item,index in typeList" :key="index" :label="item.type" :value="item.value">
                    </el-option>
                </el-select>
            </li>
            <li>
                日期查询 :
                <el-date-picker v-model="startTime" type="datetime" placeholder="开始日期"  format = "yyyy-MM-dd HH:mm"  :picker-options="pickerStart">
                </el-date-picker>
                至
                <el-date-picker v-model="endTime" type="datetime"  placeholder="结束日期"  format = "yyyy-MM-dd HH:mm"  :picker-options="pickerEnd">
                </el-date-picker>
            </li>

            <li>用户手机号 : <input type="text" v-model="mobileNumber" @input="mobileInput"></li>
            <li>
                认证名 : <input type="text" v-model="username" >
            </li>
            <li><span class="widthdrawCash-queryList-query" @click="query">查询</span></li>
            <li><span class="widthdrawCash-queryList-export">导出excel</span></li>
        </ul>
    </div>
</template>
<script>
    /**
     * Created by qxl on 2017/8/28.
     */
    import {VERSION} from '@/util/constant.js'
    import { get3MonthBefor } from '@/util/common.js'
    export default{
        data(){
            return {
                pickerStart:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerEnd:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                endTime:'', // 结束时间
                startTime:'', //开始时间
                mobileNumber:'',  //用户手机号
                username:'', //用户名
                nameList:[

                ],
                timetype:'',
                typeList:[
                    {
                        type:'业务时间',
                        value:'1',

                    },{
                        type:'提现时间',
                        value:'2',
                    }
                ]
            }
        },
        methods:{
            //
            mobileInput(){
              this.mobileNumber = this.mobileNumber.replace(/\D/g, '');
            },
             //获取时间戳
            getTime(val){
                return new Date(val).getTime();
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            useTime(value){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value);
                var year = time.getFullYear();
                var month = time.getMonth() + 1<10? '0'+ (time.getMonth() + 1):time.getMonth() + 1;
                var date = time.getDate() <10 ? '0'+ time.getDate():time.getDate();
                var hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours();
                var min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
                var sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                return `${year}-${month}-${date} ${hour}:${min}`
            },
            //查询 向父组件传递当前输入的信息
            query(){
                if(this.getTime(this.endTime)<this.getTime(this.startTime)){
                    this.msg('对不起亲爱哒，结束日期必须大于开始日期！','error');
                    return;
                }
                if(this.startTime=='' && this.endTime !=''){
                    this.msg('请填写开始时间再进行查询！','error');
                    return;
                }
                if(this.startTime !='' && this.endTime ==''){
                    this.msg('请填写结束时间再进行查询！','error');
                    return;
                }
                if((this.timetype=='' && this.startTime !='' && this.endTime !='')
                    || (this.timetype !='' && this.startTime =='' && this.endTime =='')) {
                    this.msg('类别和日期必须同时选择查询！', 'error');
                    return
                }
                if(this.mobileNumber.length > 11){
                    this.msg('手机号不能大于11位!','error');
                    return
                }
                var queryJson ={
                    startTime:this.useTime(this.startTime),
                    endTime:this.useTime(this.endTime),
                    timetype:this.timetype,
                    mobileNumber: this.mobileNumber,
                    username:this.username
                }
                this.$emit('query', queryJson)
            }
        }
    }
</script>
<style>
    .widthdrawCash-queryList-warp ul li{
        float:left;
        margin-right:15px;
        margin-bottom: 10px;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(2){
        margin-bottom: 5px;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(1) .el-input__inner,
    .widthdrawCash-queryList-warp ul li:nth-child(2) .el-input__inner{
        height:35px;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(4){
        margin-right:10px;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(5) {
        margin-right:10px;
    }
    .widthdrawCash-queryList-warp .el-date-editor.el-input{
        height:34px;
        line-height: 34px;
        width: 170px;
    }
    .widthdrawCash-queryList-warp ul{
        padding:20px 0;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(1) input,
    .widthdrawCash-queryList-warp ul li:nth-child(4) input{
        width:130px;
        height:30px;
    }
    .widthdrawCash-queryList-warp ul li:nth-child(3) input,
    .widthdrawCash-queryList-warp ul li:nth-child(4) input{
        width:130px;
        height:33px;
        border:1px solid #c6c6c6;
        border-radius:4px;
    }
    #widthdrawCash-warp .el-input__icon+.el-input__inner {
        padding-right: 0px;
    }
    .widthdrawCash-queryList-query,
    .widthdrawCash-queryList-export{
        height:32px;
        border-radius:4px;
        cursor:pointer;
        color:#fff;
        display:inline-block;
        line-height:32px;
        font-size:14px;
    }
    .widthdrawCash-queryList-query{
        background: url(../../../../../../static/images/publicImages/search.png) no-repeat 26px 6px #38acff;
        padding-left:56px;
        width:56px;
    }
    .widthdrawCash-queryList-export{
        background: url(../../../../../../static/images/publicImages/export.png) no-repeat 10px 6px #38acff;
        padding-left:37px;
        width:75px;
    }
</style>



