<<template>
    <div class="confirmWindow-warp" v-show="confirmWindowShow">
        <h3>提示</h3>
        <span class="confirmWindow-closeBtn" @click="confirmWindowClose()"></span>
        <p class="confirmWindow-info">{{info}}</p>
        <div class="confirmWindow-btnGroup">
            <button class="confirmWindow-cancelBtn" @click="confirmWindowClose()">取消</button>
            <button class="confirmWindow-yesBtn" @click="sure()">确定</button>
        </div>
    </div>
</template>
<script>
    export default{
        props:['info','confirmWindowShow'],
        methods: {
            sure(){
                this.$emit('sure');
            },
            confirmWindowClose(){
                this.$emit('confirmWindowClose');
            }
        }
    }
</script>
<style>
    .confirmWindow-warp{
        width:422px;
        height: 180px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 100000;
    }
    .confirmWindow-warp h3{
        font-size: 20px;
        line-height: 56px;
        padding-left: 20px;
    }
    .confirmWindow-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .confirmWindow-info{
        margin-top: 16px;
        height: 36px;
        padding-left:87px;
        line-height: 36px;
        font-size: 14px;
        background: url(../../../static/images/publicImages/info.png) no-repeat 30px 0;
    }
    .confirmWindow-btnGroup{
        position: absolute;
        bottom: 20px;
        right: 20px;
    }
    .confirmWindow-btnGroup button{
        width:80px;
        height: 34px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        cursor:pointer;
        outline: none;
    }
    .confirmWindow-btnGroup .confirmWindow-cancelBtn{
         color:#666;
         border:1px solid #c6c6c6;
         background: #fff;
     }
    .confirmWindow-btnGroup .confirmWindow-yesBtn{
        margin-left: 18px;
    }
</style>




