<template>
    <div id="intergralBalance-warp">
        <div class="intergralBalance-warp-padding">
            <div class="intergralBalance-radius">
                <tHeader>积分余额</tHeader>
                <div class="intergralBalance-tableQuery-padding">
                    <ul class="clearfix intergralBalance-Query">
                        <li class="intergralBalance-input"  v-show="!isSee">
                            客户名称：
                            <input placeholder="请输入客户名称" v-model="username">
                            手机号：
                            <input placeholder="请输入手机号" v-model="phone" @input="phoneNum">
                        </li>
                        <li v-show="!isSee">
                            <el-button type="primary" icon="search" @click="query">搜索</el-button>
                        </li>
                        <li v-show="isSee">
                            <el-button type="primary" icon="search" @click="back">返回</el-button>
                        </li>
                    </ul>
                    <beforeTable @seeInfo="see" @adjust="adjust" :beforeShow="beforeShow" :tableData="tableData" :currentPage="currentPage" :size="size"></beforeTable>
                    <seeTable :seeShow="seeShow" :tableData="queryTableData" :currentPage="seeCurrentPage" :size="seeSize"></seeTable>
                </div>
            </div>
        </div>
        <div class="intergralBalance-pagination">
            <div v-show="!isSee">
                <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total" :small="false" @current-change="handleCurrentChange" :current-page="currentPage"
                               :page-size="size"
                               @size-change="handleSizeChange"
                               :page-sizes="[15, 20, 30, 100]">
                </el-pagination>
            </div>
            <div v-show="isSee">
                <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="seeTotal" :small="false" @current-change="seeHandleCurrentChange" :current-page="seeCurrentPage"
                               :page-size="seeSize"
                               @size-change="seeHandleSizeChange"
                               :page-sizes="[15, 20, 30,100]">
                </el-pagination>
            </div>
        </div>
        <div class="intergralBalance-masker" v-show="mask"></div>
        <adjustWindow  :adjustData="adjustJson" :adjustPrompt="adjustInfo" @adjustWindowClose="adjustClose()" @sure="makeSure" :adjustWindowShow="adjustWindow" ></adjustWindow>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import beforeTable from './beforeTable.vue'
    import adjustWindow from './adjustWindow.vue'
    import seeTable from './seeTable.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        created(){
            //获取用户积分列表
            this.getTable(true);
        },
        components:{
            tHeader,
            beforeTable,
            adjustWindow,
            seeTable
        },
        data(){
            return{
                size:15,     // 一页容纳数据的量
                currentPage:1, // 当前页码
                total:0, // 当前总量
                seeSize:15, // 查看一页容纳数据的量
                seeTotal:0, // 查看的数据总数
                seeCurrentPage:1, // 查看的当前页码
                currentId:'', //当前id
                seeCurrentId:'', //查看的当前id
                username:'', //用户名
                beforeShow:true, //之前的页面是否显示
                seeShow:false, //查看页面是否显示
                isSee:false, //是否查看
                adjustWindow:false, //调整的窗口
                mask:false, //遮罩层
                adjustInfo:{}, //调整的提示
                adjustJson:{}, //调整的数据容器
                tableData:[], //页面的数据容器
                queryTableData:[], //查看页面的数据容器
                queryJson:{},    //查询用的data
                phone:'' // 手机号
            }
        },
        methods:{
            //搜索按钮
            query(){
                if(this.phone.length > 11){
                    this.msg('手机号最多11位！','error');
                    return;
                }
                this.currentPage = 1;
                this.getTable(true);
            },
            //返回按钮
            back(){
                //返回
                this.isSee = false;
                this.beforeShow = true;
                this.seeShow = false;
                this.seeCurrentPage = 1;
            },
            //关闭调整窗口
            adjustClose(){
                //关闭调整
                this.mask = false;
                this.adjustWindow =false;
            },
            //打开调整窗口
            adjust(index, row){
                this.currentId = row.fuserid;
                this.currentIndex = index;
                this.mask = true;
                this.adjustWindow =true;
                this.adjustJson ={
                    value:'',
                    adjust:'',
                    remarks:''
                }
                this.adjustInfo = this.tableData[index];
            },
            //查看详情
            see(index,row){
                //查看
                this.seeCurrentId = row.fuserid;
                this.getSeeTable(true);
            },
            //调整保存
            makeSure(index,row){
                //调整
                let updateUserIntegral = {
                    version:VERSION, //版本号，默认1.0
                    fuserid:this.currentId, //用户ID
                    fintegeral:this.adjustJson.adjust, // 积分
                    fintegeraljournaltype:this.adjustJson.value, //积分流水类型
                    fkeyarea :this.adjustInfo.fkeyarea, // 用户区域拆分键
                    fremark:this.adjustJson.remarks  //备注
                }
                this.$http.post('/integral/updateUserIntegral.do',updateUserIntegral).then(res => {
                    res = res.data;
                    if(res.success){
                        this.msg('保存成功！','success');
                        this.adjustWindow = false;
                        this.mask = false;
                        this.currentPage = 1;
                        this.getTable(false);
                    }else{
                        this.msg('保存失败！','error');
                    }
                });

            },
            //对象的copy；
            deepCopy(obj){
                if(typeof obj != 'object'){
                    return obj;
                }
                var newobj = {};
                for( var attr in obj){
                    newobj[attr] = this.deepCopy(obj[attr]);
                }
                return newobj;
            },
            //获取积分余额列表
            getTable(bFalg){
                if(bFalg){
                    this.queryJson = this.deepCopy(this.querydata());
                }
                this.queryJson.size = this.size;
                this.queryJson.current = this.currentPage;
                this.$http.post('/integral/getAllUserIntegral.do', this.queryJson).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.msg('获取失败！','error');
                    }
                })
            },
            //获取查看页面
            getSeeTable(bFlag){
                this.$http.post('/integral/journal/list.do',{
                    version:VERSION,
                    current:this.seeCurrentPage,
                    size:this.seeSize,
                    fuserid:this.seeCurrentId
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.queryTableData = res.data.list;
                        this.seeTotal = res.data.total;
                        if(bFlag){
                            this.isSee = true;
                            this.beforeShow = false;
                            this.seeShow = true;
                        }
                    }else{
                        this.msg('获取失败！','error');
                    }
                });
            },
            //查询要用的数据
            querydata(){
                return {
                    current:this.currentPage,
                    size:this.size,
                    username:this.username,
                    phone:this.phone
                }
            },
            //查看页面页码改变
            seeHandleCurrentChange(pageNum){
                this.seeCurrentPage = pageNum;
                this.getSeeTable(false);
            },
            //查看页面一页多少条改变
            seeHandleSizeChange(size){
                this.seeSize = size;
                this.seeCurrentPage = 1;
                this.getSeeTable(false);
            },
            //页码改变
            handleCurrentChange(pageNum){
                this.currentPage = pageNum;
                this.getTable(false);
            },
            //页面一页多少条改变
            handleSizeChange(size){
                this.currentPage = 1;
                this.size = size;
                this.getTable(false);
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            phoneNum(){
                this.phone = this.phone.replace(/\D/g,'');
            }
        }
    }
</script>

<style>
    #intergralBalance-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    .intergralBalance-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .intergralBalance-radius{
        padding-bottom: 30px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    .intergralBalance-tableQuery-padding{
        padding: 0 10px;
    }
    /*
        分页
    */
    .intergralBalance-pagination{
        text-align: center;
    }
    .intergralBalance-pagination .el-pagination button,
    .intergralBalance-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .intergralBalance-pagination .el-pager li.btn-quicknext,
    .intergralBalance-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .intergralBalance-pagination .el-pagination .el-select .el-input input,
    .intergralBalance-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .intergralBalance-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height:36px;
    }
    .intergralBalance-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 7000;
    }
    .intergralBalance-Query {
        padding: 20px 0;
    }
    .intergralBalance-Query li{
        float: left;
    }
    .intergralBalance-Query li:nth-child(1){
        margin-right: 20px;
    }
    .intergralBalance-Query li:nth-child(1) input:nth-child(1){
        margin-right:10px ;
    }
    .intergralBalance-Query input{
        width: 250px;
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    #intergralBalance-warp .el-table th>div{
        padding:0px;
    }
</style>



