<template>
    <div id="seePartner">
        <div class="main">
            <tHeader style="color: #000;">查看合作方</tHeader>
            <div class="form">
<!----------------------------选择合作方------------------------->
                <div class="partner-name">
                    <div>
                        <span class="bold">请选择合作方名称:</span>
                        <el-select v-model="partnerInfo.oname" placeholder="请选择合作方名称" disabled>
                            <el-option :label="partnerInfo.oname" :value="partnerInfo.id"></el-option>
                        </el-select>
                    </div>
                    <div class="clearfix partner-info">
                        <div class="fl">
                            <h3>合作方基础信息</h3>
                            <div class="offset">
                                <p>公司名称 : <span>{{partnerInfo.oname}}</span></p>
                                <p>注册日期 : <span>{{getDate}}</span></p>
                                <p>公司地址 : <span>{{partnerInfo.oprovince}}-{{partnerInfo.ocity}}-{{partnerInfo.oarea}}</span></p>
                            </div>
                        </div>
                        <div class="fl">
                            <h3>合作方法人信息</h3>
                            <div class="offset">
                                <p>法人代表姓名 : <span>{{partnerInfo.uname}}</span></p>
                                <p>身&nbsp;份&nbsp;证&nbsp; : <span>{{partnerInfo.ucard}}</span></p>
                                <p>联系方式 : <span>{{partnerInfo.uphone}}</span></p>
                                <p>邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱&nbsp; : <span>{{partnerInfo.uemail}}</span></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="info">
<!----------------------------基础信息------------------------->
                    <div class="base-info">
                        <p class="info-title"><span class="bold">基础信息</span></p>
                        <table>
                            <tr>
                                <td>
                                    <div>注册资本:</div>
                                    <div><span class="bd">{{partnerData.registercapital}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;万元</div>
                                </td>
                                <td>
                                    <div>年销售额:</div>
                                    <div><span class="bd">{{partnerData.annualsales}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;万元</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>银行卡号:</div>
                                    <div><span class="bd">{{partnerData.bankcard}}</span></div>
                                </td>
                                <td>
                                    <div>开户银行:</div>
                                    <div><span class="bd">{{partnerData.bank}}</span></div>
                                </td>
                                <td>
                                    <div>开户人:</div>
                                    <div><span class="bd">{{partnerData.accountholder}}</span></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>传&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;真:</div>
                                    <div><span class="bd">{{partnerData.fax}}</span></div>
                                </td>
                                <td>
                                    <div>网&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址:</div>
                                    <div><span class="bd">{{partnerData.website}}</span></div>
                                </td>
                            </tr>
                        </table>
                    </div>
                    <div class="unfolded" @click="unfolded = !unfolded">
                        <p v-show="unfolded">
                            点击查看更多&nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-left" style="transform: rotate(-90deg)"></i>
                        </p>
                        <p v-show="!unfolded">
                            点击隐藏&nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-right" style="transform: rotate(-90deg)"></i>
                        </p>
                    </div>
                    <div class="hidden-info" :style="{height: unfolded ? 0 : '640px'}">
                        <!----------------------------生产信息------------------------->
                        <div class="produce-info">
                            <p class="info-title"><span class="bold">生产信息</span></p>
                            <table>
                                <tr>
                                    <td>
                                        <div>企业类型:</div>
                                        <div><span class="bd">{{partnerData.enterprisetype}}</span></div>
                                    </td>
                                    <td>
                                        <div>企业人数:</div>
                                        <div><span class="bd">{{partnerData.enterprisenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>厂房面积:</div>
                                        <div><span class="bd">{{partnerData.plantarea}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;平方米</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>厂房属性:</div>
                                        <div><span class="bd">{{partnerData.planttype}}</span></div>
                                    </td>
                                    <td>
                                        <div>生产线数量:</div>
                                        <div><span class="bd">{{partnerData.linenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>生产线门幅:</div>
                                        <div><span class="bd">{{partnerData.linedoor}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;厘米</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>生产线品牌:</div>
                                        <div><span class="bd">{{partnerData.linebrand}}</span></div>
                                    </td>
                                    <td>
                                        <div>生产线平均车速:</div>
                                        <div><span class="bd">{{partnerData.linespeed}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;米/每分钟</div>
                                    </td>
                                    <td>
                                        <div>开机时长:</div>
                                        <div><span class="bd">{{partnerData.opentime}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;小时</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>生产班次:</div>
                                        <div><span class="bd">{{partnerData.productionshift}}</span></div>
                                    </td>
                                    <td>
                                        <div>日均产能:</div>
                                        <div><span class="bd">{{partnerData.avgcapacity}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;平方米</div>
                                    </td>
                                    <td>
                                        <div>日最大产能:</div>
                                        <div><span class="bd">{{partnerData.maxcapacity}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;平方米</div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>产品数量:</div>
                                        <div><span class="bd">{{partnerData.productquantity}}</span></div>
                                    </td>
                                    <td>
                                        <div>产量最大的三种产品:</div>
                                        <div><span class="bd">{{partnerData.maxthree}}</span></div>
                                    </td>
                                    <td>
                                        <div>运输配送方式:</div>
                                        <div><span class="bd">{{partnerData.transporttype}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>车辆数量:</div>
                                        <div><span class="bd">{{partnerData.carnumber}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;辆</div>
                                    </td>
                                </tr>
                            </table>
                        </div>
                        <!----------------------------人员构成------------------------->
                        <div class="people-info">
                            <p class="info-title"><span class="bold">人员构成</span></p>
                            <table>
                                <tr>
                                    <td>
                                        <div>总经理/厂长:</div>
                                        <div><span class="bd">{{partnerData.managername}}</span></div>
                                    </td>
                                    <td>
                                        <div>出生年份:</div>
                                        <div><span class="bd">{{ partnerData.age }}</span></div>
                                    </td>
                                    <td>
                                        <div>学历:</div>
                                        <div><span class="bd">{{partnerData.educationbackground}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>管理人员数量:</div>
                                        <div><span class="bd">{{partnerData.managenumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>研发人员数量:</div>
                                        <div><span class="bd">{{partnerData.rdnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>质量人员数量:</div>
                                        <div><span class="bd">{{partnerData.fmassnumber}}</span></div>
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        <div>销售人员数量:</div>
                                        <div><span class="bd">{{partnerData.salesnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>物流人员数量:</div>
                                        <div><span class="bd">{{partnerData.logisticsnumber}}</span></div>
                                    </td>
                                    <td>
                                        <div>一线生产人员数量:</div>
                                        <div><span class="bd">{{partnerData.onelinenumber}}</span></div>
                                    </td>
                                </tr>
                            </table>
                        </div>
                    </div>
<!---------------------------订单分发配置------------------------------------>
                    <div class="order-config">
                        <p class="info-title"><span class="bold">订单分发配置</span></p>
                        <p><span class="bd">{{partnerData.configuration}}</span></p>
                    </div>
                </div>
            </div>
            <div class="foot">
                <el-button @click="cancel">取消</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import tHeader from '@/components/public/tHeader'
    export default {
        name: 'partnerManage',
        data() {
            return {
                //合作方名称
                partnerInfo: {},
                //基础信息
                unfolded: true,
                partnerData: {}
            }
        },
        computed: {
            getDate() {
                let t = new Date(this.partnerInfo.create_time)
                let   year = t.getFullYear();
                let   month = t.getMonth()+1;
                let   date = t.getDate();
                return year+"-"+month+"-"+date;
            }
        },
        components: {
            tHeader
        },
        created() {
            //编辑合作方
            this.$http.post('partners/findOnePartners.do', this.$route.query).then( res => {
                this.partnerData = res.data.data
                this.filterSelectValue()

                //得到选择合作方数据
                this.$http.post('partners/findPartnerByOaid.do', {oaid: this.partnerData.id }).then(res => {
                    if(res.data.success) {
                        this.partnerInfo = res.data.data[0]
                    }
                }).catch(error => {
                    console.log('查看合作方的选择合作方名称:' + error)
                })

            }).catch( error => {
                this.$message({
                    message: '查看合作方信息失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log('查看所有合作方:' + error)
            })

        },
        methods: {
            cancel() {
                this.$router.push({name: 'partner'})
            },
            //对返回给select框的int类型做转换
            filterSelectValue() {
                //企业类型
                switch (this.partnerData.enterprisetype) {
                    case 0:
                        this.partnerData.enterprisetype = ''
                        break;
                    case 1:
                        this.partnerData.enterprisetype = '国企'
                        break;
                    case 2:
                        this.partnerData.enterprisetype = '外商合资'
                        break;
                    case 3:
                        this.partnerData.enterprisetype = '中外合资'
                        break;
                    case 4:
                        this.partnerData.enterprisetype = '民营'
                        break;
                }
                //厂房属性
                switch (this.partnerData.planttype) {
                    case 0:
                        this.partnerData.planttype = ''
                        break;
                    case 1:
                        this.partnerData.planttype = '租赁'
                        break;
                    case 2:
                        this.partnerData.planttype = '自有'
                        break;
                    case 3:
                        this.partnerData.planttype = '租赁+自有'
                        break;
                }
                //生产班次
                switch (this.partnerData.productionshift) {
                    case 0:
                        this.partnerData.productionshift = ''
                        break;
                    case 1:
                        this.partnerData.productionshift = '一班'
                        break;
                    case 2:
                        this.partnerData.productionshift = '二班'
                        break;
                    case 3:
                        this.partnerData.productionshift = '三班'
                        break;
                }
                //运输配送方式
                switch (this.partnerData.transporttype) {
                    case 0:
                        this.partnerData.transporttype = ''
                        break;
                    case 1:
                        this.partnerData.transporttype = '租赁'
                        break;
                    case 2:
                        this.partnerData.transporttype = '自有'
                        break;
                    case 3:
                        this.partnerData.transporttype = '租赁+自有'
                        break;
                }
                //年龄
                if('age' in this.partnerData) {
                    let t = new Date(this.partnerData.age)
                    this.partnerData.age = t.getFullYear()
                }
                else {
                    this.partnerData.age = ''
                }
                //学历
                switch (this.partnerData.educationbackground) {
                    case 0:
                        this.partnerData.educationbackground = ''
                        break;
                    case 1:
                        this.partnerData.educationbackground = '无学历'
                        break;
                    case 2:
                        this.partnerData.educationbackground = '初中'
                        break;
                    case 3:
                        this.partnerData.educationbackground = '高中'
                        break;
                    case 4:
                        this.partnerData.educationbackground = '大专'
                        break;
                    case 5:
                        this.partnerData.educationbackground = '本科'
                        break;
                    case 6:
                        this.partnerData.educationbackground = '硕士'
                        break;
                }
                //订单分发配置
                switch (this.partnerData.configuration) {
                    case 0:
                        this.partnerData.configuration = ''
                        break;
                    case 1:
                        this.partnerData.configuration = '在线接口对接'
                        break;
                    case 2:
                        this.partnerData.configuration = '离线文件对接'
                        break;
                }

            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../assets/style/seePartner.scss';
</style>



