<template>
	<div class='personal-right'>
		<div class='personal-comImg'>
               <img :class="imgClass" :src="image01" @click="showImg('1')">
			   <img :class="classImg" :src='image02' @click="showImg('2')">
		</div>
		<div class='personal-read'>
			<div v-for='name in stringArry'>
				<span :title='name.string' class='personal-firstSpan-width'>{{name.string}}</span>
				<span :title='name.information' class='personal-span-width'>{{name.information}}</span>
			</div>
		</div>
		<div class='personal-write-correcting'>
			<slot></slot>
		</div>
		<div class='personal-Result' v-show='defalt'>
			<div class='personal-radio'>
				<el-radio class="radio" v-model="radio" label="1" @click.native="passBtn">通过</el-radio>
				<el-radio class="radio" v-model="radio" label="2" @click.native="noPassBtn">驳回</el-radio>
			</div>
			<div class='personal-text' >
				<div class='personal-font'>驳回理由</div>
				<div v-if="radio === '2' ? true : false">
					<el-input type="textarea" 
					:autosize="{ minRows: 2, maxRows: 2}" 
					placeholder="请输入理由(不得少于5个字)" 
					v-model="textarea"
					@change='getText'
					resize='none'
					>
					</el-input>
				</div>
				<div v-else>
					<el-input type="textarea" 
					placeholder="请输入理由(不得少于5个字)" 
					:autosize="{ minRows: 2, maxRows: 2}" 
					resize='none'
					disabled="disabled"
					>
					</el-input>
				</div>
			</div>
		</div>
		<div class='personal-min-height' v-show='pass'>
			<div class='personal-Result personal-pass'>
				<span>已通过</span>
			</div>
		</div>
		<!--<div class='personal-min-height' v-show='noPass'>
			<div class='personal-Result personal-noPass'>
				<p>已驳回</p>
				<span>驳回理由:</span>
				<span>照片不清楚</span>
			</div>
		</div>-->
		<div class='personal-btn' v-show='!returnBtn && isEdit == false'>
			<el-button @click='returnList'>取消审核</el-button>
			<el-button type="primary" @click='completed'>审核完成</el-button>
		</div>
		<div class='personal-btn' v-show='returnBtn && isEdit == false' @click='returnList'>
			<el-button type="primary">返回列表</el-button>
		</div>
		<div class='personal-btn' v-show='isEdit'>
			<el-button @click='returnList'>取消</el-button>
			<el-button type="primary" @click='completed'>保存</el-button>
		</div>
	</div>
</template>


<script>

export default {
	props:['stringArry', 'image01', 'image02', 'imgClass', 
	'classImg', 'defalt', 'pass', 'noPass','returnBtn', 'isEdit'],
	data() {
		return {
			radio:'1',
			textarea:''
		}
	},
	methods: {
	  //得到驳回理由
      getText(){
          this.$emit('getText', this.textarea)
      },
	  //通过驳回
	  passBtn(num){
		 this.radio = '1'
		 this.$emit('setNum', '1')
	  },
	  noPassBtn(){
		 this.radio = '2'
		 this.$emit('setNum', '2')
	  },
	  //放大照片
	  showImg(number){
		  this.$emit('showImg', number)
	  },
	  //返回上一级
	  returnList(){
		  this.$emit('goBack')
	  },
	  //审核完成
	  completed(){
		  this.$emit('completed')
	  }
	}
}
</script>
    

<style>
</style>


