<template>
    <div id="commodity_add">
        <tHeader>{{ titleName }}</tHeader>
        <div class="main">
            <div class="inner">
                <Classification @getSortValue="getSortValue" :allSortValueList="allSortValueList" :classifyList="classifyList"></Classification>
                <el-form class="s_form" label-width="100px">
                    <p>
                        <span style="font-weight: bold; font-size: 15px">商品基本信息</span>
                        <span style="color: #ff6161;">(带*为必填项)</span>
                    </p>
                    <el-form-item label="商品名称:" class="name" style="width: 800px;">
                        <el-input v-model.trim="name" :maxlength="20"></el-input>
                    </el-form-item>
                    <el-form-item label="商品标签:" class="tags">
                        <el-tag
                            :key="index"
                            v-for="(tag, index) in dynamicTags"
                            :closable="true"
                            :close-transition="false"
                            @close="handleClose(index)"
                            type="primary"
                        >
                            {{tag.labelName}}
                        </el-tag>
                        <el-select v-model="inputValue" placeholder="添加标签" @change="handleInputConfirm">
                            <el-option
                                v-for="(item, index) in allTags"
                                :key="index"
                                :label="item.labelName"
                                :value="item.id"
                                :disabled="item.disabled">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="发货类型:" class="type">
                        <el-radio-group v-model="shipType">
                            <el-radio v-for="(item, index) in shipTypeMapList" :label="item.id" :key="index">{{ item.value}}</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="商品属性:" class="attr">
                        <ul>
                            <li v-for="(item, index) in attrs" :key="index">
                                <el-input v-model="item.attributeName" placeholder="请输入属性" :maxlength="10" @blur="notAttrSameName(index)"></el-input>
                                :
                                <el-input v-model="item.attributeValue" placeholder="请输入属性值" :maxlength="50" @blur="sureAttrName(index)" style="width: 300px;"></el-input>
                                <el-button type="primary" icon="delete" @click="deleteAttrs(index)"></el-button>
                            </li>
                        </ul>
                        <el-button type="primary" @click="addAttr" style="width: 100px;"><i class="fa fa-plus-circle" style="margin-right: 8px;"></i>添加属性</el-button>
                    </el-form-item>
                    <el-form-item label="商品规格:" class="norm">
                        <ul>
                            <li v-for="(item, index) in norms" :key="index">
                                {{item.specName}}:&nbsp;&nbsp;&nbsp;<normTemplate :normValueList="item.specValues" :normName="item.specName"></normTemplate>
                            </li>
                        </ul>
                        <el-button type="primary" @click="dialogVisible = true" style="width: 100px;"><i class="fa fa-plus-circle" style="margin-right: 8px;"></i>选择规格</el-button>
                        <el-dialog title="请选择规格(最多选择3个)"
                            :visible.sync="dialogVisible"
                            :before-close="beforeHandleClose"
                            :modal-append-to-body=false
                            @close="$store.commit('closeZindexModel')"
                            @open="beforeOpen"
                        >
                            <el-checkbox-group
                                v-model="checkedNorms"
                                :max="3">
                                <el-checkbox v-for="(item, index) in specList" :label="item.id" :key="index">{{item.specName}}</el-checkbox>
                            </el-checkbox-group>
                            <div class="dialogNormsShow">
                                <span v-for="(item, index) in checkedNormsNames" :key="index">
                                    {{ item }}
                                    <i v-if="index != checkedNormsNames.length-1" style="color: #bfcbd9">/</i>
                                </span>
                            </div>
                            <div slot="footer" class="dialog-footer">
                                <el-button type="primary" @click="chooseNorm">确 定</el-button>
                            </div>
                        </el-dialog>
                    </el-form-item>
                    <p style="font-weight: bold; font-size: 15px">商品销售信息</p>
                    <el-form-item label="规格报价:" class="sell">
                        <normQuotes :norms="norms" :doneQuotesList.sync="quotesInfo" :copyQuotesInfo="copyQuotesInfo"></normQuotes>
                    </el-form-item>
                    <p style="font-weight: bold; font-size: 15px">商品主图与详情图</p>
                    <div class="images">
                        <div class="content">
                            <p><i style="color: rgb(255, 97, 97);">*</i>商品列表主图 (最多上传5张) :</p>
                            <div class="tp">
                                <el-upload
                                    action="/djcrm/photosController/addPhotos.do"
                                    list-type="picture-card"
                                    :on-preview="simplePictureCardPreview"
                                    :on-success="successMainResponse"
                                    :on-error="errorMainResponse"
                                    :on-remove="handleRemove"
                                    :before-upload="beforeUpload"
                                    :file-list="fileMainList"
                                    :class="{ toggleFileImg: mainHidden }">
                                    <i class="el-icon-plus">
                                        <div class="images_font">
                                            <p>支持jpg/png格式</p>
                                            <p>RGB模式,大小<2M</p>
                                            <p>宽度比为1:1</p>
                                        </div>
                                    </i>
                                </el-upload>
                                <el-dialog
                                    v-model="dialogSimple"
                                    size="tiny"
                                    :modal-append-to-body=false
                                    @open="$store.commit('openZindexModel')"
                                    @close="$store.commit('closeZindexModel')">
                                        <img width="100%" :src="simpleUrl" alt="">
                                </el-dialog>
                            </div>
                        </div>
                        <div class="content">
                            <p><i style="color: rgb(255, 97, 97);">*</i>商品详情图 (最多上传5张) :</p>
                            <div class="tp">
                                <el-upload
                                    action="/djcrm/photosController/addPhotos.do"
                                    list-type="picture-card"
                                    :on-preview="fullPictureCardPreview"
                                    :on-success="successFullResponse"
                                    :on-error="errorFullResponse"
                                    :on-remove="fullRemove"
                                    :before-upload="beforeUpload"
                                    :file-list="fileDetailList"
                                    :class="{ toggleFileImg: fullHidden }">
                                    <i class="el-icon-plus">
                                        <div class="images_font">
                                            <p>支持jpg/png格式</p>
                                            <p>RGB模式,大小<2M</p>
                                            <p>宽=1100</p>
                                        </div>
                                    </i>
                                </el-upload>
                                <el-dialog
                                    v-model="dialogFull"
                                    size="tiny"
                                    :modal-append-to-body=false
                                    @open="$store.commit('openZindexModel')"
                                    @close="$store.commit('closeZindexModel')">
                                    <img width="100%" :src="fullUrl" alt="">
                                </el-dialog>
                            </div>
                        </div>
                    </div>
                </el-form>
            </div>
            <div class="foot">
                <el-button @click="cancel">取消</el-button>
                <el-button type="primary" @click="save(0)">保存</el-button>
                <el-button type="primary" @click="save(1)">保存并上架</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import backFn  from './backEdit'
    export default {
        components: {
            tHeader: () => import('@/components/public/tHeader.vue'),
            Classification: () => import('./Classification.vue'),
            normTemplate: () => import('./Norm.vue'),
            normQuotes: () => import('./Quotes.vue'),
        },
        data() {
            return {
                titleName: '新增商品',
                saveToggle: false,
                //商品编号
                goodId: '',
                //分类123级
                allSortValueList: [],
                classifyList: [],
                //商品名称
                name: '',
                //商品标签
                allTags: [],
                dynamicTags: [],
                inputValue: '',
                //发货类型
                shipTypeMapList: [],
                shipType: 1,
                //商品属性
                attrs: [],
                //商品规格
                norms: [],              //总的
                specList: [],           //复选框显示的规格
                checkedNorms: [],       //选择的规格id
                checkedNormsNames:[],        //选择的规格名字
                copyCheckedNorms: [],
                dialogVisible: false,
                //商品销售信息
                quotesInfo: {},
                copyQuotesInfo: {},
                //图片上传
                fileMainList: [],       //主图组
                fileDetailList: [],     //详情组
                simpleUrl: '',
                dialogSimple: false,
                fullUrl: '',
                dialogFull: false,
                mainHidden: false,
                fullHidden: false
            }
        },
        computed: {
            admin() {
                return this.$store.getters.doneAdmin
            }
        },
        created() {
            this.goodId = this.$route.query.goodId
            //空为新增  非空为编辑
            if(this.goodId !=='') {   //修改
                this.titleName = '修改商品'
                backFn(this)
            }
            else {              //新增
                this.titleName = '新增商品'
                //分类标签
                this.$http({
                     url: '/productController/initClassify.do',
                     method: 'post'
                 }).then( res => {
                     if(res.data.success) {
                         this.classifyList = res.data.data.classifyList
                     }
                     else {
                         this.$message({
                             message: '获取分类信息失败',
                             duration: 1000,
                             type: 'error'
                         });
                    }
                 }).catch( error => {
                     this.$message({
                         message: '操作失败',
                         duration: 1000,
                         type: 'error'
                    });
                    console.log(error)
                 })
                //商品标签
                this.$http.post('/productController/initLabel.do').then( res => {
                    if(res.data.success) {
                        this.allTags = res.data.data.labelList
                        for(let item of this.allTags) { //为了选择的标签不能选择
                            item.disabled = false
                        }
                    }
                    else {
                        this.$message({
                            message: '获取标签信息失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
                //发货类型:
                this.$http({
                    url: '/productController/shipType.do',
                    method: 'post',
                }).then( res => {
                    if(res.data.success) {
                        this.shipTypeMapList = res.data.data.shipTypeMapList
                    }
                    else {
                        this.$message({
                            message: '获取发货类型信息失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
                //商品规格
                this.$http.post('/productController/initSpec.do').then( res => {
                    if(res.data.success) {
                        this.specList = res.data.data.specList
                    }
                    else {
                        this.$message({
                            message: '获取商品规格信息失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log(error)
                })
            }
        },
        methods: {
            //分类123级值
            getSortValue(value) {
                this.allSortValueList = value
            },
            //商品标签
            handleClose(index) {
                for(let item of this.allTags) {
                    if(item.id === this.dynamicTags[index].id) {
                        item.disabled = false
                        break
                    }
                }
                this.dynamicTags.splice(index, 1);
            },
            handleInputConfirm() {
                if(this.inputValue === '') {
                    return
                }
                if(this.dynamicTags.length<15) {
                    for(let item of this.allTags) {
                        if(item.id === this.inputValue) {
                            this.dynamicTags.push(item)
                            this.inputValue = ''
                            item.disabled = true
                            return
                        }
                    }
                }
                else {
                    this.inputValue = ''
                    this.$message({
                        message: '商品标签最多只能添加15个',
                        duration: 1000,
                        type: 'info'
                    });
                }
            },
            //商品属性
            addAttr() {
                if(this.attrs.length >= 10) {
                    this.$message({
                        message: '商品属性最多只能添加10个',
                        duration: 1000,
                        type: 'info'
                    });
                    return
                }
                //如果上面有没填属性和值的,则不能添加
                for(let item of this.attrs) {
                    if(item.attributeValue === '') {
                        this.$message({
                            message: '请确保输入完整的商品属性',
                            duration: 1000,
                            type: 'info'
                        });
                        return false
                    }
                }

                let dict = {
                    attributeName: '',
                    attributeValue: '',
                    attributeId: ''
                }
                this.attrs.push(dict)
            },
            deleteAttrs(index){
                this.attrs.splice(index, 1)
            },
            notAttrSameName(index) {
                for(let i=0, len=this.attrs.length; i<len; i++) {
                    if(i !== index) {
                        if(this.attrs[index].attributeName === this.attrs[i].attributeName && this.attrs[i].attributeName !== '') {
                            this.$message({
                                message: '属性重名,请重新填写。',
                                duration: 1000,
                                type: 'warning'
                            });
                            this.attrs[index].attributeName = ''
                            this.attrs[index].attributeValue = ''
                            break
                        }
                    }
                }
            },
            sureAttrName(index) {
                if(this.attrs[index].attributeName === '' && this.attrs[index].attributeValue !== '') {
                    this.$message({
                        message: '请确保有属性名',
                        duration: 1000,
                        type: 'warning'
                    });
                    this.attrs[index].attributeValue = ''
                }
            },
            //商品规格
            beforeHandleClose(done) {
                this.checkedNorms = this.copyCheckedNorms
                done()
            },
            beforeOpen() {
                this.$store.commit('openZindexModel')
                this.copyCheckedNorms = [...this.checkedNorms]
            },
            chooseNorm() {
                this.dialogVisible = false
                let copyNorms = this.norms
                this.norms = []
                for(let item of this.checkedNorms) {
                    let dict = {
                        specId: item,              //规格id
                        specName: '',           //规格名
                        specValues: [],      //规格value
                    }
                    //相应的id对应相应的名字
                    for(let i of this.specList) {
                        if(item === i.id) {
                            dict.specName = i.specName
                            break
                        }
                    }
                    this.norms.push(dict)
                }
                for(let item of copyNorms) {
                    for(let [index, value] of Object.entries(this.norms)) {
                        if(item.specId === value.specId) {
                            this.norms.splice(index, 1, item)
                            break
                        }
                    }
                }
            },
            /**
             * 图片上传
             */
            handleRemove(file, fileList) {
                this.fileMainList = fileList
            },
            simplePictureCardPreview(file) {
                this.simpleUrl = file.url;
                this.dialogSimple = true;
            },
            successMainResponse(response, file, fileList) {
                new Promise((resolve, reject) => {
                    let image = new Image();
                    image.onload = () => {
                        let isSize
                        let width = image.width;
                        let height = image.height;
                        //图片的尺寸比0.8 -- 1.2  主图
                        isSize = width / height
                        isSize = 0.8 < isSize && isSize < 1.2
                        if (!isSize) {
                            this.$message({
                                message: '注意! 上传的图片宽高比要接近1：1',
                                duration: 1000,
                                type: 'warning'
                            });
                            fileList.pop()
                        }
                        else {
                            resolve()
                        }
                    }
                    image.src= file.url;
                }).then( res => {
                    if(response.success) {
                        file.fileid = response.data.fileid
                        file.fileurl = response.data.fileurl
                        this.fileMainList = fileList
                    }
                    else {
                        fileList.pop()
                        this.$message({
                            message: '图片加载失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                })
            },
            errorMainResponse(err, file, fileList) {
                this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
            },
            fullRemove(file, fileList) {
                this.fileDetailList = fileList
            },
            fullPictureCardPreview(file) {
                this.fullUrl = file.url;
                this.dialogFull = true;
            },
            successFullResponse(response, file, fileList) {
                new Promise((resolve, reject) => {
                    let image = new Image();
                    image.onload = () => {
                        let isSize
                        let width = image.width;
                        let height = image.height;
                        //图片的尺寸比0.8 -- 1.2  主图
                        isSize = (1000 < width ) && (width < 1200)
                        if (!isSize) {
                            if(!isSize) {
                                this.$message({
                                    message: '注意! 上传的图片宽接近1100。',
                                    duration: 1000,
                                    type: 'warning'
                                });
                                fileList.pop()
                            }
                        }
                        else {
                            resolve()
                        }
                    }
                    image.src= file.url;
                }).then( res => {
                    if(response.success) {
                        file.fileid = response.data.fileid
                        file.fileurl = response.data.fileurl
                        this.fileDetailList = fileList
                    }
                    else {
                        fileList.pop()
                        this.$message({
                            message: '图片加载失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                })
            },
            errorFullResponse(err, file, fileList) {
                this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
            },
            beforeUpload(file) {
                let isJpgPng = (file.type === 'image/jpeg') || (file.type === 'image/png')
                let isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJpgPng) {
                    this.$message({
                        message: '注意! 上传的图片只能是JPG或者PNG',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                if (!isLt2M) {
                    this.$message({
                        message: '注意! 上传的图片大小不能超过 2MB!',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
            },
            //保存
            save(state) {
                if(!this.beforeSaveFormTest()) return
                this.saveToggle = true
                let parameter = this.beforeSave()
                parameter.status = state
                this.$http.post('/productController/saveGoods.do', parameter).then( res => {
                    if(res.data.success) {
                        this.$message({
                            message: '保存成功',
                            duration: 1000,
                            type: 'success',
                            onClose: () => {
                                this.$router.push({ name: 'commodity_all'})
                                this.saveToggle = false
                            }
                        });
                    }
                    else {
                        this.$message({
                            message: '保存失败',
                            duration: 1000,
                            type: 'error'
                        });
                        this.saveToggle = false
                    }
                }).catch( error => {
                    console.log(error)
                    this.$message({
                        message: '操作失败',
                        duration: 1000,
                        type: 'error'
                    });
                    this.saveToggle = false
                })
            },
            cancel() {
                this.$router.push({ name: 'commodity_all'})
            },
            beforeSave() {
                //商品编号
                let goodId = this.goodId
                //保存类型
                let status = ''
                //用户
                let userName = this.admin.uname
                let userId = this.admin.id
                //分类123级
                let goodTypel1Id = this.allSortValueList[0]
                let goodTypel2Id = this.allSortValueList[1]
                let goodTypeId = this.allSortValueList[2]
                //商品名字
                let goodName = this.name
                //商品标签
                let goodsLabelIds = []
                for(let item of this.dynamicTags) {
                    goodsLabelIds.push(item.id)
                }
                //发货类型
                let shipType = this.shipType
                //商品属性
                let goodsAttrList = this.attrs
                for(let i=0; i<goodsAttrList.length; i++) {
                    if(goodsAttrList[i].attributeName === '') {
                        goodsAttrList.splice(i, 1)
                        i--
                    }
                }
                //商品规格
                let goodsSpecList = this.norms
                //商品销售信息
                let specPriceList = JSON.parse(JSON.stringify(this.quotesInfo))
                if(this.norms.length !== 0) {
                    for(let item of specPriceList.specPrices) {
                        if(item.isExist) {
                            item.isExist = 1
                        }
                        else {
                            item.isExist = 0
                            if(item.proPrice === '') {
                                item.proPrice = null
                            }
                            if(item.usedIntegral === '') {
                                item.usedIntegral = null
                            }
                            if(item.marketPrice === '') {
                                item.marketPrice = null
                            }
                        }
                    }
                }
                //商品主图与详情图
                let fileMainList = []
                let fileDetailList = []
                this.fileMainList.forEach( (item, index) => {
                    let dictPic = {}
                    dictPic.id = ''                         //唯一id
                    dictPic.type = 1                        //主图还是详情图
                    dictPic.displayOrder = index + 1        //顺序
                    dictPic.picId = item.fileid             //图片id
                    dictPic.url = item.fileurl              //图片url
                    if(item.tag) {
                        dictPic.tag = item.tag
                    }
                    fileMainList.push(dictPic)
                })
                this.fileDetailList.forEach( (item, index) => {
                    let dictPic = {}
                    dictPic.id = ''
                    dictPic.type = 2
                    dictPic.displayOrder = index + 1
                    dictPic.picId = item.fileid
                    dictPic.url = item.fileurl
                    if(item.tag) {
                        dictPic.tag = item.tag
                    }
                    fileDetailList.push(dictPic)
                })
                let goodsPicList = [...fileMainList, ...fileDetailList]
                return {
                    goodId,
                    status,
                    userName,
                    userId,
                    goodTypel1Id,
                    goodTypel2Id,
                    goodTypeId,
                    goodName,
                    goodsLabelIds,
                    shipType,
                    goodsAttrList,
                    goodsSpecList,
                    specPriceList,
                    goodsPicList
                }
            },
            beforeSaveFormTest() {
                if(this.saveToggle) return   //阻止频繁请求
                //请选择合适的分类
                if(this.allSortValueList.length === 0) {
                    this.$message({
                        message: '必填项!，选择合适的分类。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                //商品名称
                if(this.name === '') {
                    this.$message({
                        message: '必填项!，请填写商品名称。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                //发货类型
                if(this.shipType === '') {
                    this.$message({
                        message: '必填项!，请选择发货类型。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                //商品属性
                for(let item of this.attrs) {
                    if(item.attributeValue === '') {
                        this.$message({
                            message: '请确保商品属性存在值',
                            duration: 1000,
                            type: 'warning'
                        });
                        return false
                    }
                }
                //商品销售信息
                if(this.norms.length) {
                    for(let item of this.norms) {
                        if(!item.specValues.length) {
                            this.$message({
                                message: '必填项!，请确保规格报价表格中的必填项。',
                                duration: 1000,
                                type: 'warning'
                            });
                            return false
                        }
                    }
                }
                if(JSON.stringify(this.quotesInfo) === '{}') {
                    this.$message({
                        message: '必填项!，请确保规格报价表格中的必填项。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                for(let item of this.quotesInfo.specPrices) {
                    if('isExist' in item) {
                        if(item.isExist) {
                            if(item.proPrice === '' || item.usedIntegral === '' || item.marketPrice === '') {
                                this.$message({
                                    message: '必填项!，请确保规格报价表格中的必填项。',
                                    duration: 1000,
                                    type: 'warning'
                                });
                                return false
                            }
                        }
                    }
                    else {
                        if(item.proPrice === '' || item.usedIntegral === '' || item.marketPrice === '') {
                            this.$message({
                                message: '必填项!，请确保规格报价表格中的必填项。',
                                duration: 1000,
                                type: 'warning'
                            });
                            return false
                        }
                    }
                    if(+item.proPrice > +item.marketPrice) {
                        this.$message({
                            message: '必填项!，请确保规格报价表格中的促销价格不大于原价。',
                            duration: 2000,
                            type: 'warning'
                        });
                        return false
                    }
                    if(+item.proPrice < +item.usedIntegral) {
                        this.$message({
                            message: '必填项!，请确保规格报价表格中的促销价格大于积分最高抵用金额。',
                            duration: 2000,
                            type: 'warning'
                        });
                        return false
                    }
                }
                //商品主图
                if(this.fileMainList.length === 0) {
                    this.$message({
                        message: '警告!，请至少上传一张商品主图。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                if(this.fileMainList.length > 5) {
                    this.$message({
                        message: '警告!，请最多上传5张商品主图。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                //商品详情图
                if(this.fileDetailList.length === 0) {
                    this.$message({
                        message: '警告!，请至少上传一张商品详情图。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                if(this.fileDetailList.length > 5) {
                    this.$message({
                        message: '警告!，请最多上传5张商品详情图。',
                        duration: 1000,
                        type: 'warning'
                    });
                    return false
                }
                return true
            }
        },
        watch: {
            //监听选择复选框
            checkedNorms(newValue) {
                this.checkedNormsNames = []
                for(let item of newValue) {
                    for(let i of this.specList) {
                        if(item === i.id) {
                            this.checkedNormsNames.push(i.specName)
                            break
                        }
                    }
                }
            },
            fileMainList(newValue) {
                if(this.fileMainList.length >= 5) {
                    this.mainHidden = true
                }
                else {
                    this.mainHidden = false
                }
            },
            fileDetailList(newValue) {
                if(this.fileDetailList.length >= 5) {
                    this.fullHidden = true
                }
                else {
                    this.fullHidden = false
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @media screen and (max-width: 1500px) {
        #commodity_add .main .inner {
            width: 100% !important;
        }
    }
    #commodity_add {
        padding: 30px 30px 44px;
        .main {
            background: #fff;
            border-width: 0 1px 2px 1px;
            border-style: solid;
            border-color: #ededed;
            border-bottom-color: #e2e2e2;
            border-radius: 0 0 6px 6px;
            .inner {
                width: 1237px;
                margin: 0 auto;
            }
            .s_form {
                .name {
                    margin: 34px 0 19px 0;
                }
                .tags {
                    margin-bottom: 28px;
                    .el-tag {
                        margin-right: 10px;
                    }
                    .el-select {
                        width: 120px;
                    }
                }
                .type {
                    margin-bottom: 30px;
                }
                .attr {
                    margin-bottom: 32px;
                    ul {
                        li {
                            margin-bottom: 20px;
                        }
                    }
                    .el-input, .el-select {
                        width: 150px;
                    }
                }
                .norm {
                    @extend .attr;
                    margin-bottom: 39px;
                    .dialogNormsShow {
                        position: absolute;
                        bottom: 30px;
                        left: 20px;
                    }
                }
                .sell {
                    margin: 30px 0 50px;
                }
                .images {
                    margin: 30px 0 40px;
                    .content {
                        padding-left: 55px;
                        margin-bottom: 39px;
                        .tp {
                            margin-top: 23px;
                            padding-left: 82px;
                        }
                        .el-icon-plus {
                            padding-top: 35px;
                        }
                        .images_font {
                            margin-top: 9px;
                            font-size: 12px;
                            color: #a6a6a6;
                            line-height: 19px;
                        }
                    }
                }
            }
            .foot {
                padding: 40px 0 27px;
                text-align: center;
                button {
                    margin: 0 25px;
                    width: 160px;
                    height: 44px;
                }
            }
        }
    }
</style>



