<template>
    <div id="app">
        <router-view></router-view>
        <div class="backToTop" @click="up">
            <transition name="el-fade-in-linear">
                <div v-show="show" class="transition-box">
                    <i class="fa fa-angle-double-up"></i>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            show: false
        }
    },
    mounted() {
        this.$el.addEventListener('scroll', _ => {
            let scrolled = this.$el.scrollTop
            if(scrolled < 800) {
                this.show = false
            }
            if (scrolled >= 800) {
                this.show = true
            }
        })
    },
    methods: {
        up() {
            let scrolled = this.$el.scrollTop
            let time = setInterval(_ => {
                this.$el.scrollTop -= 70
                if(this.$el.scrollTop == 0) {
                    clearInterval(time)
                }
            }, 20)
        }
    }
}
</script>

<style>
	#app {
		height: 100%;
		padding-top: 50px;
        overflow-y: auto;
        box-sizing: border-box;
	}
    #app>.backToTop {
        position: fixed;
        bottom: 50px;
        right: 30px;
        opacity: .4;
        cursor: pointer;
    }
    #app>.backToTop .transition-box {
        color: #fff;
        font-size: 60px;
        background: #1F2D3D;
        width: 60px;
        height: 60px;
        text-align: center;
        line-height: 60px;
        border-radius: 15px;
    }
  @import './assets/style/entrance.css';
</style>



