<template>
    <div id="chargeIntergral-warp">
        <div class="chargeIntergral-warp-padding">
            <div class="chargeIntergral-radius">
                <tHeader>充值送积分<span class="chargeIntergral-titleInfo">（温馨提示：等比例送积分和按区间送积分只能选择一种生效，也可都不生效）</span></tHeader>
                <div class="chargeIntergral-content">
                    <div class="chargeIntergral-proportion">
                        <h3 class="chargeIntergral-proportionTitle">
                            <input type="radio"  class="mgr mgr-primary mgr-lg" v-model="radio" @click="radioClick1" value="1" name="effect"> 等比例送积分:
                        </h3>
                        <div class="chargeIntergral-proportionContent">
                            <p>
                                <span>充值金额</span>
                                <span>积分数量</span>
                            </p>
                            <div class="chargeIntergral-proportionFormWarp">
                                <span>每充值</span>
                                <input type="text" :class="{inputInfo:proportionChargeEmpty}" @input="proportionChargeNum()" v-model="proportionForm.fpaymentsl1">
                                <span>元：赠送</span>
                                <input type="text" :class="{inputInfo:proportionSendEmpty}" @input="proportionSendNum()" v-model="proportionForm.fintegral">
                                <span>积分</span>
                            </div>
                        </div>
                        <div class="chargeIntergral-region">
                            <h3 class="chargeIntergral-regionTitle">
                                <input type="radio" class="mgr mgr-primary mgr-lg" v-model="radio" @click="radioClick2" value="2" name="effect"> 按区间送积分:
                            </h3>
                            <div class="chargeIntergral-regionContent">
                                <p>
                                    <span>充值金额区间</span>
                                    <span>积分数量</span>
                                </p>
                                <div class="chargeIntergral-regionFormWarp">
                                    <ul>
                                        <li v-for="item,index in regionForm">
                                            <input type="text" v-model="item.fpaymentsl1" @blur="beforeMoneyCompare(index)" :class="{inputInfo:checkForm[index].beforeMoney || checkForm[index].chargeCompare }" @input="regionChargeMoneyNum(index)">
                                            <span>≤充值金额＜</span>
                                            <input type="text" @blur="afterMoneyCompare(index)" v-model="item.fpaymentsl2" :class="{inputInfo:checkForm[index].afterMoney || checkForm[index].compare}" @input="regionSendNum(index)">
                                            <span>赠送</span>
                                            <input :class="{inputInfo:checkForm[index].intergral || checkForm[index].intergralCompare}"  type="text" @input="regionIntergralNum(index)" v-model="item.fintegral">
                                            <span>积分</span>
                                            <button type="primary" icon="search" @click="del()" v-if="index === regionForm.length -1 && index !=0">删除</button>
                                            <p v-show="checkForm[index].compare" class="chargeIntergral-moneyInfo">充值金额后面数值必须大于前面数值</p>
                                            <p v-if="checkForm[index].intergralCompare" class="chargeIntergral-intergralInfo">本行的积分数量必须大于上一行的积分数量</p>
                                            <p v-if="checkForm[index].chargeCompare" class="chargeIntergral-chargeInfo">充值金额最小值必须大于等于上一行区间最大值</p>
                                        </li>
                                    </ul>
                                    <span class="chargeIntergral-regionFullInfo" v-show="fullInfo">只有信息填写完整无误后才能增加下一行优惠区间</span>
                                </div>
                                <div class="chargeIntergral-regionadd">
                                    <div class="chargeIntergral-addBtn" @click="add()">增加优惠区间</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="chargeIntergral-submitWarp">
                        <button class="chargeIntergral-submitBtn" @click="save">保存</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    export default {
        components:{
            tHeader
        },
        created(){
            this.init();
        },
        data(){
            return{

                //是否填满
                fullInfo:false,
                //radio改成可取消的 用的计数器
                clickCount1:0, //计数器
                clickCount2:0, // 计数器
                proportionChargeEmpty:false, //比例充值的金额是否为空
                proportionSendEmpty:false,//比例赠送的积分是否为空
                proportionForm:{
                    fpaymentsl1:'',//比例充值的金额
                    fintegral:'',//比例赠送的积分
                    fid:''
                },
                //检验输入款要用的
                checkForm:[
                    { // 区间送积分的规则判断
                        beforeMoney:false, //  区间前面的充值金额的规则判断
                        afterMoney:false,  //  区间后面的充值金额的规则判断
                        intergral:false, //  区间积分金额的规则判断
                        compare:false,  // 前后两值的比较
                        intergralCompare:false, // 积分比较
                        chargeCompare:false // 充值的金额的比较
                    }
                ],
                radio:'', // 单选框的值
                regionForm:[
                    {
                        fpaymentsl1:'', //区域充值的金额区间1
                        fpaymentsl2:'',//区域充值的金额区间2
                        fintegral:'',//区域充值的赠送的积分
                        fid:''
                    }
                ]
            }
        },
        methods:{
            //区域送积分调加
            add(){
                // 最大添加10条
                var canAdd = true;
                if(this.regionForm.length == 10){
                    this.msg('按区间送积分最多添加10条！','error');
                    canAdd= false;
                }
                let regionFormLast =this.regionForm[this.regionForm.length - 1];

                //下一次的fpaymentsl1 必须大于 fpaymentsl2

                if(this.regionForm.length > 1){
                    if(this.regionForm[this.regionForm.length-1].fpaymentsl1 !='' && this.regionForm[this.regionForm.length-2].fpaymentsl2 != ''){
                        if(parseFloat(this.regionForm[this.regionForm.length-1].fpaymentsl1)< parseFloat(this.regionForm[this.regionForm.length-2].fpaymentsl2)){
                            this.checkForm[this.regionForm.length-1].chargeCompare = true;
                            canAdd= false;
                        }else{
                            this.checkForm[this.regionForm.length-1].chargeCompare = false;
                        }
                    }
                }

                //充值金额比较
                let checkFormLast = this.checkForm[this.regionForm.length - 1];
                if(parseFloat(regionFormLast.fpaymentsl1) >= parseFloat(regionFormLast.fpaymentsl2)){
                    checkFormLast.compare = true;
                    canAdd= false;
                }else{
                    checkFormLast.compare = false;
                }

                // 积分下一行必须大于上一行
                if(this.regionForm.length >1){
                    if(this.regionForm[this.regionForm.length-1].fintegral !='' && this.regionForm[this.regionForm.length-2].fintegral != ''){
                        if(parseFloat(regionFormLast.fintegral) <= parseFloat(this.regionForm[this.regionForm.length-2].fintegral)){
                            this.checkForm[this.regionForm.length-1].intergralCompare = true;
                            canAdd= false;
                        }else{
                            this.checkForm[this.regionForm.length-1].intergralCompare = false;
                        }
                    }

                }

                // 判断输入款是否为空
                if(regionFormLast.fpaymentsl1 == ''){
                    this.checkForm[this.regionForm.length-1].beforeMoney =true;
                    this.fullInfo = true;
                    canAdd = false;
                }else{
                    this.checkForm[this.regionForm.length-1].beforeMoney = false;
                    this.fullInfo = false;
                }
                if(regionFormLast.fpaymentsl2 == ''){
                    this.checkForm[this.regionForm.length-1].afterMoney =true;
                    this.fullInfo = true;
                    canAdd = false;
                }else{
                    this.checkForm[this.regionForm.length-1].afterMoney = false;
                    this.fullInfo = false;
                }
                if(regionFormLast.fintegral == ''){
                    this.checkForm[this.regionForm.length-1].intergral =true;
                    this.fullInfo = true;
                    canAdd = false;
                }else{
                    this.checkForm[this.regionForm.length-1].intergral = false;
                    this.fullInfo = false;
                }
                if(!canAdd){
                    return;
                }

               // 添加
               this.regionForm.push({
                   fpaymentsl1:'',
                   fpaymentsl2:'',
                   fintegral:'',
                   fid:''
               })

                //提示容器
                this.checkForm.push({
                    beforeMoney:false,
                    afterMoney:false,
                    intergral:false,
                    compare:false,
                    intergralCompare:false,
                    chargeCompare:false
                })
            },
            //删除
            del(){
                this.regionForm.splice(this.regionForm.length -1 ,1);
                this.checkForm.splice(this.checkForm.length -1 ,1);
                this.fullInfo = false;
            },
            //输入款的输入限制
            proportionChargeNum (){
                this.proportionForm.fpaymentsl1 = this.proportionForm.fpaymentsl1.replace(/\D+|^0/g,'');
            }
            ,proportionSendNum (){
                this.proportionForm.fintegral = this.proportionForm.fintegral.replace(/\D+|^0/g,'')
            }
            ,regionChargeMoneyNum (index){
                this.regionForm[index].fpaymentsl1 =this.regionForm[index].fpaymentsl1.replace(/\D+|^0/g,'')
            }
            ,regionSendNum (index){
                this.regionForm[index].fpaymentsl2 =this.regionForm[index].fpaymentsl2.replace(/\D+|^0/g,'')
            }
            ,regionIntergralNum(index){
                this.regionForm[index].fintegral =this.regionForm[index].fintegral.replace(/\D+|^0/g,'')
            },
            //radio改成可取消的
            radioClick1(){
                this.clickCount2 = 0;
                this.clickCount1 += 1;
                if(this.clickCount1 == 2){
                    this.radio = '';
                    this.clickCount1 = 0;
                }
            },
            radioClick2(){
                this.clickCount1 = 0;
                this.clickCount2 += 1;
                if(this.clickCount2 == 2){
                    this.radio = '';
                    this.clickCount2 = 0;
                }
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },

            //后面的钱 大于前面的钱
            beforeMoneyCompare(index){
                if(this.regionForm[index].fpaymentsl1 == '' || this.regionForm[index].fpaymentsl2 == ''){

                }else{
                    if(parseFloat(this.regionForm[index].fpaymentsl1) >=  parseFloat(this.regionForm[index].fpaymentsl2)){
                        this.checkForm[index].compare = true;
                    }else{
                        this.checkForm[index].compare = false;
                    }
                }
            },

            //钱失去焦点的判断
            afterMoneyCompare(index){
                if(this.regionForm[index].fpaymentsl1 == '' || this.regionForm[index].fpaymentsl2 == ''){
                    return;
                }
                if(parseFloat(this.regionForm[index].fpaymentsl1) >= parseFloat(this.regionForm[index].fpaymentsl2)){
                    this.checkForm[index].compare = true;
                }else{
                    this.checkForm[index].compare = false;
                }
            },

            //保存按钮
            save(){
                this.fullInfo = false;//保存的时候防止错误提示
                let bFlag = false;
                let regionForm = this.regionForm;
                if(this.proportionForm.fpaymentsl1 == '' && this.proportionForm.fintegral == ''&&
                    regionForm[0].fpaymentsl1 == ''&& regionForm[0].fpaymentsl2 == '' && regionForm[0].fintegral ==''){
                    this.msg('宝贝儿你什么都没填！','error');
                    return;
                }
                //等比例送积分  输入框判断
                if(this.proportionForm.fpaymentsl1 == '' && this.proportionForm.fintegral == ''){
                    this.proportionChargeEmpty = false;
                    this.proportionSendEmpty = false;
                }else if(this.proportionForm.fpaymentsl1 == '' && this.proportionForm.fintegral != ''){
                    this.proportionChargeEmpty = true;
                    this.msg('检查相应的规则！','error');
                    return;
                }else if(this.proportionForm.fpaymentsl1 != '' && this.proportionForm.fintegral == ''){
                    this.proportionSendEmpty = true;
                    this.msg('检查相应的规则！','error');
                    return;
                }else{
                    this.proportionChargeEmpty = false;
                    this.proportionSendEmpty = false;
                }

                 //按区间送积分 输入框判断
                if(regionForm.length == 1 && regionForm[0].fpaymentsl1 == ''
                    && regionForm[0].fpaymentsl2 == '' && regionForm[0].fintegral ==''){
                    this.checkForm[0].compare = false;
                    this.checkForm[0].beforeMoney = false;
                    this.checkForm[0].intergral = false;
                }else{
                    for(var i=0; i<regionForm.length; i++){
                        if(parseFloat(regionForm[i].fpaymentsl1) >= parseFloat(regionForm[i].fpaymentsl2)){
                            this.checkForm[i].compare =true;
                            bFlag = true;
                        }else{
                            this.checkForm[i].compare = false;
                        }
                        if(regionForm[i].fpaymentsl1 == ''){
                            this.checkForm[i].beforeMoney =true;
                            bFlag = true;
                        }else{
                             this.checkForm[i].beforeMoney = false;
                        }
                        if(regionForm[i].fpaymentsl2 == ''){
                            this.checkForm[i].afterMoney =true;
                            bFlag = true;
                        }else{
                             this.checkForm[i].afterMoney = false;
                        }
                        if(regionForm[i].fintegral == ''){
                            this.checkForm[i].intergral =true;
                            bFlag = true;
                        }else{
                             this.checkForm[i].intergral = false;
                        }
                        // 判断积分对比
                        if(i >= 1){
                            if(parseFloat(regionForm[i].fintegral) <= parseFloat(regionForm[i-1].fintegral)){
                                this.checkForm[i].intergralCompare =true;
                                bFlag = true;
                            }else{
                                this.checkForm[i].intergralCompare = false;
                            }
                            if(parseFloat(regionForm[i].fpaymentsl1) < parseFloat(regionForm[i-1].fpaymentsl2)){
                                this.checkForm[i].chargeCompare = true;
                                bFlag= true;
                            }else{
                                this.checkForm[i].chargeCompare = false;
                            }
                        }
                    }
                }
                // 规则错误的时候 判断
                if(bFlag){
                    this.msg('检查相应的规则！','error');
                    return;
                }
                if(this.proportionForm.fpaymentsl1.length > 8){
                    this.msg(`等比例送积分充值金额过大！`, 'error');
                    return;
                }
                if(this.proportionForm.fintegral.length > 8){
                    this.msg(`等比例送积分赠送积分过大！`, 'error');
                    return;
                }
                for(var i=0; i<this.regionForm.length; i++){
                    if(this.regionForm[i].fpaymentsl1.length > 8){
                        this.msg(`按区间送积分${i+1}行第一个充值过大！`, 'error');
                        return;
                    }
                    if(this.regionForm[i].fpaymentsl2.length > 8){
                        this.msg(`按区间送积分${i+1}行第二个充值过大！`, 'error');
                        return;
                    }
                    if(this.regionForm[i].fintegral.length > 8){
                        this.msg(`按区间送积分${i+1}行赠送积分过大！`, 'error');
                        return;
                    }
                }
                //判断生效的对应的输入框有没有值
                if((this.radio == 2 &&
                    regionForm[0].fpaymentsl1 == ''&& regionForm[0].fpaymentsl2 == '' && regionForm[0].fintegral =='') ||
                    (this.radio == 1 && this.proportionForm.fpaymentsl1 === ''&& this.proportionForm.fintegral === '')) {
                    this.msg('生效的对应的输入框没值！', 'error');
                    return;
                }
                //保存接口
                this.$http.post('/integral/rule/giftWhenRecharge.do ',{json:this.queryData()}).then(res => {
                    res = res.data;
                    if(res.success){
                        this.msg('保存成功！','success');
                        this.init();
                        // 初始化页面
                        //Object.assign(this.$data, this.$options.data());
                    }else{
                        this.msg('保存失败！','error');
                    }
                });
            },
            queryData(){
                var arr = [];
                if(this.proportionForm.fintegral == '' && this.proportionForm.fpaymentsl1 == ''){
                    if(this.radio == 2){
                        for(var i =0; i<this.regionForm.length; i++){
                            arr.push(this.regionForm[i]);
                            arr[i].effect = true;
                        }
                    }else{
                        for(var i =0; i<this.regionForm.length; i++){
                            arr.push(this.regionForm[i]);
                            arr[i].effect = '';
                        }
                    }
                }else if(this.regionForm[0].fpaymentsl1 == ''&& this.regionForm[0].fpaymentsl2 == '' && this.regionForm[0].fintegral == ''){
                    if(this.radio == 1){
                        arr.push(this.proportionForm);
                        arr[0].effect = true;
                    }else{
                        arr.push(this.proportionForm);
                        arr[0].effect = '';
                    }
                }else{
                    if(this.radio == 1){
                        for(var i =0; i<this.regionForm.length; i++){
                            arr.push(this.regionForm[i]);
                            arr[i].effect = '';
                        }
                        arr.push(this.proportionForm);
                        arr[arr.length-1].effect = true;
                    }else if(this.radio==2){
                        for(var i =0; i<this.regionForm.length; i++){
                            arr.push(this.regionForm[i]);
                            arr[i].effect = true;
                        }
                        arr.push(this.proportionForm);
                        arr[arr.length -1].effect = '';
                    }else{
                        for(var i =0; i<this.regionForm.length; i++){
                            arr.push(this.regionForm[i]);
                            arr[i].effect = '';
                        }
                        arr.push(this.proportionForm);
                        arr[arr.length -1].effect = '';
                    }
                }
                return arr;
            },
            init(){
                this.$http.post('/integral/rule/recharge/list.do').then(res => {
                  res = res.data;
                  if(res.success){
                      var intergral = res.data.interval;
                      var ratio = res.data.ratio;
                      if(intergral){
                          if(intergral.length !=0){
                              this.checkForm = [];
                              this.regionForm = [];
                              for(var i=0; i<intergral.length; i++){
                                  this.regionForm.push({
                                        fpaymentsl1:intergral[i].fpaymentsl1, //区域充值的金额区间1
                                        fpaymentsl2:intergral[i].fpaymentsl2,//区域充值的金额区间2
                                        fintegral:intergral[i].fintegral,//区域充值的赠送的积分
                                        fid:intergral[i].fid
                                  });
                                 this.checkForm.push({ // 区间送积分的规则判断
                                     beforeMoney:false, //  区间前面的充值金额的规则判断
                                     afterMoney:false,  //  区间后面的充值金额的规则判断
                                     intergral:false, //  区间积分金额的规则判断
                                     compare:false,  // 前后两值的比较
                                     intergralCompare:false, // 积分比较
                                     chargeCompare:false // 充值的金额的比较
                                 });
                                 if(intergral[i].fiseffective){
                                    this.radio = 2;
                                 }
                              }
                          }
                      }
                      if(ratio){
                          if(ratio.length !=0){
                              this.proportionForm.fpaymentsl1 = ratio.fpaymentsl1;
                              this.proportionForm.fid = ratio.fid;
                              this.proportionForm.fintegral = ratio.fintegral;
                              if(ratio.fiseffective == true){
                                  this.radio = 1;
                              }
                          }
                      }
                  }else{
                      this.msg('获取失败！','error');
                  }
                }).catch(e => {
                    console.log(e)
                })
            }
        }
    }
</script>

<style>
    #chargeIntergral-warp{
        width: 100%;
        min-height:100%;
        padding-bottom:50px;
    }
    .chargeIntergral-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .chargeIntergral-radius{
        padding-bottom: 150px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    #chargeIntergral-warp .chargeIntergral-titleInfo{
        color: #f57777;
        font-size: 12px;
        font-weight: normal;
    }
    #chargeIntergral-warp .chargeIntergral-content{
        width: 1004px;
        margin: 0 auto;
    }
    #chargeIntergral-warp .chargeIntergral-proportion{
        margin-top: 50px;
    }
    #chargeIntergral-warp .chargeIntergral-region{
        margin-top: 100px;
    }
    #chargeIntergral-warp .chargeIntergral-proportionTitle,
    #chargeIntergral-warp .chargeIntergral-regionTitle{
        font-size: 14px;
        font-weight: bold;
        margin-bottom: 12px;
    }
    #chargeIntergral-warp .chargeIntergral-proportionTitle input[type="radio"],
    #chargeIntergral-warp .chargeIntergral-regionTitle input[type="radio"]{
        cursor:pointer;
    }
    #chargeIntergral-warp .chargeIntergral-proportionContent,
    #chargeIntergral-warp .chargeIntergral-regionContent{
        margin-left: 108px;
        border: 1px solid #999999;
    }
    .chargeIntergral-proportionContent>p ,
    .chargeIntergral-regionContent >p{
        border-bottom: 1px solid #c6c6c6;
    }
    .chargeIntergral-proportionContent>p span:nth-child(1),
    .chargeIntergral-proportionContent>p span:nth-child(2),
    .chargeIntergral-regionContent>p span:nth-child(1),
    .chargeIntergral-regionContent>p span:nth-child(2){
        color: #333333;
        line-height: 40px;
        font-weight: bold;
    }
    .chargeIntergral-proportionContent>p span:nth-child(1){
         padding-left: 250px;
    }

    .chargeIntergral-proportionContent>p span:nth-child(2){
        padding-left:260px;
    }
    .chargeIntergral-regionContent>p span:nth-child(1){
         padding-left: 230px;
    }

    .chargeIntergral-regionContent>p span:nth-child(2){
        padding-left:320px;
    }
    .chargeIntergral-proportionFormWarp{
        padding: 30px 0 25px 90px;
    }
    .chargeIntergral-regionFormWarp{
        padding: 8px 0px 8px 15px;
        border-bottom: 1px solid #c6c6c6;
        position: relative;
    }
    .chargeIntergral-proportionFormWarp input{
        border: 1px solid #c6c6c6;
        width: 270px;
        height: 22px;
        text-align: center;
    }
    .chargeIntergral-regionFormWarp >ul >li{
        margin: 23px 0;
        position:relative;
    }
    .chargeIntergral-regionFormWarp > ul >li >span,
    .chargeIntergral-proportionFormWarp span{
        padding: 0 8px;
    }
    .chargeIntergral-regionFormWarp input{
        border: 1px solid #c6c6c6;
        width: 200px;
        height: 22px;
        text-align: center;
    }
    .chargeIntergral-radioWarp input[type="radio"]{
        cursor: pointer;
    }
    #chargeIntergral-warp .chargeIntergral-regionadd{
        height: 66px;
        text-align: center;
    }
    #chargeIntergral-warp .chargeIntergral-addBtn{
        display: inline-block;
        line-height: 66px;
        text-align: center;
        width: 140px;
        height:66px;
        color: #38adff;
        font-size: 14px;
        cursor: pointer;
        background: url("../../../../../../static/images/publicImages/add.png") no-repeat 0px 22px;
    }
    .chargeIntergral-regionFormWarp button,
    .chargeIntergral-submitWarp .chargeIntergral-submitBtn{
        background: #38adff;
        color: #fff;
        border: 0px;
        border-radius: 4px;
        cursor: pointer;
    }
