<template>
    <div class="userManage-info" v-show="infoShow" :style="{height:contentShow == 3?'520px':'590px'}">
        <h2>
            详细资料
        </h2>
        <div>
            <h3>
                基础信息
            </h3>
            <div class="userManage-info-warp"><table>
                <tr>
                    <td>用户名称: {{topdata.faccount}}</td>
                    <td>手机号码: {{topdata.fphone}}</td>
                    <td>余额: {{userbaseinfo.balance}}</td>
                </tr>
                <tr>
                    <td>积分: {{userbaseinfo.integral}}</td>
                    <td v-if="topdata.feffect == 0">是否启用: 禁用</td>
                    <td v-else="topdata.feffect == 1">是否启用: 启用</td>
                    <td>创建时间: <span v-if="topdata.fcreateTime">{{topdata.fcreateTime.substring(0,10)}}</span></td>
                </tr>
            </table></div>
            <h3>
                认证信息
            </h3>
            <div class="userManage-info-warp">
                <table v-if="contentShow == 2"> <!-- 企业-->
                <tr>
                    <td>企业名称: {{seeData.fcompanyName}}</td>
                    <td colspan="2" class="userManage-differentId">统一社会信息代码/注册号: {{seeData.fregistrationNumber}}</td>
                </tr>
                <tr>
                    <td>法人代表: {{seeData.frepresentative}}</td>
                    <td class="userManage-companyType">类型: {{seeData.fcompanyType}}</td>
                    <td>注册资本: <span v-if="seeData.fregisteredCapital">{{seeData.fregisteredCapital}}万元</span></td>
                </tr>
                <tr>
                    <td>税字号: {{seeData.ftaxNumber}}</td>
                    <td>成立日期: <span v-if="seeData.festablishDate">{{seeData.festablishDate.substring(0,10)}}</span></td>
                    <td>营业期限:<span v-if="seeData.fbusinesstermStart || seeData.fbusinesstermEnd"> {{seeData.fbusinesstermStart.substring(0,10)}} 到 {{seeData.fbusinesstermEnd.substring(0,10)}}</span></td>
                </tr>
                <tr>
                    <td colspan="3">地址: {{seeData.fcompanyAddress}}</td>
                </tr>
            </table>
                <table v-else-if="contentShow == 1" > <!-- 个人-->
                    <tr>
                        <td>真实姓名: {{seeData.fname}}</td>
                        <td>身份证号: {{seeData.fidNumber}}</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td v-if="seeData.fsex == 1">性别: 男</td>
                        <td v-else-if="seeData.fsex == 2">性别: 女</td>
                        <td v-else="seeData.fsex == 3">性别: &nbsp;</td>
                        <td>民族: {{seeData.fnation}}</td>
                        <td>出生: {{seeData.fbirthday}}</td>
                    </tr>
                    <tr>
                        <td>有效期限: {{seeData.fvalidDate}}</td>
                        <td colspan="2">地址: {{seeData.faddress}}</td>
                    </tr>
                </table>
                <table v-else="contentShow == 3" class="see-noContent"><!--未认证-->
                </table>
            </div>
            <h3>
                其他信息
            </h3>
            <div class="userManage-info-warp">
                <table>
	                <tr>
	                    <td>性别:
                            <span v-if="otherInfo.fsex == 1"> 男</span>
                            <span v-else-if="otherInfo.fsex == 2">女</span>
                            <span v-else>&nbsp;</span>
                        </td>
	                    <td>生日:<span v-if="otherInfo.fbirthday">{{otherInfo.fbirthday.substring(0,otherInfo.fbirthday.length -2)}}</span></td>
	                    <td>籍贯:{{otherInfo.fprovince}}{{otherInfo.fcity}}</td>
	                </tr>
	                <tr>
	                    <td>邮箱地址:{{otherInfo.femail}}</td>
	                    <td colspan="2">传真号码:{{otherInfo.ffax}}</td>
	                </tr>
	                <tr>
	                    <td colspan="3">地址:{{otherInfo.femail}}</td>
	                </tr>
	                <tr>
	                    <td>公司名称:{{otherInfo.fcompanyName}}</td>
	                    <td>企业主姓名:{{otherInfo.fname}}</td>
                        <td>创建时间:<span v-if="otherInfo.fcreateTime">{{otherInfo.fcreateTime.substring(0,otherInfo.fcreateTime.length -2)}}</span></td>
	                </tr>
	                <tr>
	                    <td>厂房面积:</td>
	                    <td>厂房属性:</td>
	                    <td>公司人数:</td>
	                </tr>
	                <tr>
	                    <td>年采购额:</td>
	                    <td colspan="2">运输方式:</td>
	                </tr>
	                <tr>
	                    <td>主要纸板产品:</td>
	                    <td colspan="2">服务的主要终端产品:</td>
	                </tr>
	                <tr>
	                    <td colspan="3">拥有设备:</td>
	                </tr>
	            </table>
            </div>
        </div>
        <div class="userManage-info-closeBtn" @click="closeInfo"></div>
    </div>
</template>
<script>
    export default{

        props:[
            'infoShow',  // 是否显示
            'contentShow', // 显示对应的内容
            'seeData', // 查看数据
            'topdata', //头部信息
            'userbaseinfo', // 基础信息
            'otherInfo' //其他信息
        ],
        data(){
            return {}
        },
        mounted(){

        },
        methods: {
            closeInfo(){
                this.$emit('closeInfo');
            }
        }
    }
</script>
<style>
    /*
        详细信息
    */
    .see-noContent{
        height: 10px;
    }
    .userManage-info-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .userManage-info{
        width:912px;
        background: #fff;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        z-index: 100000;
        border-radius: 8px;
    }
    .userManage-info h2{
        height: 59px;
        border-bottom: 1px #6c6c6c solid;
        line-height: 59px;
        padding-left: 30px;
        font-weight:bold;
    }
    .userManage-info-warp{
        margin-left: 48px;
        line-height: 24px;
        padding: 6px 0 28px 0;
    }
    .userManage-info table{
        display: block;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(1){
        width:324px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(2){
        width:260px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(2).userManage-differentId{
        width: 520px;
    }
    .userManage-info table tr:nth-child(1) td:nth-child(3){
        width:240px;
    }
    .userManage-info h3:nth-child(1){
        margin-top: 20px;
    }
    .userManage-info h3{
        height: 24px;
        width:836px;
        line-height: 24px;
        background: #d7efff;
        padding-left: 10px;
        font-weight: bold;
        margin: 0 auto;
        font-size: 14px;
    }
    .userManage-companyType{
        width:260px;
    }
</style>



