<template>
    <div class="balanceReport-warp">
        <query-list @query="query" :timeType="timeType" :timeSearch="false" :isActiveChange="query"></query-list>
        <el-table border ref="singleTable" highlight-current-row  style="width: 100%"  height="700" :data="tableData">
            <el-table-column property="fphone" label="手机号" align="center">
            </el-table-column>
            <el-table-column property="fcertificateName" label="认证名" align="center">
            </el-table-column>
            <el-table-column property="famount" label="实时余额(元)" align="center">
            </el-table-column>
        </el-table>
        <div class="balanceReport-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'',
                total:0,
                size:15,
                currentPage:0,
                tableData:[], // 列表容器
                certificateName:'',
                phone:''
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.$http.post(`/finance/balanceForms.do`,{
                    pageSize:this.size,
                    pageNo:this.currentPage,
                    certificateName:this.certificateName==''?null:this.certificateName,
                    phone:this.phone==''?null:this.phone,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                    }
                })
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.certificateName = queryJson.certificateName;
                this.phone = queryJson.phone;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .balanceReport-warp{
        position:relative;
    }
    .balanceReport-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .balanceReport-pagination .el-pagination button,
    .balanceReport-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .balanceReport-pagination .el-pager li.btn-quicknext,
    .balanceReport-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .balanceReport-pagination .el-pagination .el-select .el-input input,
    .balanceReport-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .balanceReport-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .balanceReport-query{
        width:56px;
        height:32px;
        border-radius:4px;
        cursor:pointer;
        color:#fff;
        display:inline-block;
        line-height:32px;
        padding-left: 56px;
        background: url(../../../../../../static/images/publicImages/search.png) no-repeat 26px 6px #38acff;
    }
</style>



