<template>
    <div id="abnormalOrders-warp">
        <div class="abnormalOrders-warp-padding">
            <div class="abnormalOrders-radius">
                <tHeader>异常订单</tHeader>
                <div class="abnormalOrders-content">
                    <!--切换导航-->
                    <top-nav :listData="navList" @tab="topTab"></top-nav>
                    <div class="abnormalOrders-queryWarp">
                        <ab-query @query="query"></ab-query>
                        <div class="abnormalOrders-btnGroup">
                            <span v-if="tabType==1" @click="add">新增</span>
                            <span v-if="tabType == 2" @click="disposeOpen">财务受理</span>
                            <span v-else-if="tabType==3" @click="disposeOpen">客服受理</span>
                            <span @click="closeOrder">关闭</span>
                        </div>
                    </div>
                    <!--列表-->
                    <ab-table @checkChange="checkChange" :listData="listData" :pageNumber="pageNo" :pageSize="size"></ab-table>
                </div>
            </div>
        </div>
        <div class="abnormalOrders-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false"
                           @current-change="handleCurrentChange"
                           :current-page="pageNo + 1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[10, 20, 30, 100]">
            </el-pagination>
        </div>
        <div class="abnormalOrders-masker" v-show="mask"></div>
        <confirm-window :confirmWindowShow="confirm" :info="infoMsg" @sure="sureConfirm" @confirmWindowClose="closeConfirm"></confirm-window>
        <common-window :commonWindowShow="commonWindowShow" :windowStyle="windowStyle" @close="closeCommon">
            <div slot="title">{{title}}</div>
            <div slot="content" class="abnormalOrders-handle">
                <!--物流-->
                <ul v-if="tabType ==1">
                    <li class="abnormalOrders-handle-one">
                        <ul class="clearfix abnormalOrders-td">
                            <li  style="padding: 5px 0 0 10px">认证名<span class="red">*</span>:</li>
                            <li  style="padding-left: 10px;">
                                <el-select  v-model="fUserId" clearable filterable remote placeholder="请输入关键词" :remote-method="queryName"   @visible-change="visibleName" @change="getOrderId">
                                    <el-option v-for="item,index in nameList" :key="index" :label="item.fcertificateName" :value="item.fuserId">
                                    </el-option>
                                </el-select>
                            </li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-two">
                        <ul class="clearfix abnormalOrders-td">
                            <li  style="padding: 5px 0 0 10px">手机号<span class="red">*</span>:</li>
                            <li  style="padding-left: 10px;">
                                <el-select  v-model="fuserId" clearable filterable remote placeholder="请输入关键词" :remote-method="queryPhone"   @visible-change="visible" @change="getOrderId">
                                    <el-option v-for="item,index in phoneList" :key="index" :label="item.fphone" :value="item.fuserId">
                                    </el-option>
                                </el-select>
                            </li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-two">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px">订单编号<span class="red">*</span>:</li>
                            <li style="padding-left: 10px;">
                                <el-select  v-model="order"  placeholder="请选择"  @visible-change="visible" :disabled="orderDisabled" @change="orderChange">
                                    <el-option v-for="item,index in orderList" :key="index" :label="item.fchildorderid" :value="item.fchildorderid">
                                    </el-option>
                                </el-select>
                            </li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-three">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px" >异常原因<span class="red">*</span>:</li>
                            <li>
                                &nbsp;&nbsp;<textarea v-model="explain" @input="explainInput()" placeholder="不超过一百字" class="abnormalOrders-handle-explain"  cols="30" rows="10"></textarea>
                            </li>
                        </ul>
                    </li>
                </ul>
                <!--财务-->
                <ul v-if="tabType==2">
                    <li class="abnormalOrders-handle-one">
                        <ul class="clearfix abnormalOrders-td">
                            <li>处理方式:<span class="red">*</span>:</li>
                            <li >
                                <span style="margin: 0 20px">
                                    <el-radio v-model="finhandlingresult" label="1"> 退款</el-radio>
                                </span>
                                <span>
                                    <el-radio v-model="finhandlingresult" label="2"> 驳回</el-radio>
                                </span>
                            </li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-two">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px">退款金额<span class="red">*</span>:</li>
                            <li> &nbsp;&nbsp;<input type="text" class="abnormalOrders-handle-money" v-model="refundamount" disabled> 元</li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-three">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px" >财务说明<span class="red">*</span>:</li>
                            <li>
                                &nbsp;&nbsp;<textarea v-model="fincomments" @input="fincommentsInput()" placeholder="请输入财务说明" class="abnormalOrders-handle-explain"  cols="30" rows="10"></textarea>
                            </li>
                        </ul>
                    </li>
                </ul>
                <!--客服-->
                <ul v-if="tabType==3">
                    <li class="abnormalOrders-handle-one">
                        <ul class="clearfix abnormalOrders-td">
                            <li>处理方式<span class="red">*</span>:</li>
                            <li>
                                 <el-radio-group v-model="fcshandlingtype" @change="radioChange">
                                    <span style="margin: 0 20px">
                                        <el-radio  label="1"> 退款</el-radio>
                                    </span>
                                    <span>
                                        <el-radio label="2" > 补数</el-radio>
                                    </span>
                                 </el-radio-group>
                            </li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-two">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px">订单片数<span class="red">*</span>:</li>
                            <li> &nbsp;&nbsp;<input type="text" class="abnormalOrders-handle-pian" disabled="true"   v-model="amountpiece"> 片</li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-two">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px">异常数量<span class="red">*</span>:</li>
                            <li> &nbsp;&nbsp;<input type="text" class="abnormalOrders-handle-pian" :disabled="patchamountDisabled" @input="patchamountCheck()" v-model="patchamount"> 片</li>
                        </ul>
                    </li>
                    <li class="abnormalOrders-handle-three">
                        <ul class="clearfix abnormalOrders-td">
                            <li style="padding-top: 5px">异常沟通<span class="red">*</span>:</li>
                            <li>
                                &nbsp;&nbsp;<textarea @input="fcscommrecordInput()" v-model="fcscommrecord" placeholder="请输入沟通异常" class="abnormalOrders-handle-explain"  cols="30" rows="10"></textarea>
                            </li>
                        </ul>
                    </li>
                </ul>
                <div class="abnormalOrders-handle-btnwarp">
                    <el-button type="primary" @click="dispose">提交处理</el-button>
                </div>
            </div>
        </common-window>
    </div>
</template>
<script>
    /**
     * Created by qxl on 2017/9/11.
     */
    import confirmWindow from '../../../public/confirmWindow.vue'
    import commonWindow from '../../../public/commonWindow.vue'
    import topNav from './topNav.vue'
    import tHeader from '../../../public/tHeader.vue'
    import abQuery from './abQuery.vue'
    import abTable from './abTable.vue'
    import {VERSION} from '@/util/constant.js'
    import {selectToHighIndex} from '@/util/common.js'
    export default{
        props:['tabType'],
        components:{
            confirmWindow,
            topNav,
            tHeader,
            abQuery,
            abTable,
            commonWindow
        },
        created(){
          this.getListData();
        },
        data(){
            return {
                title:this.getTitle(), //获取标题
                listData:[

                ],//列表数据
                navList: this.getTablist(), //导航数据
                total:0, //总数
                mask:false, //遮罩层
                confirm:false, // 提示窗口
                infoMsg:'确定要关闭吗?', // 提示文字
                commonWindowShow:false, // 财务受理窗口状态
                tabIndex:-2, // 切换索引
                windowStyle:{ //财务受理窗口大小
                    width:'610px',
                    height: this.tabType==3 || this.tabType == 1?'470px':'420px'
                },
                //查询用的
                size:10, //条数
                pageNo:0, // 当前页,
                status:this.tabType== 2?7:null, //状态
                phone:'', //手机
                startTime:'', //开始时间
                endTime:'', //结束时间
                name:'', //名字
                currentRow:null, //当前行
                //物流
                currentUser:[], //当前用户信息
                currentOrder:[],// 但前选中的一行数据
                orderDisabled:true,
                phoneList:[], //手机号容器
                orderList:[], //订单号容器
                nameList:[], // 用户名
                fUserId:'',
                fuserId:'', //用户id
                order:'', //订单号
                explain:'', // 说明
                fphone:'', // 手机号
                phoneQuery:false,
                nameQuery:false,
                noName:false,
                //财务
                fincomments:'',//财务说明
                refundamount:'', //退款金额
                finhandlingresult:'', //财务处理结果
                //客服
                fcshandlingtype:'', //客服处理方式
                patchamount:'', //补片数量
                fcscommrecord:'', //客服沟通记录
                amountpiece:'', // 下单片数
                patchamountDisabled:false
            }
        },
        methods:{
            //受理
            disposeOpen(){
                //财务受理
                if(this.tabType == 2){
                    if(this.currentRow){
                        if(this.currentRow.fstatus != 1){
                            this.msg('只能处理退款中的！','error');
                            return;
                        }
                        this.$http.post(`/customerService/getOrderByOrderId.do`,{
                            childOrderId:this.currentRow.forderid,
                            version:VERSION,
                            fkeyarea:this.currentRow.fkeyarea
                        }).then(res => {
                            res = res.data;
                            if(res.success){
                                this.commonWindowShow = true;
                                this.mask=true;
                                //金额计算方式=纸板片数（客服填写）*单价（取该笔订单单价/片数）
                                //防止报错
                                if(res.data.famountprice && res.data.famountpiece && this.currentRow.fpatchamount ){
                                    this.refundamount = new Number((parseFloat(res.data.famountprice)/parseFloat(res.data.famountpiece))* parseFloat(this.currentRow.fpatchamount)).toFixed(2);
                                }
                            }else{
                                this.msg('获取失败！','error');
                            }
                        })
                    }else{
                        this.msg('请选择一条数据做处理！','error');
                    }
                }
                //客服
                if(this.tabType == 3){
                    if(this.currentRow) {
                        if(this.currentRow.fstatus != 0){
                            this.msg('只能处理待处理的！','error');
                            return;
                        }
                        this.$http.post(`/customerService/getOrderByOrderId.do`,{
                            childOrderId:this.currentRow.forderid,
                            version:VERSION,
                            fkeyarea:this.currentRow.fkeyarea
                        }).then(res => {
                            res = res.data;
                            if(res.success){
                                this.amountpiece =  res.data.famountpiece;//获取下单片数
                                this.commonWindowShow = true;
                                this.mask = true;
                            }else{
                                this.msg('获取失败！','error');
                            }
                        })
                    }else{
                        this.msg('请选择一条数据做处理！','error');
                    }
                }
            },
            //提交
            dispose(){
                //物流
                if(this.tabType == 1){
                    if(this.fuserId == ''){
                        this.msg('请选择手机号！','error');
                        return;
                    }
                    if(this.order == ''){
                        this.msg('请选择订单编号！','error');
                        return;
                    }
                    if(this.explain == ''|| this.explain.length> 100){
                        this.msg('说明不能空或者大于100个字！','error');
                        return;
                    }
                    this.$http.post(`/customerService/saveAbnormalOrder.do`,{
                        fuserid:this.fuserId,
                        fphone:this.currentUser[0].fphone,
                        forderid:this.order,
                        fgroupgoodname: this.currentOrder[0].fgroupgoodname,
                        fexceptionreason:this.explain,
                        fcreator:this.$store.getters.doneAdmin.uname,
                        fcreatorid:this.$store.getters.doneAdmin.id,
                        fkeyarea:this.currentUser[0].fkeyArea,
                        version:VERSION
                    }).then(res => {
                        res = res.data;
                        if(res.success){
                            this.commonWindowShow = false;
                            this.mask=false;
                            this.phoneList =[];
                            this.nameList = [];
                            this.orderList=[];
                            this.fuserId='';
                            this.fUserId = '';
                            this.phoneQuery = false;
                            this.nameQuery = false;
                            this.noName = false;
                            this.order='';
                            this.explain='' ;
                            this.pageNo = 0;
                            this.getListData();
                            this.msg('操作成功！','success');
                        }else{
                            this.msg('操作失败！','error');
                        }
                    })
                }
                //财务
                if(this.tabType == 2){
                    if(this.finhandlingresult == ''){
                        this.msg('请选择处理方式！','error');
                        return;
                    }
                    if(this.fincomments == ''|| this.fincomments.length> 100){
                        this.msg('财务说明不能空或者大于100个字！','error');
                        return;
                    }
                    this.$http.post(`/customerService/financeUpdateAbnormalOrder.do`,{
                        fcscptjournalid:this.currentRow.fcscptjournalid, //客诉流水号
                        ffincomments:this.fincomments, //财务说明
                        ffinhandlingresult:this.finhandlingresult, //财务处理结果
                        ffinoperator: this.$store.getters.doneAdmin.uname,//财务操作人
                        ffinoperatorid:this.$store.getters.doneAdmin.id, //财务操作人id
                        frefundamount:this.refundamount, //退款金额
                        fkeyarea:this.currentRow.fkeyarea, //区域拆分键
                        version:VERSION, //版本号
                        fuserid:this.currentRow.fuserid, //客户id
                        forderid:this.currentRow.forderid //订单id
                    }).then(res =>{
                        res = res.data;
                        if(res.success){
                            this.commonWindowShow = false;
                            this.mask=false;
                            this.finhandlingresult = '';
                            this.refundamount ='';
                            this.fincomments ='';
                            this.pageNo = 0;
                            this.getListData();
                            this.msg('操作成功！','success');
                        }else{
                            this.msg('操作失败！','error');
                        }
                    })
                }
                //客服
                if(this.tabType == 3){
                    if(this.fcshandlingtype == ''){
                        this.msg('请选择处理方式！','error');
                        return;
                    }
                    if(this.patchamount == '' && this.fcshandlingtype == 1){
                        this.msg('请填写异常数量！','error');
                        return;
                    }
                    if((parseFloat(this.amountpiece) <parseFloat(this.patchamount)) && this.fcshandlingtype == 1){
                        this.msg('异常片数大于订单片数！','error');
                        return;
                    }
                    if(parseFloat(this.patchamount) > 10000){
                        this.msg('异常数量最多一万片！','error');
                        return;
                    }
                    if(this.fcscommrecord == ''|| this.fcscommrecord.length> 100){
                        this.msg('异常沟通不能空或者大于100个字！','error');
                        return;
                    }
                    this.$http.post(`/customerService/customerUpdateAbnormalOrder.do`,{
                        fcscptjournalid:this.currentRow.fcscptjournalid, //客诉流水号
                        fcscommrecord:this.fcscommrecord, //财务说明
                        fcshandlingtype:this.fcshandlingtype, //客服处理结果
                        fcsoperator:this.$store.getters.doneAdmin.uname, //客服操作人
                        fcsoperatorid:this.$store.getters.doneAdmin.id, //客服操作人id
                        fpatchamount:this.patchamount == ''?null:this.patchamount, //补片数量
                        fkeyarea:this.currentRow.fkeyarea, //区域拆分键
                        version:VERSION, //版本号
                        orderid:this.currentRow.forderid //订单id
                    }).then(res =>{
                        res = res.data;
                        if(res.success){
                            this.commonWindowShow = false;
                            this.mask=false;
                            this.fcshandlingtype='';
                            this.patchamount='';
                            this.fcscommrecord ='';
                            this.amountpiece = '';
                            this.pageNo = 0;
                            this.getListData();
                            this.msg('操作成功！','success');
                        }else{
                            this.msg(res.msg,'error');
                        }
                    })
                }
            },
            //新增异常;
            add(){
               this.commonWindowShow = true;
               this.mask=true;
            },
            //关闭财务受理窗口
            closeCommon(){
                if(this.tabType == 1){
                    this.phoneList =[];
                    this.nameList = [];
                    this.orderList=[];
                    this.fuserId='';
                    this.fUserId = '';
                    this.phoneQuery = false;
                    this.nameQuery = false;
                    this.noName = false;
                    this.order='';
                    this.explain='' ;
                }
                if(this.tabType == 2){
                    this.finhandlingresult = '';
                    this.refundamount ='';
                    this.fincomments ='';
                }
                if(this.tabType == 3){
                    this.fcshandlingtype='';
                    this.patchamount='';
                    this.fcscommrecord ='';
                    this.amountpiece = '';
                }
                this.commonWindowShow = false;
                this.mask=false;
            },
            //关闭按钮
            closeOrder(){
                if(this.currentRow){
                    let status = this.currentRow.fstatus
                    if( status== 5 || status == 2 || status ==3 || status ==4 ){
                        this.msg('订单已处理,不能关闭!','error');
                        return;
                    }
                    this.mask=true;
                    this.confirm = true;
                }else{
                    this.msg('请选择一条数据做处理！','error');
                }
            },
            //确定关闭
            sureConfirm(){
                this.$http.post(`/customerService/updateAbnormalOrderStatus.do`,{
                    fcscptjournalid:this.currentRow.fcscptjournalid,
                    fstatus:5,
                    fkeyarea:this.currentRow.fkeyarea,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.mask=false;
                        this.confirm = false;
                        this.pageNo = 0;
                        this.getListData();
                        this.msg('关闭成功！','success');
                    }else{
                        this.msg('关闭失败！','error');
                    }
                })

            },
            //取消关闭
            closeConfirm(){
                this.mask=false;
                this.confirm = false;
            },
            //topNav切换
            topTab(index){
                if(this.tabIndex == index){
                    return;
                }
                for(var i=0; i<this.navList.length; i++){
                    this.navList[i].navState = false;
                }
                this.navList[index].navState = true;

                //物流
                if(this.tabType == 1){
                    switch (index) {
                        case 1:
                            this.status = 0;
                            break;
                        case 2:
                            this.status = 6;
                            break;
                        case 3:
                            this.status = 5;
                            break;
                        default:
                            this.status = null;
                            break;
                    }
                }
                //财务
                if(this.tabType== 2 ){
                    switch (index) {
                        case 1:
                            this.status = 1;
                            break;
                        case 2:
                            this.status = 3;
                            break;
                        case 3:
                            this.status = 4;
                            break;
                        case 4:
                            this.status = 5;
                            break;
                        default:
                            this.status = 7;
                            break;
                    }
                }
                //客服
                if( this.tabType == 3 ){
                    switch (index) {
                        case 1:
                            this.status = 0;
                            break;
                        case 2:
                            this.status = 2;
                            break;
                        case 3:
                            this.status = 4;
                            break;
                        case 4:
                            this.status =  1;
                            break;
                        case 5:
                            this.status  = 3;
                            break;
                        case 6:
                            this.status = 5;
                            break;
                        default:
                            this.status = null;
                            break;
                    }
                }
                this.pageNo = 0;
                this.getListData();
                this.tabIndex = index;
            },
            // 一页多少条变化
            handleSizeChange(size){
                this.size = size;
                this.pageNo = 0;
                this.getListData();
            },
            //页码变化
            handleCurrentChange(page){
                this.pageNo = page - 1;
                this.getListData();
            },
            //check 选择
            checkChange(row){
                this.currentRow = row;
            },
            // 获取tablist
            getTablist(){
                if(this.tabType == 1){
                    return[
                        {navContent:'全部', navState:true},
                        {navContent:'待处理', navState:false},
                        {navContent:'已处理', navState:false},
                        {navContent:'已关闭', navState:false},
                    ]
                }else if(this.tabType == 2){
                    return[
                        {navContent:'全部', navState:true},
                        {navContent:'退款中', navState:false},
                        {navContent:'已退款', navState:false},
                        {navContent:'已驳回', navState:false},
                        {navContent:'已关闭', navState:false}
                    ]
                }else if(this.tabType == 3){
                    return [
                        {navContent:'全部', navState:true},
                        {navContent:'待处理', navState:false},
                        {navContent:'已补数', navState:false},
                        {navContent:'已驳回', navState:false},
                        {navContent:'退款中', navState:false},
                        {navContent:'已退款', navState:false},
                        {navContent:'已关闭', navState:false}
                    ]
                }
            },
            //获取 手机
            queryPhone(val){
                if(this.nameQuery){
                    return;
                }
                if(val.length >= 3 && val.length <= 11){
                    this.$http.post(`/outerUser/selectUserIdByPhoneOrCustomerName.do`,{
                        version:VERSION,
                        phone:val
                    }).then(res => {
                        res = res.data;
                        if(res.success){
                            this.phoneList = res.data.result;
                            this.nameList = res.data.result;
                            if(this.phoneList.length){
                                if(this.phoneList[0].fcertificateName){
                                    this.noName = false;
                                }else{
                                    this.fUserId = '';
                                    this.nameList = [];
                                    this.noName = true;
                                }
                            }
                           // console.log(this.phoneList,this.nameList)
                        }else{
                            this.nameList =[];
                            this.phoneList = [];
                            this.fUserId = '';
                            this.fuserId  = '';
                        }
                        selectToHighIndex();
                    })
                }else{
                    this.nameList =[];
                    this.phoneList = [];
                    this.fUserId = '';
                    this.fuserId  = '';
                }
            },
            queryName(val){
                this.noName = false; // 防止制空
                if(this.phoneQuery){
                    return;
                }
                //console.log('name')
                if(val.length){
                    this.$http.post(`/outerUser/selectUserIdByPhoneOrCustomerName.do`,{
                        version:VERSION,
                        certificateName:val
                    }).then(res => {
                        res = res.data;
                        if(res.success){
                            this.nameList = res.data.result;
                            this.phoneList =res.data.result;
                        }else{
                            this.fUserId = '';
                            this.fuserId  = '';
                            this.nameList =[];
                            this.phoneList = [];
                        }
                         //console.log(this.phoneList,this.nameList)
                        selectToHighIndex();
                    })
                }else{
                    this.nameList =[];
                    this.phoneList = [];
                    this.fUserId = '';
                    this.fuserId  = '';
                }
            },
            //获取订单号
            getOrderId(val){
                console.log(val+'val');
               this.fuserId = val;
               if(this.noName){
                   this.fUserId =''
               }else{
                   this.fUserId = val;
               }
               //console.log(this.fUserId+'fuserId',this.fuserId+'fuserId')
                if(this.fuserId == ''){
                    this.orderDisabled = true;
                    this.order='';
                    this.orderList=[];
                    this.phoneList=[];
                    return;
                }else{
                    this.orderDisabled = false;
                }
                this.currentUser = this.phoneList.filter(item =>{
                    return val == item.fuserId;
                });
                this.$http.post(`/customerService/selectOrderIdByUserId.do`,{
                    fuserid:val,
                    fkeyarea:this.currentUser[0].fkeyArea,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.order = '';
                        this.orderList = res.data;
                    }
                })
            },
            //订单号选择
            orderChange(val){
                this.currentOrder = this.orderList.filter(item =>{
                    return val == item.fchildorderid;
                })
            },
            //提高层级
            visible(a){
               if(a){
                   this.phoneQuery = true;
                   selectToHighIndex();
               }else{
                   this.phoneQuery = false;
               }
            },
            visibleName(a){
                if(a){
                    selectToHighIndex();
                    this.nameQuery = true;
                }else{
                    this.nameQuery = false;
                }
            },
            //请求参数
            queryData(){
                return {
                    version:VERSION,
                    pageSize:this.size,
                    pageNo:this.pageNo,
                    status:this.status,
                    phone:this.phone == ''? null: this.phone,
                    startTime:this.startTime==''?null:this.startTime,
                    endTime:this.endTime == ''? null: this.endTime,
                    certificateName:this.name == ''? null: this.name
                }
            },
            //查询事件
            query(queryJson){
                this.phone = queryJson.phone;
                this.startTime = queryJson.startTime;
                this.endTime = queryJson.endTime;
                this.name = queryJson.name;
                this.pageNo = 0;
                this.getListData();
            },
            //查询接口
            getListData(){
                this.$http.post(`/customerService/selectAbnormalOrderList.do`,this.queryData()).then( res => {
                    res = res.data;
                    if(res.success){
                        this.listData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.msg('列表获取失败！','error');
                    }
                })
            },
            //获取title
            getTitle(){
                if(this.tabType == 1){
                    return '物流处理';
                }else if(this.tabType == 2){
                    return '财务处理';
                }else if(this.tabType == 3){
                    return '客服处理';
                }
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            //片数输入
            patchamountCheck(){
                var t = this.patchamount.charAt(0);
                if(t == 0){
                    this.patchamount = '';
                }
                this.patchamount = this.patchamount.replace(/\D+/g,'');
            },
            radioChange(){
                if(this.fcshandlingtype == 2){
                    this.patchamount = '';
                    this.patchamountDisabled = true;
                }else{
                    this.patchamountDisabled = false;
                }
            },
            fincommentsInput(){
                this.fincomments = this.fincomments.replace(/\s/g,'');
            },
            explainInput(){
                this.explain = this.explain.replace(/\s/g,'');
            },
            fcscommrecordInput(){
                this.fcscommrecord = this.fcscommrecord.replace(/\s/g,'');
            }
        }
    }
</script>
<style>
    #abnormalOrders-warp{
        width: 100%;
        height:100%;
        padding-bottom:250px;

    }
    .abnormalOrders-warp-padding{
        padding:30px 30px 20px 30px;
        overflow: hidden;
    }
    .abnormalOrders-radius{
        background: #fff;
        border-radius:8px;
        padding-bottom:100px;
        overflow: hidden;
    }
    .abnormalOrders-content{
        padding:0px 10px;
    }
    .abnormalOrders-td li{
        float:left;
        font-size: 14px;
    }
    .abnormalOrders-td li .red{
        color:red;
    }
    .abnormalOrders-handle{
        padding-left: 38px;
    }
    .abnormalOrders-handle-one{
        padding: 32px 0px;
        height: 26px;
    }
    .abnormalOrders-handle-two{
        height: 32px;
        padding-bottom: 28px;
    }
    .abnormalOrders-handle-money,
    .abnormalOrders-handle-pian,
    .abnormalOrders-handle-phone{
        padding-left: 5px;
        height: 30px;
        width: 195px;
        border: 1px solid #ccc;
        border-radius: 4px;
    }
    .abnormalOrders-handle-phone{
        width: 210px;
    }
    .abnormalOrders-handle-explain{
        width: 450px;
        height: 88px;
        padding: 5px;
        border: 1px solid #ccc;
        border-radius:4px ;
    }
    .abnormalOrders-handle-btnwarp{
        margin: 20px 0 0px 72px;
    }
    /*
        分页
    */
    .abnormalOrders-pagination{
        text-align: center;
    }
    .abnormalOrders-pagination .el-pagination button,
    .abnormalOrders-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .abnormalOrders-pagination .el-pager li.btn-quicknext,
    .abnormalOrders-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .abnormalOrders-pagination .el-pagination .el-select .el-input input,
    .abnormalOrders-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .abnormalOrders-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .abnormalOrders-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 3000;
    }
    .abnormalOrders-btnGroup span{
        width:90px;
        height:35px ;
        text-align: center;
        cursor:pointer;
        line-height: 35px;
        display: inline-block;
        border-radius: 4px;
        margin: 0px 10px;
        background:#38acff;
        color:#fff;
    }
    .abnormalOrders-queryWarp{
        padding-bottom: 20px;
    }


</style>



