<template>
    <div class='carouse-uoloadImg'>
        <el-dialog title="编辑图片" :visible.sync="editImgs" :show-close='false' 
        @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
            <img src="../../../../../static/images/publicImages/close.png" class='carouse-editImg-closeIcon' @click='closeEditImg'>
            <div @mouseout="webImgClass != '' ? showImg = false : ''" @mouseover="webImgClass != '' ? showImg = true : ''" v-if='!IE'>
                <el-upload class="upload-demo" 
                drag 
                action="https://jsonplaceholder.typicode.com/posts/" 
                :on-change='upLoadImg'
                :multiple='false'
                accept='png/jpg' 
                :auto-upload='false'
                :show-file-list='false'
                >
                <div class='carouse-upload-icon' >
                    <img :src='uploadUrl' :class='webImgClass'>
                    <div class="el-upload__text"><em>点击上传</em></div>
                </div>
                <img src='../../../../../static/images/authentication/clickUpload.png' class='carouse-uploadImg-bg' v-show="showImg">
                </el-upload>
            </div>
            <div v-if='IE' class='ie-upload-app carouse-editImg' @mouseout="showImg = false "  @mousemove="showImg = true">
                <upload-ie :checkData='checkIEData' 
                :uploadDate='uploadDate' 
                :uploadSrc='uploadSrc' 
                action='djcrm/banner/addAndUpdataBanner.do'
                @openEditImg='openEditImg'
                @getProportion='getProportion'
                @addImgSuccess='addImgSuccess'></upload-ie>
                <img src='../../../../../static/images/authentication/upload.png' class='ie-default-img'/>
                <img src='../../../../../static/images/authentication/clickUpload.png' class='ie-shack-img' v-if="uploadSrc == '' ? false : showImg "/>
                <p>点击上传</p>
            </div>
            <div class='carouse-upload-font' v-if='webPageType == 1 && fbooth == 1 || fbooth == 2 ? true : false'>建议宽度:  1200px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 500px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:12:5&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
            <div class='carouse-upload-font' v-if='webPageType == 2 && fbooth == 1 ? true : false'>建议宽度:  1125px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 750px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:3:2&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
            <div class='carouse-upload-font' v-if='webPageType == 1 && fbooth == 3 ? true : false'>建议宽度:  760px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 450px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:76:45&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
            <div class='carouse-upload-font' v-if='webPageType == 2 && fbooth == 3 ? true : false'>建议宽度:  1125px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 600px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:15:8&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>  
            <div class='carouse-upload-font' v-if='webPageType == 2 && fbooth == 5 || fbooth == 4 && groupNum != 1 && selectImg != 3 ? true : false'>建议宽度:  540px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 300px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:9:5&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
            <div class='carouse-upload-font' v-if='fbooth == 4 && selectImg == 3 ? true : false'>建议宽度:  540px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 606px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:90:101&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
            <div class='carouse-upload-font' v-if='fbooth == 4 && groupNum == 1 ? true : false'>建议宽度:  1080px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;建议高度: 210px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:36:7&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小:< 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>       
                    <carouse-select 
                        :region='webPageType == 1 && fbooth != 4 ? pcLinkType : appLinkType' 
                        textName='链接类型'
                        @getSelect='getSelectType'
                        :regionName='pcLinkTypeName'
                        :isDisableds='fbooth == 4 ? true : false'
                        >
                    </carouse-select>
                    <div class='carouse-uoloadImg-input' v-if='showInput'>
                        <el-input v-model="upLoadInput" :maxlength='36' :placeholder="webPageType == 1 ? '请直接输入域名如:www.baidu.com,长度不超过36位' : '请直接输入域名如:https://www.baidu.com,不超过36位'"></el-input>
                    </div>
                    <div class='carouse-uoloadImg-select' v-if='showList'>
                        <carouse-select 
                        :region='selectNum == 2 ? buyList : selectNum == 3 ? ShopList : selectNum == 4 ? buyDetal : shopDetal'
                        @getSelect='getGroupSelect'
                        :regionName = "defaultName != '' ? defaultName : selectNum  == 2 ? buyList[0].regionName : selectNum == 3 ? ShopList[0].regionName : selectNum  == 4 ? buyDetal[0].regionName : shopDetal[0].regionName"
                        :isDisableds='fbooth == 4 && this.num != 4 ? true : false'
                        > </carouse-select>
                    </div>
                    <div class='carouse-upload-btn'>
                        <el-button @click='closeEditImg'>取消</el-button>
                        <el-button type='primary' @click='saveUpImg'>确定</el-button>
                    </div>
        </el-dialog>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import propt from '../../../public/propt'
import {VERSION} from '@/util/constant.js' 
import {checkURL} from '@/util/common.js'
import uploadIe from '@/components/public/uploadIE'
export default {
    props:['editImg', 'fids', 'pleaseCodes', 'webPageTypes','fbooths',
    'ffileIds','uploadUrls','webClass','nums', 'groupNums','upLoadInputs',
    'fproductids','files','pcLinkTypeNames','buyDetals','shopDetals','appLinkTypes', 'selectImg'],
    created(){
        if(navigator.appName == "Microsoft Internet Explorer" && 
            navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
            this.IE = true;  
            return
        } 
    },
	data () {
		return {
            IE:false,
            uploadSrc:'',
            addSuccess:false,
            checkIEData:{
                size:1,
                Proportion:12/5
            },
            uploadDate:[],
            pcLinkType:[
                {regionName:'普通网页' , number:1} //选择上传图片类型 
            ],
            appLinkType:this.appLinkTypes != undefined ? this.appLinkTypes : [
                {regionName:'普通网页' , number:1}, //选择上传图片类型 
                {regionName:'团购列表' , number:2},
                {regionName:'商城列表' , number:3},
                {regionName:'团购商品详情' , number:4},
                {regionName:'商城商品详情' , number:5}
            ],
            buyList:[
                {regionName:'全部列表', number:0},     //团购列表
                {regionName:'限量抢购列表', number:1},
                {regionName:'爆款特价列表', number:2}
            ],
            ShopList:[
                {regionName:'商城首页',  number:3}    //商城列表
            ],
            buyDetal:this.buyDetals != '' ? this.buyDetals : [
                {regionName:'无团购产品',  id:''}    //团购详情列表
            ],
            shopDetal:this.shopDetals != '' ? this.shopDetals : [
                {regionName:'无积分商城商品', id:''}    //积分商城详情列表
            ],
            isSavePropt:false,
            showImg:false,                  //已经上传图片时，鼠标移入显示遮罩
            showInput:false,                //选择内嵌网页类型显示输入框
            showList:false,                 //选择团购列表显示下拉列表
            upLoadInput:this.upLoadInputs,  //普通网页链接地址
            uploadUrl:this.uploadUrls,
            webImgClass:this.webClass,         
            Propt:false,
            text:'',                         //提示信息自定义
            file:this.files,                 //上传图片文件
            num:this.nums,                   //链接类型
            fid:'',                          //fid
            pleaseCode:this.pleaseCodes,     //区域拆分键
            webPageType:this.webPageTypes,   //pc或者移动
            groupNum:this.groupNums,         //功能列表Num
            fbooth:this.fbooths,             //页面
            ffileId:this.ffileIds,           //文件ID
            selectNum:'',
            getId:this.fproductids,
            editImgs:this.editImg,
            pcLinkTypeName:this.pcLinkTypeNames,//默认链接类型名称
            defaultName:''                   //链接类型对应列表默认名称
		}
	},
	methods:{
       //获取添加，修改图片链接类型
       getSelectType(type){
            this.showInput = false;
            this.showList = false;
            this.defaultName = '';
            if(type === '普通网页'){
                this.showInput = true;
                this.groupNum = 1;
                this.num = 1;
            }else{
                this.showList = true;
                this.upLoadInput = '';
            }
            for(let i=0; i<this.appLinkType.length; i++){
                if(type == this.appLinkType[i].regionName){
                    this.num = this.appLinkType[i].number;
                }
            }
            this.selectNum = this.num;
            //团购商品默认值
            if(this.num === 4){
                 this.getId = this.buyDetal[0].id;
                 return
            }
            //商城商品默认值
            if(this.num === 5){
                 this.getId = this.shopDetal[0].id;
                 return
            }
            //商城列表默认值
            if(this.num === 3){
                 this.groupNum = 3;
                 return
            }
            //团购列表默认值
            if(this.num === 2){
                 this.groupNum = 0;
                 return
            }
       },
       //关闭弹窗
       closeEditImg(){
          this.$emit('closeEditImg');
       },
       //确定
       saveUpImg(){
            if(this.IE){
                if(this.num == '' && this.addSuccess){
                    this.$confirm('链接类型未选择,是否直接保存?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    closeOnClickModal:false,
                    type: 'warning'
                    }).then(() => {
                        this.editImgs = false; 
                        this.saveIeImg();
                    }).catch(() => {
                        this.editImgs = false; 
                        setTimeout(()=> {
                            this.editImgs = true;
                        }, 1);     
                    });
                    return
                }
                if(this.num == '' && !this.addSuccess){
                    this.$emit('closeEditImg');
                    return
                }
                this.saveIeImg();
                return
            }
            if(this.num == '' && this.uploadUrl !== '../../../../../static/images/authentication/upload.png'){
                if(this.num == ''){
                    this.$confirm('链接类型未选择,是否直接保存?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    closeOnClickModal:false,
                    type: 'warning'
                    }).then(() => {
                        this.editImgs = false; 
                        this.commonUpImg();
                    }).catch(() => {
                        this.editImgs = false; 
                        setTimeout(()=> {
                            this.editImgs = true;
                        }, 1);     
                    });
                    return
                }
            }
            if(this.uploadUrl == '../../../../../static/images/authentication/upload.png'){
                this.$emit('closeEditImg');
                return
            }
            this.commonUpImg();
       },
       //上传图片
       upLoadImg(file){
            var img_url = file.url;
            var img = new Image();
            img.src = img_url;
            var q = this;
            img.onload = function(){
                var b = img.width/img.height + '';
                var imgType = file.raw.type.slice(6);
                if(imgType != "png" &&
                    imgType != "jpg" &&
                    imgType != "jpeg"){
                    q.$confirm('图片格式不符，请重新上传!', '提示', {
                        confirmButtonText: '确定',
                        showCancelButton:false,
                        type: 'warning'
                    }).then(() => {
                       q.editImgs = false; 
                        setTimeout(()=> {
                            q.editImgs = true;
                        }, 1);  
                    })
                    return;
                }
                //12:5
                if(q.webPageType == 1 && q.fbooth == 1 || q.fbooth == 2){
                    if(b.slice(0,3) != 2.4 ){
                       q.showPropt(q);
                       return;
                    }
                }
                //3:2
                if(q.webPageType == 2 && q.fbooth == 1 ){
                    if(b.slice(0,3) != 1.5 ){
                       q.showPropt(q);
                       return;
                    }
                }
                //76:45
                if(q.webPageType == 1 && q.fbooth == 3 ){
                    if(b.slice(0,4) < 1.68 || b.slice(0,4) > 1.70){
                       q.showPropt(q);
                       return;
                    }
                }
                //15:8
                if(q.webPageType == 2 && q.fbooth == 3 ){
                    if(b != 1.875 ){
                       q.showPropt(q);
                       return;
                    }
                }
                //9:5
                if(q.webPageType == 2 && q.fbooth == 5 || q.fbooth == 4 && q.groupNum != 1 && q.selectImg != 3){
                    if(b != 1.8 ){
                       q.showPropt(q);
                       return;
                    }
                }
                //90:101
                if(q.fbooth == 4 && q.selectImg == 3){
                    if(b != 90/101 ){
                       q.showPropt(q);
                       return;
                    }
                }
                //36:7
                if(q.fbooth == 4 && q.groupNum == 1){
                    if(b != 36/7 ){
                       q.showPropt(q);
                       return;
                    }
                }
                var size = file.size / 1024 / 1024;
                if(size > 1){
                    q.$confirm('图片大小不符，请重新上传!', '提示', {
                        confirmButtonText: '确定',
                        showCancelButton:false,
                        type: 'warning'
                    }).then(() => {
                            q.editImgs = false; 
                            setTimeout(()=> {
                            q.editImgs = true;
                        }, 1);  
                    })
                    return;
                }
                q.file = file;
                q.uploadUrl = img.src;
                q.webImgClass = 'carouse-web-img';
            }
       },
       //上传图片接口
       commonUpImg(){
            var data = new FormData();
            if(this.file.raw == undefined && this.ffileId !== ''){
                    data.append('fhasPicture', false);
            }
            if(this.file.raw !== undefined && this.ffileId !== ''){
                    data.append('fhasPicture', true);
            }
            //初始化团购入口位置
            if(this.fbooth == 4 || this.fbooth == 5){
                    if(this.fid !== '' && this.ffileId == '' ){
                        this.ffileId = '';
                        data.append('fhasPicture', true);
                        data.append('fadFirstEdit', true);
                    }else{
                        data.append('fhasPicture', false);
                        data.append('fadFirstEdit', false);
                    }
            }
            data.append('fkeyarea', this.pleaseCode);
            data.append('farea', this.pleaseCode);
            data.append('fclient', this.webPageType);
            data.append('moduleid', 'P11006');
            data.append('file', this.file.raw);
            data.append('fbooth', this.fbooth);
            data.append('fid', this.fid);
            data.append('ffileid', this.ffileId);
            data.append('furl', this.upLoadInput);
            if(this.num == 1 && this.upLoadInput != ''){
                if(!checkURL(this.upLoadInput) && this.num === 1 || this.upLoadInput.indexOf('http') != -1 && this.webPageType == 1){
                    this.$message({
                        message: '请输入正确的域名如:www.baidu.com',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            if(this.num == 1 && this.upLoadInput != ''){
                if(!checkURL(this.upLoadInput) && this.num === 1 || this.upLoadInput.slice(0,8) != 'https://' && this.webPageType == 2){
                    this.$message({
                        message: '请输入正确的域名如:https://www.baidu.com',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            if(this.num === 1){
               data.append('fjumpposition', this.num);
            }
            if(this.num != '' && this.num !== 4 && this.num !== 5 && this.num !== 1){
                data.append('fjumpposition', this.num);
                data.append('ffunctiontype', this.groupNum);
            }
            if(this.num === 4 || this.num === 5){
                data.append('fjumpposition', this.num);
                data.append('fproductid', this.getId);
            }
            // console.log(this.file + '----' + 'file')
            // console.log(this.webPageType + '----' + 'fclient')
            // console.log(this.pleaseCode +  '----' + 'pleaseCode')
            // console.log(this.ffileId +  '----' + 'ffileId')
            // console.log(this.fid +  '----' +'fid')
            // console.log(this.fbooth +  '----' + 'fbooth')
            // console.log(this.upLoadInput +  '----' + 'furl')
            // console.log(this.groupNum +  '----' + 'ffunctiontype')
            // console.log(this.getId +  '----' + 'fproductid')
            // console.log(this.num +  '----' + 'fjumpposition')
            this.$http.post('/banner/addAndUpdataBanner.do', data).then(res => {
                if (res.data.success) {
                    this.$emit('saveUpImg');
                    if(this.fid === ''){
                        this.$message({
                            message: '上传成功',
                            type: 'success',
                            duration: 2000
                        });
                    }else{
                        this.$message({
                            message: '修改成功',
                            type: 'success',
                            duration: 2000
                        });
                    }
                    this.file = '';
                    this.fid = '';
                }else{
                    if(this.fid === ''){
                        this.$message({
                            message: '上传失败',
                            type: 'error',
                            duration: 2000
                        });
                    }else{
                        this.$message({
                            message: '修改失败',
                            type: 'error',
                            duration: 2000
                        });
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
       },
       //获取团购列表消息,根据num获取对应的数组
       getGroupSelect(name){
            if(this.num === 2){
                for(let i=0; i<this.buyList.length; i++){
                    if(name == this.buyList[i].regionName){
                        this.groupNum = this.buyList[i].number;
                        return
                    }
                }
            }
            if(this.num === 4){
                for(let i=0; i<this.buyDetal.length; i++){
                    if(name == this.buyDetal[i].regionName){
                        this.getId = this.buyDetal[i].id;
                        return
                    }
                }
            }
            if(this.num === 5){
                for(let i=0; i<this.shopDetal.length; i++){
                    if(name == this.shopDetal[i].regionName){
                        this.getId = this.shopDetal[i].id;
                        return
                    }
                }
            }
       },
       openDialog(){
            this.$store.commit('openZindexModel');
       },
       closeDialog(){
            this.$store.commit('closeZindexModel');
       },
       //上传图片时调用
       showPropt(q){
           q.$confirm('图片比例尺寸不符，请重新上传!', '提示', {
                confirmButtonText: '确定',
                showCancelButton:false,
                type: 'warning'
            }).then(() => {
                q.editImgs = false; 
                setTimeout(()=> {
                     q.editImgs = true;
                }, 1);  
            })
       },
       //IE9专用
       openEditImg(){
            this.editImgs = false; 
            setTimeout(()=> {
                this.editImgs = true;
            }, 1); 
       },
       //IE9专用
       getProportion(){
                //12:5
                if(this.webPageType == 1 && this.fbooth == 1 || this.fbooth == 2){
                      this.checkIEData.Proportion = 12/5;
                }
                //3:2
                if(this.webPageType == 2 && this.fbooth == 1 ){
                      this.checkIEData.Proportion = 3/2;
                }
                //76:45
                if(this.webPageType == 1 && this.fbooth == 3 ){
                    this.checkIEData.Proportion = 76/45;
                }
                //15:8
                if(this.webPageType == 2 && this.fbooth == 3 ){
                    this.checkIEData.Proportion = 15/8;
                }
                //9:5
                if(this.webPageType == 2 && this.fbooth == 5 || this.fbooth == 4 && this.groupNum != 1 && this.selectImg != 3){
                    this.checkIEData.Proportion = 9/5;
                }
                //90:101
                if(this.fbooth == 4 && this.selectImg == 3){
                    this.checkIEData.Proportion = 90/101;
                }
                //36:7
                if(this.fbooth == 4 && this.groupNum == 1){
                    this.checkIEData.Proportion = 36/7;
                }          
       },
       //Ie9获取图片成功
       addImgSuccess(){
          this.addSuccess = true;
       },
       //IE上传
       saveIeImg(){
            this.uploadDate = [];
            if(!this.addSuccess && this.ffileId !== ''){
                this.uploadDate.push({name:'fhasPicture', data:false});
            }
            if(this.addSuccess && this.ffileId !== ''){
                this.uploadDate.push({name:'fhasPicture', data:true});
            }
            //初始化团购入口位置
            if(this.fbooth == 4 || this.fbooth == 5){
                    if(this.fid !== '' && this.ffileId == '' ){
                        this.ffileId = '';
                        this.uploadDate.push({name:'fhasPicture', data:true});
                        this.uploadDate.push({name:'fadFirstEdit', data:true});
                    }else{
                        this.uploadDate.push({name:'fhasPicture', data:false});
                        this.uploadDate.push({name:'fadFirstEdit', data:false});
                    }
            }
            this.uploadDate.push({name:'fkeyarea', data:this.pleaseCode});
            this.uploadDate.push({name:'farea', data:this.pleaseCode});
            this.uploadDate.push({name:'fclient', data:this.webPageType});
            this.uploadDate.push({name:'moduleid', data:'P11006'});
            this.uploadDate.push({name:'fbooth', data:this.fbooth});
            this.uploadDate.push({name:'fid', data:this.fid});
            this.uploadDate.push({name:'ffileid', data:this.ffileId});
            this.uploadDate.push({name:'furl', data:this.upLoadInput});
            if(this.num == 1 && this.upLoadInput != ''){
                if(!checkURL(this.upLoadInput) && this.num === 1 || this.upLoadInput.indexOf('http') != -1 && this.webPageType == 1){
                    this.$message({
                        message: '请输入正确的域名如:www.baidu.com',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            if(this.num == 1 && this.upLoadInput != ''){
                if(!checkURL(this.upLoadInput) && this.num === 1 || this.upLoadInput.slice(0,8) != 'https://' && this.webPageType == 2){
                    this.$message({
                        message: '请输入正确的域名如:https://www.baidu.com',
                        type: 'error',
                        duration: 2000
                    });
                    return
                }
            }
            if(this.num === 1){
                this.uploadDate.push({name:'fjumpposition', data:this.num});
            }
            if(this.num != '' && this.num !== 4 && this.num !== 5 && this.num !== 1){
                this.uploadDate.push({name:'fjumpposition', data:this.num});
                this.uploadDate.push({name:'ffunctiontype', data:this.groupNum});
            }
            if(this.num === 4 || this.num === 5){
                this.uploadDate.push({name:'fjumpposition', data:this.num});
                this.uploadDate.push({name:'fproductid', data:this.getId});
            }
            setTimeout(()=> {
                document.getElementById("uploadForm").submit();
            }, 100);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    if(this.fid === ''){
                        this.$message({
                                message: '上传成功',
                                type: 'success',
                                duration: 2000
                        });
                    }else{
                        this.$message({
                            message: '修改成功',
                               type: 'success',
                            duration: 2000
                        });
                    }
                    this.addSuccess = false;
                    this.$emit('saveUpImg');
                }else{
                    if(this.fid === ''){
                        this.$message({
                            message: '上传失败',
                            type: 'error',
                            duration: 2000
                        });
                    }else{
                        this.$message({
                            message: '修改失败',
                            type: 'error',
                            duration: 2000
                        });
                    }
                }
            }, 500);
       }
    },
    components:{
        carouseSelect,
        propt,
        uploadIe
    },
    watch:{
        fids(fid){
            this.fid = fid;
        },
        pleaseCodes(code){
            this.pleaseCode = code;
        },
        webPageTypes(type){
            this.webPageType = type;
        },
        ffileIds(id){
            this.ffileId = id;
        },
        //初始化图
        uploadUrls(imgPath){
            this.uploadUrl = imgPath;
            //IE9
            this.addSuccess = false;
            if(imgPath != '../../../../../static/images/authentication/upload.png'){
                this.uploadSrc = imgPath;
            }else{
                this.uploadSrc = '1';
                setTimeout(()=> {
                    this.uploadSrc = '';
                }, 1);
            }
        },
        editImg(val){
            this.editImgs = val;
        },
        //链接类型
        nums(val){
            if(val == '-2'){
                this.pcLinkTypeName = '';
                return
            }
            if(val == '' || val == undefined || val == '-1'){
                this.num = '';
                this.pcLinkTypeName = '';
                this.showInput = false;
                setTimeout(()=> {
                    this.showList = false;
                }, 1);
                return
            }
            this.num = val;
            this.selectNum = val;
            this.showInput = false;
            if(this.num == 1 ){
                this.showInput = true;
            }
            if(val == 5 && this.fbooth == 5 ){
                this.pcLinkTypeName = this.appLinkType[1].regionName;
                return
            }
            if(val == 3 && this.fbooth == 5 ){
                this.pcLinkTypeName = this.appLinkType[0].regionName;
                return
            }
            this.pcLinkTypeName = this.appLinkType[val - 1].regionName;
        },
        //选择链接类型下的对应列表
        groupNums(val){
            setTimeout(()=> {
                this.groupNum = val;
            },100)
            if(this.num > 1 ){
                setTimeout(()=> {
                    this.showList = true;
                },2)
            }
            if(val == 3){
                this.defaultName = this.ShopList[0].regionName;
                return
            }
            if(this.num == 2){
                this.defaultName = '';
                setTimeout(()=> {
                    this.defaultName = this.buyList[val].regionName;
                }, 1);
                return
            }
        },
        //网页链接类型
        upLoadInputs(url){
            this.upLoadInput = url; 
        },
        //产品ID
        fproductids(id){
            this.getId = id;
            //团购入口位默认id位undefined时,编辑取列表默认第一个id
            if(this.getId == '' && this.num == 4 || this.getId == undefined && this.num == 4){
                this.getId = this.buyDetal[0].id;
                return
            }
            if(this.getId && this.num == 4){
                for(let i=0; i<this.buyDetal.length; i++){
                    if(this.getId == this.buyDetal[i].id){
                        this.defaultName = '';
                        setTimeout(()=> {
                            this.defaultName = this.buyDetal[i].regionName;
                        }, 1);
                        return
                    }
                }
                this.getId = this.buyDetal[0].id;
            }
            //积分商城入口位默认id位undefined时,编辑取列表默认第一个id
            if(this.getId == '' && this.num == 5 || this.getId == undefined && this.num == 5){
                this.getId = this.shopDetal[0].id;
                return
            }
            if(this.getId && this.num == 5){
                for(let i=0; i<this.shopDetal.length; i++){
                    if(this.getId == this.shopDetal[i].id){
                        this.defaultName = '';
                        setTimeout(()=> {
                            this.defaultName = this.shopDetal[i].regionName;
                        }, 1);
                        return
                    }
                }
                this.getId = this.shopDetal[0].id;
            }
        },
        //默认样式
        webClass(val){
            this.webImgClass = val;
        },
        //是否有新图片更改
        files(val){
            this.file = val;
        },
        //链接类型默认值
        pcLinkTypeNames(val){
            this.pcLinkTypeName = val
        },
        //团购列表
        buyDetals(ary){
            this.buyDetal = ary;
        },
        //积分商城列表
        shopDetals(ary){
            this.shopDetal = ary;
        },
        //链接类型
        appLinkTypes(ary){
            this.appLinkType = ary;
        }
    }
  
}
</script>



