<template>
  <el-pagination 
    @current-change="changePage" 
    @size-change='changSize' 
    :page-sizes="showPageArry" 
    :page-size="showPagelist" 
    layout="total, sizes, prev, pager, next, jumper" 
    :total="listLength"
    :current-page='currentPage'
    >
 </el-pagination>
</template>

<script>
export default {
    props:['showPageArry', 'listLength', 'showPagelist', 'currentPage'],
	data () {
		return {
		}
	},
	methods:{
        //当前页面切换时触发
        changePage(page){
            this.$emit('changePage', page)
        },
        //每页条数改变时触发
        changSize(num){
            this.$emit('changSize', num)
        }
    }  
}
</script>


