<template>
    <div id="lineSendIntergral-warp">
        <div class="lineSendIntergral-warp-padding">
            <div class="lineSendIntergral-radius">
                <tHeader>线下送积分</tHeader>
                <div class="lineSendIntergral-content">
                    <ul class="lineSendIntergral-step">
                        <li class="clearfix">
                            <span class="fl">01</span>
                            <span class="fr">02</span>
                        </li>
                        <li class="clearfix">
                            <div class="fl">
                                <span></span>
                            </div>
                            <div class="fl"></div>
                            <div class="fl">
                                <span></span>
                            </div>
                        </li>
                        <li class="clearfix">
                            <span class="fl">下载调整积分模板</span>
                            <span class="fr">导入调整积分文档</span>
                        </li>
                    </ul>
                    <ul class="clearfix lineSendIntergral-downUpload">
                        <li class="fl lineSendIntergral-down" @mouseover="downMask= true" @mouseout="downMask= false">
                            <div v-show="!downMask"></div>
                            <div v-show="!downMask">
                                <p>下载调整积分模板</p>
                                <p>请勿修改模板格式并严格按照模板格式填写</p>
                                <p>否则导致积分调整积分失败</p>
                            </div>
                            <a href="../../../../../../static/excel/module.xlsx" download="积分模板.xlsx" class="lineSendIntergral-downMask" v-show="downMask">
                                <p>下载积分模板</p>
                            </a>
                        </li>
                        <li class="fr lineSendIntergral-upload" @mouseover="uploadMask= true" @mouseout="uploadMask= false">
                            <div v-show="!uploadMask"></div>
                            <div v-show="!uploadMask">
                                <p>导入调整积分文档</p>
                                <p>请将填好的积分调整文档导入</p>
                            </div>
                            <a href="javascript:;" class="lineSendIntergral-uploadMask" v-show="uploadMask" @click="uploadOpen" >
                                <p>导入调整积分文档</p>
                                <input type="file" v-on:change="changefile"  id="uploadFile" class="lineSendIntergral-uploadFile">
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="lineSendIntergral-waitMask" v-show="waitMask">
            <p>数据导入中，请耐心等待……</p>
        </div>
        <div class="lineSendIntergral-errinfo" v-show="errInfo">
            <h3>提示</h3>
            <span class="lineSendIntergral-errinfo-closeBtn" @click="closeInfo"></span>
            <button class="lineSendIntergral-errinfo-checkBtn" @click="closeInfo">确定</button>
            <p class="lineSendIntergral-error">批量调整积分失败，请检查以下信息：</p>
            <ul>
                <li v-for="item in errorList" class="fl">{{item}}</li>
            </ul>
            <p class="lineSendIntergral-again">请将文件调整后重新导入</p>
        </div>
        <div class="lineSendIntergral-errorMasker" v-show="errInfo"></div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    export default {
        components:{
            tHeader
        },
        created(){
        },
        data(){
            return{
                downMask:false,//下载的mask
                uploadMask:false,//上传的mask
                waitMask:false,//上传读取的mask
                errInfo:false, // 上传提示失败
                errorList:[
                ] // 错误信息列表
            }
        },
        methods:{
            closeInfo(){
                this.errInfo = false; // 关闭错误信息的窗口
            },
            uploadOpen(){
                //清空value
                let uploadFile = document.getElementById('uploadFile');
                uploadFile.value = '';
            },
            msg(msg,type){
                //提示信息
                this.$message({
                    message: msg,
                    type: type
                });
            },
            changefile(){
                //上传file
                let formData = new FormData();
                let uploadFile = document.getElementById('uploadFile');
                let files = uploadFile.files;
                formData.append('file',files[0]);
                let name = files[0].name;
                if(name.endsWith('.xls') || name.endsWith('.xlsx') ){
                    this.waitMask = true;
                    this.$http.post('/integral/rule/uploadExcel.do',formData).then(res => {
                        res = res.data;
                        if(res.success){
                            this.waitMask = false;
                            this.msg('上传成功！','success');
                        }else{
                            this.waitMask = false;
                            this.errInfo =true;
                            this.errorList = res.data;
                        }
                    })
                }else{
                    this.msg('检查上传文件的后缀名！(必须是.xls.xlsx)','error');
                    return;
                }
            }
        }
    }
</script>

<style>
    #lineSendIntergral-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    .lineSendIntergral-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .lineSendIntergral-radius{
        padding-bottom: 300px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    #lineSendIntergral-warp .lineSendIntergral-content{
        width: 1028px;
        margin:114px auto 0;

    }
    .lineSendIntergral-step li:nth-child(1),
    .lineSendIntergral-step li:nth-child(2){
        margin-bottom: 10px;
    }
    .lineSendIntergral-step li:nth-child(1) span:nth-child(1){
        margin-left: 40px;
        font-weight: bold;
    }
    .lineSendIntergral-step li:nth-child(1) span:nth-child(2){
        margin-right: 40px;
        font-weight: bold;
    }
    .lineSendIntergral-step li:nth-child(2) div:nth-child(1){
        margin-left: 38px;
    }
    .lineSendIntergral-step li:nth-child(2) div:nth-child(3){
        margin-right: 38px;
    }
    .lineSendIntergral-step li:nth-child(2) div:nth-child(1),
    .lineSendIntergral-step li:nth-child(2) div:nth-child(3){
        position: relative;
        width: 24px;
        height:24px;
        border-radius: 50%;
        background: #ffab90;
    }
    .lineSendIntergral-step li:nth-child(2) div:nth-child(2){
        width: 636px;
        margin: 13px 8px 0;
        height: 6px;
        border-top:1px dashed #ff8761 ;
    }
    .lineSendIntergral-step li:nth-child(2) div:nth-child(1) span,
    .lineSendIntergral-step li:nth-child(2) div:nth-child(3) span{
        position: absolute;
        width: 14px;
        height:14px;
        left: 50%;
        top:50%;
        margin-top: -7px;
        margin-left: -7px;
        border-radius: 50%;
        background: #ff8761;
    }
    #lineSendIntergral-warp .lineSendIntergral-step{
        width: 776px;
        margin: 0 auto 70px;
        color:#ff8761;
        font-size: 14px;
    }
    .lineSendIntergral-downUpload{
        width:1028px;
        color:#fff;
        font-size: 16px;
        text-align: center;
    }
    .lineSendIntergral-downUpload li{
        width:360px;
        height: 346px;
        position: relative;
    }
    .lineSendIntergral-downUpload .lineSendIntergral-down{
        background: #38adff;
        box-shadow: 0 0 30px #38adff;
    }
    .lineSendIntergral-downUpload .lineSendIntergral-upload{
        background: #ff8761;
        box-shadow: 0 0 30px #ff8761;
    }
    .lineSendIntergral-down div:nth-child(1){
        background:url("../../../../../../static/images/publicImages/download.png") no-repeat 100px 28px;
    }
    .lineSendIntergral-upload div:nth-child(1){
        background:url("../../../../../../static/images/publicImages/upload.png") no-repeat 100px 28px;
    }
    .lineSendIntergral-down div:nth-child(1),
    .lineSendIntergral-upload div:nth-child(1){
        height: 195px;
        border-bottom: 1px solid #fff;
    }
    .lineSendIntergral-down p:nth-child(1){
        font-size: 24px;
        margin-top: 20px;
        margin-bottom: 18px;
    }
    .lineSendIntergral-down p:nth-child(2){
        margin-bottom: 14px;
    }
    .lineSendIntergral-upload p:nth-child(1){
        font-size: 24px;
        margin-top: 20px;
        margin-bottom: 18px;
    }
    .lineSendIntergral-downMask,
    .lineSendIntergral-uploadMask{
        width: 100%;
        height: calc(100% - 256px);
        position:absolute;
        left:0;
        top:0;
        padding-top: 256px;
    }
    .lineSendIntergral-uploadFile{
        cursor:pointer;
    }
    .lineSendIntergral-downMask{
        background:url("../../../../../../static/images/publicImages/download.png") no-repeat 98px 62px rgba(0,0,0,.5);
    }
    .lineSendIntergral-uploadMask{
        background:url("../../../../../../static/images/publicImages/upload.png") no-repeat 98px 62px rgba(0,0,0,.5);
    }
    .lineSendIntergral-downMask p,
    .lineSendIntergral-uploadMask p{
        color: #fff;
    }
    .lineSendIntergral-uploadMask input{
        position: absolute;
        width: 100%;
        height: 100%;
        right: 0;
        top: 0;
        opacity: 0;
        filter: alpha(opacity=0);
    }


    .lineSendIntergral-waitMask{
        position: fixed;
        left: 0px;
        top:0px;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.5);
    }
    .lineSendIntergral-waitMask p{
        left:50%;
        top:50%;
        margin-left: -200px;
        margin-top: -15px;
        position: absolute;
        width: 400px;
        background: url(../../../../../../static/images/publicImages/wait.png) no-repeat 130px 0px;
        font-size: 30px;
        color: #fff;
        padding-top: 180px;
        height: 30px;
    }
    .lineSendIntergral-errinfo{
        left:50%;
        top:50%;
        margin-left: -250px;
        margin-top: -200px;
        position: fixed;
        width: 500px;
        height: 400px;
        background: #fff;
        z-index: 99999;
        border-radius: 4px;
    }
    .lineSendIntergral-errinfo-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .lineSendIntergral-errorMasker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 999;
    }
    .lineSendIntergral-errinfo ul{
        height: 200px;
        padding-left: 90px;
        overflow-y: auto;
        width: 408px;
    }
    .lineSendIntergral-errinfo ul li {
        height: 24px;
        line-height: 24px;
        font-weight: bold;
        width: 130px;
    }
    .lineSendIntergral-errinfo h3{
        font-size: 20px;
        line-height: 56px;
        padding-left: 20px;
    }
    .lineSendIntergral-errinfo-checkBtn{
        position: absolute;
        bottom: 15px;
        right: 20px;
        width:80px;
        height: 40px;
        color:#fff;
        background: #38adff;
        outline: none;
        border: 0px;
        border-radius: 4px;
        cursor: pointer;
    }
    .lineSendIntergral-errinfo p{
        font-size: 14px;
    }
    .lineSendIntergral-error{
        margin-top: 20px;
        height: 34px;
        padding-left: 70px;
        line-height: 34px;
        background: url(../../../../../../static/images/publicImages/redclose.png) no-repeat 25px 0px;
    }
    .lineSendIntergral-again{
        height: 34px;
        padding-left: 70px;
        line-height: 34px;
    }
</style>




