import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/components/login'
import Home from '@/components/home/Home.vue'
import * as Error from '@/components/errLog'
import First from './first'
import Goods from './good'
import Sell from './sell'
import Order from './order'
import Operation from './operation'
import basic from './basic'
import integral from './integration'

var routerConfig = [].concat(First, Goods, Sell, Order, Operation, basic, integral);
Vue.use(Router)


export default new Router({
	mode: 'hash',
    routes: [
        {
            path: '/',
            component: Login
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
		{
			path: '/home',
			name: 'home',
			component: Home,
			children: routerConfig
		},
        {
			path: '*',
			name: 'error',
			component: Error.notFond
		}
	]
})




