<template>
    <div id="intergralSendList-warp">
        <div class="intergralSendList-warp-padding">
            <div class="intergralSendList-radius">
                <tHeader>购买产品送积分</tHeader>
                <div class="intergralSendList-tableQuery-padding">
                    <ul class="intergralSendList-Query clearfix">
                        <li>
                            区<span>域:</span>
                            <el-select v-model="provinceVal" placeholder="请选择" clearable @change="provinceChange" :disabled="selectDisable">
                                <el-option v-for="item in province" :key="item.fprovincecode" :label="item.fprovincename" :value="item.fprovincecode">
                                </el-option>
                            </el-select>
                            <span class="intergralSendList-cross">-</span>
                            <el-select v-model="cityVal" placeholder="请选择" clearable @change="cityChange" :disabled="selectDisable">
                                <el-option v-for="item in city" :key="item.fcitycode" :label="item.fcityname" :value="item.fcitycode">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            合作方：
                            <el-select v-model="partnerVal" placeholder="请选择" clearable :disabled="selectDisable">
                                <el-option v-for="item in partner" :key="item.id" :label="item.oname" :value="item.id">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            <el-button type="primary" icon="search" @click="query()">查询</el-button>
                        </li>
                    </ul>
                     <sendListTable :data="tableData" :currentPage="currentPage" :size="size" :errorList="errorInfoList" @editSave="editSave"></sendListTable>
                </div>
            </div>
        </div>
        <div class="intergralSendList-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total" :small="false"
                           :current-page="currentPage"
                           @current-change="handleCurrentChange"
                           @size-change="handleSizeChange"
                           :page-size="size"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import sendListTable from './sendListTable.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        components:{
            tHeader,
            sendListTable
        },
        created(){
            //上来获取table数据
            this.queryPost(true);
            if(this.$store.getters.doneAdmin.ucompany != 100){
                this.selectDisable = true;
            }
            //省
            this.$http.post('/address/findProvince.do',{ code: "0", status: "1" }).then(res => {
                res = res.data;
                if(res.success){
                    this.province = res.data;
                    if(this.$store.getters.doneAdmin.ucompany != 100){
                        for(var i=0; i<this.province.length; i++){
                            if(this.province[i].fprovincename == this.$store.getters.doneAdmin.oprovince){
                                this.provinceVal = this.province[i].fprovincecode;
                                break;
                            }
                        }
                    }
                }
            })
        },
        data(){
            return{
                selectDisable:false, // 下拉框是否能用状态
                size:15, // 一页数据量
                currentPage:1, // 当前页码
                total:0, // 总数据量
                errorInfoList:[], // 错误提示容器
                queryJson:{}, // 查询数据容器
                provinceVal:'', // 省的value
                cityVal:'', // 市的value
                partnerVal:'', // 合作方的value
                province: [], // 省的数据容器
                city:[], // 市的数据容器
                partner:[], // 合作方数据容器
                //表格里面的数据
                tableData:[],
                fprovinceLabel:'',
                fcityLabel:''
            }
        },
        methods:{
            //一页多少数据
            handleSizeChange(size){
                this.currentPage =1;
                this.size = size;
                this.queryPost(false);
            },
            //页码的变化
            handleCurrentChange(pageNum){
                this.currentPage = pageNum;
                this.queryPost(false);
            },
            //消息
            msg(msg,type){
                //提示信息
                this.$message({
                    message: msg,
                    type: type
                });
            },
            //查询按钮
            query(){
                if(this.cityVal == '' || this.provinceVal == ''){
                    this.fcityLabel = '';
                    this.fprovinceLabel = '';
                }
                this.queryPost(true);
            },
            //查询接口的封装
            queryPost(bFlag){
                // 查询判断是否保存
                /*if(this.errorInfoList != null){
                    for(let i =0; i<this.errorInfoList.length; i++){
                        if(this.errorInfoList[i].editSaveStatus == false){
                            this.msg('宝贝儿你还有东西没有保存！','error');
                            return;
                        }
                    }
                }*/
                // copy 查询数据
                if(bFlag){
                    this.queryJson = this.deepCopy(this.querydata());
                }
                this.queryJson.current=this.currentPage;
                this.queryJson.size=this.size;
                this.productPost();
            },
            //保存和修改
            editSave(index){
                if(this.tableData[index].cellPayment.length > 10){
                    this.msg('付款金额最多10位！','error');
                    return;
                }
                if(this.tableData[index].cellIntegral.length > 10){
                    this.msg('赠送积分最多10位！','error');
                    return;
                }
                if( this.errorInfoList[index].editSaveStatus == false){
                    var bFlag = false;
                    // 判断金额是否为空
                    if(this.tableData[index].cellPayment === ''){
                        this.msg('付款金额不能空！','error');
                        this.errorInfoList[index].money = true;
                        bFlag = true;
                     }else{
                         this.errorInfoList[index].money = false;
                     }
                      // 判断积分是否为空
                     if(this.tableData[index].cellIntegral === '' ){
                        this.msg('积分数量不能空！','error');
                        this.errorInfoList[index].intergral = true;
                        bFlag = true;
                     }else{
                         this.errorInfoList[index].intergral = false;
                     }
                     if(bFlag){
                         return;
                     }
                     if(parseFloat(this.tableData[index].cellPayment) === 0 && parseFloat(this.tableData[index].cellIntegral) >0){
                         this.msg('付款金额为零送积分不合理！','error');
                         this.errorInfoList[index].money = true;
                         return;
                     }else{
                         this.errorInfoList[index].money = false;
                     }
                     // 保存接口
                     this.$http.post('/integral/rule/giftWhenPurchasingWithRatio.do',{
                         version:VERSION, // 版本号
                         fpaymentsl1:this.tableData[index].cellPayment, // 付款金额
                         fintegral:this.tableData[index].cellIntegral, // 赠送积分
                         fpartnerid:this.tableData[index].fsupplierid, // 合作方id
                         fcommodityid:this.tableData[index].fid, // 产品id
                         integralruleid:this.tableData[index].IntegralRuleId, //积分规则id
                         fkeyarea:this.cityVal == ''? this.$store.getters.doneAdmin.ocode:this.cityVal
                     }).then(res =>{
                         res = res.data;
                         if(res.success){
                             this.errorInfoList[index].editSaveStatus = true;
                             this.msg('保存成功！','success');
                             // 只有保存完事所有才刷新下页面
                             if(this.errorInfoList.length){
                                for(let i =0; i<this.errorInfoList.length; i++){
                                    if(this.errorInfoList[i].editSaveStatus == false){
                                        return;
                                    }
                                }
                             }
                             this.productPost();
                         }else{
                             this.msg('保存失败！','error');
                         }
                    })
                }else{
                     this.errorInfoList[index].editSaveStatus = false;
                }
            },
            //查询的数据
            querydata(){
                return {
                    version:VERSION, // 版本号；
                    size:this.size, //  一页数据量
                    current:this.currentPage, // 当前的页码
                    fpartner:this.partnerVal == ''? this.$store.getters.doneAdmin.ucompany:this.partnerVal, // 合作方
                    fkeyarea:this.cityVal == ''? this.$store.getters.doneAdmin.ocode:this.cityVal
                }
            },
            //对象的copy；
            deepCopy(obj){
                if (typeof obj != 'object') {
                    return obj;
                }
                var newobj = {};
                for (var attr in obj) {
                    newobj[attr] = this.deepCopy(obj[attr]);
                }
                return newobj;
            },
            //省change
            provinceChange(val){
                for(let i =0; i<this.province.length; i++){
                    if(val == this.province[i].fprovincecode){
                        this.fprovinceLabel = this.province[i].fprovincename;
                        break;
                    }
                }
                this.$http.post('address/findCity.do',{"code":val,"status":1}).then(res =>{
                    res = res.data;
                    if(res.success){
                        this.city = res.data;
                        this.cityVal = '';
                        if(this.$store.getters.doneAdmin.ucompany != 100){
                            this.cityVal = parseFloat(this.$store.getters.doneAdmin.ocode);
                        }
                    }else{
                        this.city = [];
                        this.cityVal = '';
                    }
                })
            },
            //市change
            cityChange(val){
                let cityLabel = ''
                for(let i =0; i<this.city.length; i++){
                    if(val == this.city[i].fcitycode){
                        cityLabel = this.city[i].fcityname;
                        this.fcityLabel = this.city[i].fcityname;
                        break;
                    }
                }
                // 合作方接口
                this.$http.post('/partners/findOption.do', {city:cityLabel}).then(res =>{
                    res = res.data;
                    if(res.success) {
                        if(res.data.length){
                            this.partner = res.data;
                            this.partnerVal = '';
                            if(this.$store.getters.doneAdmin.ucompany != 100){
                                this.partnerVal = parseFloat(this.$store.getters.doneAdmin.ucompany);
                            }
                        }else{
                            this.partner = [];
                            this.partnerVal = '';
                        }
                    }else{
                        this.partner = [];
                        this.partnerVal = '';
                    }
                })
            },
            // 请求列表接口
            productPost(){
                this.$http.post('/integral/rule/product/list.do ',this.queryJson).then(res =>{
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        if(this.$store.getters.doneAdmin.ucompany != 100 ||
                            (this.fcityLabel=='' && this.fprovinceLabel =='')){
                            for(var i=0; i<this.tableData.length; i++){
                                this.tableData[i].fsupplierarea = `${this.$store.getters.doneAdmin.oprovince}-${this.$store.getters.doneAdmin.ocity}`;
                            }
                        }else{
                            for(var i=0; i<this.tableData.length; i++){
                                this.tableData[i].fsupplierarea = `${this.fprovinceLabel}-${this.fcityLabel}`;
                            }
                        }

                        this.total = res.data.total;
                        // push 错误列表数组
                        this.errorInfoList = [];
                        for(let i in this.tableData){
                            this.errorInfoList.push({
                                money:false, // 付款金额要用的
                                intergral:false, // 积分判断用的
                                editSaveStatus:true // 是否保存要用的
                            })
                        }
                    }
                })
            }
        }
    }
</script>

<style>
    #intergralSendList-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    .intergralSendList-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .intergralSendList-radius{
        padding-bottom: 30px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    /*
        分页
    */
    .intergralSendList-pagination{
        text-align: center;
    }
    .intergralSendList-pagination .el-pagination button,
    .intergralSendList-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .intergralSendList-pagination .el-pager li.btn-quicknext,
    .intergralSendList-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .intergralSendList-pagination .el-pagination .el-select .el-input input,
    .intergralSendList-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .intergralSendList-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .intergralSendList-tableQuery-padding{
        padding: 0 10px;
    }
    #intergralSendList-warp .intergralSendList-Query li:nth-child(1) .el-input__inner,
    #intergralSendList-warp .intergralSendList-Query li:nth-child(3) .el-input__inner{
        height: 34px;
        line-height: 34px;
        width:200px;
    }
    #intergralSendList-warp .intergralSendList-Query li:nth-child(2) .el-input__inner{
        height: 34px;
        line-height: 34px;
        width:200px;
    }
    .intergralSendList-Query{
        padding: 20px 0;
    }
    .intergralSendList-Query li{
        float: left;
    }
    .intergralSendList-Query li:nth-child(1){
        margin-right: 10px;
    }
    .intergralSendList-Query li:nth-child(2){
        margin-right: 20px;
    }
    .intergralSendList-Query li:nth-child(4){
        margin-left: 10px;
    }
</style>



