<template>
  <div class="partnerManage">
        <tHeader>{{topName}}</tHeader>
        <div class='commodity-bg'>
            <commonTop @changeTab='changeTab'
            @getStartTime ='getStartTime'
            @getEndTime = 'getEndTime'
            @searchComdity ='searchComdity'
            @searchOrder ='searchOrder'
            @isChecked ='isChecked'
            @search ='searchFunc'
            @getSelect ='getSelect'
            :regionName ='regionName'
            :startTimes = 'startTimes'
            :endTimes = 'endTimes'
            :commodityNames = 'commodityNames'
            :orders = 'orders'
            :tabArrys="shipType == '0' ? tabArrys : undefined"
            :typeArrys ="shipType == '0' ? typeArrys : undefined"
            :shipTypes='shipType'>
            </commonTop>
            <commonTable
            :listData='listData'
            :shipTypes='shipType'
            @commonFun='commonFun'
            @deliver='deliver'
            @sign='sign'
            @cancelOrder='cancelOrder'
            >
            </commonTable>
            <div class='prchase-list commodity-page'>
                <pageing v-if='listLength > 10'
                        @changePage="changePage"
                        @changSize='changSize'
                        :showPageArry="[10,15,20]"
                        :showPagelist="showPagelist"
                        :listLength="listLength"
                        :currentPage='currentPage'
                    >
                </pageing>
            </div>
            <div class='commodity-dialog'>
                <el-dialog title="取消订单" :visible.sync="dialogFormVisible" :show-close='false' 
                    @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false' >
                    <img src='../../../../../static/images/publicImages/close.png' class='commodity-close-btn' @click='dialogFormVisible = false'>
                    <div class='commodity-dialog-input'>
                        <span>取消原因:</span>
                        <el-input
                            type="textarea"
                            :rows="2"
                            placeholder="请输入取消原因(最多30个字)"
                            v-model="cancelValue"
                            :maxlength="30">
                        </el-input>
                    </div>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureCancelOrder">确 定</el-button>
                    </div>
                </el-dialog>
            </div>
        </div>
   </div>
</template>

<script>
import tHeader from '@/components/public/tHeader'
import pageing from '@/components/public/paging'
import commonTop from './commonTop'
import commonTable from './commonTable'
import {VERSION} from '@/util/constant.js'
export default {
    props:['topName','shipTypes'],
    created(){
       this.admin = this.$store.getters.doneAdmin;
       if(this.shipType == '0'){
           this.searchAllList();
           this.activeName = '0';
           this.status = '-1';
           return
       }
       this.searchFunc();
    },
	data () {
		return {
            cancelIndex:'',
            cancelValue:'',
            dialogFormVisible:false,
            admin:{},
            listLength:0,
            showPagelist:10,
            currentPage:1,
            activeName:'3', //处理状态  
            beginTime:'',   //开始时间
            endTime:'',     //结束时间
            goodName:'',    //商品名称
            orderId:'',     //订单号
            cancle:false,   //是否显示已取消订单
            listData:[],
            status:0,       //发货状态 全部0
            regionName:'',  //订单状态默认值
            startTimes: '', //开始时间默认值
            endTimes:'',    //结束时间默认值
            commodityNames:'', //商品名称默认值
            orders:'',       //订单号默认值
            shipType:this.shipTypes, //发货类型
            tabArrys:[
                { name:'全部', value:'0'},
                { name:'待发货',value:'1'},
                { name:'待消费',value:'2'},
                { name:'待签收',value:'3'},
                { name:'已完成',value:'4'},
                { name:'已取消',value:'5'}
            ],
            typeArrys:[
                { regionName: '全部', value: '2' },
                { regionName: '待处理', value: '0' },
                { regionName: '已处理', value: '1' }
            ]
		}
	},
    methods:{
        //切换页面
        changePage(page){
            this.currentPage = page;
            if(this.shipType == '0'){
                this.searchCommdityFunc();
                return
            }
            this.searchFunc();
        },
        //切换页面条数
        changSize(pageLength){
            this.showPagelist = pageLength;
            if(this.shipType == '0'){
                this.searchCommdityFunc();
                return
            }
            this.searchFunc();
        },
        //选择下单开始时间
        getStartTime(time){
            if(time != ''){
                 var startTimes = (Date.parse(time) / 1000).toString();
                 startTimes = startTimes.slice(0,10);
            }else{
                 var startTimes = '';
            }
            this.beginTime = startTimes;
            this.startTimes = time;
        },
        //选择下单结束时间
        getEndTime(time){
            if(time != ''){
                var endTimes = (Date.parse(time) / 1000).toString();
                endTimes = Number(endTimes.slice(0,10)) + 86400;
            }else{
                var endTimes = '';
            }
            this.endTime = endTimes;
            this.endTimes = time;
        },
        //切换tab
        changeTab(num){
            this.activeName = num;
            this.regionName = '';
            this.currentPage = 1;
            this.startTimes = '';
            this.endTimes = '';
            this.commodityNames = '';
            this.orders = '';
            this.beginTime ='';    //开始时间
            this.endTime = '';     //结束时间
            this.goodName = '';    //商品名称
            this.orderId = '';     //订单号
            this.status = 0;       //订单状态
            if(this.shipType == '0'){
                if(num == 3 || num == 4 || num == 5){
                    this.status = 1
                }
                if(num == 0){
                    this.status = -1
                }
                this.searchCommdityFunc();
                return
            }
            this.searchFunc();
        },
        //查询商品名称
        searchComdity(name){
            this.goodName = name;
            this.commodityNames = name;
        },
        //查询订单号
        searchOrder(num){
            this.orderId = num;
            this.orders = num;
        },
        //查询列表
        searchList(data){
            if(data.beginTime > data.endTime){
                this.$message({
                        message: '下单时间结束时间不能小于开始时间',
                        type: 'warning',
                        duration: 2000
                });
                return
            }
            this.$http.post('/orderController/findOrderPage.do', data).then(res => {
               if(res.data.success){
                   if(res.data.data.orderList == undefined){
                        this.listData = [];
                        this.listLength = 0;
                        return
                   }
                   this.listLength = res.data.data.orderList.totalSize;
                   this.listData = res.data.data.orderList.content;
                   for(var i=0; i<this.listData.length; i++){
                        this.listData[i].number = i + 1;
                        this.listData[i].spec = [];
                        if(this.listData[i].specNameDisplay.length != 0){
                            for(let l=0; l <this.listData[i].specNameDisplay.length; l++){
                                this.listData[i].spec.push(this.listData[i].specNameDisplay[l] + ':' + this.listData[i].specValueDisplay[l])
                            }
                            this.listData[i].spec = this.listData[i].spec.join(',')
                        }else{
                            this.listData[i].spec = '';
                        }
                   }
               }else{
                   this.$message({
                        message: res.data.msg == '开始日期超出范围' ? '开始日期超出范围,只能查询最近3个月' :  res.data.msg,
                        type: 'warning',
                        duration: 2500
                     });
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //查询方法
        searchFunc(){
           if(this.shipType == '0'){
                this.searchCommdityFunc();
                return
           }
           let data = {
               page:this.currentPage,
               shipType:this.shipType,
               cancle:this.cancle ? 1 : 2,
               beginTime:this.beginTime,
               endTime:this.endTime,
               goodName:this.goodName,
               orderId:this.orderId,
               pageSize:this.showPagelist,
               dealStatus:Number(this.activeName),
               status:this.status
           }
           this.searchList(data);
           
        },
        //是否显示
        isChecked(val){
            this.cancle = val;
            this.searchFunc();
        },
        //订单状态获取
        getSelect(data){
            this.status = data.value; 
            this.regionName = data.name;
        },
        //完成.充值
        commonFun(index){
           let data ={
               orderId:this.listData[index].orderId,
               handler:this.admin.uname
           }
           this.$http.post('/orderController/handle.do', data).then(res => {
               if(res.data.success){
                    this.$message({
                        message: '操作成功！',
                        type: 'success',
                        duration: 2000
                     });
                   this.searchFunc();
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //后台签收
        sign(index){
           let data ={
               orderId:this.listData[index].orderId,
               receiver:this.admin.uname
           }
           this.$http.post('/orderController/sign.do', data).then(res => {
               if(res.data.success){
                    this.$message({
                        message: '签收成功！',
                        type: 'success',
                        duration: 2000
                     });
                   this.searchFunc();
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //发货
        deliver(index){
           let data ={
               orderId:this.listData[index].orderId,
               freceiver:this.admin.uname
           }
           this.$http.post('/orderController/deliver.do', data).then(res => {
               if(res.data.success){
                    this.$message({
                        message: '发货成功！',
                        type: 'success',
                        duration: 2000
                     });
                   this.searchFunc();
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //取消订单
        cancelOrder(index){
           this.cancelIndex = index;
           this.cancelValue = '';
           this.dialogFormVisible = true;
        },
        //确定取消订单
        sureCancelOrder(){
           	let data ={
               orderId:this.listData[this.cancelIndex].orderId,
               canceler:this.admin.uname,
               cancelReasons:this.cancelValue,
               areaKey:this.listData[this.cancelIndex].keyArea,
               userId:this.listData[this.cancelIndex].puserId,
               integralRuleType:4,
               integral:this.listData[this.cancelIndex].integral,
               partnerId:this.admin.ucompany,
               remark:this.cancelValue,
               integralJournalType:2,
               partnerAreaKey:this.admin.ocode,
               creator:this.admin.uname,
               isCallback:false,
               keyArea:this.listData[this.cancelIndex].keyArea,
               amount:this.listData[this.cancelIndex].balance,
               forderId:this.listData[this.cancelIndex].orderId,
               ftype:5,
               fremark:this.cancelValue,
               version:VERSION,
               fisSend:false,
               payType:this.listData[this.cancelIndex].payType
           }
           if(this.cancelValue == ''){
               this.$message({
                    message: '请填写取消原因！',
                    type: 'warning',
                    duration: 2000
               });
               return
           }
           this.$http.post('/orderController/cancle.do', data).then(res => {
               if(res.data.success){
                    this.$message({
                        message: '取消订单成功！',
                        type: 'success',
                        duration: 2000
                     });
                    this.dialogFormVisible = false;
                    this.searchFunc();
               }
           }).catch(function (error) {
                 console.log(error);
           });
        },
        //查询所有发货类
        searchAllList(){
            let data = {
                page:this.currentPage,
                pageSize:this.showPagelist
            }
            this.$http.post('/orderController/findAllOrder.do', data).then(res => {
               if(res.data.success){
                    if(res.data.data.allOrder.content == undefined){
                            this.listData = [];
                            this.listLength = 0;
                            return
                    }
                    this.listLength = res.data.data.allOrder.totalSize;
                    this.listData = res.data.data.allOrder.content;
                    for(var i=0; i<this.listData.length; i++){
                            this.listData[i].number = i + 1;
                            this.listData[i].spec = [];
                            if(this.listData[i].specNameDisplay.length != 0){
                                for(let l=0; l <this.listData[i].specNameDisplay.length; l++){
                                    this.listData[i].spec.push(this.listData[i].specNameDisplay[l] + ':' + this.listData[i].specValueDisplay[l])
                                }
                                this.listData[i].spec = this.listData[i].spec.join(',')
                            }else{
                                this.listData[i].spec = '';
                            }
                    }
               }
            }).catch(function (error) {
                    console.log(error);
            });
        },
        //商品发货查询方法
        searchCommdityFunc(){
            let data = {
               page:this.currentPage,
               cancle:this.cancle ? 1 : 2,
               beginTime:this.beginTime,
               endTime:this.endTime,
               goodName:this.goodName,
               orderId:this.orderId,
               pageSize:this.showPagelist,
               dealStatus:Number(this.status),
               status:Number(this.activeName)
           }
           this.searchList(data);
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components: {
        tHeader,
        pageing,
        commonTop,
        commonTable
    },
}
</script>


