<template>
    <div class="adjustWindow-warp" v-show="adjustWindowShow">
        <div class="adjustWindow-titleWarp">
            <h3>积分调整</h3>
        </div>
        <span class="adjustWindow-closeBtn" @click="adjustWindowClose()"></span>
        <div class="adjustWindow-talbe-warp">
            <table class="adjustWindow-table">
            <tr>
                <td>手机号码：</td>
                <td>{{adjustPrompt.fphone}}</td>
            </tr>
            <tr>
                <td>客户名称：</td>
                <td>{{adjustPrompt.fcustName}}</td>
            </tr>
            <tr>
                <td>积分余额：</td>
                <td>{{adjustPrompt.fintegral}}</td>
            </tr>
            <tr>
                <td>积分类型：</td>
                <td>
                    <el-select @visible-change="visible"  v-model="adjustData.value" placeholder="请选择" clearable >
                        <el-option v-for="item in fintegraljournaltypeOpt" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </td>
            </tr>
            <tr>
                <td>积分调整：</td>
                <td>
                    <input type="text" @input="adjustInput()" v-model="adjustData.adjust" class="adjustWindow-adjustText" placeholder="注意:正数为赠送积分，负数为扣除积分">
                </td>
            </tr>
            <tr>
                <td>积分备注：</td>
                <td>
                    <textarea  class="adjustWindow-remarks" @input="remarksInput()" v-model="adjustData.remarks" :autosize="{ minRows: 2, maxRows: 4}" placeholder="选填，不超过20个字符" >
                    </textarea>
                </td>
            </tr>
        </table>
        </div>
        <p class="adjustWindow-notice" v-show="noticeShow1 || noticeShow2 || noticeShow3">{{noticeMsg}}</p>
        <div class="adjustWindow-btnGroup">
            <button class="adjustWindow-cancelBtn" @click="adjustWindowClose()">取消</button>
            <button class="adjustWindow-yesBtn" @click="sure()">确定</button>
        </div>
    </div>
</template>
<script>
    import {selectToHighIndex} from '@/util/common.js'
    export default{
        props:['adjustWindowShow','adjustData','adjustPrompt'],
        data(){
            return{
                value:'',
                noticeMsg:'',
                noticeShow1:false,
                noticeShow2:false,
                noticeShow3:false,
                fintegraljournaltypeOpt: [
                    {
                        value:5,
                        label:'社区活动积分'
                    },{
                        value:6,
                        label:'商城活动积分'
                    },
                    {
                        value:7,
                        label:'包辅活动积分'
                    },{
                        value:8,
                        label:'会员返利积分'
                    },{
                        value:9,
                        label:'专享返利积分'
                    },{
                        value:10,
                        label:'其他返利积分'
                    }
                ]
            }
        },
        methods: {
            adjustWindowClose(){
                this.noticeShow1 = false;
                this.noticeShow2 = false;
                this.noticeShow3 = false;
                this.$emit('adjustWindowClose');
            },
            testSelect(){
                if(this.adjustData.value === ''){
                    this.noticeMsg = '请选择积分类型！';
                    this.noticeShow1 = true;
                }else{
                    this.noticeShow1 = false;
                }
            },
            testAdjust(){
                if(this.adjustData.adjust.length > 10 ){
                    this.noticeMsg = '积分调整数值过大！';
                    this.noticeShow2 = true;
                }else if(this.adjustData.adjust.length ==0 || this.adjustData.adjust === '-'){
                    this.noticeMsg = '请输入积分调整！';
                    this.noticeShow2 = true;
                }else{
                    this.noticeShow2 = false;
                }
            },
            testRemarks(){
                if(this.adjustData.remarks.length > 20){
                    this.noticeMsg = '积分备注释不超过20字符！';
                    this.noticeShow3 = true;
                }else{
                    this.noticeShow3 = false;
                }
            },
            adjustInput(){
                var t = this.adjustData.adjust.charAt(0);
                var t1 = this.adjustData.adjust.charAt(1);
                this.adjustData.adjust = this.adjustData.adjust.replace(/\D/g,'');
                if (t == '-') {
                    this.adjustData.adjust = '-' + this.adjustData.adjust;
                }
                if(t == '0'){
                    this.adjustData.adjust = '';
                }
                if(t =='-' && t1 == '0'){
                    this.adjustData.adjust = '-';
                }
            },
            remarksInput(){
                this.adjustData.remarks = this.adjustData.remarks.replace(/\s/g,'');
            },
            sure(){
                this.testRemarks();
                this.testAdjust();
                this.testSelect();
                if(this.noticeShow1 == true || this.noticeShow2 == true || this.noticeShow3 == true){
                    return;
                }
                this.$emit('sure');
            },
            visible(a){
               if(a){
                   selectToHighIndex();
               }
            }
        }
    }
</script>
<style>
    .adjustWindow-warp{
        width:412px;
        height: 466px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 9000;
    }
    .adjustWindow-talbe-warp{
        padding-left:24px;
    }
    .adjustWindow-table{
        display: block;
    }
    .adjustWindow-table tr:nth-child(1),
    .adjustWindow-table tr:nth-child(2),
    .adjustWindow-table tr:nth-child(3){
        height: 45px;
        vertical-align:text-top;
    }
    .adjustWindow-table tr:nth-child(4),
    .adjustWindow-table tr:nth-child(5){
        height: 52px;
    }
    .adjustWindow-table tr:nth-child(6){
        height: 65px;
    }
    .adjustWindow-titleWarp{
        overflow: hidden;
    }
    .adjustWindow-warp h3{
        font-size: 16px;
        height: 50px;
        line-height: 50px;
        padding-left: 20px;
        margin-bottom: 25px;
        box-shadow:0px 6px 10px #efefef;
    }
    .adjustWindow-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .adjustWindow-btnGroup{
        position: absolute;
        bottom: 20px;
        right: 20px;
    }
    .adjustWindow-btnGroup button{
        width:80px;
        height: 34px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        cursor:pointer;
        outline: none;
    }
    .adjustWindow-btnGroup .adjustWindow-cancelBtn{
        color:#666;
        border:1px solid #c6c6c6;
        background: #fff;
    }
    .adjustWindow-btnGroup .adjustWindow-yesBtn{
        margin-left: 18px;
    }
    .adjustWindow-adjustText{
        width: 280px;
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    .adjustWindow-table .el-input__inner{
        height: 34px;
        line-height: 34px;
    }
    .adjustWindow-table .el-select{
        width: 100%;
    }
    .adjustWindow-notice{
        line-height: 26px;
        padding-left: 110px;
        color:red;
        background: url("../../../../../../static/images/publicImages/notice.png") no-repeat 90px 8px;
    }
    .adjustWindow-remarks{
        height: 56px;
        display: block;
        resize: vertical;
        padding: 5px 7px;
        line-height: 1.5;
        width: 100%;
        color: #1f2d3d;
        border: 1px solid #bfcbd9;
        border-radius: 4px;
        width:264px;
        resize: none;
    }
</style>



