<template>
    <div id="reportIndex-warp">
        <div class="reportIndex-warp-padding">
            <div class="reportIndex-radius">
                <tHeader>报表管理</tHeader>
                <div class="reportIndex-content">
                    <ul class="reportIndex-nav clearfix">
                        <li v-for="(item,index) in navList">
                            <span :class="{active:item.navState}"  @click="tab(index)">{{item.navContent}}</span>
                        </li>
                    </ul>
                    <div v-if="navList[0].navState">
                        <sktj-report></sktj-report>
                    </div>
                    <div v-if="navList[1].navState">
                        <zbtg-report></zbtg-report>
                    </div>
                    <div v-if="navList[2].navState">
                        <hhss-report></hhss-report>
                    </div>
                    <div v-if="navList[3].navState">
                        <balance-report></balance-report>
                    </div>
                    <div v-if="navList[4].navState">
                        <balance-warn></balance-warn>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import sktjReport from './sktjReport.vue'
    import zbtgReport from './zbtgReport.vue'
    import hhssReport from './hhssReport.vue'
    import balanceReport from './balanceReport.vue'
    import balanceWarn from './balanceWarn.vue'
    export default {
        components:{
            tHeader,
            sktjReport,
            zbtgReport,
            hhssReport,
            balanceReport,
            balanceWarn
        },
        data(){
            return {
                navList:[
                    {navContent:'收款统计', navState:true},
                    {navContent:'纸板团购销售报表', navState:false},
                    {navContent:'混合销售报表', navState:false},
                    {navContent:'余额报表', navState:false},
                    {navContent:'余额预警', navState:false}
                ]
            }
        },
        methods:{
            tab(index){
                for(var i=0; i<this.navList.length; i++){
                    this.navList[i].navState = false;
                }
                this.navList[index].navState = true;
            }
        }
    }
</script>

<style>
    #reportIndex-warp{
        width: 100%;
        height:100%;
        padding-bottom:250px;
    }
    .reportIndex-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .reportIndex-radius{
        background: #fff;
        border-radius:8px;
        padding-bottom:100px;
    }
    .reportIndex-content{
        padding:0px 10px;
    }
    .reportIndex-nav{
        height:50px;
        font-size:16px;
        border-bottom:2px solid #b0b0b0;
        padding-left:10px;
    }
    .reportIndex-nav li{
        float:left;
        height:50px;
        line-height:50px;
        padding:0px 22px;
    }
    .reportIndex-nav li span{
        cursor: pointer;
    }
    .reportIndex-nav li span.active{
        color:#38acff;
    }
</style>




