<template>
    <div class="partnerManage partner-propt">
        <tHeader>价格方案审核</tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <div class='commodity-select-three'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                        :isProduce='true'
                    >
                    </regional-choice>
                    <div class='parter-select-top'>
                        <partner
                            @getSelect='getPatnerId'
                            :codeName='codeName'
                            :partName='partnerName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                        ></partner>
                    </div>
                    <div class='partner-select'>
                        <carouse-select
                            textName='审核状态:'
                            :region='produceType'
                            @getSelect='getSelect'
                        ></carouse-select>
                    </div>
                </div>
                <div class='partner-input'>
                    <span>产品名称:</span>
                    <el-input v-model="fproductname" placeholder="请输入内容"></el-input>
                </div>
                <el-button type="primary" icon="search" @click='searchFunc'>搜索</el-button>
            </div>
            <div class='partner-table prchase-list'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="序号" width="60">
                    </el-table-column>
                    <el-table-column prop="viewType" label="审核状态" width='100'>
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="区域" width='130'>
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="合作方名称" width='190'>
                    </el-table-column>
                    <el-table-column prop="fproductname" label="产品名称" width='150'>
                    </el-table-column>
                    <el-table-column prop="type" label="价格方案类型" width="120">
                    </el-table-column>
                    <el-table-column prop="fgroupprice" label="团购价格" width="150">
                    </el-table-column>
                    <el-table-column prop="fmaxarea" label="限量结束" width="110">
                    </el-table-column>
                    <el-table-column prop="flimitedtime" label="限时结束" width="110">
                    </el-table-column>    
                    <el-table-column prop="fcreatetime" label="申请时间" width='130'>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="审核人" width='100'>
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="审核时间" width='150'>
                    </el-table-column>
                    <el-table-column label="操作" fixed="right">
                        <template scope="scope" >
                            <el-button  type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus == '1' && admin.ucompany == 100 ?  true : false">
                                审核
                            </el-button>
                            <el-button type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus != '1' || admin.ucompany != 100 ?  true : false">
                                查看
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class='commodity-propt partner-product-propt'>
            <el-dialog title="价格方案审核" :visible.sync="addNewProduce" :show-close='false' 
             @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='addNewProduce = false'>
                <div class='partner-product-bg'>
                    <div class='partner-plan-title'>
                        <p class='parter-maxLngth'><span>价格方案名称:</span><span>{{parterDetal.fmarketingname}}</span></p>
                        <p class='parter-maxLngth-type'><span class='partner-plan-span'>方案类型:</span><span>{{parterDetal.fgrouptype == '1' ? '限量团购' : parterDetal.fgrouptype == '2' ? '限时团购' : ''}}</span></p>
                        <p class='parter-maxLngth'><span class='partner-plan-span'>产品名称:</span><span>{{parterDetal.fproductname}}</span></p>
                    </div>
                    <div class='partner-plan-font'>方案信息</div>
                    <div class='partner-plan-information'>
                        <p class='parter-maxLngth-price'><span>起步单量:</span><span>{{parterDetal.fminarea}}</span><span>平方米</span></p><span>正式单量:</span><span>{{parterDetal.fnormalarea}}</span><span>平方米</span>
                    </div>
                    <div class='partner-plan-information'>
                        <span v-if="parterDetal.fgrouptype == 2 ? true : false">限时特价:</span><span v-if="parterDetal.fgrouptype == 2 ? true : false">{{parterDetal.flimitedtime}}</span>
                        <span v-if="parterDetal.fgrouptype == 1 ? true : false">总量限购:</span><span v-if="parterDetal.fgrouptype == 1 ? true : false">{{parterDetal.fmaxarea}}</span><span v-if="parterDetal.fgrouptype == 1 ? true : false">平方米</span>
                    </div>
                    <div class='partner-plan-information'>
                        <p class='parter-maxLngth-price'><span>市场价:</span><span>{{parterDetal.funitprice}}</span><span>元/平方米</span></p><span>团购价:</span><span>{{parterDetal.fgroupprice}}</span><span>元/平方米</span>
                    </div>
                    <div class='partner-plan-information'>
                        <span>小单价:</span><span>{{parterDetal.fsmallprice}}</span><span>元/平方米</span>
                    </div>
                    <div class='partner-plan-font'>图片信息</div>
                    <div class='partner-plan-img'>
                        <p class='partner-add-img'>
                            价格列表主图:
                        </p>
                        <img :src='parterDetal.startfigures' class='partner-product-list'>
                    </div>
                    <div class='partner-plan-img'>
                        <p class='partner-add-img'>
                            团购结束显示图:
                        </p>
                        <img :src='parterDetal.endFurls' class='partner-product-list'>
                    </div>
                </div>
                <div class='partner-product-isPass'>
                       <p>审核结果</p>
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '1'  && admin.ucompany == 100 ? true: false">
                            <el-radio class="radio" v-model="radio" label="1">通过</el-radio>
                            <el-radio class="radio" v-model="radio" label="2">驳回</el-radio>
                            <div class='partner-product-input'>
                                <el-input v-model="reject" placeholder="请输入驳回理由（不超过20字符）" :maxlength='20' v-if="radio == '2'"></el-input>
                            </div>
                       </div>
                       <div class='partner-product-btn' v-if="parterDetal.fauditstatus == '1'  && admin.ucompany == 100 ? true: false">
                            <el-button @click='addNewProduce = false'>取消审核</el-button>
                            <el-button type="primary" @click='saveView'>保存审核</el-button>
                       </div>
                        <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '2' ? true: false">
                           <div class='partner-check-pass'>通过</div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>
                       <div class='partner-product-radio' v-if="admin.ucompany != 100 &&　parterDetal.fauditstatus != '3' &&　parterDetal.fauditstatus != '2' ? true: false">
                           <div class='partner-check-pass'>审核中</div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>   
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' ? true: false">
                           <div class='partner-check-pass'>驳回理由:
                               <span>{{parterDetal.fcomment}}</span>
                           </div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>      
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import {VERSION, IMGPATH} from '@/util/constant.js' 
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fauditorid = this.admin.ucompany;
        this.fauditor = this.admin.uname;
        this.parterId = this.admin.ucompany;       //合作方id
        //区域拆分键
        this.fkeyarea = this.admin.ocode;
        //非东京账号登录
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
           }, 1);
           this.searchFunc();
        }
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            produceType:[
                {regionName:'全部', value:'0'},
                {regionName:'审核中', value:'1'},
                {regionName:'已通过', value:'2'},
                {regionName:'已驳回', value:'3'}
            ],
            addNewProduce:false,
            codeName:'',               //查询合作方市
            fkeyarea:'',               //区域拆分键
            parterId:'',               //合作方ID
            radio:'1',
            fauditstatus:'0',          //审核状态
            fproductname:'',           //产品名称
            reject:'',                 //驳回理由
            fgroupareaid:'',           //服务区域ID
            fauditorid:'',             //审核人ID
            fauditor:'',               //审核人
            tableData:[],
            parterDetal:{},
            typeArry:{
                '0':'待上架',
                '1':'审核中',
                '2':'已通过',
                '3':'已驳回'
            }
		}
	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //区域code
        getPlease(please){
            //  this.fgroupareaid = please.code;
             this.codeName = please;
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
            this.tableData = [];
        },
        //合作方选择
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //状态查询
        getSelect(name){
            for(let i=0; i<this.produceType.length; i++){
               if(name == this.produceType[i].regionName){
                    this.fauditstatus = this.produceType[i].value;
               }
            }
        },
        //查询搜索方法
        searchFunc(){
            let data={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                fproductname:this.fproductname,
                pagenum:0,
                pagesize:1000,
                sort:'2'
                // fgroupareaid:this.fgroupareaid
            }
            if(this.partnerName == '' && this.admin.ucompany == 100){
                this.$message({
                    message: '请选择区域合作方！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.$http.post('/product/loadListsPMarketing.do', data).then(res => {
                if(res.data.success){
                    this.tableData = res.data.data;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = i + 1;
                        this.tableData[i].type = this.tableData[i].fgrouptype == '1' ? '限量团购' : this.tableData[i].fgrouptype == '2' ? '限时团购' :'';
                        this.tableData[i].viewType = this.typeArry[this.tableData[i].fauditstatus];
                        this.tableData[i].flimitedtime = this.tableData[i].fgrouptype == 1 ? '/' : this.tableData[i].flimitedtime;
                        this.tableData[i].fmaxarea = this.tableData[i].fgrouptype ==  2 ? '/' : this.tableData[i].fmaxarea;
                        if(this.tableData[i].fauditstatus == 1){
                            this.tableData[i].fupdatetime = '/';
                            this.tableData[i].fauditor = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查看审核
        viewDetails(data){
            this.addNewProduce = true;
            this.reject = '';
            this.radio = '1'
            let datas = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fmarketingplanid:data.fmarketingplanid
            }
            this.$http.post('/product/loadListsPMarketing.do', datas).then(res => {
                if(res.data.success){
                   this.parterDetal = res.data.data[0];
                   this.parterDetal.endFurls = IMGPATH +  res.data.data[0].endfigure.furl;
                   this.parterDetal.startfigures = IMGPATH +  res.data.data[0].startfigure.furl;
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //保存审核
        saveView(){
           let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:this.parterDetal.fmarketingplanid,
                fcomment:this.reject,
                operate:this.radio == '1' ? true : false,
                fauditorid:this.fauditorid,
                fauditor:this.fauditor
            }
            if(this.radio == '2' && this.reject ==''){
                this.$message({
                        message: '请输入驳回理由！',
                        duration: 2000
                     });
                return
            }
            this.$http.post('/product/examineProgrammes.do', data).then(res => {
                if(res.data.success) {
                   this.$message({
                        message: '审核成功！',
                        type: 'success',
                        duration: 2000
                     });
                     this.addNewProduce = false; 
                     this.searchFunc();
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing
    }  
}
</script>


