<template>
    <div class="htmleaf-container">
        <header class="header">
            <p>欢迎登录CRM系统</p>
        </header>
        <canvas id="canvas" v-if="bl"></canvas>
        <div v-else id="canvasFb">
            <img src="./xk.png" alt="">
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            bl: true
        }
    },
    mounted() {
        var browser=navigator.appName
        var b_version=navigator.appVersion
        if(browser=="Microsoft Internet Explorer") {
            var version=b_version.split(";");
            var trim_Version=version[1].replace(/[ ]/g,"");
        }
        if(browser=="Microsoft Internet Explorer" && trim_Version=="MSIE6.0") {
            alert("该版本只支持IE9以上，为了友好的体验，建议升级浏览器版本。");
        }
        else if(browser=="Microsoft Internet Explorer" && trim_Version=="MSIE7.0") {
            alert("该版本只支持IE9以上，为了友好的体验，建议升级浏览器版本。");
        }
        else if(browser=="Microsoft Internet Explorer" && trim_Version=="MSIE8.0") {
            alert("该版本只支持IE9以上，为了友好的体验，建议升级浏览器版本。");
        }
        else if(browser=="Microsoft Internet Explorer" && trim_Version=="MSIE9.0") {   //ie9 的问题
            this.bl = false
        }
        else {
            var canvas = document.getElementById('canvas'),
                ctx = canvas.getContext('2d'),
                w = canvas.width = window.innerWidth,
                h = canvas.height = window.innerHeight,
                hue = 217,
                stars = [],
                count = 0,
                maxStars = 1400;
            var canvas2 = document.createElement('canvas'),
                ctx2 = canvas2.getContext('2d');
            canvas2.width = 100;
            canvas2.height = 100;
            var half = canvas2.width/2,
                gradient2 = ctx2.createRadialGradient(half, half, 0, half, half, half);
            gradient2.addColorStop(0.025, '#fff');
            gradient2.addColorStop(0.1, 'hsl(' + hue + ', 61%, 33%)');
            gradient2.addColorStop(0.25, 'hsl(' + hue + ', 64%, 6%)');
            gradient2.addColorStop(1, 'transparent');

            ctx2.fillStyle = gradient2;
            ctx2.beginPath();
            ctx2.arc(half, half, half, 0, Math.PI * 2);
            ctx2.fill();
// End cache
            function random(min, max) {
                if (arguments.length < 2) {
                    max = min;
                    min = 0;
                }
                if (min > max) {
                    var hold = max;
                    max = min;
                    min = hold;
                }
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }
            var Star = function() {
                this.orbitRadius = random(w / 2 - 50);
                this.radius = random(100, this.orbitRadius) / 10;
                this.orbitX = w / 2;
                this.orbitY = h / 2;
                this.timePassed = random(0, maxStars);
                this.speed = random(this.orbitRadius) / 100000;
                this.alpha = random(2, 10) / 10;
                count++;
                stars[count] = this;
            }
            Star.prototype.draw = function() {
                var x = Math.sin(this.timePassed + 1) * this.orbitRadius + this.orbitX,
                    y = Math.cos(this.timePassed) * this.orbitRadius/2 + this.orbitY,
                    twinkle = random(10);
                if (twinkle === 1 && this.alpha > 0) {
                    this.alpha -= 0.05;
                } else if (twinkle === 2 && this.alpha < 1) {
                    this.alpha += 0.05;
                }
                ctx.globalAlpha = this.alpha;
                ctx.drawImage(canvas2, x - this.radius / 2, y - this.radius / 2, this.radius, this.radius);
                this.timePassed += this.speed;
            }
            for (var i = 0; i < maxStars; i++) {
                new Star();
            }
            function animation() {
                ctx.globalCompositeOperation = 'source-over';
                ctx.globalAlpha = 0.8;
                ctx.fillStyle = 'hsla(' + hue + ', 64%, 6%, 1)';
                ctx.fillRect(0, 0, w, h)
                ctx.globalCompositeOperation = 'lighter';
                for (var i = 1, l = stars.length; i < l; i++) {
                    stars[i].draw();
                };
                window.requestAnimationFrame(animation);
            }
            animation();
        }
        //得到url问号后面的oncetoken
        let paramsMsg = this.$cache.getQueryString('oncetoken')
        this.$http.post('inneruser/handleoncetoken.do', {"oncetoken": paramsMsg}).then(res => {
            if(res.data.success) {
                let toggle = true;
                //将登录得个人信息存储在缓存中
                localStorage.setItem('admin', JSON.stringify(res.data.data))
                this.$store.commit('getAdmin')
                //进入
                document.onclick = () => {
                    toggle = false;
                    this.$router.push({name: 'first'})
                    document.onclick = null
                }
                setTimeout(() => {
                    if(toggle) {
                        this.$router.push({name: 'first'})
                    }
                }, 3000)
            }
            else {
                // 失败的话 返回 登入页
                this.$alert('登入失败,请重新登录', '提示', {
                    confirmButtonText: '确定',
                    callback: action => {
                        // localStorage.clear();
                        // location.href = this.$store.state.base.loginIp
                    }
                });
            }
        }).catch(error => {
            let _this = this
            this.$notify.error({
                offset:200,
                title: '错误',
                message: '登入失败,请重新登录',
                duration: 2000,
                onClose() {
                    // localStorage.clear();
                    // location.href = _this.$store.state.base.loginIp
                }
            });
        })
    }
}
</script>

<style scoped lang="scss">
    @import "./normalize.css";
    .htmleaf-container {
        height: 100%;
    }
    .header {
        position: relative;
        z-index: 100;
        text-align: center;
        color: #fff;
        font-size: 30px;
        i {
            cursor: pointer;
        }
    }
    #canvas {
        position: fixed;
        top: 0;
        left: 0;
    }
    #canvasFb {
        position: absolute;
        left:0;
        top: 0;
        width: 100%;
        height: calc(100% + 50px);
        img {
            width: 100%;
            height: 100%;
        }
    }
</style>



