<template>
    <div class="auditWindow-warp" v-if="auditWindowShow">
        <div class="auditWindow-titleWarp">
            <h3>审核</h3>
        </div>
        <span class="auditWindow-closeBtn" @click="auditWindowClose()"></span>
        <div class="auditWindow-table-warp">
            <table class="auditWindow-table">
            <tr>
                <td>手机号：</td>
                <td>{{currentRow[0].fphone}}</td>
            </tr>
            <tr>
                <td>认证名：</td>
                <td>{{currentRow[0].customerName}}</td>
            </tr>
            <tr>
                <td>资金类型：</td>
                <td>
                    <div>
                        <span  v-show="currentRow[0].ffundType == 1">现金</span>
                        <span  v-show="currentRow[0].ffundType == 2">承兑</span>
                        <span  v-show="currentRow[0].ffundType == 3"> 电汇</span>
                        <span  v-show="currentRow[0].ffundType == 4">积分</span>
                        <span  v-show="currentRow[0].ffundType == 0">其他</span>
                    </div>
                </td>
            </tr>
            <tr>
                <td>金额(元)：</td>
                <td>{{currentRow[0].famount}}</td>
            </tr>
            <tr>
                <td>大写金额：</td>
                <td>
                    {{currentRow[0].fuppercaseAmount}}
                </td>
            </tr>
            <tr>
                <td>备注：</td>
                <td>
                    <div class="auditWindow-beizhu">{{currentRow[0].fremark}}</div>
                </td>
            </tr>
            <tr>
                <td>审核说明：</td>
                <td>
                    <textarea v-model="remark" class="auditWindow-remarks" @input="remarksInput()" placeholder="限100字内" >
                    </textarea>
                </td>
            </tr>
        </table>
        </div>
        <div class="auditWindow-btnGroup">
            <button class="auditWindow-passBtn" @click="passSure()" :disabled="passDisabled">通过</button>
            <button class="auditWindow-rejectBtn" @click="rejectSure()" :disabled="rejectDisabled">驳回</button>
        </div>
    </div>
</template>
<script>
    import {VERSION} from '@/util/constant.js'
    export default{
        props:['auditWindowShow','currentRow'],
        data(){
            return{
                remark:'',
                passDisabled:false, //避免发起多次请求
                rejectDisabled:false
            }
        },
        methods: {
            // 关闭审核的框
            auditWindowClose(){
                this.remark ='';
                this.$emit('auditWindowClose');
            },
            //
            remarksInput(){
                this.remark = this.remark.replace(/\s/g,'');
            },
            //通过
            passSure(){
                if(this.remark.length > 100){
                    this.msg('备注不大于100字!','error');
                    return;
                }
                this.passDisabled = true;
               this.$http.post('/rechargeOrder/audit.do',this.sureData(true)).then(res => {
                   res = res.data;
                   if(res.success){
                       this.remark ='';
                       this.$emit('passSure');
                   }else{
                       this.msg(res.msg,'error');
                   }
                   this.passDisabled = false;
               })
            },
            //驳回
            rejectSure(){
                if(this.remark.length > 100){
                    this.msg('备注不大于100字!','error');
                    return;
                }
                this.rejectDisabled=true;
                this.$http.post('/rechargeOrder/audit.do',this.sureData(false)).then(res => {
                   res = res.data;
                   if(res.success){
                       this.remark ='';
                       this.$emit('rejectSure');
                   }else{
                       this.msg(res.msg,'error');
                   }
                   this.rejectDisabled=false;
               })
            },
            sureData(bflag){
                return {
                    fid:this.currentRow[0].fid,
                    auditor:this.$store.getters.doneAdmin.uname, //操作人
                    auditExplain:this.remark==''?null:this.remark, // 说明
                    orderId:this.currentRow[0].forderId, // 订单号
                    forderId:this.currentRow[0].forderId, // 订单号
                    auditType :bflag?1:2, // 通过 还是驳回
                    userId:this.currentRow[0].fuserId, // 用户id
                    keyArea:this.currentRow[0].fkeyArea, // 区域拆封键
                    amount:this.currentRow[0].famount, // 金额
                    ftype:this.currentRow[0].forderType, // 类型
                    ffundType:this.currentRow[0].ffundType, // 资金类型
                    version:VERSION // 版本号
                }
            },
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            }
        }
    }
</script>
<style>
    .auditWindow-beizhu{
        width: 300px;
        height: 100%;
        word-wrap:break-word ;
    }
    .auditWindow-warp{
        width:440px;
        height: 500px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 10000;
    }
    .auditWindow-table-warp{
       padding-left:50px;
    }
    .auditWindow-table{
        display: block;
    }
    .auditWindow-table tr td:nth-child(1){
        padding-right: 10px;
    }
    .auditWindow-table tr{
        height: 45px;
    }
    .auditWindow-titleWarp{
        overflow: hidden;
    }
    .auditWindow-warp h3{
        font-size: 16px;
        height: 50px;
        line-height: 50px;
        padding-left: 48px;
        margin-bottom: 25px;
        box-shadow:0px 6px 10px #efefef;
    }
    .auditWindow-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .auditWindow-btnGroup{
        margin-top: 20px;
        text-align: center;
    }
    .auditWindow-btnGroup button{
        width:80px;
        height: 36px;
        text-align: center;
        line-height: 36px;
        border-radius: 4px;
        font-size: 14px;
        display: inline-block;
        cursor:pointer;
        border: 0px;
    }
    .auditWindow-btnGroup .auditWindow-passBtn{
        background: #38adff;
        color:#fff;
        margin-right: 30px;
    }
    .auditWindow-btnGroup .auditWindow-rejectBtn{
        border: 1px solid #c6c6c6;
        color:#aaaaaa;
        background: #fff;
    }
    .auditWindow-table .el-input__inner{
        height: 34px;
        line-height: 34px;
    }
    .auditWindow-table .el-select{
        width: 100%;
    }
    .auditWindow-notice{
        line-height: 26px;
        padding-left: 110px;
        color:red;
        background: url("../../../../../../static/images/publicImages/notice.png") no-repeat 90px 8px;
    }
    .auditWindow-remarks{
        height: 56px;
        display: block;
        resize: vertical;
        padding: 5px 7px;
        line-height: 1.5;
        width: 100%;
        color: #1f2d3d;
        border: 1px solid #bfcbd9;
        border-radius: 4px;
        width:264px;
        resize: none;
    }
</style>



