<template>
    <div id="home">
        <vHeader></vHeader>
        <div class="box">
            <div class="main" :class="[isActive ? 'marginLeftOpen' : 'marginLeftClose']">
                <transition name="fade" mode="out-in">
                    <router-view></router-view>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
import vHeader from './Header.vue'

export default {
    name: 'home',
    components: {
        vHeader,
    },
    computed: {
        isActive() {
            return this.$store.state.home.barsState
        }
    },
    created() {
        //过滤权限菜单
        this.$http.post('/menu/allMenu.do').then( res => {
            if(res.data.success) {
                //过滤成 有权限的菜单
                this.$store.commit('getAuthMenu', res.data.data)
            }
            else {
                this.$notify.error({
                    title: '错误',
                    message: '连接超时，请重新登录!'
                });
                setTimeout(() => {
                    // location.href = this.$store.state.base.loginIp
                }, 2000)
            }
        }).catch( error => {
            console.log(error)
        })
    }
}
</script>

<style lang="scss" scoped>
    .marginLeftOpen {
        margin-left: 180px;
    }
    .marginLeftClose {
        margin-left: 64px;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .1s
    }
    .fade-enter, .fade-leave-to {
        opacity: 0
    }
    #home {
    height: 100%;
        .box {
            min-height: 100%;
            background: #f4f4f4;
            .main {
                min-height: 100%;
            }
        }
    }
</style>



