<template>
    <div id="partnerManage">
        <div class="main">
            <tHeader style="color: #000;">{{this.$route.name == 'addPartner' ? '新增合作方' : '编辑合作方'}}</tHeader>
            <div class="form">
<!----------------------------选择合作方------------------------->
                <div class="partner-name">
                    <div>
                        <span class="bold">请选择合作方名称:</span>
                        <!--新增合作方-->
                        <el-select v-model="oaid" placeholder="请选择合作方名称" :loading="partnerLoading" @change="selectPartner(oaid)" :disabled="partnerDisabled" v-if="this.$route.name == 'addPartner'">
                            <el-option
                              v-for="item in partnerList"
                              :key="item.id"
                              :label="item.oname"
                              :value="item.id">
                            </el-option>
                        </el-select>
                        <!--编辑合作方-->
                        <el-select v-model="partnerInfo.id" placeholder="请选择合作方名称" :disabled="partnerDisabled" v-else>
                            <el-option
                                :label="partnerInfo.oname"
                                :value="partnerInfo.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="clearfix partner-info" v-show="oaid">
                        <div class="fl">
                            <h3>合作方基础信息</h3>
                            <div class="offset">
                                <p>公司名称 : <span>{{partnerInfo.oname}}</span></p>
                                <p>注册日期 : <span>{{getDate}}</span></p>
                                <p>公司地址 : <span>{{partnerInfo.oprovince}}-{{partnerInfo.ocity}}-{{partnerInfo.oarea}}</span></p>
                            </div>
                        </div>
                        <div class="fl">
                            <h3>合作方法人信息</h3>
                            <div class="offset">
                                <p>法人代表姓名 : <span>{{partnerInfo.uname}}</span></p>
                                <p>身&nbsp;份&nbsp;证&nbsp; : <span>{{partnerInfo.ucard}}</span></p>
                                <p>联系方式 : <span>{{partnerInfo.uphone}}</span></p>
                                <p>邮&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;箱&nbsp; : <span>{{partnerInfo.uemail}}</span></p>
                            </div>
                        </div>
                    </div>
                </div>
                <el-form class="info" :model="partnerData" :rules="rules" ref="partnerData">
<!----------------------------基础信息------------------------->
                    <div class="base-info">
                        <p class="info-title"><span class="bold">基础信息</span><span class="red">(带*为必填项)</span></p>
                        <div class="el-form--inline">
                            <el-form-item label="注册资本:" prop="registercapital">
                                <el-input v-model.trim="partnerData.registercapital" :maxlength="9" class="aW"></el-input> 万元
                            </el-form-item>
                            <el-form-item label="年销售额:" prop="annualsales">
                                <el-input v-model.trim="partnerData.annualsales" :maxlength="9" class="bW"></el-input> 万元
                            </el-form-item>
                        </div>
                        <div class="el-form--inline">
                            <el-form-item label="银行卡号:" prop="bankcard" >
                                <!--<span class="sure">*</span>-->
                                <el-input v-model.trim="partnerData.bankcard" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label="开户银行:" prop="bank">
                                <!--<span class="sure">*</span>-->
                                <el-input v-model.trim="partnerData.bank" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label="开户人:" prop="accountholder">
                                <!--<span class="sure" style="left: -57px;">*</span>-->
                                <el-input v-model.trim="partnerData.accountholder" :maxlength="10"></el-input>
                            </el-form-item>
                        </div>
                        <div class="el-form--inline">
                            <el-form-item label=" " prop="fax">
                                <label class="label-change" style="left: -65px;" >传&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;真:</label>
                                <el-input v-model.trim="partnerData.fax" :maxlength="20"></el-input>
                            </el-form-item>
                            <el-form-item label=" " prop="website">
                                <label class="label-change" style="left: -65px;">网&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址:</label>
                                <el-input v-model.trim="partnerData.website" :maxlength="30"></el-input>
                            </el-form-item>
                        </div>
                    </div>
                    <div class="unfolded" @click="unfolded = !unfolded">
                        <p v-show="unfolded">
                            点击查看更多&nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-left" style="transform: rotate(-90deg)"></i>
                        </p>
                        <p v-show="!unfolded">
                            点击隐藏&nbsp;&nbsp;&nbsp;<i class="el-icon-d-arrow-right" style="transform: rotate(-90deg)"></i>
                        </p>
                    </div>
                    <div class="hidden-info" :style="{height: unfolded ? 0 : '640px'}">
<!----------------------------生产信息------------------------->
                        <div class="produce-info">
                            <p class="info-title"><span class="bold">生产信息</span></p>
                            <div class="el-form--inline">
                                <el-form-item label="企业类型:">
                                    <el-select v-model="partnerData.enterprisetype" placeholder="请选择">
                                        <el-option label="请选择" disabled value="0"></el-option>
                                        <el-option label="国企" value="1"></el-option>
                                        <el-option label="外商独资" value="2"></el-option>
                                        <el-option label="中外合资" value="3"></el-option>
                                        <el-option label="民营" value="4"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="企业人数:" prop="enterprisenumber">
                                    <el-input v-model.trim="partnerData.enterprisenumber" :maxlength="6"></el-input>
                                </el-form-item>
                                <el-form-item label="厂房面积:" prop="plantarea">
                                    <el-input v-model.trim="partnerData.plantarea" :maxlength="5" class="cW"></el-input> 平方米
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="厂房属性:">
                                    <el-select v-model="partnerData.planttype" placeholder="请选择">
                                        <el-option label="请选择" disabled value="0"></el-option>
                                        <el-option label="租赁" value="1"></el-option>
                                        <el-option label="自有" value="2"></el-option>
                                        <el-option label="租赁+自有" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="生产线数量:" prop="linenumber">
                                    <el-input v-model.trim="partnerData.linenumber" :maxlength="2"></el-input>
                                </el-form-item>
                                <el-form-item label="生产线门幅:" prop="linedoor">
                                    <el-input v-model.trim="partnerData.linedoor" :maxlength="3" class="cW"></el-input> &nbsp;&nbsp;&nbsp;厘米
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="生产线品牌:" prop="linebrand">
                                    <el-input v-model.trim="partnerData.linebrand":maxlength="20"></el-input>
                                </el-form-item>
                                <el-form-item label="生产线平均车速:" prop="linespeed">
                                    <el-input v-model.trim="partnerData.linespeed" :maxlength="8" class="dW"></el-input> 米/每分钟
                                </el-form-item>
                                <el-form-item label="开机时长:" prop="opentime">
                                    <el-input v-model.trim="partnerData.opentime" :maxlength="4" class="cW" @blur="toFloat(partnerData.opentime, 'opentime', 1)"></el-input> &nbsp;&nbsp;&nbsp;小时
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="生产班次:">
                                    <el-select v-model="partnerData.productionshift" placeholder="请选择">
                                        <el-option label="请选择" disabled value="0"></el-option>
                                        <el-option label="一班" value="1"></el-option>
                                        <el-option label="两班" value="2"></el-option>
                                        <el-option label="三班" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="日均产能:" prop="avgcapacity">
                                    <el-input v-model.trim="partnerData.avgcapacity" :maxlength="9" class="eW"></el-input> 平方米
                                </el-form-item>
                                <el-form-item label="日最大产能:" prop="maxcapacity">
                                    <el-input v-model.trim="partnerData.maxcapacity" :maxlength="9" class="cW"></el-input> 平方米
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="产品数量:" prop="productquantity">
                                    <el-input v-model.trim="partnerData.productquantity" :maxlength="3"></el-input>
                                </el-form-item>
                                <el-form-item label="产品最大的三种产品:" prop="maxthree">
                                    <el-input v-model.trim="partnerData.maxthree" :maxlength="20"></el-input>
                                </el-form-item>
                                <el-form-item label="运输配送方式:">
                                   <el-select v-model="partnerData.transporttype" placeholder="请选择">
                                       <el-option label="请选择" disabled value="0"></el-option>
                                        <el-option label="自营" value="1"></el-option>
                                        <el-option label="外包" value="2"></el-option>
                                        <el-option label="自营+外包" value="3"></el-option>
                                    </el-select>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="车辆数量:" prop="carnumber">
                                    <el-input v-model.trim="partnerData.carnumber" :maxlength="3" class="fW"></el-input> 辆
                                </el-form-item>
                            </div>
                        </div>
<!----------------------------人员构成------------------------->
                        <div class="people-info">
                            <p class="info-title"><span class="bold">人员构成</span></p>
                            <div class="el-form--inline">
                                <el-form-item label="总经理/厂长:" prop="managername">
                                    <el-input v-model.trim="partnerData.managername" :maxlength="10"></el-input>
                                </el-form-item>
                                <el-form-item label="出生年份:">
                                    <el-date-picker
                                      v-model="partnerData.age"
                                      align="right"
                                      type="year"
                                      placeholder="选择年">
                                    </el-date-picker>
                                </el-form-item>
                                <el-form-item label="学历:">
                                   <el-select v-model="partnerData.educationbackground" placeholder="请选择">
                                       <el-option label="请选择" disabled value="0"></el-option>
                                        <el-option label="无学历" value="1"></el-option>
                                        <el-option label="初中" value="2"></el-option>
                                        <el-option label="高中" value="3"></el-option>
                                        <el-option label="大专" value="4"></el-option>
                                        <el-option label="本科" value="5"></el-option>
                                        <el-option label="硕士" value="6"></el-option>
                                        <el-option label="博士" value="7"></el-option>
                                    </el-select>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline" prop="managenumber">
                                <el-form-item label="管理人员数量:">
                                    <el-input v-model.trim="partnerData.managenumber" :maxlength="3"></el-input>
                                </el-form-item>
                                <el-form-item label="研发人员数量:" prop="rdnumber">
                                    <el-input v-model.trim="partnerData.rdnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label="质量人员数量:" prop="fmassnumber">
                                   <el-input v-model.trim="partnerData.fmassnumber" :maxlength="5"></el-input>
                                </el-form-item>
                            </div>
                            <div class="el-form--inline">
                                <el-form-item label="销售人员数量:" prop="salesnumber">
                                    <el-input v-model.trim="partnerData.salesnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label="物流人员数量:" prop="logisticsnumber">
                                    <el-input v-model.trim="partnerData.logisticsnumber" :maxlength="5"></el-input>
                                </el-form-item>
                                <el-form-item label="一线生产人员数量:" prop="onelinenumber">
                                   <el-input v-model.trim="partnerData.onelinenumber" :maxlength="5"></el-input>
                                </el-form-item>
                            </div>
                        </div>
                    </div>
<!---------------------------订单分发配置------------------------------------>
                    <div class="order-config">
                        <p class="info-title"><span class="bold">订单分发配置</span><span class="red">(此项必填项)</span></p>
                        <div class="el-form--inline">
                            <el-form-item label=" " prop="configuration">
                               <el-select v-model="partnerData.configuration" placeholder="请选择">
                                    <el-option label="请选择" disabled value="0"></el-option>
                                    <el-option label="在线接口对接" value="1"></el-option>
                                    <el-option label="离线文件对接" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </el-form>
            </div>
            <!--填写表单部分-->
            <div class="submit">
                <el-button @click="cancel">取消</el-button>
                <el-button type="primary" @click="save('partnerData')">保存</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import qs from 'qs'
    import tHeader from '@/components/public/tHeader'
    import rules from './rules'
    import partnerData from './partnerData'

    export default {
        name: 'partnerManage',
        data() {
            return {
                //合作方名称
                partnerDisabled: false,
                partnerLoading: true,
                oaid: '',
                partnerInfo: {},
                partnerList: [],
                //基础信息
                labelPosition: 'right',
                unfolded: true,
                partnerData: JSON.parse(JSON.stringify(partnerData)),        //  data
                rules               //验证规则
            }
        },
        computed: {
            getDate() {
                let t = new Date(this.partnerInfo.create_time)
                let   year = t.getFullYear();
                let   month = t.getMonth()+1;
                let   date = t.getDate();
                return year+"-"+month+"-"+date;
            }
        },
        components: {
            tHeader
        },
        mounted() {
            if(this.$route.name == 'addPartner') {
                //得到所有合作方名字
                this.$http.post('partners/findOaPartners.do').then( res => {
                    if(res.data.success) {
                        if(res.data.data.length) {
                            this.partnerLoading = false
                            this.partnerList = res.data.data
                        }
                        else {
                            this.$message({
                                message: '合作方已全部添加完',
                                duration: 1000,
                                type: 'info'
                            });
                        }
                    }
                 }).catch( error => {
                    this.$message({
                        message: '获取合作方信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('得到所有合作方:' + error)
                 })
            }
            else {
                //编辑合作方
                this.partnerDisabled = true
                this.$http.post('partners/findOnePartners.do', this.$route.query).then( res => {
                    let partnerData = res.data.data
                    if(partnerData.age == null) {
                        partnerData.age = ''
                    }
                    else {
                        let age = new Date(partnerData.age).toISOString()   // 将时间转为utc格式
                        partnerData.age = age
                    }
                    for(let item in partnerData) {
                        partnerData[item] = partnerData[item] + ''
                    }
                    partnerData.ocode = +partnerData.ocode
                    this.partnerData = partnerData

                    //得到选择合作方数据
                    this.$http.post('partners/findPartnerByOaid.do', {oaid: partnerData.id }).then(res => {
                        if(res.data.success) {
                            this.partnerInfo = res.data.data[0]
                            this.oaid = this.partnerInfo.id
                        }
                    }).catch(error => {
                        console.log('编辑合作方的选择合作方名称:' + error)
                    })

                }).catch( error => {
                    this.$message({
                        message: '获取合作方信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('得到所有合作方:' + error)
                })
            }
        },
        methods: {
            //选择合作方
            selectPartner(oaid) {
                this.$http.post('partners/findPartnerByOaid.do', {oaid:oaid }).then( res => {
                    if(res.data.success) {
                        this.partnerInfo = res.data.data[0]
                        this.partnerData.id = oaid
                        this.partnerData.oname = this.partnerInfo.oname
                        this.partnerData.uname = this.partnerInfo.uname
                        this.partnerData.oprovince = this.partnerInfo.oprovince
                        this.partnerData.ocity = this.partnerInfo.ocity
                        this.partnerData.uphone = this.partnerInfo.uphone
                        this.partnerData.ocode = this.partnerInfo.ocode
                    }
                    else {
                        this.$message({
                            message: '获取合作方信息失败',
                            duration: 1000,
                            type: 'error'
                        });
                    }
                }).catch( error => {
                    this.$message({
                        message: '获取合作方信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('选择合作方:' + error)
                })
            },
            //转小数
            toFloat(val, key, num) {    //值 键 精确位数
                if(val === '') return
                let float = (+val).toFixed(num)
                this.partnerData[key] = float
            },
            cancel() {
                this.$router.push({name: 'partner'})
            },
            save(formName) {
                this.$confirm('此操作将永久保存, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消'
                }).then(() => {
                    //判断合作方是否存在
                    if(!this.oaid) {
                        this.$message({
                            message: '请选择合作方',
                            duration: 1000,
                            type: 'warning'
                        });
                        return false
                    }
                    this.$refs[formName].validate((valid) => {
                        if (valid) {
                            //新增合作方和编辑合作方是不同的url地址
                            let url = ''
                            if(this.$route.name == 'addPartner') {
                                url = 'partners/addPartners.do'
                            }
                            else {
                                url = 'partners/updatePartners.do'
                            }
                            this.$http.post(url, this.partnerData).then( res => {
                                if(res.data.success) {
                                    this.$router.push({name: 'partner'})
                                }
                                else {
                                    this.$message({
                                        message: '保存失败',
                                        duration: 1000,
                                        type: 'error'
                                    });
                                }
                            }).catch( error => {
                                console.log('新增合作方:' + error)
                            });
                        } else {
                            this.$message({
                                message: '请输入正确的内容',
                                duration: 1000,
                                type: 'warning'
                            });
                            return false;
                        }
                    });
                });
            }
        }
    }
</script>

<style lang="scss">
    @import '../../../../assets/style/partnerManage.scss';
</style>



