<template>
    <div class="partnerManage">
        <tHeader>产品下架审核</tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <regional-choice
                    @getPleaseProvice='getPlease'
                    @getRegionProvice='getRegion'
                    :parterCode='-1'
                    :defaultPlease='defaultPlease'
                    :defaultRegion='defaultRegion'
                    :isDisableds='admin.ucompany == 100 ? false : true'
                    :isProduce='true'
                >
                </regional-choice>
                <div class='parter-select-top'>
                    <partner
                        @getSelect='getSelect'
                        :codeName='codeName'
                        :partName='partnerName'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                    ></partner>
                </div>
                <div class='partner-select'>
                    <carouse-select
                        textName='审核状态:'
                        :region='produceType'
                        @getSelect = 'getSelectType'
                    ></carouse-select>
                </div>
                <el-button type="primary" icon="search" @click='search'>搜索</el-button>
            </div>
            <div class='partner-table partner-table-produce prchase-list'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="序号" width="60">
                    </el-table-column>
                    <el-table-column prop="type" label="审核状态" width="120">
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="区域" width="120">
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="合作方名称" width="120">
                    </el-table-column>
                    <el-table-column prop="fimg" label="产品图片" width="150">
                        <template scope="scope">
                           <img :src='scope.row.fimg' class='partner-table-img'>
                        </template>
                    </el-table-column>
                    <el-table-column prop="fproductname" label="产品名称" width="150">
                    </el-table-column>
                    <el-table-column prop="moneyType" label="价格方案类型" width="150">
                    </el-table-column>
                    <el-table-column prop="fgroupprice" label="团购价格" width="150">
                    </el-table-column>
                    <el-table-column prop="fmaxarea" label="限量结束" width="110">
                    </el-table-column>
                    <el-table-column prop="flimitedtime" label="限时结束" width="120">
                    </el-table-column>
                    <el-table-column prop="fauditor" label="操作人" width="120">
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="操作时间" width="110">
                    </el-table-column>
                    <el-table-column label="操作" width="140" fixed="right">
                        <template scope="scope">
                            <div v-show='scope.row.fauditstatus == 1 && admin.ucompany == 100 ? true : false'>
                                <el-button  type="text" size="small" @click='pass(scope.row, true)' >
                                    通过
                                </el-button>
                                <el-button type="text" size="small" @click='pass(scope.row, false)' >
                                    驳回
                                </el-button>
                            </div>
                            <div v-show='scope.row.fauditstatus != 1 ? true : false'>
                                   已审核
                            </div> 
                            <div v-show='scope.row.fauditstatus == 1 && admin.ucompany != 100 ? true : false'>
                                   审核中
                            </div>     
                        </template>
                    </el-table-column>
                </el-table>
                <div class='partner-page'>
                    <pageing v-if='listLength > 10'
                            @changePage="changePage"
                            @changSize='changSize'
                            :showPageArry="listLength > 15 ? [10,15,20] : [10,15]"
                            :showPagelist="showPagelist"
                            :listLength="listLength"
                            :currentPage='currentPage'
                        >
                    </pageing>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import {VERSION, IMGPATH} from '@/util/constant.js'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.partner = this.$store.getters.donePartner;
        this.fauditorid = this.admin.ucompany
        this.fauditor = this.admin.uname;
        this.fsupplierid = this.admin.ucompany;   //合作方id
        //区域拆分键
        this.fkeyarea = this.admin.ocode;
        //非东京账号登录
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
            }, 1);
           this.searchFunc();
        }
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            produceType:[
                {regionName:'全部', value:'-1'},
                {regionName:'审核中', value:'1'},
                {regionName:'已通过', value:'2'},
                {regionName:'已驳回', value:'3'},
            ],
            fgroupareaid:'',        //服务区域
            fkeyarea:'',            //区域拆分键
            fsupplierid:'',         //合作方ID
            codeName:'',            //合作方查询市
            fauditstatus:'-1',      //上架状态
            fauditorid:'',          //审核人Id
            fauditor:'',            //审核人
            listLength:0,
            showPagelist:10,
            currentPage:1,
            addNewProduce:false,
            tableData:[],
            typeArry:{
                '0':'待上架',
                '1':'审核中',
                '2':'已通过',
                '3':'已驳回'
            }
		}
	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //区域code
        getPlease(please){
            // this.fgroupareaid = please.code;
            this.codeName = please;
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
            this.tableData = [];
        },
        //合作方选择
        getSelect(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.fsupplierid = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //切换页面
        changePage(page){
            this.currentPage = page;
            this.searchFunc();
        },
        //切换页面条数
        changSize(pageLength){
            this.showPagelist = pageLength;
            this.searchFunc();
        },
        //审核状态查询
        getSelectType(name){
           for(let i=0; i<this.produceType.length; i++){
               if(name == this.produceType[i].regionName){
                    this.fauditstatus = this.produceType[i].value;
               }
           }

        },
        //查询列表
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.tableData=[];
                    this.tableData = res.data.data;
                    this.listLength = res.data.total;
                    for(let i=0; i<this.tableData.length; i++){
                        
                        this.tableData[i].number = this.currentPage * this.showPagelist - this.showPagelist + i + 1;
                        this.tableData[i].fimg =  IMGPATH + this.tableData[i].figure.furl;
                        this.tableData[i].type = this.typeArry[this.tableData[i].fauditstatus];
                        this.tableData[i].moneyType = this.tableData[i].fgrouptype == 1 ? '限量团购' : this.tableData[i].fgrouptype ==  2 ? '限时团购' : '';
                        this.tableData[i].flimitedtime = this.tableData[i].fgrouptype == 1 ? '/' : this.tableData[i].flimitedtime;
                        this.tableData[i].fmaxarea = this.tableData[i].fgrouptype ==  2 ? '/' : this.tableData[i].fmaxarea;
                        if(this.tableData[i].moneyType == ''){
                            this.tableData[i].fmaxarea = '/';
                            this.tableData[i].moneyType = '/';
                            this.tableData[i].flimitedtime = '/';
                            this.tableData[i].fgroupprice = '/';
                        }
                        if(this.tableData[i].fauditstatus == 1){
                            this.tableData[i].fupdatetime = '/';
                            this.tableData[i].fauditor = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查询方法
        searchFunc(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.fsupplierid,
                fauditstatus:this.fauditstatus,
                // fgroupareaid:this.fgroupareaid,
                pagenum:this.currentPage - 1,
                pagesize:this.showPagelist,
                sort:'2'
            }
            this.searchList(data);
        },
        //通过/驳回
        pass(datas, isPass){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:datas.fid,
                fcomment:'1',
                operate:isPass,
                fauditorid:this.fauditorid,
                fauditor:this.fauditor
            }
           this.$http.post('/product/examineProduct.do', data).then(res => {
                 if(res.data.success) {
                     this.$message({
                        message: '审核成功！',
                        type: 'success',
                        duration: 2000
                     });
                     this.searchFunc();
                 }else{
                     this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                 } 
           }).catch(function (error) {
                console.log(error);
           });
        },
        //搜索
        search(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.fsupplierid,
                fauditstatus:this.fauditstatus,
                // fgroupareaid:this.fgroupareaid,
                pagenum:0,
                pagesize:this.showPagelist
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '请选择区域合作方！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.searchList(data);
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing
    }  
}
</script>


