<template>
    <div id="partner">
        <tHeader>合作方管理</tHeader>
        <div class="main">
            <ul class="clearfix search">
                <li>
                    行政区域&nbsp;:&nbsp;
                    <el-select v-model="province" filterable clearable :loading="pLoading" @change="getArea(province)" placeholder="请选择省">
                        <el-option
                        v-for="(item, index) in provinceTable"
                        :key="index"
                        :label="item.oprovince"
                        :value="item.oprovince">
                        </el-option>
                    </el-select>
                    <span class="line"></span>
                    <el-select v-model="city" filterable clearable :loading="cLoading" placeholder="请选择市">
                        <el-option
                        v-for="(item, index) in cityTable"
                        :key="index"
                        :label="item.ocity"
                        :value="item.ocity">
                        </el-option>
                    </el-select>
                </li>
                <li>
                    企业名称&nbsp;:&nbsp;
                </li>
                <li>
                    <el-input v-model.trim="company" placeholder="请输入内容"></el-input>
                </li>
                <li>
                    <el-button type="primary" icon="search" @click="search">查询</el-button>
                </li>
            </ul>
            <div class="btns">
                <el-button type="primary" @click="addPartner">新增</el-button>
            </div>
            <div class="table">
                <el-table
                    v-loading="tableLoading"
                    :data="tableData"
                    stripe
                    border
                    style="width: 100%">
                    <el-table-column
                        type="index"
                        label="序号"
                        align="center"
                        width="65">
                    </el-table-column>
                    <el-table-column
                        prop="oname"
                        label="企业名称"
                        align="center"
                        width="230">
                    </el-table-column>
                    <el-table-column
                        prop="uname"
                        label="法人"
                        align="center"
                        width="130">
                    </el-table-column>
                    <el-table-column
                        prop="uphone"
                        label="联系电话"
                        align="center"
                        width="140">
                    </el-table-column>
                    <el-table-column
                        label="行政区域"
                        align="center"
                        width="150">
                        <template scope="scope">
                            {{scope.row.oprovince}}-{{scope.row.ocity}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="账户状态"
                        align="center"
                        width="100">
                        <template scope="scope">
                            {{scope.row.effect == 1 ? '启用' : '禁用'}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="updatetime"
                        label="最近更新日期"
                        align="center"
                        width="180">
                        <template scope="scope">
                            {{scope.row.updatetime | time}}
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="操作"
                        align="center"
                        width="220">
                        <template scope="scope">
                            <el-button type="text" @click="see(scope.row)">查看</el-button><span class="sss">|</span>
                            <el-button type="text" @click="editPartner(scope.row)">编辑</el-button><span class="sss">|</span>
                            <el-button type="text" @click="state(scope.row)" :style="{color: scope.row.effect == 1 ? '#f75555':'#4fbd61'}">{{scope.row.effect == 1 ? '禁用' : '启用'}}</el-button><span class="sss">|</span>
                            <el-button type="text" @click="areaConfig(scope.row)">区域配置</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class='prchase-list'>
            <Paging :showPageArry="showPageArry" :showPagelist="showPagelist" :listLength="listLength" :currentPage="currentPage" @changePage="changePage" @changSize="changSize"></Paging>
        </div>
    </div>
</template>

<script>
    import tHeader from '@/components/public/tHeader'
    import Paging from '@/components/public/paging'
    export default {
        name: 'partner',
        components: {
            tHeader,
            Paging
        },
        data() {
            return {
                province: '',               //省code
                city: '',                   //市code
                provinceName: '',           //省name
                cityName: '',               //市name
                company: '',                //公司name
                /**
                 * loading
                 */
                pLoading: true,
                cLoading: false,
                tableLoading: true,

                accountState: '',

                provinceTable: [],
                cityTable: [],
                tableData: [],

                showPageArry: [15, 30, 45, 60],     //每页显示个数选择器的选项设置
                showPagelist: 15,                   //每页显示默认15
                listLength: 0,                      //总页数
                currentPage: 1                      //选择页
            }
        },
        created() {
            this.$http.post('/partners/findPartnersProvince.do', { code: "0", status: 1 }).then( res => {
                if(res.data.success) {
                    this.provinceTable = res.data.data
                }
            }).catch( error => {
                this.$message({
                    message: '获取地区失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log('合作方管理区域:' + error)
            })
            //获取分页 每页数据
            this.$http.post('partners/findPartnersPage.do', { page: this.currentPage, pageSize: this.showPagelist }).then( res => {
                let info = res.data.data
                if(res.data.success) {
                    this.tableData = info.partnersList.content
                    this.listLength = info.partnersList.totalSize
                }
                else {
                    this.tableData = []
                    this.tableLoading = false
                }
            }).catch( error => {
                this.tableLoading = false
                this.$message({
                    message: '获取数据失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log('获取第一页数据:' + error)
            })
        },
        updated() {
            let table1 = document.querySelector('.el-table__header');
            let table2 = document.querySelector('.el-table__body');
            table1.style.width = '100%'
            table2.style.width = '100%'
        },
        filters: {
            time(value) {
                let t = new Date(value)
                let year = t.getFullYear();
                let month = t.getMonth()+1;
                let date = t.getDate();
                let hour = t.getHours();
                let minute = t.getMinutes();
                let second = t.getSeconds();
                if(hour < 10) {
                    hour  = '0' + hour
                }
                if(minute < 10) {
                    minute  = '0' + minute
                }
                if(second < 10) {
                    second  = '0' + second
                }
                return year + "-" + month + "-" + date + ' ' + hour + ':' + minute + ':' + second;
            }
        },
        methods: {
            //新增合作方router
            addPartner() {
                this.$router.push({name: 'addPartner'})
            },
            editPartner(row) {
                let query = {
                    pid: row.pid,
                    fkeyarea: row.ocode
                }
                this.$router.push({ name: 'editPartner', query })
            },
            //查看合作方router
            see(row) {
                let query = {
                    pid: row.pid,
                    fkeyarea: row.ocode
                }
                this.$router.push({name: 'seePartner', query })
            },
            //区域配置router
            areaConfig(row) {
                let query = {
                    pid: row.pid,
                    oname: row.oname,
                    keyArea: row.ocode,
                    oaid: row.id
                }
                this.$router.push({name: 'areaConfig', query })
            },
            //账户状态
            state(scope) {   //1为开启   0为禁用
                this.$confirm('是否确认此操作?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(scope.effect == 0) {
                        this.accountState = 1
                    }
                    else {
                        this.accountState = 0
                    }
                    this.$http.post('partners/updateStatus.do', {companyID: scope.id, crm: this.accountState }).then( res => {
                        if(res.data.success) {
                            if(scope.effect == 0) {
                                scope.effect = 1
                            }
                            else {
                                scope.effect = 0
                            }
                            this.$message({
                                type: 'success',
                                message: '账户状态已改变!'
                            });
                        }
                        else {
                            this.$message.error('账户状态改变失败');
                            this.$message({
                                message: '操作失败',
                                duration: 1000,
                                type: 'warning'
                            });
                        }
                    }).catch( error => {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })
                }).catch(() => {});
            },
            //搜索select
            getArea(value) {
                if(value != '') {
                    this.$http.post('/partners/findPartnersCity.do ', { province: this.province }).then(res => {
                        if(res.data.success) {
                            this.cityTable = res.data.data
                        }
                    }).catch(function (error) {
                        this.$message({
                            message: '获取地区失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log('合作方管理得到市:' + error);
                    });
                }
            },

            //查询,分页 页码
            getTableData() {
                this.$http.post('partners/findPartnersPage.do', {oprovince: this.province, ocity: this.city, oname: this.company, page: this.currentPage, pageSize: this.showPagelist}).then( res => {
                    let info = res.data.data
                    if(res.data.success) {
                        if(info.partnersList) {
                            this.tableData = info.partnersList.content
                            this.listLength = info.partnersList.totalSize
                        }
                        else {
                            this.tableData = []
                            this.listLength = 0
                            this.tableLoading = false
                        }
                    }
                }).catch( error => {
                    this.tableLoading = false
                    this.$message({
                        message: '获取数据失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('合作方管理模糊查询:' + error)
                })
            },
            search() {
                this.getTableData()
            },
            changePage(page) {
                this.currentPage = page
                this.getTableData()
            },
            //选择显示页
            changSize(page) {
                this.showPagelist = page
                this.getTableData()
            }
        },

        watch: {
            provinceTable(val) {
                this.pLoading = false
            },
            province(val) {
                this.city = ''
                if(val === '') {
                    this.cityTable = []
                }
                else {
                    this.cLoading = true
                }
            },
            cityTable(val) {
                this.cLoading = false
            },
            tableData(val) {
                this.tableLoading = false
            }
        }
    }
</script>

<style scoped lang="scss">
    #partner {
        padding: 30px 19px 0 40px;
        box-sizing: border-box;
        .main {
            background-color: #fff;
            padding: 0 10px 10px;
            box-sizing: border-box;
            ul.search {
                line-height: 32px;
                padding-top: 21px;
                padding-bottom: 19px;
                li {
                    float: left;
                }
                li:nth-child(2) {
                    margin-left: 39px;
                }
                li:last-child {
                    margin-left: 30px;
                }
                .line {
                    display: inline-block;
                    width: 13px;
                    background: #8c8c8c;
                    height: 1px;
                    margin-bottom: 5px;
                }
                /*element search change*/
                .el-select {
                    width: 150px;
                    height: 32px;
                }
                .el-input {
                    width: 200px;
                    height: 32px;
                }
                .el-button {
                    width: 110px;
                    height: 36px;
                }
            }
            .btns {
                margin-bottom: 19px;
                .el-button {
                    width: 110px;
                    height: 36px;
                    background: url("../../../../../static/images/add.png") #20a0ff no-repeat 20px 10px;
                }
            }
            .table {
                .el-button {
                    font-size: 12px;
                }
                .el-button+.el-button {
                    margin-left: 0;
                }
                .sss {
                    padding: 0 2px 0 4px;
                    color: #cccccc;
                }
            }
        }
        .prchase-list .el-pagination {
            top: 0;
            margin-top: 38px;
            margin-bottom: 40px;
        }
    }
</style>



