<template>
    <div class="getUserList clearfix">
        <div class="fl">
            用户手机号 :
            <el-select v-model="phone" clearable  filterable remote placeholder="请输入手机号" :remote-method="phoneRemote"  @clear="clear()" @change="phoneChange" 	>
                <el-option v-for="item,index in phoneList" :key="index" :label="item.fphone" :value="item.fphone">
                </el-option>
            </el-select>
        </div>
        <div class="fl">
            认证名 :
            <el-select v-model="certificateName" clearable   filterable remote placeholder="请输入认证名" @clear="clear()" :remote-method="certificateNameRemote"  @change="certificateNameChange">
                <el-option v-for="item,index in certificateNameList" :key="index" :label="item.fcertificateName" :value="item.fcertificateName">
                </el-option>
            </el-select>
        </div>
    </div>
</template>
<script>
    /**
     * Created by qxl on 2017/9/19.
     */
    import {VERSION} from '@/util/constant.js'
    export default{
        data(){
            return {
                phone:'',
                phoneList:[],
                certificateName:'',
                certificateNameList:[],
                userId:''
            }
        },
        methods:{
            clear(){
                this.certificateName = '';
                this.certificateNameList = [];
                this.phone = '';
                this.phoneList = [];
                this.userId = '';
                this.$emit('clear',this.phone,this.certificateName,this.userId);
            },
            phoneRemote(val){
                if(val == ''){
                    this.certificateName = '';
                    this.certificateNameList = [];
                    this.phone = '';
                    this.phoneList = [];
                    this.userId = '';
                }else{
                    this.$http.post(`/outerUser/selectUserIdByPhoneOrCustomerName.do`,{
                        version:VERSION,
                        phone:val,
                        pageNo:0,
                        pageSize:30
                    }).then(res => {
                        res = res.data;
                        if(res.success){
                            this.phoneList = res.data.result;
                        }
                    })
                }
            },
            phoneChange(val){
               if(val != ''){
                   this.certificateNameList =  this.phoneList.filter(item => {
                        return val == item.fphone;
                    })
                   if(this.certificateNameList[0].fcertificateName){
                       this.certificateName =  this.certificateNameList[0].fcertificateName
                       this.userId = this.certificateNameList[0].fuserId;
                   }else{
                       this.userId = this.certificateNameList[0].fuserId;
                       this.certificateName ='';
                       this.certificateNameList =[];
                   }
               }
               this.$emit('getByPhone',this.phone,this.certificateName,this.userId);
            },
            certificateNameRemote(val){
                if(val == ''){
                    this.certificateName = '';
                    this.certificateNameList = [];
                    this.phone = '';
                    this.phoneList = [];
                    this.userId = '';
               }else{
                     this.$http.post(`/outerUser/selectUserIdByPhoneOrCustomerName.do`,{
                        version:VERSION,
                        certificateName:val,
                        pageNo:0,
                        pageSize:30
                    }).then(res => {
                        res = res.data;
                        if(res.success){
                            this.certificateNameList = res.data.result;
                        }
                    })
                }
            },
            certificateNameChange(val){
                if(val !=''){
                    if(this.certificateNameList.length !=0){
                        this.phoneList =  this.certificateNameList.filter(item => {
                            return val == item.fcertificateName;
                        })
                        this.phone = this.phoneList[0].fphone;
                        this.userId = this.phoneList[0].fuserId;
                    }
                }
                this.$emit('getByName',this.phone,this.certificateName,this.userId)
            }
        }
    }
</script>
<style>
    .getUserList>div:nth-child(1){
        margin-right: 10px;
    }
</style>



