/**
 * 运营管理路由
 */
import UserManage from '@/components/common/operation/userManage/userManage.vue'

import certificationList from '@/components/common/operation/certification/certificationList.vue'

import homePageImg from '@/components/common/operation/carouselFigure/homePageCarouse.vue'
import integralMallImg from '@/components/common/operation/carouselFigure/IntegralMallCarouse.vue'
import groupImg from '@/components/common/operation/carouselFigure/groupCarouse.vue'
import appStartPage from '@/components/common/operation/carouselFigure/appStartPage.vue'
import integralMall from '@/components/common/operation/carouselFigure/integralMall.vue'
import groupPurchase from '@/components/common/operation/carouselFigure/groupPurchase.vue'

import Partner from '@/components/common/operation/partner/partner.vue'
import PartnerManage from '@/components/common/operation/partner/PartnerManage.vue'
import SeePartner from '@/components/common/operation/partner/SeePartner.vue'
import AreaConfig from '@/components/common/operation/partner/AreaConfig.vue'

//积分
import intergralBalance from '@/components/common/operation/intergral/intergralBalance/intergralBalance.vue'
import intergralDetailed from '@/components/common/operation/intergral/intergralDetailed/intergralDetailed.vue'

//财务管理
import reportManage from '@/components/common/operation/financeManage/reportManage/reportIndex' //报表管理
import rechargeManage from '@/components/common/operation/financeManage/rechargeManage/rechargeManage' //充值管理
import rechargeAudit from '@/components/common/operation/financeManage/rechargeAudit/rechargeAudit' //充值审核
import withdrawCash from '@/components/common/operation/financeManage/withdrawCashManage/withdrawCash' //提现审核

// 客服模块

import financeAbnormal  from '@/components/common/operation/customerService/financeAbnormal' //财务异常订单
import logisticsAabnormal from '@/components/common/operation/customerService/logisticsAabnormal' //物流异常订单
import serviceAbnormal from '@/components/common/operation/customerService/serviceAbnormal' //客服异常订单

let operation = [
    { path:'operation', name: 'operation', redirect: { name: 'userManage' }}
];
/**
 * 用户管理
 */
let userManage = [
    { path:'operation/userManage', name: 'userManage', component: UserManage }
];

/**
 * 身份认证
 */
let cert = [
    { path:'operation/certificationList', name: 'certificationList', component: certificationList }
];
/**
 * 积分管理
 */
let integralManage = [
    { path:'operation/intergralBalance', name: 'intergralBalance',component: intergralBalance},
    { path:'operation/intergralDetailed',name: 'intergralDetailed',component: intergralDetailed}
];
/**
 * 合作方管理
 */
let partner = [
    { path:'operation/partner', name: 'partner', component: Partner },
    { path:'operation/partner/addPartner', name: 'addPartner', component: PartnerManage },
    { path:'operation/partner/editPartner', name: 'editPartner', component: PartnerManage },
    { path:'operation/partner/seePartner', name: 'seePartner', component: SeePartner },
    { path:'operation/partner/areaConfig', name: 'areaConfig', component: AreaConfig },
];
/**
 * 客服模块
 */
let ad = [
    { path:'operation/homePageImg', name: 'homePageImg', component: homePageImg },
    { path:'operation/integralMallImg', name: 'integralMallImg', component: integralMallImg },
    { path:'operation/groupImg', name: 'groupImg', component: groupImg },
    { path:'operation/appStartPage', name: 'appStartPage', component: appStartPage },
    { path:'operation/integralMall', name: 'integralMall', component: integralMall },
    { path:'operation/groupPurchase', name: 'groupPurchase', component: groupPurchase },
]
/**
 * 财务管理
 */
let financeManage = [
    { path:'operation/reportManage', name: 'reportManage', component: reportManage },
    { path:'operation/rechargeManage', name: 'rechargeManage', component: rechargeManage},
    { path:'operation/rechargeAudit', name: 'rechargeAudit', component: rechargeAudit},
    { path:'operation/withdrawCash', name: 'withdrawCash', component: withdrawCash}

]
/**
 * 财务管理
 *
 */
let customerService = [
    { path:'operation/financeAbnormal', name: 'financeAbnormal', component: financeAbnormal},
    { path:'operation/logisticsAabnormal', name: 'logisticsAabnormal', component: logisticsAabnormal},
    { path:'operation/serviceAbnormal', name: 'serviceAbnormal', component: serviceAbnormal}
]

export default operation.concat(userManage, cert, partner, ad,integralManage, financeManage ,customerService)



