<template>
    <div class="partnerManage">
		<tHeader>APP启动页管理</tHeader>
        <div class='common-bg'>
                <regional-choice
                @getPlease = 'getPlease'
                >
                </regional-choice>
                <div class='carouse-time-setting'>
                    <span>停留时间:</span>
                    <input v-model='timeInput' @input='getTime' style="ime-mode:disabled" class='carouse-time-input' @blur='setTime' maxlength='2'>
                    <span>秒</span>
                </div>
                <div class='carouse-upload'>
                    <div v-if='!IE' @mouseout="uploadClass != '' ? uploadImg = false : '' " @mousemove="uploadClass != '' ? uploadImg = true : ''">
                        <el-upload class="upload-demo" 
                        drag 
                        action="https://jsonplaceholder.typicode.com/posts/" 
                        :auto-upload='false' 
                        accept='png/jpg' 
                        :on-change='upLoadImg'
                        :multiple='false'
                        :show-file-list='false'>
                        <div class='carouse-upload-icon'>
                                <img :src='uploadImgUrl' :class='uploadClass'>
                                <div class="el-upload__text"><em>点击上传</em></div>
                        </div>
                        <img src='../../../../../static/images/authentication/mouseImg.png' v-show="uploadImgUrl != '../../../../../static/images/authentication/upload.png'? uploadImg : false" class='carouse-img-bacgroud'>
                        </el-upload>
                    </div>
                    <div v-if='IE' class='ie-upload-app' @mouseout="uploadImg = false "  @mousemove="uploadImg = true">
                        <upload-ie 
                        :checkData='checkIEData'
                        :uploadDate='uploadDate' 
                        :uploadSrc='uploadSrc'
                        action='djcrm/banner/addAndUpdataBanner.do'></upload-ie>
                        <img src='../../../../../static/images/authentication/upload.png' class='ie-default-img'/>
                        <img src='../../../../../static/images/authentication/mouseImg.png' class='ie-shack-img' v-if="uploadSrc == '' ? false : uploadImg "/>
                        <p>点击上传</p>
                    </div>
                    <div class='carouse-upload-font'>宽度:1125px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;高度:2000px&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;比例:9:16&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;大小: < 1M &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格式: jpg png</div>
                    <div class='carouse-upload-font'>
                        <el-button type='primary' @click='saveUpload'>保存</el-button>
                    </div>
                </div>
        </div> 
        <propt
           :text='Proport'
           :dialogVisible='ProportionPropt'
           @closeVisible='ProportionPropt = false'
           @sure='ProportionPropt = false'
        ></propt> 
	</div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import {IMGPATH} from '@/util/constant.js'
import uploadIe from '@/components/public/uploadIE'
import propt from '@/components/public/propt'
export default {
    created(){
        var initImg={ 
            farea:3303, 
            fclient:2, 
            fbooth:6, 
            fkeyarea:'3303' 
        }
        //获取初始化浙江温州轮播图
        this.findImg(initImg);
        var data ={
            fbooth:6,
            fkeyarea:'3303'
        }
        //初始化时间
        this.getinitTime(data);
        if(navigator.appName == "Microsoft Internet Explorer" && 
        navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
          this.IE = true;  
          return
        } 
    },
	data () {
		return {
            IE:false,
            uploadSrc:'',
            Proport:'',
            ProportionPropt:false,
            pleaseCode:3303,
            timeInput:'5',
            uploadImg:false,
            uploadImgUrl:'../../../../../static/images/authentication/upload.png',
            uploadClass:'',
            file:'',
            fid:'',
            ffileid:'',
            checkIEData:{
                size:1,
                Proportion:9/16
            },
            uploadDate:[]
		}
	},
	methods:{
        //选择市
        getPlease(please){
           this.pleaseCode = please.code;
           var data ={
               fbooth:6,
               fkeyarea:this.pleaseCode + ''
           }
           this.getinitTime(data);
            var initImg={ 
                farea:this.pleaseCode, 
                fclient:2, 
                fbooth:6, 
                fkeyarea:this.pleaseCode + '' 
            }
            this.findImg(initImg);
            this.file = ''
        },
        //时间校验
        getTime(){
            var check = new RegExp('(^[0-9]{1}[0-9]*$)');
            if(check.test(this.timeInput)){
                if(this.timeInput > 15){
                    this.timeInput = 15;
                }
                if(this.timeInput == 0){
                    this.timeInput = '';
                }
            }else{
                if(this.timeInput.length === 1){
                   this.timeInput = '';
                }else if(this.timeInput.length >= 2){
                    var arry = this.timeInput.split('')
                    var getNum = [];
                    for(var i=0; i<arry.length; i++){
                      if(check.test(arry[i])){
                         getNum.push(arry[i]);
                      }
                    }
                    this.timeInput = getNum.join("");
                }
            }
        },
        //设置时间
        setTime(){
          var timeData = {
              fbooth:6,
              fdwelltime:this.timeInput,
              fsliptime:0,
              fkeyarea:this.pleaseCode + ''
         }
         this.$http.post('/banner/updateBannerTime.do', timeData).then(res => {
                if (res.data.success) {
                    console.log('设置时间成功')
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //获取停留时间
        getinitTime(data){
          this.$http.post('/banner/loadBannerTime.do ', data).then(res => {
                if (res.data.success) {
                    if(res.data.data.length == 0){
                        this.timeInput = '5'
                        return
                    }
                    this.timeInput = res.data.data[0].fdwelltime
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查询市对应的轮播图
        findImg(getData){
            this.$http.post('/banner/loadBanner.do', getData).then(res => {
                if (res.data.success) {
                    if(res.data.data.length == 0){
                        this.uploadImgUrl = '../../../../../static/images/authentication/upload.png';
                        this.uploadClass = '';
                        this.fid = '';
                    }
                    if(res.data.data[0] !== ''){
                       this.fid = res.data.data[0].fid;
                       this.ffileid = res.data.data[0].ffileId;
                       this.uploadImgUrl = IMGPATH + res.data.data[0].fimgpath;
                       this.uploadClass = 'carouse-upload-img';
                       //IE9专用
                       this.uploadSrc = IMGPATH + res.data.data[0].fimgpath;
                    }
                    //IE9专用
                    this.uploadDate=[
                        {name:'fkeyarea', data:this.pleaseCode},
                        {name:'farea', data:this.pleaseCode},
                        {name:'fclient', data:2},
                        {name:'fbooth', data:6},
                        {name:'moduleid', data:'P11006'},
                        {name:'fid', data:this.fid},
                        {name:'ffileid', data:this.ffileid},
                        {name:'fjumpposition', data:1},
                        {name:'fhasPicture', data:true},
                    ]
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //上传图片
        upLoadImg(file){
            this.file = file;
            var img_url = file.url;
            var img = new Image();
            img.src = img_url;
            var q = this;
            img.onload = function(){
                var b = img.width/img.height + '';
                var imgType = file.raw.type.slice(6);
                if(imgType != "png" &&
                    imgType != "jpg" &&
                    imgType != "jpeg"){
                    q.ProportionPropt = true;
                    q.Proport = '图片格式不符，请重新上传!'
                    q.file = '';
                    return;
                }
                if(b.slice(0,4) != 0.56 ){
                    q.ProportionPropt = true;
                    q.Proport = '图片比例尺寸不符，请重新上传!'
                    q.file = '';
                    return;
                }
                var size = file.size / 1024;
                if(size > 1024 ){
                    q.ProportionPropt = true;
                    q.Proport = '图片大小不符,请重新上传!'
                    q.file = '';
                    return;
                }        
                q.uploadImgUrl = img.src;
                q.uploadClass = 'carouse-upload-img';
            }
        },
        //保存上传
        saveUpload(){
            if(this.IE){
                document.getElementById("uploadForm").submit();
                this.setTime();
                setTimeout(()=> {
                    var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent
                    var index = str.indexOf('true');
                    if(index != -1 || str == ''){
                        this.$message({
                            message: '上传成功',
                            type: 'success',
                            duration: 2000
                        });
                    }
                }, 300);
                return
            }
            if(this.file == ''){
                this.$message({
                    message: '请添加或更新图片',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            var data = new FormData();
            data.append('fkeyarea', this.pleaseCode);
            data.append('farea', this.pleaseCode);
            data.append('fclient', 2);
            data.append('file', this.file.raw);
            data.append('fbooth', 6);
            data.append('moduleid', 'P11006');
            data.append('fid', this.fid);
            data.append('ffileid',this.ffileid);
            data.append('fjumpposition', 1);//跳转位置 1.普通网页2.团购产品详情3.商城列表
            data.append('fhasPicture', true);
            this.setTime();
            this.$http.post('/banner/addAndUpdataBanner.do', data).then(res => {
                if (res.data.success) {
                    this.$message({
                        message: '上传成功',
                        type: 'success',
                        duration: 2000
                    });
                    var upImg={ 
                        farea:this.pleaseCode, 
                        fclient:2, 
                        fbooth:6, 
                        fkeyarea:this.pleaseCode + ''
                    }
                    this.findImg(upImg);
                }else{
                    this.$message({
                        message: '上传失败',
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
    },
    components:{
        tHeader,
        regionalChoice,
        propt,
        uploadIe
    }
}
</script>





