import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import admin from './admin.js'
import home from './home'
import menu from './menu'
import base from './base'
import certification from './certification'
export default new Vuex.Store({
	modules: {
        admin,
		home,
        menu,
        base,
		certification
	}
})



