<template>
    <div class="partnerManage partner-propt">
        <tHeader>产品上架审核</tHeader>
        <div class='commodity-bg'>
            <div class='commodity-select-top'>
                <div class='commodity-select-three'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                        :isProduce='true'
                    >
                    </regional-choice>
                    <div class='parter-select-top'>
                        <partner
                            @getSelect='getPatnerId'
                            :codeName='codeName'
                            :partName='partnerName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                        ></partner>
                    </div>
                    <div class='partner-select'>
                        <carouse-select
                            textName='审核状态:'
                            :region='produceType'
                            @getSelect='getSelect'
                        ></carouse-select>
                    </div>
                </div>
                <div class='partner-input'>
                    <span>产品名称:</span>
                    <el-input v-model="fproductname" placeholder="请输入内容"></el-input>
                </div>
                <el-button type="primary" icon="search" @click='search'>搜索</el-button>
            </div>
            <div class='partner-table  prchase-list'>
                <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                    <el-table-column prop="number" label="序号" width="60">
                    </el-table-column>
                    <el-table-column prop="type" label="审核状态" width='155'>
                    </el-table-column>
                    <el-table-column prop="fgrouparea" label="区域" width='170'>
                    </el-table-column>
                    <el-table-column prop="fsuppliername" label="合作方" width='210'>
                    </el-table-column>
                    <el-table-column prop="fproductname" label="产品名称" width='260'>
                    </el-table-column>
                    <el-table-column prop="fcreatetime" label="申请时间" width='210'>
                    </el-table-column>
                    <el-table-column prop="fauditor" label="审核人" width='130'>
                    </el-table-column>
                    <el-table-column prop="fupdatetime" label="审核时间" width='210'>
                    </el-table-column>
                    <el-table-column label="操作" fixed="right">
                        <template scope="scope">
                            <el-button  type="text" size="small" @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus == '1' && admin.ucompany == 100 ?  true : false">
                                审核
                            </el-button>
                            <el-button type="text" size="small"  @click='viewDetails(scope.row)' v-if="scope.row.fauditstatus !='1' || admin.ucompany != 100 ?  true : false">
                                查看
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class='partner-page'>
                    <pageing v-if='listLength > 10'
                            @changePage="changePage"
                            @changSize='changSize'
                            :showPageArry="listLength > 15 ? [10,15,20] : [10,15]"
                            :showPagelist="showPagelist"
                            :listLength="listLength"
                            :currentPage='currentPage'
                        >
                    </pageing>
                </div>
            </div>
        </div>
        <div class='commodity-propt partner-product-propt'>
            <el-dialog title="产品上架审核" :visible.sync="addNewProduce" :show-close='false' 
             @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='addNewProduce = false'>
                <div class='partner-product-bg'>
                    <div class='partner-product-text'>
                        <div class='partner-product-title'>
                            <span>合作方名称:</span><span>{{parterDetal.fsuppliername}}</span><span>|</span><span>{{parterDetal.fgrouparea}}</span>
                        </div>
                        <div>
                            <p class='parter-maxLngth-css'><span>市场名称:</span><span>{{parterDetal.fmarketproductname}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>产品名称:</span><span>{{parterDetal.fproductname}}</span></p>
                            <p class='parter-maxLngth-css'><span class='partner-product-span'>用料名称:</span><span>{{parterDetal.fmaterialid}}</span></p>
                        </div>
                        <div>
                            <p class='parter-maxLngth-css'><span>推荐行业:</span><span>{{parterDetal.frecommendindustry}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>适用行业:</span><span>{{parterDetal.fsuitindustry}}</span></p>
                            <p class='parter-maxLngth-css'><span class='partner-product-span'>物流:</span><span>{{parterDetal.flogistics}}</span></p>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>交期天数:</span><span>{{parterDetal.fdeliveryday + '天'}}</span></p><p class='parter-maxLngth'><span class='partner-product-span'>交期时间:</span><span>{{parterDetal.fdeliverytime}}</span></p>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>楞型:</span><span>{{fluteType[parterDetal.fflutetype]}}</span></p>
                        </div>
                        <div>
                            <p class='parter-maxLngth'><span >层数:</span><span>{{parterDetal.flayer}}</span></p>
                        </div>
                        <div>
                             <p class='parter-maxLngth'><span>最大接单长:</span><span>{{parterDetal.fmaxorderlength}}</span><span>CM</span></p><span >最小接单长:</span><span>{{parterDetal.fminorderlength}}</span><span>CM</span>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>最大接单宽:</span><span>{{parterDetal.fmaxorderwidth}}</span><span>CM</span></p><span >最小接单宽:</span><span>{{parterDetal.fminorderwidth}}</span><span>CM</span>
                        </div> 
                        <div>
                            <p class='parter-maxLngth'><span>落料长加:</span><span>{{parterDetal.fmateriallengthplus}}</span><span>CM</span></p><span >落料宽加:</span><span>{{parterDetal.fmaterialwidthplus}}</span><span>CM</span>
                        </div>   
                    </div>
                    <p>
                        产品列表主图:
                    </p>
                    <img :src='parterDetal.url' class='partner-product-shelf'>
                    <p class='partner-add-img'>
                        产品详情图:
                    </p>
                    <div v-for='imgList in parterDetal.detailsfigure' :key='imgList.url' style='display:inline-block'>
                        <img :src='imgList.url' class='partner-product-list'>
                    </div>
                </div>
                <div class='partner-product-isPass'>
                       <p>审核结果</p>
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '1' && admin.ucompany == 100 ? true: false">
                            <el-radio class="radio" v-model="radio" label="1">通过</el-radio>
                            <el-radio class="radio" v-model="radio" label="2">驳回</el-radio>
                            <div class='partner-product-input'>
                                <el-input v-model="reject" placeholder="请输入驳回理由（不超过20字符）" :maxlength='20' v-if="radio == '2'"></el-input>
                            </div>
                       </div>
                       <div class='partner-product-btn' v-if="parterDetal.fauditstatus == '1' && admin.ucompany == 100 ? true: false">
                            <el-button @click='addNewProduce = false'>取消审核</el-button>
                            <el-button type="primary" @click='saveView'>保存审核</el-button>
                       </div>
                        <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '2' ? true: false">
                           <div class='partner-check-pass'>通过</div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>
                       <div class='partner-product-radio' v-if="admin.ucompany != 100 &&　parterDetal.fauditstatus != '3' &&　parterDetal.fauditstatus != '2' ? true: false">
                           <div class='partner-check-pass'>审核中</div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>  
                       <div class='partner-product-radio' v-if="parterDetal.fauditstatus == '3' ? true: false">
                           <div class='partner-check-pass'>驳回理由:
                               <span>{{parterDetal.fcomment}}</span>
                           </div>
                           <el-button @click='addNewProduce = false'>关闭查看</el-button>
                       </div>  
                </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import {VERSION, IMGPATH} from '@/util/constant.js'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fauditorid = this.admin.ucompany
        this.fauditor = this.admin.uname;
        this.parterId = this.admin.ucompany;      //合作方id
         //区域拆分键
        this.fkeyarea = this.admin.ocode;
        //非东京账号登录
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
           }, 1);
           this.searchFunc();
        }
    },
	data () {
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            produceType:[
                {regionName:'全部', value:'-1'},
                {regionName:'审核中', value:'1'},
                {regionName:'已通过', value:'2'},
                {regionName:'已驳回', value:'3'}
            ],
            listLength:0,
            showPagelist:10,
            currentPage:1,
            radio:'1',
            fkeyarea:'',           //区域拆分键
            fgroupareaid:'',       //服务区域 
            fauditstatus:'-1',     //审核状态
            reject:'',             //驳回理由
            codeName:'',           //合作方查询市
            parterId:'',           //合作方ID
            fproductname:'',       //产品名称
            fauditorid:'',         //审核人Id
            fauditor:'',           //审核人
            addNewProduce:false,
            typeArry:{
                '0':'待上架',
                '1':'审核中',
                '2':'已通过',
                '3':'已驳回'
            },
            tableData:[],
            parterDetal:{},       //编辑获取详细信息
            fluteType:{
                '1': 'BC瓦',
                '2': 'BE瓦',
                '3': '单C瓦',
                '4': '单B瓦',
                '5': '单E瓦',
                '6': 'EBC瓦'
            }
		}
	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //区域code
        getPlease(please){
            //  this.fgroupareaid = please.code;
             this.codeName = please;
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
            this.tableData = [];
        },
        //合作方选择
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.partnerName = data.name;
        },
        //切换页面
        changePage(page){
            this.currentPage = page;
            this.searchFunc();
        },
        //切换页面条数
        changSize(pageLength){
            this.showPagelist = pageLength;
            this.searchFunc();
        },
        //查询方法
        searchFunc(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                // fgroupareaid:this.fgroupareaid,
                fproductname:this.fproductname,
                pagenum:this.currentPage - 1,
                pagesize:this.showPagelist,
                sort:'2'
            }
            this.searchList(data);
        },
        //查询列表
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.tableData=[];
                    this.tableData = res.data.data;
                    this.listLength = res.data.total;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = this.currentPage * this.showPagelist - this.showPagelist + i + 1;
                        this.tableData[i].type = this.typeArry[this.tableData[i].fauditstatus];
                        if(this.tableData[i].fauditstatus == 1){
                            this.tableData[i].fupdatetime = '/'
                            this.tableData[i].fauditor = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //获取审核状态列表
        getSelect(name){
            for(let i=0; i<this.produceType.length; i++){
               if(name == this.produceType[i].regionName){
                    this.fauditstatus = this.produceType[i].value;
               }
            }
        },
        //搜索
        search(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fauditstatus:this.fauditstatus,
                // fgroupareaid:this.fgroupareaid,
                fproductname:this.fproductname,
                pagenum:0,
                pagesize:this.showPagelist
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '请选择区域合作方！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            this.searchList(data);
        },
        //查看
        viewDetails(data){
            this.addNewProduce = true; 
            this.reject = '';
            this.radio = '1'
            let datas = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                fid:data.fid
            }
            this.$http.post('/product/loadProducts.do', datas).then(res => {
                if(res.data.success){
                   this.parterDetal = res.data.data[0];
                   this.parterDetal.url = IMGPATH +  res.data.data[0].figure.furl;
                   var data = res.data.data[0].detailsfigure;
                   for(let i=0; i<data.length; i++){
                        data[i].url = IMGPATH + data[i].furl;
                   }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //保存审核
        saveView(){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:this.parterDetal.fid,
                fcomment:this.reject,
                operate:this.radio == '1' ? true : false,
                fauditorid:this.fauditorid,
                fauditor:this.fauditor
            }
            if(this.radio == '2' && this.reject ==''){
                this.$message({
                        message: '请输入驳回理由！',
                        duration: 2000
                     });
                return
            }
            this.$http.post('/product/examineProduct.do', data).then(res => {
                if(res.data.success) {
                   this.$message({
                        message: '审核成功！',
                        type: 'success',
                        duration: 2000
                     });
                     this.addNewProduce = false; 
                     this.searchFunc();
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing
    }  
}
</script>


