
export default {
    //过滤权限菜单
    getAuthMenu(state, httpMsg) {
        let fn = function (source) {
            for(let item of source) {
                for(let list of httpMsg) {
                    if(item.menu.url == list.pinterface) {
                       if(list.phtmlid == 'true') {   //true 和 false   为true有权限 false无权限  晕oa上传过来的是个字符串的true/false
                           item.auth = true;
                           break
                       }
                    }
                }
                if(item.childrenNode.length) {
                    fn(item.childrenNode)
                }
            }
        }
        fn(state.allMenu)
    },
    //获取一级菜单对应的子菜单
    getMenuList(state, name) {
        if(name == 'first') return
        for(let item of state.allMenu) {
            if(name == item.menu.url) {
                state.childMenu = item.childrenNode
                break
            }
        }
    }
}



