export default {
    state: {
        admin: localStorage.getItem('admin')
    },
    getters: {
        doneAdmin(state) {
            return JSON.parse(state.admin)
        }
    },
    mutations: {
        getAdmin(state) {
            let admin = localStorage.getItem('admin')
            state.admin = admin
        },
    },
    actions: {

    }
}



