<template>
    <div class='commodity-tab-top'>
        <el-tabs v-model="activeName" @tab-click="changeTab">
            <el-tab-pane :label='item.name' :name='item.value' v-for="item in tabArry" :key="item.name"></el-tab-pane>
        </el-tabs>
        <div>
            <el-checkbox v-model="checked" @change='isChecked' v-show="shipTypes == '0' && activeName == '0' ? true : false">不显示已取消订单</el-checkbox>
            <div class='commodity-top-time'>
                <div class="block">
                    <span class="demonstration">下单时间:</span>
                    <el-date-picker v-model="startTime" 
                    type="date" 
                    placeholder="开始时间"
                    @change="getStartTime"
                    :editable='false'
                    :picker-options="pickerOptions"  
                      >
                    </el-date-picker>
                </div>
            </div>
            <span>至</span>
            <div class='commodity-top-time'>
                <div class="block">
                    <el-date-picker v-model="endTime" 
                    type="date" 
                    placeholder="结束时间"
                    @change="getEndTime" 
                    :editable='false'
                    :picker-options="pickerOptions">
                    </el-date-picker>
                </div>
            </div>
            <div class='commodity-top-type' v-if="shipTypes != '0' && activeName == '3' || shipTypes == '0' && activeName == '0' ?  true : false">
                <select-type textName='订单状态:' :region='typeArry' @getSelect='getSelect' :regionName='regionNames'></select-type>
            </div>
            <div class='commodity-top-time'>
                <span>商品名称:</span>
                <el-input placeholder="请输入商品名称搜索" icon="search" v-model="commodityName" @change="searchComdity">
                </el-input>
            </div>
            <div class='commodity-top-time'>
                <span>订单号:</span>
                <el-input placeholder="请输入单号搜索" icon="search" v-model="order" @change="searchOrder">
                </el-input>
            </div>
            <el-button type="primary" icon="search"  @click='search'>搜索</el-button>
        </div>
    </div>
</template>
<script>
import select from '@/components/public/select'
import {getTimes, get3MonthBefor} from '@/util/common.js'
export default {
    props: ['tabArrys','startTimes','endTimes','commodityNames','orders','shipTypes', 'typeArrys', 'regionName'],
    created(){
        if(this.shipTypes == 3 || this.shipTypes == 5 || this.shipTypes == 4){
            this.typeArry = [
                {regionName:'全部', value:0 },
                {regionName:'待消费', value:2 },
                {regionName:'已完成', value:4 },
                {regionName:'已取消', value:5 }
            ]
            return
        }
        if(this.shipTypes == 1 || this.shipTypes == 2){
            this.typeArry = [
                {regionName:'全部', value:0 },
                {regionName:'待发货', value:1 },
                {regionName:'待签收', value:3 },
                {regionName:'已完成', value:4 },
                {regionName:'已取消', value:5 }
            ]
            return
        }
    },
    data() {
        return {
            checked:false,
            startTime:this.startTimes,
            endTime:this.endTimes,
            commodityName:this.commodityNames,
            order:this.orders,
            regionNames:this.regionName,
            typeArry:this.typeArrys === undefined ? [
                {regionName:'全部', value:0 },
                {regionName:'待发货', value:1 },
                {regionName:'待消费', value:2 },
                {regionName:'待签收', value:3 },
                {regionName:'已完成', value:4 },
                {regionName:'已取消', value:5 }
            ] : this.typeArrys,
            activeName:this.shipTypes == '0' ? '0' : '3',
            tabArry: this.tabArrys === undefined ? [
                { name: '全部', value: '3' },
                { name: '待处理', value: '0' },
                { name: '已处理', value: '1' }
            ] : this.tabArrys,
            currentTabe:'',
            //3月内
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                }
            }
        }
    },
    methods: {
        //切换标签页
        changeTab() {
            this.$emit('changeTab', this.activeName);
        },
        //选择下单时间
        getStartTime(){
            this.$emit('getStartTime', this.startTime);
        },
        getEndTime(){
            this.$emit('getEndTime', this.endTime);
        },
        //查询商品名称,订单号
        searchComdity(){
            this.$emit('searchComdity', this.commodityName);
        },
        searchOrder(){
            this.$emit('searchOrder', this.order);
        },
        //不显示已取消订单
        isChecked(){
            this.$emit('isChecked', this.checked);
        },
        //搜索
        search(){
            this.$emit('search'); 
        },
        //获取订单状态
        getSelect(val){
            for(let i=0; i<this.typeArry.length; i++){
                if(val === this.typeArry[i].regionName){
                    let data ={
                        name:val,
                        value:this.typeArry[i].value
                    }
                    this.$emit('getSelect', data);
                }
            }
        }
    },
    components: {
        selectType:select
    },
    watch:{
        regionName(name){
            this.regionNames = name;
        },
        startTimes(time){
            this.startTime = time;
        },
        endTimes(time){
            this.endTime = time;
        },
        commodityNames(value){
            this.commodityName = value;
        },
        orders(value){
            this.order = value;
        }
    }
}
</script>


