<template>
    <div class="prchase-commInput">
        <span>{{text}}</span>
	    <el-input v-model="input" placeholder="请输入内容" @change='setValue'></el-input>
	</div>
</template>


<script>

export default {
    props:['text'],
	data () {
		return {
			input:''
		}
	},
	methods:{
        setValue(){
            this.$emit('setValue', this.input)
        }
    }  
}
</script>
    

<style>

</style>


