<template>
    <div class="partnerManage">
        <tHeader>产品开团时间设置</tHeader>
        <div class='commodity-bg'>
            <div class='partner-opTime'>
                <div class="block partner-selectTime" >
                    <i class='partner-icon'>*</i>
                        <span class="demonstration">每日团购开团时期:</span>
                        <el-time-select placeholder="起始时间" v-model="startTime" :editable='false' :picker-options="{ 
                                start: '00:00',
                                step: '00:30',
                                end: '23:30'
                                }">
                        </el-time-select>
                        <span>点</span>
                </div>
                <div class="block partner-selectTime" >
                    <i class='partner-icon'>*</i>
                        <span class="demonstration">每日团购结束时期:</span>
                        <el-time-select placeholder="结束时间" v-model="endTime" :editable='false' :picker-options="{
                                start: '00:00',
                                step: '00:30',
                                end: '23:30',
                                minTime: startTime
                                }">
                        </el-time-select>
                        <span>点</span>
                </div>
                 <div class='partner-cycle-input'>
                        <common-input textName='价格方案提前展示期:' :error='errorAry[0].error' :errorPropt='errorAry[0].errorPropt' @setValue='getValue' defaultName='请输入提前展示的时长(2位有效小数)'  ref='leadTime' :defaultValue='leadTime' :maxlength='5'></common-input><span class='partner-cycle-input-span'>小时</span>
                </div>
                <div class='partner-cycle-input-two'>
                    <common-input textName='倒计时展示提前期:' :error='errorAry[1].error' :errorPropt='errorAry[1].errorPropt' @setValue='getValue' defaultName='请输入提前展示的时长(2位有效小数)'  ref='countDown' :defaultValue='countDown' :maxlength='5'></common-input><span class='partner-cycle-input-span'>小时</span>
                </div>  
                <el-button type="primary" @click='save'>保存设置</el-button>
            </div>        
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import partner from '@/components/public/Partner'
import commonInput from './input'
import {VERSION} from '@/util/constant.js'
import {checkDecimal} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        //区域拆分键
        this.fkeyarea = this.admin.ocode;
        let data = {
            version:VERSION,
            fkeyarea:this.fkeyarea
        }
        //获取已经设置的开团时间
        this.$http.post('/product/getGroupOpenCycle.do', data).then(res => {
            if(res.data.success){
                let data = res.data.data;
                this.startTime = data.fstarttime;
                this.endTime = data.fendtime;
                this.leadTime = data.faheadtime;
                this.countDown = data.fcutdowntime;
            }else{
                 this.$message({
                    message: res.data.msg,
                    type: 'error',
                    duration: 2000
                 });
             }
        }).catch(function (error) {
             console.log(error);
        });
    },
	data () {
        let now = new Date()
		return {
            admin:{},
            fkeyarea:'',
            regionName:'',
            startTime:'',
            endTime:'',
            leadTime:'',
            countDown:'',
            errorAry:[
                {error: false, errorPropt:'请输入正确的时长(2位有效小数)'},
                {error: false, errorPropt:'请输入正确的时长(2位有效小数)'}
            ]
		}
	},
	methods:{
        //获取时间
        getValue(){
           this.leadTime = this.$refs.leadTime.input;
           this.countDown = this.$refs.countDown.input;
        },
        //保存
        save(){
            this.errorAry[0].error = checkDecimal(this.leadTime, 100, true);
            this.errorAry[1].error = checkDecimal(this.countDown, 100, true);
            if(this.errorAry[0].error || this.errorAry[1].error){
                this.$message({
                            message: '请输入正确的时长',
                            type: 'error',
                            duration: 1500
                    });
                    return
            }
            let data = {
                version:VERSION,
                fkeyarea:'3303',
                fstarttime:this.startTime,
                fendtime:this.endTime,
                faheadtime:this.leadTime,
                fcutdowntime:this.countDown
            }
            this.$http.post('/product/setGroupOpenCycle.do', data).then(res => {
                if(res.data.success)
                    this.$message({
                            message: '设置成功',
                            type: 'success',
                            duration: 1500
                    });
            }).catch(function (error) {
                console.log(error);
            });
        }
    },
    components:{
        carouseSelect,
        tHeader,
        partner,
        commonInput
    }  
}
</script>


