<template>
    <div class="balanceWarn-warp">
        <query-list :timeType="timeType" :timeSearch="true" @query="query"></query-list>
        <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  height="700">
            <el-table-column property="fcreateTime" label="日期" align="center" width="155">
                <template scope="scope">
                    {{scope.row.fcheckDate.substring(0,scope.row.fcheckDate.length -2)}}
                </template>
            </el-table-column>
            <el-table-column property="fphone" label="手机号" align="center" width="115">
            </el-table-column>
            <el-table-column property="fcertificateName" label="认证名" align="center"  width="115">
            </el-table-column>
            <el-table-column property="flastDayBalance" label="昨日金额" align="center">
            </el-table-column>
            <el-table-column property="fonlineInto" label="线上转入" align="center">
            </el-table-column>
            <el-table-column property="fofflineInto" label="线下转入" align="center">
            </el-table-column>
            <el-table-column property="fofflineOut" label="线下转出" align="center">
            </el-table-column>
            <el-table-column property="fonlineConsumer" label="线上消费" align="center">
            </el-table-column>
            <el-table-column property="fonlineRefund" label="线上退款" align="center">
            </el-table-column>
            <el-table-column property="ftheoryBalance" label="理论余额" align="center">
            </el-table-column>
            <el-table-column property="factualBalance" label="实际余额" align="center">
            </el-table-column>
            <el-table-column property="fisAbnormal" label="是否异常" align="center">
                <template scope="scope">
                    <span  v-show="scope.row.fisAbnormal == true" >是</span>
                    <span  v-show="scope.row.fisAbnormal == false">否</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="balanceWarn-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'日期',
                total:0,
                size:15,
                currentPage:0,
                tableData:[], // 列表容器
                startTime:'',
                endTime:'',
                userId:''
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.$http.post(`/finance/selectBalanceWarningForms.do`,{
                    pageSize:this.size,
                    pageNo:this.currentPage,
                    userId:this.userId == ''?null:this.userId,
                    startTime:this.startTime==''?null:this.startTime,
                    endTime:this.endTime==''?null:this.endTime,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                    }
                })
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.userId = queryJson.userId;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .balanceWarn-warp{
        position:relative;
    }
    .balanceWarn-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .balanceWarn-pagination .el-pagination button,
    .balanceWarn-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .balanceWarn-pagination .el-pager li.btn-quicknext,
    .balanceWarn-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .balanceWarn-pagination .el-pagination .el-select .el-input input,
    .balanceWarn-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .balanceWarn-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
</style>



