<template>
    <div>
        <div class='ie-upload-css'>
            <form name="uploadForm" :id="uploadForm" method="post" enctype="multipart/form-data" 
            :action="action" target="uploadFrame">
                <input type="file" name="file" @change="change" ref="input" id='file' class='file-input' />
                <input :name="item.name" type="hidden" :value='item.data' v-for='item in uploadDate' :key='item.name'/>
                <iframe name="uploadFrame" id="uploadFrame" style="display:none;"></iframe>
                </p>
            </form>
            <img :src="src" ref="img" class='uploadImg-IE'>
        </div>
        <div>
            <propt :text='Proport' 
            :dialogVisible='ProportionPropt' 
            @closeVisible='ProportionPropt = false' 
            @sure='sure'></propt>
        </div>
    </div>
</template>


<script>
import { getSizeIncompatible } from '@/util/common.js'
import propt from '@/components/public/propt'
export default {
    props: ['uploadDate', 'checkData','uploadSrc','action','uploadFormsId'],
    mounted(){
        setTimeout(()=> {
            if(this.uploadSrc !=''){
                this.src = this.uploadSrc;
            }else{
                this.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';
            }
        }, 200);
    },
    data() {
        return {
            src: 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7',
            elInput: null,
            ProportionPropt: false,
            Proport: '',
            uploadForm:this.uploadFormsId === undefined ? 'uploadForm' : this.uploadFormsId
        }
    },
    methods: {
        //IE9上传
        change() {
            const refs = this.$refs;
            const elInput = refs.input;
            const elImg = refs.img;
            elInput.select();
            elInput.blur();
            var src = document.selection.createRange().text;
            console.log(src)
            var fso = new ActiveXObject("Scripting.FileSystemObject");
            //获取文件大小
            var file = fso.GetFile(src);
            //需要动态获取比例时调用
            this.$emit('getProportion');
              //获取图片宽高
            var data = getSizeIncompatible(src);
            var fileSize = file.size;
            var imgType = src.slice(-3);
            if (imgType != "png" &&
                imgType != "jpg" &&
                imgType != "peg" && this.checkData.needPropt == undefined) {
                this.ProportionPropt = true;
                this.Proport = '图片格式不符，请重新上传!'
                return;
            }
            if (imgType != "png" &&
                imgType != "jpg" &&
                imgType != "peg" && !this.checkData.needPropt) {
                this.$message.error('图片格式不符，请重新上传!!');
                return;
            }
            //限制大小
            if(this.checkData.minWidth !== undefined && this.checkData.minHeight !== undefined){
                if (data.width != data.height || data.width < this.checkData.minWidth || data.height < this.checkData.minHeight) {
                    this.$message.error('图片宽高不小于' + this.checkData.minWidth + ',且比例为1:1!');
                    return;
                }
            }
            if(this.checkData.width !== undefined){
                if(data.width != this.checkData.width){
                    this.$message.error('图片宽等于' + this.checkData.width + '!');
                    return
                } 
            }
            var size = fileSize / 1024 / 1024;
            if (size > this.checkData.size && this.checkData.needPropt == undefined) {
                this.ProportionPropt = true;
                this.Proport = '图片大小不符,请重新上传!'
                return;
            }
            if (size > this.checkData.size && !this.checkData.needPropt){
                this.$message.error('图片大小不符,请重新上传!');
            }
            var b = data.width / data.height;
            if(this.checkData.Proportion !== undefined){
                if (b != this.checkData.Proportion && this.checkData.needPropt == undefined) {
                    this.ProportionPropt = true;
                    this.Proport = '图片比例尺寸不符，请重新上传!'
                    return;
                }
                if (b != this.checkData.Proportion && !this.checkData.needPropt) {
                    this.$message.error('图片比例尺寸不符，请重新上传!');
                    return;
                }
            }
            this.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7';
            document.selection.empty();
            elImg.style.filter = `progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='${src}')`;
            //添加成功
            this.$emit('addImgSuccess');
        },
        //弹框确定方法
        sure(){
            this.ProportionPropt = false;
            this.$emit('openEditImg');
        }
    },
    components: {
        propt
    },
    watch:{
        uploadSrc(path){
               this.src = path;
               console.log('path' + '----------' + path)
               if(this.src == ''){
                    // setTimeout(()=> {
                    //     var obj = document.getElementById('file')
                    //     obj.select(); 
                    //     document.selection.clear();
                    // }, 1000); 
                    this.src = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'; 
                    const elImg = this.$refs.img;
                    elImg.style.filter = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'; 
               }
        },
        uploadDate(ary){
            // for(let i=0; i<ary.length; i++){
            //     console.log(ary[i].data + '------------' + ary[i].name)
            // }
           this.uploadDate = ary;
        }
    }
}
</script>



