<template>
    <div class="rechargeWindow-warp" v-if="rechargeWindowShow">
        <div class="rechargeWindow-titleWarp">
            <h3>充值</h3>
        </div>
        <span class="rechargeWindow-closeBtn" @click="rechargeWindowClose()"></span>
        <div class="rechargeWindow-table-warp">
            <table class="rechargeWindow-table">
	            <tr>
	                <td>手机号<span class="rechargeWindow-red">*</span>：</td>
	                <td> <input type="text" class="rechargeWindow-adjustText" v-model="phone" @input="phoneInput"></td>
	            </tr>
	            <tr>
	                <td>认证名：</td>
	                <td><input type="text" class="rechargeWindow-adjustText" v-model="certificateName" disabled></td>
	            </tr>
	            <tr>
	                <td>资金类型：</td>
	                <td>
	                    <el-select  v-model="fundType" placeholder="请选择" clearable @visible-change="visible">
		                    <el-option v-for="item,index in fundTypeList" :key="index" :label="item.label" :value="item.value">
		                    </el-option>
		                </el-select>
	                </td>
	            </tr>
	            <tr>
	                <td>金额(元)<span class="rechargeWindow-red">*</span>：</td>
	                <td><input type="text" class="rechargeWindow-adjustText" @input="uppercase" v-model="amount" ></td>
	            </tr>
	            <tr>
	                <td>大写金额：</td>
	                <td>
	                    <input type="text" class="rechargeWindow-adjustText" disabled v-model="uppercaseAmount">
	                </td>
	            </tr>
	            <tr>
	                <td>备注：</td>
	                <td>
	                    <textarea  v-model="remark" class="rechargeWindow-remarks" @input="remarksInput()" placeholder="限100字内" >
	                    </textarea>
	                </td>
	            </tr>
	        </table>
        </div>
        <div class="rechargeWindow-btnGroup">
            <button class="rechargeWindow-yesBtn" @click="rechargeSure()" :disabled="rechargeDisabled">申请审核</button>
        </div>
    </div>
</template>
<script>
    import {VERSION} from '@/util/constant.js'
    import {convertCurrency, selectToHighIndex} from '@/util/common.js'
    export default{
        props:['rechargeWindowShow'],
        data(){
            return{
                certificateName:'', //认证名
                faccount:'',
                orderId:'', //订单号
                keyArea:'', //区域拆分键
                userId:'', //用户id
                phone:'', // 手机
                amount:'', // 金额
                uppercaseAmount:'', //大写金额
                fundType:'', //资金类型
                remark:'', // 备注
                fundTypeList: [
                    {
                        value:1,
                        label:'现金'
                    },{
                        value:2,
                        label:'承兑'
                    },{
                        value:3,
                        label:'电汇'
                    },{
                        value:4,
                        label:'积分'
                    },{
                        value:0,
                        label:'其他'
                    }
                ],
                rechargeDisabled:false
            }
        },
        methods: {
            // phone在输入的事件
            phoneInput(){
                this.phone = this.phone.replace(/\D/g,'');
                if(this.phone.length == 11){
                     //查询用户名
                  this.$http.post('/rechargeOrder/selectPhones.do',{
                      version:VERSION,
                      phone:this.phone
                  }).then(res => {
                      res = res.data;
                      if(res.success){
                          //判空
                          if(res.data[0]){
                              this.certificateName = res.data[0].fcertificateName;
                              this.faccount = res.data[0].faccount;
                              this.userId = res.data[0].fuserId;
                              this.keyArea = res.data[0].fkeyArea;
                          }else{
                              this.certificateName = '';
                              this.userId = '';
                              this.keyArea = '';
                              this.faccount = '';
                          }
                      }
                  })
                }else{
                    this.certificateName = '';
                    this.userId = '';
                    this.keyArea = '';
                    this.faccount = '';
                }
            },
            //关闭窗口
            rechargeWindowClose(){
                this.$emit('rechargeWindowClose');
                Object.assign(this.$data, this.$options.data())
            },
            //备注input事件
            remarksInput(){
                this.remark = this.remark.replace(/\s/g,'');
            },
            //提交审核
            rechargeSure(){
                if(this.phone === '' || this.amount === '' || this.fundType === ''){
                    this.msg('填写完整!','error');
                    return;
                }
                if(this.phone.length != 11){
                    this.msg('手机号必须11位!','error');
                    return;
                }
                if(this.remark.length > 100){
                    this.msg('备注不大于100字!','error');
                    return;
                }
                if((this.certificateName === ''|| this.certificateName == undefined)&&
                (this.faccount === '' || this.faccount == undefined)){
                    this.msg('用户未认证!','error');
                    return;
                }
                if(parseFloat(this.amount) == 0){
                    this.msg('金额不能为零!','error');
                    return;
                }
                if(parseFloat(this.amount)>10000000){
                    this.msg('金额小于10000000!','error');
                    return;
                }
                this.rechargeDisabled =true;
                this.$http.post('/rechargeOrder/save.do',this.saveData()).then(res => {
                    res = res.data;
                    if(res.success){
                        this.$emit('rechargeSure');
                        Object.assign(this.$data, this.$options.data());
                    }else{
                        this.msg('操作失败!','error');
                    }
                    this.rechargeDisabled =false;
                })
            },
            // 输入事件
            uppercase(){
                this.amount = this.amount.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
                this.amount = this.amount.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的
                this.amount = this.amount.replace(".","$#$").replace(/\./g,"").replace("$#$",".");
                this.amount = this.amount.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数
                if(this.amount.indexOf(".")< 0 && this.amount !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                    this.amount= parseFloat(this.amount);
                }
                if(parseFloat(this.amount) > 10000000){
                    this.amount = 10000000;
                }
                this.uppercaseAmount = convertCurrency(this.amount);
            },
            saveData(){
                return {
                    operator:this.$store.getters.doneAdmin.uname, //操作人
                    orderId:this.orderId, //订单号
                    keyArea:this.keyArea, //区域拆分键
                    status:0, //状态
                    orderType:1, // 类别
                    userId:this.userId, //用户id
                    phone:this.phone, // 手机
                    amount:this.amount, // 金额
                    uppercaseAmount:this.uppercaseAmount == ''?null:this.uppercaseAmount, //大写金额
                    fundType:this.fundType, //资金类型
                    remark:this.remark==''?null:this.remark // 备注
                }
            },
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            visible(a){
               if(a){
                   selectToHighIndex();
               }
            }
        }
    }
</script>
<style>
    .rechargeWindow-warp{
        width:460px;
        height: 470px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 9000;
    }
    .rechargeWindow-table-warp{
        padding-left:50px;
    }
    .rechargeWindow-table{
        display: block;

    }
    .rechargeWindow-table tr td:nth-child(1){
        padding-right: 15px;
    }
    .rechargeWindow-red{
        color:red;
    }
    .rechargeWindow-table tr:nth-child(1),
    .rechargeWindow-table tr:nth-child(2),
    .rechargeWindow-table tr:nth-child(3){
        height: 45px;
    }
    .rechargeWindow-table tr:nth-child(4),
    .rechargeWindow-table tr:nth-child(5),
    .rechargeWindow-table tr:nth-child(6){
        height: 52px;
    }
    .rechargeWindow-titleWarp{
        overflow: hidden;
    }
    .rechargeWindow-warp h3{
        font-size: 16px;
        height: 50px;
        line-height: 50px;
        padding-left: 48px;
        margin-bottom: 25px;
        box-shadow:0px 6px 10px #efefef;
    }
    .rechargeWindow-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .rechargeWindow-btnGroup{
        margin-top: 20px;
        text-align: center;
    }
    .rechargeWindow-btnGroup button{
        width:80px;
        height: 36px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        cursor:pointer;
        outline: none;
    }
    .rechargeWindow-adjustText{
        width: 280px;
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    .rechargeWindow-table .el-input__inner{
        height: 34px;
        line-height: 34px;
    }
    .rechargeWindow-table .el-select{
        width: 100%;
    }
    .rechargeWindow-notice{
        line-height: 26px;
        padding-left: 110px;
        color:red;
        background: url("../../../../../../static/images/publicImages/notice.png") no-repeat 90px 8px;
    }
    .rechargeWindow-remarks{
        height: 56px;
        display: block;
        resize: vertical;
        padding: 5px 7px;
        line-height: 1.5;
        width: 100%;
        color: #1f2d3d;
        border: 1px solid #bfcbd9;
        border-radius: 4px;
        width:264px;
        resize: none;
    }
</style>



