<template>
    <div id="intergralDetailed-warp">
        <div class="intergralDetailed-warp-padding">
            <div class="intergralDetailed-radius">
                <tHeader>查看明细</tHeader>
                <div class="intergralDetailed-tableQuery-padding">
                    <ul class="intergralDetailed-Query clearfix">
                        <li>
                            区<span>域:</span>
                            <el-select v-model="provinceVal" placeholder="请选择" clearable @change="provinceChange">
                                <el-option v-for="item in province" :key="item.fprovincecode" :label="item.fprovincename" :value="item.fprovincecode">
                                </el-option>
                            </el-select>
                            -
                            <el-select v-model="cityVal" placeholder="请选择" clearable @change="cityChange">
                                <el-option v-for="item in city" :key="item.fcitycode" :label="item.fcityname" :value="item.fcitycode">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            合作方：
                            <el-select v-model="partnerVal" placeholder="请选择" clearable>
                                <el-option v-for="item in partner" :key="item.id" :label="item.oname" :value="item.id">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            积分类型：
                            <el-select v-model="intergralVal" placeholder="请选择" clearable>
                                <el-option v-for="item in intergralType" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            客户名称：
                            <input type="text" v-model="searchname" placeholder="请输入客户名称">
                            手机号:
                            <input type="text" v-model="phone" placeholder="请输入手机号" @input="phoneNum" >
                        </li>
                        <li>
                            操作时间：
                            <el-date-picker
                                v-model="startTime"
                                type="date"
                                placeholder="开始日期"
                                format = "yyyy-MM-dd"
                                :picker-options="pickerStart">
                            </el-date-picker>
                                &nbsp;-
                            <el-date-picker
                                v-model="endTime"
                                type="datetime"
                                placeholder="结束日期"
                                format = "yyyy-MM-dd HH:mm"
                                :picker-options="pickerEnd">
                            </el-date-picker>
                        </li>
                        <li>
                            <el-button type="primary" icon="search" @click="query()">搜索</el-button>
                        </li>
                    </ul>
                    <detailedTable :tableData="tableData" :currentPage="currentPage" :size="size"></detailedTable>
                </div>
            </div>
        </div>
        <div class="intergralDetailed-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange" :current-page="currentPage"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import detailedTable from './intergralDetailedTable.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        components:{
            tHeader,
            detailedTable
        },
        created(){
            this.getTable(true);
            //省
            this.$http.post('/address/findProvince.do',{ code: "0", status: "1" }).then(res => {
                res = res.data;
                if(res.success){
                    this.province = res.data;
                }
            })
        },
        data(){
            return{
                pickerStart:{
                    disabledDate(time) {
                        return  time.getTime() > Date.now() - 8.64e7 + 86400000 ;
                    }
                },
                pickerEnd:{
                    disabledDate(time) {
                        return  time.getTime() > Date.now() - 8.64e7 + 86400000 ;
                    }
                },
                total:0, //总数
                size:15, // 当前的一页显示的数据条数
                currentPage:1, // 当前的页码
                endTime:'', // 结束时间
                startTime:'', //开始时间
                provinceVal:'',//省份value
                cityVal:'', //城市value
                partnerVal:'', // 合作方value
                intergralVal:'', // 积分的value
                searchname:'', // 客户名称
                phone:'',//手机号
                province: [], //省份数据容器
                city:[], //城市数据容器
                partner:[], //合作方数据容器
                intergralType:[{
                        value:1,
                        label:' 购买产品送积分'
                    },{
                        value:2,
                        label:'商城订单取消'
                    },{
                        value:3,
                        label:'商城消费'
                    },{
                        value:4,
                        label:'团购订单取消'
                    },{
                        value:5,
                        label:'社区活动积分'
                    },{
                        value:6,
                        label:'商城活动积分'
                    },{
                        value:7,
                        label:'包辅活动积分'
                    },{
                        value:8,
                        label:'会员返利积分'
                    },{
                        value:9,
                        label:'专享返利积分'
                    },{
                        value:10,
                        label:'其他返利积分'
                    },{
                        value:11,
                        label:'充值送积分'
                    },{
                        value:12,
                        label:'充值失败扣积分'
                    },{
                        value:13,
                        label:'商城订单取消异常扣积分'
                    },{
                        value:14,
                        label:'商城下单异常加积分'
                    },{
                        value:15,
                        label:'团购下单异常扣积分'
                    },{
                        value:16,
                        label:'团购订单取消异常加积分'
                    }], // 积分类型
                tableData:[], // 列表数据
                queryJson:{} // 查询用的数据容器
            }
        },
        methods:{
            //获取时间戳
            getTime(val){
                return new Date(val).getTime();
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            //查询
            query(){
                if(this.phone.length > 11){
                     this.msg('手机号最多11位！','error');
                     return;
                }
                if(this.getTime(this.endTime)<this.getTime(this.startTime)){
                    this.msg('对不起亲爱哒，结束日期必须大于开始日期！','error');
                    return;
                }
                if(this.startTime=='' && this.endTime !=''){
                    this.msg('请填写开始时间再进行查询！','error');
                     return;
                }
                if(this.startTime !='' && this.endTime ==''){
                    this.msg('请填写结束时间再进行查询！','error');
                     return;
                }
                this.currentPage = 1;
                this.getTable(true);
            },
            //获取查询的数据
            querydata(){
                let begintime = this.useTime(this.startTime, false);
                let endTime = this.useTime(this.endTime, true);
                return{
                    version:VERSION, // 版本号；
                    current:this.currentPage, //当前的页面
                    size:this.size, // 一页的数据总数
                    begintime:begintime, // 开始时间
                    endtime:endTime, // 结束时间
                    ftype:this.intergralVal, // 积分类型
                    fpartner:this.partnerVal, // 合作方id
                    searchname:this.searchname, // 用户名
                    fkeyarea:this.cityVal == ''?this.provinceVal:this.cityVal,
                    phone:this.phone
                }
            },
            //省change
            provinceChange(val){
                this.$http.post('/address/findCity.do',{"code":val,"status":1}).then(res =>{
                    res = res.data;
                    if(res.success){
                        this.city = res.data;
                        this.cityVal = '';
                    }else{
                        this.city = [];
                        this.cityVal = '';
                    }
                })
            },
            //城市查询
            cityChange(val){
                let cityLabel = '';
                for(let i =0; i<this.city.length; i++){
                    if(val == this.city[i].fcitycode){
                        cityLabel = this.city[i].fcityname;
                        break;
                    }
                }
                //获取合作方
                this.$http.post('/partners/findOption.do', {city:cityLabel}).then(res =>{
                    res = res.data;
                    if(res.success) {
                        if(res.data.length){
                            this.partner = res.data;
                            this.partnerVal = '';
                        }else{
                            this.partner = [];
                            this.partnerVal = '';
                        }
                    }else{
                        this.partner = [];
                        this.partnerVal = '';
                    }
                })
            },
            //对象的copy；
            deepCopy(obj){
                if(typeof obj != 'object'){
                    return obj;
                }
                var newobj = {};
                for( var attr in obj){
                    newobj[attr] = this.deepCopy(obj[attr]);
                }
                return newobj;
            },
            //封装的表格获取数据方法
            getTable(bFalg){
                if(bFalg){
                    this.queryJson = this.deepCopy(this.querydata());
                }
                this.queryJson.current=this.currentPage;
                this.queryJson.size=this.size;
                this.$http.post('/integral/journal/list.do',this.queryJson).then(res => {
                    var res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.msg(res.msg,'error');
                    }
                })
            },
            //页码改变
            handleCurrentChange(pageNum){
                this.currentPage = pageNum;
                this.getTable(false);
            },
            //一页总量改变
            handleSizeChange(size){
                this.currentPage = 1;
                this.size = size;
                this.getTable(false);
            },
            useTime(value, bFlag){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value);
                var year = time.getFullYear();
                var month = time.getMonth() + 1<10? '0'+ (time.getMonth() + 1):time.getMonth() + 1;
                var date = time.getDate() <10 ? '0'+ time.getDate():time.getDate();
                var hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours();
                var min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
                var sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                if(bFlag){
                    return `${year}-${month}-${date} ${hour}:${min}`
                }else{
                    return `${year}-${month}-${date}`
                }
            },
            phoneNum(){
                this.phone = this.phone.replace(/\D/g,'');
            }
        }
    }
</script>

<style>
    #intergralDetailed-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    .intergralDetailed-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .intergralDetailed-radius{
        padding-bottom: 30px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    /*
        分页
    */
    .intergralDetailed-pagination{
        text-align: center;
    }
    .intergralDetailed-pagination .el-pagination button,
    .intergralDetailed-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .intergralDetailed-pagination .el-pager li.btn-quicknext,
    .intergralDetailed-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .intergralDetailed-pagination .el-pagination .el-select .el-input input,
    .intergralDetailed-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .intergralDetailed-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .intergralDetailed-tableQuery-padding{
        padding: 0 10px;
    }
    #intergralDetailed-warp .intergralDetailed-Query .el-input__inner{
        height: 34px;
        line-height: 34px;
        width: 200px;
    }

    #intergralDetailed-warp .intergralDetailed-Query li:nth-child(1) .el-input__inner{
        width: 148px;
    }
    #intergralDetailed-warp .intergralDetailed-Query li:nth-child(2) .el-input__inner,
    #intergralDetailed-warp .intergralDetailed-Query li:nth-child(3) .el-input__inner{
        width: 250px;
    }
    .intergralDetailed-Query{
        padding: 20px 0;
        width: 1050px;
    }
    .intergralDetailed-Query li{
        float: left;
    }
    .intergralDetailed-Query li:nth-child(2),
    .intergralDetailed-Query li:nth-child(3){
        margin-bottom: 15px;
    }
    .intergralDetailed-Query li:nth-child(1){
        padding-right: 30px;
    }

    .intergralDetailed-Query li:nth-child(2){
        padding-right: 19px;
    }

    .intergralDetailed-Query li:nth-child(4){
        padding-right: 37px;
    }

    .intergralDetailed-Query li:nth-child(5){
        padding-right: 20px;
    }
    .intergralDetailed-Query li:nth-child(4) input:nth-child(1){
        margin-right: 10px;
    }
    .intergralDetailed-Query li:nth-child(1) span{
        padding-left: 2em;
    }
    .intergralDetailed-Query li input{
        width: 160px;
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 34px;
        line-height: 1;
        padding: 3px 10px;
    }
</style>



