<template>
    <div class="receivables-warp">
        <query-list @query="query" :timeType="timeType" :timeSearch="true"></query-list>
        <div class="receivables-btn-warp">
            <span :class="{isactive:isActive}" @click="changeActive(0)">充值</span>
            <span :class="{isactive:!isActive}" @click="changeActive(1)">消费</span>
        </div>
        <!--充值-->
        <div v-if="isActive">
            <el-table border :data="tableData1"  highlight-current-row  style="width: 100%"  height="700">
                <el-table-column property="fphone" label="手机号" align="center">
                </el-table-column>
                <el-table-column property="fcertificateName" label="认证名" align="center">
                </el-table-column>
                <el-table-column property="famount" label="金额(元)" align="center">
                </el-table-column>
                <el-table-column  label="类别" align="center">
                    <template scope="scope">
                        <span v-show="scope.row.ftype ==1">余额充值</span>
                        <span v-show="scope.row.ftype ==2">团购余额消费</span>
                        <span v-show="scope.row.ftype ==3">商场余额消费</span>
                        <span v-show="scope.row.ftype ==4">团购订单取消退款</span>
                        <span v-show="scope.row.ftype ==5">商场订单取消退款</span>
                        <span v-show="scope.row.ftype ==6">异常退款</span>
                        <span v-show="scope.row.ftype ==7">余额转出</span>
                        <span v-show="scope.row.ftype ==8">线下充值</span>
                        <span v-show="scope.row.ftype ==9">线下扣款</span>
                        <span v-show="scope.row.ftype ==10">团购订单异常退款</span>
                        <span v-show="scope.row.ftype ==11">积分商城订单异常退款</span>
                    </template>
                </el-table-column>
                <el-table-column label="资金类型" align="center">
                    <template scope="scope">
                        <span v-show="scope.row.ffundType ==='0'">其他</span>
                        <span v-show="scope.row.ffundType ==1">现金</span>
                        <span v-show="scope.row.ffundType ==2">承兑</span>
                        <span v-show="scope.row.ffundType ==3">电汇</span>
                        <span v-show="scope.row.ffundType ==4">积分</span>
                        <span v-show="scope.row.ffundType ==5">支付宝二维码</span>
                        <span v-show="scope.row.ffundType ==6">微信二维码</span>
                        <span v-show="scope.row.ffundType ==7">支付宝SDK</span>
                        <span v-show="scope.row.ffundType ==8">微信SDK</span>
                        <span v-show="scope.row.ffundType ==9">招商银行</span>
                    </template>
                </el-table-column>
                <el-table-column property="fremark" label="备注" align="center">
                </el-table-column>
                <el-table-column property="fupdateTime" label="业务时间" align="center">
                </el-table-column>
            </el-table>
        </div>
        <!--消费-->
        <div v-if="!isActive">
            <el-table border  :data="tableData2"  highlight-current-row  style="width: 100%"  height="700"
            v-loading="loading"
            element-loading-text="拼命加载中">
                <el-table-column property="phone" label="手机号" align="center">
                </el-table-column>
                <el-table-column property="fcertificateName" label="认证名" align="center">
                </el-table-column>
                <el-table-column property="famountprice" label="金额(元)" align="center">
                </el-table-column>
                <el-table-column  label="类别" align="center">
                    <template scope="scope">
                        <span v-if="scope.row.fpaytype ===0">余额支付</span>
                        <span v-else-if="scope.row.fpaytype===1">微信</span>
                        <span v-else-if="scope.row.fpaytype===2">支付宝</span>
                        <span v-else-if="scope.row.fpaytype===3">银行卡</span>
                        <span v-else-if="scope.row.fpaytype===10">微信</span>
                        <span v-else-if="scope.row.fpaytype===20">支付宝</span>
                        <span v-else-if="scope.row.fpaytype===30">银行卡</span>
                    </template>
                </el-table-column>
                <el-table-column  label="资金类型" align="center">
                    <template scope="scope">
                        <span v-if="scope.row.fpaytype ===0">余额支付</span>
                        <span v-else-if="scope.row.fpaytype===1">微信二维码</span>
                        <span v-else-if="scope.row.fpaytype===2">支付宝二维码</span>
                        <span v-else-if="scope.row.fpaytype===3">银行卡</span>
                        <span v-else-if="scope.row.fpaytype===10">微信SDK</span>
                        <span v-else-if="scope.row.fpaytype===20">支付宝SDK</span>
                        <span v-else-if="scope.row.fpaytype===30">招商银行</span>
                    </template>
                </el-table-column>
                <el-table-column label="业务时间" property="fpaymenttime" align="center">
                    <!--<template scope="scope">-->
                        <!--<span>{{scope.row.fpaymenttime | useTime}}</span>-->
                    <!--</template>-->
                </el-table-column>
            </el-table>
        </div>
        <div class="receivables-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'业务时间',
                total:0,
                size:15,
                currentPage:0,
                tableData1:[], // 列表容器
                tableData2:[], // 列表容器
                startTime:'',
                endTime:'',
                userId:'',
                isActive:true,
                loading:true
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.loading = true;
                this.$http.post(this.isActive?`/finance/balanceCollectionForms.do`:`/finance/consumerForms.do`,{
                    pageSize:this.size,
                    pageNo:this.currentPage,
                    userId:this.userId==''?null:this.userId,
                    startTime:this.startTime==''?null:this.startTime,
                    endTime:this.endTime==''?null:this.endTime,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        if(this.isActive){
                            this.tableData1 = res.data.list;
                            this.tableData2 = [];
                        }else{
                            this.tableData2 = res.data.list;
                            this.tableData1 = [];
                        }
                        this.total = res.data.total;
                        this.loading = false;
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                        this.total = 0 ;
                        this.loading = false;
                    }
                })/*.catch(e => {
                    this.loading = false;
                })*/
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.userId = queryJson.userId;
                this.currentPage = 0;
                this.getListData();
            },
            changeActive(index){
                if(this.loading){
                    this.$message({
                        message: '等当前列表数据加载完再进行切换!',
                        type: 'error'
                    });
                    return;
                }
                if(index === 0){
                    this.isActive = true;
                }else{
                    this.isActive = false;
                }
                //this.total = 0;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .receivables-warp{
        position:relative;
    }
    .receivables-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .receivables-pagination .el-pagination button,
    .receivables-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .receivables-pagination .el-pager li.btn-quicknext,
    .receivables-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .receivables-pagination .el-pagination .el-select .el-input input,
    .receivables-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .receivables-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .receivables-btn-warp{
        padding: 20px;
        border-top:2px solid #c6c6c6;
    }
    .receivables-btn-warp span{
        display: inline-block;
        border: 1px solid #c6c6c6;
        color:#8c8c8c;
        width: 90px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor:pointer;
        border-radius: 2px;
    }
    .receivables-btn-warp span:nth-child(2){
        margin-left: 20px;
    }
    .receivables-btn-warp span.isactive{
        width: 92px;
        height: 32px;
        background: #38adff;
        line-height: 32px;
        color:#fff;
        border: 0px;
    }
</style>



