/* ================ 时间格式化，传入标准时间获取标准日期 ================ */
export const getTimes=(time)=>{
		var result
        var d = new Date(time);
		var year = d.getFullYear();
		var mouth
		var day
		if(d.getMonth() + 1 < 10){
			mouth = "0" + (d.getMonth() + 1)
		}else{
            mouth = d.getMonth() + 1
		}
		if(d.getDate() < 10){
            day = "0" + d.getDate()
		}else{
            day = d.getDate()
		}
		    result = year + "-" + mouth + "-" + day;
		return result;
}
export const get3MonthBefor = function() {
    var resultDate,year,month,date,hms;
    var currDate = new Date();
    currDate.setDate(1);
    year = currDate.getFullYear();
    month = currDate.getMonth()+1;
    date = currDate.getDate();
    hms = currDate.getHours() + ':' + currDate.getMinutes() + ':' + (currDate.getSeconds() < 10 ? '0'+currDate.getSeconds() : currDate.getSeconds());
    switch(month)  {
        case 1:
        case 2:
        case 3:
            month += 9;
            year--;
            break;
        default:
            month -= 3;
            break;
    }
    month = (month < 10) ? ('0' + month) : month;
    resultDate = year + '/'+month+'/'+date+' ' + hms;
    var oldDate = new Date(resultDate).getTime();
    return oldDate - 86400000 ;
}

/* ================ 转化成标准时间 ================ */
export const parserDate =(date)=> {
    console.log(date)
    var t = Date.parse(date);
    if (!isNaN(t)) {
        return new Date(Date.parse(date.replace(/-/g, "/")));
    } else {
        return new Date();
    }
};
/* ================ 时间格式化,获取时分秒 ================ */
export const getBranch =(time)=>{
        if(time == ''){
            return time
        }
		var result
        var d = new Date(time);
		var hour = d.getHours();
        var minutes = d.getMinutes();
        var second = d.getSeconds();
		result = hour + ':' + minutes +  ":" + second;
		return result;
}

/* ================ 浅拷贝 ================ */
export const simpleClone=(initalObj) =>{
       var obj = {}
       for( var i in initalObj) {
          obj[i] = initalObj[i]
       }
       return obj
}

/* ================ 深拷贝 ================ */
export const deepClone = (initalObj, finalObj)=> {
    var obj = finalObj || {}
    for (var i in initalObj) {
        var prop = initalObj[i]
        // 避免相互引用对象导致死循环，如initalObj.a = initalObj的情况
        if(prop === obj) {
            continue
        }
        if (typeof prop === 'object') {
            obj[i] = (prop.constructor === Array) ? [] : {}
            arguments.callee(prop, obj[i])
        } else {
            obj[i] = prop
        }
    }
    return obj;
}

/* ================ 剪切省 ================ */
export const clippingProvice = (provice)=>{
        var index = provice.indexOf('省');
        if(index !== -1){
            provice = provice.slice(0, index);
        }else{
            var index = provice.indexOf('市');
            if(index !== -1){
                 provice = provice.slice(0, index);
            }
        }
        return provice
}

/* ================ 剪切市 ================ */
export const clippingCity = (city)=>{
        var index = city.indexOf('市');
        if(index !== -1){
            city = city.slice(0, index);
        }
        return city
}
/* ================ 校验整数 ================ */
export const checkNumber = (num)=>{
        var reg = new RegExp("^[0-9]*$");
        if(!reg.test(num)){
            return true
        }else{
            return false
        }
}

/* ================ 校验整数.保留一位小数 ================ */
export const checkDecimal = (num, maxNum, isZero)=>{
       var n = 0;
       var reg = new RegExp("^[0-9]*$");
       for(let i=0 ; i<num.length; i++){
           if(!reg.test(num[0])){
               return true
           }else{
               if(num[i] == '.'){
                  n ++ ;
               }
               if(!reg.test(num[i]) && num[i] != '.'){
                    return true
               }
               if(num[i] == '.' && num[i + 3] !== undefined){
                   return true
               }
           }
       }
       if(n > 1 || n == 1 && num >= maxNum || num > maxNum){
           return true;
       }else{
           if(isZero && num == 0 && num != ''){
               return true
           }
           return false;
       }
}
/* ================ 金额转中文大写 ================ */
export const convertCurrency = (money) => {
    //汉字的数字
    var cnNums = new Array('零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖');
    //基本单位
    var cnIntRadice = new Array('', '拾', '佰', '仟');
    //对应整数部分扩展单位
    var cnIntUnits = new Array('', '万', '亿', '兆');
    //对应小数部分单位
    var cnDecUnits = new Array('角', '分', '毫', '厘');
    //整数金额时后面跟的字符
    var cnInteger = '整';
    //整型完以后的单位
    var cnIntLast = '元';
    //最大处理的数字
    var maxNum = 999999999999999.9999;
    //金额整数部分
    var integerNum;
    //金额小数部分
    var decimalNum;
    //输出的中文金额字符串
    var chineseStr = '';
    //分离金额后用的数组，预定义
    var parts;
    if (money == '') { return ''; }
    money = parseFloat(money);
    if (money >= maxNum) {
        //超出最大处理数字
        return '';
    }
    if (money == 0) {
        chineseStr = cnNums[0] + cnIntLast + cnInteger;
        return chineseStr;
    }
    //转换为字符串
    money = money.toString();
    if (money.indexOf('.') == -1) {
        integerNum = money;
        decimalNum = '';
    } else {
        parts = money.split('.');
        integerNum = parts[0];
        decimalNum = parts[1].substr(0, 4);
    }
    //获取整型部分转换
    if (parseInt(integerNum, 10) > 0) {
        var zeroCount = 0;
        var IntLen = integerNum.length;
        for (var i = 0; i < IntLen; i++) {
            var n = integerNum.substr(i, 1);
            var p = IntLen - i - 1;
            var q = p / 4;
            var m = p % 4;
            if (n == '0') {
                zeroCount++;
            } else {
                if (zeroCount > 0) {
                    chineseStr += cnNums[0];
                }
                //归零
                zeroCount = 0;
                chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
            }
            if (m == 0 && zeroCount < 4) {
                chineseStr += cnIntUnits[q];
            }
        }
        chineseStr += cnIntLast;
    }
    //小数部分
    if (decimalNum != '') {
        var decLen = decimalNum.length;
        for (var i = 0; i < decLen; i++) {
            var n = decimalNum.substr(i, 1);
            if (n != '0') {
                chineseStr += cnNums[Number(n)] + cnDecUnits[i];
            }
        }
    }
    if (chineseStr == '') {
        chineseStr += cnNums[0] + cnIntLast + cnInteger;
    } else if (decimalNum == '') {
        chineseStr += cnInteger;
    }
    return chineseStr;
}

/* ================ 校验域名 ================ */
export const checkURL=(str_url)=> {
    var reg = /((https|http|ftp|rtsp|mms):\/\/)?(([0-9a-z_!~*'().&=+$%-]+:)?[0-9a-z_!~*'().&=+$%-]+@)?(([0-9]{1,3}\.){3}[0-9]{1,3}|([0-9a-z_!~*'()-]+\.)*([0-9a-z][0-9a-z-]{0,61})?[0-9a-z]\.[a-z]{2,6})(:[0-9]{1,4})?((\/?)|(\/[0-9a-z_!~*'().;?:@&=+$,%#-]+)+\/?)/g
    var re=new RegExp(reg);
    if (re.test(str_url) || str_url == ''){
        return true;
    }else{
        return false;
    }
}



/* ================ IE9下获取图片的宽高 ================ */
let tempEl
export const getSizeIncompatible = (src) => {
    if (!tempEl) {
        tempEl = document.createElement('div');
        tempEl.style.position = 'absolute';
        tempEl.style.width = '1px';
        tempEl.style.height = '1px';
        tempEl.style.left = '-9999px';
        tempEl.style.top = '-9999px';
        tempEl.style.filter = 'progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod=image)';
        document.body.insertBefore(tempEl, document.body.firstChild);
    }
    tempEl.filters.item('DXImageTransform.Microsoft.AlphaImageLoader').src = src;
    let data ={
        width:tempEl.offsetWidth,
        height:tempEl.offsetHeight
    }
    return data
};

/*================ 下拉框提升层级 ================*/

export const selectToHighIndex = () =>{
    document.body.children[document.body.children.length - 1].style.zIndex = 100000;
}





