<template>
    <div v-show="seeShow">
        <el-table border ref="singleTable" :data="tableData" highlight-current-row  height="680">
            <el-table-column  label="序号" width="70" align="center">
                <template scope="scope">
                    <span>{{scope.$index + size*(currentPage - 1)+1}}</span>
                </template>
            </el-table-column>
            <el-table-column property="partnerAreaName" label="区域" align="center" width="130">
            </el-table-column>
            <el-table-column property="partnerName" label="合作方" align="center" width="150">
            </el-table-column>
            <el-table-column property="fphone" label="手机号" align="center" width="120">
            </el-table-column>
            <el-table-column property="fcustName" label="客户名称" align="center" width="180">
            </el-table-column>
            <el-table-column property="ftypename" label="积分类型" align="center" width="120"><!--
                <template scope="scope">
                    <span v-if="scope.row.fintegraljournaltype == 1"> 购买产品送积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 2"> 商城订单取消</span>
                    <span v-if="scope.row.fintegraljournaltype == 3"> 商城消费</span>
                    <span v-if="scope.row.fintegraljournaltype == 4"> 团购订单取消</span>
                    <span v-if="scope.row.fintegraljournaltype == 5">社区活动积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 6">商城活动积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 7">包辅活动积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 8">会员返利积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 9">专享返利积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 10">其他返利积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 11">充值送积分</span>
                    <span v-if="scope.row.fintegraljournaltype == 12">充值失败扣积分</span>
                </template>-->
            </el-table-column>
            <el-table-column property="fintegral" label="积分数" align="center">
            </el-table-column>
            <el-table-column property="forderid" label="订单编号" align="center" width="180">
            </el-table-column>
            <el-table-column property="createrName" label="操作人" align="center" width="180">
            </el-table-column>
            <el-table-column property="fcreattimestr" label="操作时间" align="center"  width="180">
            </el-table-column>
            <el-table-column property="fremark" label="备注" align="center" width="300">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default{
        props:['tableData','currentPage','size','seeShow'],
        data(){
            return{

            }
        },
        mounted(){

        },
        methods: {

        }
    }
</script>
<style>
</style>



