<template>
    <div>
        <div class="partnerManage" v-show="currentPages == 'pagelist' ? true : false">
            <tHeader >认证审核</tHeader>
            <div class="partManage-bg partManage-position">
                <div class='prchase-state '>
                <span class='prchase-span-state'>认证状态</span>
                    <el-select v-model="typeValue" filterable placeholder="请选择" @change='getValueNum'>
                        <el-option v-for="item in options" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                    <com-input :text='phoneNum' @setValue='getNum'></com-input>
                    <com-input :text='name' @setValue='getName'></com-input>
                    <div class='prchase-btn'>
                        <el-button type="primary" icon="search" @click='search'>查询</el-button>
                    </div>
                </div>
                <div class='prchase-list'>
                    <el-table :data="tableData" border style="width: 100%" :row-class-name="tableRowClassName">
                        <el-table-column prop="number" label="序号">
                        </el-table-column>
                        <el-table-column prop="states" label="状态">
                        </el-table-column>
                        <el-table-column prop="phone" label="手机号">
                        </el-table-column>
                        <el-table-column prop="certificationTypes" label="认证类型">
                        </el-table-column>
                        <el-table-column prop="certificationName" label="认证名称">
                        </el-table-column>
                        <el-table-column prop="createTime" label="申请时间">
                        </el-table-column>
                        <el-table-column prop="verifier" label="审核人">
                        </el-table-column>
                        <el-table-column prop="censorTime" label="审核时间">
                        </el-table-column>
                        <el-table-column label="操作" width="120">
                            <template scope="scope">
                                <el-button type="text" size="small" @click="goCheckId(scope.$index, tableData, '-2')">
                                    {{scope.row.operation}}
                                </el-button>
                                <el-button type="text" size="small" @click="goCheckId(scope.$index, tableData, '-1')">
                                    {{scope.row.see}}
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <pageing
                        @changePage = "changePage"
                        @changSize = 'changSize'
                        :showPageArry = "showPageArry"
                        :showPagelist = "showPagelist"
                        :listLength = "listLength"
                        :currentPage = "goCurrnetPage"
                    ></pageing>
                </div>
            </div>
            <propt :dialogVisible='showNopass'
                :text='reason'
                @closeVisible='showNopass = false'
                @sure='showNopass = false'
            ></propt>
        </div>
        <div v-if="currentPages == 'pageId' ? true : false">
            <certId 
            :getInitData='setPageData'
            @goBack='goBack'>
            </certId>
        </div>
        <div v-if="currentPages == 'pageTax' ? true : false">
            <certTax 
            :getInitData='setPageData' 
             @goBack='goBack'>
             </certTax>
        </div>
    </div>
</template>


<script>
import comInput from './input'
import tHeader from '../../../public/tHeader'
import pageing from '../../../public/paging'
import propt from '../../../public/propt'
import {VERSION} from '@/util/constant.js' 
import certId from './certificationID'
import certTax from './certificationtax'
export default {
    created() {
        var initData = {
            version:VERSION, 
            pageNo:0,
            pageSize:10,
            phone:'', 
            state:-1, 
            certificationName:''
        }
        this.getList(initData);
    },
    data() {
        return {
            reason:'',
            showNopass:false,
            isshow:false,
            phoneNum: '手机号:',
            name: '认证名称:',
            showPageArry:[10,15,20],
            options: [
                { value: '全部'  , num: -1},
                { value: '待审核', num: 0},
                { value: '已通过', num: 1},
                { value: '已驳回', num: 2}
            ],
            telNum: '',           //手机号
            userName: '',         //认证名称
            typeValue: '',        //认证状态
            typeValueNum:-1,      //认证状态对应的码
            goCurrnetPage: 1,     //获取当前页
            showPagelist: 10,     //当前显示条数
            listLength:0,         //总共的条数
            tableData: [],        //列表显示数组
            isSearch:false,
            temporaryName:'',     //临时认证名称
            temporaryPhone:'',    //临时认证状态
            temporaryValueNum:-1, //临时认证状态对应的码
            currentPages:'pagelist', //初始化显示当前页面
            setPageData:''        //传递给组件的获取到的数据
        }
    },
    components: {
        tHeader,
        comInput,
        pageing,
        propt,
        certId,
        certTax
    },
    methods: {
        //查询
        search() {
            this.userName = this.temporaryName;
            this.telNum = this.temporaryPhone;
            this.typeValueNum = this.temporaryValueNum;
            let serachData = {
                version:VERSION, 
                pageNo:this.goCurrnetPage - 1,
                pageSize:this.showPagelist, 
                phone:this.telNum,
                state:this.typeValueNum, 
                certificationName:this.userName
            }
            this.getList(serachData);
        },
        //获取电话号码
        getNum(num) {
           this.temporaryPhone = num;
        },
        //获取认证名称
        getName(name) {
           this.temporaryName = name;
        },
        //更改当前页
        changePage(page) {
            this.goCurrnetPage = page;
            let pageData = {
                version:VERSION, 
                pageNo:this.goCurrnetPage - 1,
                pageSize:this.showPagelist, 
                phone:this.telNum,
                state:this.typeValueNum, 
                certificationName:this.userName
            }
            this.getList(pageData);
        },
        //列表样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //每页显示条数
        changSize(num){
            this.showPagelist = num;
            let numData = {
                version:"1.0", 
                pageNo:this.goCurrnetPage - 1,
                pageSize:this.showPagelist, 
                phone:this.telNum,
                state:this.typeValueNum, 
                certificationName:this.userName
            }
            this.getList(numData)
        }, 
        //获取下拉列表状态码
        getValueNum(name){
           for(let i=0; i<this.options.length; i++){
               if(this.options[i].value === name){
                    this.temporaryValueNum = this.options[i].num;
               }
           }
        },
        //操作
        goCheckId(index, dataArry, num){
            var postData = {
                version:VERSION,
                certificationId:dataArry[index].certificationId,
                certificationType:dataArry[index].certificationType
            }
            this.$http.post('/outerUser/loadAuditDetail.do', postData).then(res => {
                if (res.data.success) {
                     res.data.data.num = num;
                     this.$router.push({name: 'certificationList', query: { plan: 'otherPage' }})
                     if(dataArry[index].state == 0 ){
                        res.data.data.num = 0;
                     }
                     if(res.data.data.fstate == 2 ){
                        this.showNopass = false;
                        setTimeout(()=> {
                             this.showNopass = true;
                        }, 1);
                        this.reason = '驳回理由:' + res.data.data.frejectReason;
                        return 
                     }

                     if(dataArry[index].certificationType === 1 ){
                        this.currentPages = 'pageId';
                        this.setPageData = res.data.data;
                     }
                     if(dataArry[index].certificationType === 2){
                        this.currentPages = 'pageTax';
                        this.setPageData = res.data.data;
                     }
                   }
            }).catch(function (error) {
                console.log(error);
            });
            
        },
        //获取列表
        getList(data){
             var postData = data;
             this.$http.post('/outerUser/loadAuditList.do', postData).then(res => {
                if (res.data.success) {
                    this.tableData = res.data.data.result;
                    this.listLength = res.data.data.total;
                    var number = this.goCurrnetPage * this.showPagelist - this.showPagelist;
                    for (let i = 0; i < this.tableData.length; i++) {
                        this.tableData[i].number = number + 1 + i;
                        if(this.tableData[i].state === 0){
                            this.tableData[i].states = '待审核';
                            this.tableData[i].operation = '审核';
                        }else if(this.tableData[i].state === 1){
                            this.tableData[i].states = '已通过';
                            this.tableData[i].operation = '修改'
                            this.tableData[i].see = '查看';
                        }else{
                            this.tableData[i].states = '已驳回';
                            this.tableData[i].operation = '查看';
                        }
                        if(this.tableData[i].certificationType === 0){
                            this.tableData[i].certificationTypes = '未认证'
                        }else if(this.tableData[i].certificationType === 1){
                            this.tableData[i].certificationTypes = '个人认证'
                        }else{
                            this.tableData[i].certificationTypes = '企业认证'
                        }
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //返回列表
        goBack(){
           this.currentPages = 'pagelist'; 
           let pageData = {
                version:VERSION, 
                pageNo:this.goCurrnetPage - 1,
                pageSize:this.showPagelist, 
                phone:this.telNum,
                state:this.typeValueNum, 
                certificationName:this.userName
            }
            this.getList(pageData);
        }
    },
    watch:{
        '$route' (to, from) {
             if(this.$route.fullPath == '/home/operation/certificationList'){
                 this.currentPages = 'pagelist';
             }
        }
    }
}
</script>


<style>
</style>



