<template>
    <div>
        <div class="partnerManage partner-propt" v-show="currentPages === 'productManger'">
            <tHeader>产品管理</tHeader>
            <div class='commodity-bg'>
                <div class='commodity-select-top'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 ? false : true'
                        :isProduce='true'
                    >
                    </regional-choice>
                    <div class='parter-select-top'>
                        <partner
                            @getSelect='selectParter'
                            :codeName='codeName'
                            :partName='partnerName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                        ></partner>
                    </div>
                    <div class='partner-select'>
                        <carouse-select
                            textName='产品状态:'
                            :region='produceType'
                            @getSelect='getType'
                            :regionName='produceTypeDefault'
                        ></carouse-select>
                    </div>
                    <el-button type="primary" @click='addNewProduct'>
                        <i class='partner-add'></i>
                        新增产品
                    </el-button>
                </div>
                <div class='partner-table partner-table-produce prchase-list'>
                    <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                        <el-table-column prop="number" label="序号" width="60">
                        </el-table-column>
                        <el-table-column prop="fgrouparea" label="区域" width="120">
                        </el-table-column>
                        <el-table-column prop="fsuppliername" label="合作方" >
                        </el-table-column>
                        <el-table-column prop="fimg" label="产品图片" width="150">
                            <template scope="scope">
                            <img :src='scope.row.fimg' class='partner-table-img'>
                            </template>
                        </el-table-column>
                        <el-table-column prop="fproductname" label="产品名称" >
                        </el-table-column>
                        <el-table-column prop="type" label="产品状态" width="100">
                        </el-table-column>
                        <el-table-column prop="moneyType" label="价格方案类型" width="150">
                        </el-table-column>
                        <el-table-column prop="fgroupprice" label="团购价格" width="150">
                        </el-table-column>
                        <el-table-column prop="fmaxarea" label="限量结束" width="110">
                        </el-table-column>
                        <el-table-column prop="flimitedtime" label="限时结束" width="110">
                        </el-table-column>
                        <el-table-column label="操作" width="230" fixed="right">
                            <template scope="scope">
                                <div class='partner-btn' @click='applayShelves(scope.row, 0)' v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'>申请上架</div><div class='partner-btn' @click="changePage(scope.row)" v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 2 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'>选择价格方案</div>
                                <div class='partner-btn' @click='editProduct(scope.row)' v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'>编辑产品</div><div class='partner-btn' @click="delPropt(scope.row)" v-show='scope.row.fonoffstatus == 0 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus != 1 ? true : false'>删除</div>
                                <div class='partner-btn' @click='applayShelves(scope.row, 1)' v-show='scope.row.fonoffstatus == 2 ? true : false'>申请下架</div><div class='partner-btn ' @click='revoke(scope.row)' v-show='scope.row.fonoffstatus == 1 || scope.row.fonoffstatus == 3 && scope.row.fauditstatus == 1 ? true : false'>撤销申请</div>
                            </template>
                        </el-table-column>
                        <el-table-column label="查看" width="150" fixed="right">
                            <template scope="scope">
                                <div class='partner-btn' @click='seeDetal(scope.row)'>查看</div>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class='partner-page'>
                        <pageing v-if='listLength > 5'
                                @changePage="changePages"
                                @changSize='changSize'
                                :showPageArry="listLength > 5 ? [5,10,15] : [10,15]"
                                :showPagelist="showPagelist"
                                :listLength="listLength"
                                :currentPage='currentPage'
                            >
                        </pageing>
                    </div>
                </div>
            </div>
            <div class='commodity-propt'  v-if='addNewProduce'>
                <el-dialog title="新增产品" :visible.sync="addNewProduce" :show-close='false' 
                @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                    <img src='../../../../../static/images/publicImages/close.png' class='partaner-close-btn' @click='addNewProduce = false'>
                    <div class='produce-bg'>
                        <div class='partner-selet-parter'>
                            <i>*</i>&nbsp;<partner 
                            @getSelect='selectAddParter'
                            :codeName='codeName'
                            :partName='editPartName'
                            :isDisableds='admin.ucompany == 100 ? false : true'
                            >
                            </partner>
                        </div>  
                        <div class='partner-selet-region'>
                            <i>*</i>&nbsp;<regional-choice 
                            @getRegionSale = 'getRegionName' 
                            @getPleaseSale = 'getPleaseName'
                            :defaultRegion='addDefaultRegion'
                            :defaultPlease='addDefaultPlease'
                            :parterCode='uuId'
                            :keyArea='key'
                            regionNameDf='销售区域:'></regional-choice>
                        </div>
                        <div class ='partner-propt-font'>
                            基础信息
                            <span>(</span>
                            <i>*</i>
                            <span>为必填项</span>
                            <span>)</span>
                        </div>
                        <div class='partner-input-left'>
                            <common-input textName='市场名称:' 
                            @setValue='getValue' 
                            ref='fmarketproductname' 
                            :defaultValue='fmarketproductname'
                            :maxlength='20'
                            ></common-input>
                            <common-input textName='产品名称:' @setValue='getValue' :maxlength='20' ref='fproductname' :defaultValue='fproductname'></common-input>
                            <common-input textName='交期天数:' 
                            @setValue='getValue' 
                            ref='fdeliveryday' 
                            :defaultValue='fdeliveryday' 
                            defaultName='请输入整数天,不超过999'
                            :error='errorAry[0].error' 
                            :errorPropt='errorAry[0].errorPropt'
                            ></common-input>
                            <common-input textName='用料名称:' @setValue='getValue' ref='fmaterialid' :defaultValue='fmaterialid' :maxLength='40'></common-input>
                            <div class='partner-input-small'>
                            <i>*</i>&nbsp;<carouse-select textName='楞型:' :region='fluteType' @getSelect='getFlute' :regionName='fflutetypeDf'></carouse-select>
                            </div>
                            <div class='partner-input-bottom'>
                                <common-input textName='最大接单长:' 
                                @setValue='getValue' 
                                :maxlength='6' 
                                ref='fmaxorderlength' 
                                :defaultValue='fmaxorderlength'
                                 defaultName='0-500之间保留二位有效小数'
                                 :error='errorAry[1].error' 
                                 :errorPropt='errorAry[1].errorPropt'
                                 ></common-input><span class='partner-input-bottom-span'>CM</span>
                                <common-input textName='最大接单宽:' 
                                @setValue='getValue' 
                                :maxlength='6' 
                                ref='fmaxorderwidth'  
                                :defaultValue='fmaxorderwidth' 
                                defaultName='0-500之间保留二位有效小数'
                                :error='errorAry[3].error' 
                                :errorPropt='errorAry[3].errorPropt'
                                ></common-input><span class='partner-input-bottom-span2'>CM</span>
                            </div>
                            <common-input textName='落料长加:'
                             @setValue='getValue' 
                             ref='fmateriallengthplus' 
                             :defaultValue='fmateriallengthplus' 
                             :maxlength='6' defaultName='0-100之间保留二位有效小数'
                             :error='errorAry[5].error' 
                             :errorPropt='errorAry[5].errorPropt'
                             ></common-input><span class='partner-input-bottom-span3'>CM</span>
                        </div>
                        <div class='partner-input-right'>
                            <common-input textName='推荐行业:' @setValue='getValue' :maxlength='50' ref='frecommendindustry' :defaultValue='frecommendindustry'></common-input>
                            <common-input textName='适用行业:' @setValue='getValue' :maxlength='50' ref='fsuitindustry' :defaultValue='fsuitindustry'></common-input>
                            <div class="block partner-selectTime" >
                                    <i class='partner-icon'>*</i>
                                    <span class="demonstration">交期时间:</span>
                                    <el-time-picker
                                    v-model="endTime"
                                    :editable='false'
                                    :picker-options="{
                                        selectableRange: '00:00:00 - 23:59:59'
                                    }"
                                    placeholder="选择时间">
                                </el-time-picker>
                            </div>
                            <div class='partner-input-small'>
                                <common-input textName='物流:' @setValue='getValue' :maxlength='20' ref='flogistics' :defaultValue='flogistics'></common-input>
                                <i>*</i>&nbsp;<carouse-select textName='层数:' :region='numberOf' @getSelect='getNumber' :regionName='flayer'></carouse-select>
                            </div>
                            <div class='partner-input-bottom'>
                                <common-input textName='最小接单长:' 
                                @setValue='getValue' 
                                :maxlength='6'
                                ref='fminorderlength'
                                :defaultValue='fminorderlength' 
                                defaultName='0-500之间保留二位有效小数'
                                :error='errorAry[2].error' 
                                :errorPropt='errorAry[2].errorPropt'
                                ></common-input><span class='partner-input-bottom-span'>CM</span>
                                <common-input textName='最小接单宽:'
                                 @setValue='getValue' 
                                 :maxlength='6'
                                 ref='fminorderwidth'
                                 :defaultValue='fminorderwidth'
                                 defaultName='0-500之间保留二位有效小数'
                                 :error='errorAry[4].error' 
                                 :errorPropt='errorAry[4].errorPropt'
                                    ></common-input><span class='partner-input-bottom-span2'>CM</span>
                            </div>
                            <common-input textName='落料宽加:' 
                            @setValue='getValue' 
                            ref='fmaterialwidthplus' 
                            :defaultValue='fmaterialwidthplus' 
                            :maxlength='6' 
                            defaultName='0-100之间保留二位有效小数'
                            :error='errorAry[6].error' 
                            :errorPropt='errorAry[6].errorPropt'
                            ></common-input><span class='partner-input-bottom-span3'>CM</span>
                        </div>
                        <p>
                            <i class='partner-icon'>*</i>产品列表主图:
                        </p>
                        <div class='partner-produce-img partner-price-img'>
                            <div class='partner-upload partner-upload-first' v-if='!IE'>
                                <div class='partner-upload-font-first'>
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>宽高比1:1,不小于360</p>
                                </div>
                                <el-upload 
                                    class="avatar-uploader"
                                    :show-file-list="false" 
                                    :auto-upload='false'
                                    action=''
                                    :on-change='beforeAvatarUpload'
                                    >
                                    <img v-if="imageUrl" :src="imageUrl" class="avatar" >
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDate' 
                                :uploadSrc='uploadSrc' 
                                @addImgSuccess='addSuccess' 
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>宽高比1:1,不小于360</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                        <p class='partner-add-img'>
                            <i class='partner-icon'>*</i>产品详情图(最多上传5张):
                        </p>
                        <div :class="[detailsfigure.length == 5 ? 'partner-upload partner-upload-hidden' : 'partner-upload']" v-if='!IE'>
                            <div :class="[cssAry[detailsfigure.length]]">
                                <p>支持jpg/png格式</p>
                                <p>RGB模式,大小 < 2M </p>
                                <p>宽 = 1100</p>
                            </div>
                            <el-upload 
                            list-type="picture-card" 
                            :auto-upload='false'
                            :on-change='checkPicture'
                            :file-list='fileList'
                            :on-remove="removeImg"
                            action=''
                            >
                                <i class="el-icon-plus"></i>
                            </el-upload>
                        </div>
                        <div class='IE-productMange' v-if='IE' v-for="item in imgListUpload" :key='item.number' @mouseout="imgListUpload[item.number].isShow ? imgListUpload[item.number].mask = false : ''" @mousemove="imgListUpload[item.number].isShow ? imgListUpload[item.number].mask = true : ''">
                            <div class='partner-img-Mask' v-show='imgListUpload[item.number].mask'>
                                <img class='partner-delImg-Mask' src='../../../../../static/images/del.png' @click="removeImg(item.number)">
                            </div>
                            <div class='IE-upload'>
                                <upload-ie :checkData='checkIEDataList' 
                                    :uploadDate='uploadDate' 
                                    :uploadSrc='imgListUpload[item.number].src' 
                                    @addImgSuccess='addSuccessList(item.number)'
                                    :uploadFormsId="'uploadForm' + item.number"
                                    action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>宽 = 1100</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                        <div class='partner-btn-propt'>
                            <el-button @click='addNewProduce = false'>取消</el-button>
                            <el-button type="primary" @click="save('0')">保存</el-button>
                            <el-button type="primary" @click="save('1')">保存并上架</el-button>
                        </div>
                    </div>
                </el-dialog>
            </div>
            <div class='commodity-propt partner-product-propt commodity-productPage'>
                <el-dialog title="产品详情查看" :visible.sync="produceDetail" :show-close='false' 
                @open='openDialog' @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                    <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='produceDetail = false'>
                    <div class='partner-product-bg'>
                        <div class='partner-product-text'>
                            <div class='partner-product-title'>
                                <span>合作方名称:</span><span>{{parterDetal.fsuppliername}}</span><span>|</span><span>{{parterDetal.fgrouparea}}</span>
                            </div>
                            <div>
                                <p class='parter-maxLngth-css'><span>市场名称:</span><span>{{parterDetal.fmarketproductname}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>产品名称:</span><span>{{parterDetal.fproductname}}</span></p>
                                <p class='parter-maxLngth-css'><span class='partner-product-span'>用料名称:</span><span>{{parterDetal.fmaterialid}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth-css'><span>推荐行业:</span><span>{{parterDetal.frecommendindustry}}</span></p><p class='parter-maxLngth-css'><span class='partner-product-span'>适用行业:</span><span>{{parterDetal.fsuitindustry}}</span></p>
                                <p class='parter-maxLngth-css'><span class='partner-product-span'>物流:</span><span>{{parterDetal.flogistics}}</span></p>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>交期天数:</span><span>{{parterDetal.fdeliveryday + '天'}}</span></p><p class='parter-maxLngth'><span class='partner-product-span'>交期时间:</span><span>{{parterDetal.fdeliverytime}}</span></p>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>楞型:</span><span>{{flutetypeArry[parterDetal.fflutetype]}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth'><span >层数:</span><span>{{parterDetal.flayer}}</span></p>
                            </div>
                            <div>
                                <p class='parter-maxLngth'><span>最大接单长:</span><span>{{parterDetal.fmaxorderlength}}</span><span>CM</span></p><span >最小接单长:</span><span>{{parterDetal.fminorderlength}}</span><span>CM</span>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>最大接单宽:</span><span>{{parterDetal.fmaxorderwidth}}</span><span>CM</span></p><span >最小接单宽:</span><span>{{parterDetal.fminorderwidth}}</span><span>CM</span>
                            </div> 
                            <div>
                                <p class='parter-maxLngth'><span>落料长加:</span><span>{{parterDetal.fmateriallengthplus}}</span><span>CM</span></p><span >落料宽加:</span><span>{{parterDetal.fmaterialwidthplus}}</span><span>CM</span>
                            </div>   
                        </div>
                        <p>
                            产品列表主图:
                        </p>
                        <img :src='parterDetal.url' class='partner-product-shelf'>
                        <p class='partner-add-img'>
                            产品详情图:
                        </p>
                        <div v-for='imgList in parterDetal.detailsfigure' :key='imgList.url' style='display:inline-block'>
                            <img :src='imgList.url' class='partner-product-list'>
                        </div>
                    </div>
                </el-dialog>
            </div>
        </div>
        <div v-if="currentPages === 'optionPricesCheme'">
            <optionPrices :getProductData='setOptionData'></optionPrices>
        </div>
        <propt
            :dialogVisible = 'isDel'
            text='是否确认删除'
            :needCancel = 'true'
            @closeVisible='isDel = false'
            @sure='delData'
        ></propt>
        <propt
            :dialogVisible = 'delProptImg'
            text='该产品已经被绑定到了相应区域的纸板入口位中，请先到入口位中关联其他产品才能删除'
            :needCancel = 'false'
            @closeVisible='delProptImg = false'
            @sure='delProptImg = false'
        ></propt>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import pageing from '@/components/public/paging'
import commonInput from './input'
import {VERSION, IMGPATH} from '@/util/constant.js' 
import {simpleClone} from '@/util/common.js'
import optionPrices from './optionPricesCheme'
import propt from '@/components/public/propt'
import uploadIe from '@/components/public/uploadIE'
import {clippingProvice, clippingCity, checkNumber, checkDecimal, getBranch, parserDate} from '@/util/common.js'
export default {
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fcreatorid = this.admin.ucompany; 
        this.fsupplierid = this.admin.ucompany;       //合作方id
        //非东京账号登录
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
           }, 1);
           this.fparterId = this.admin.ucompany;    //添加时的合作方ID
           this.searchFunc();
        }
        //区域拆分键
        this.pleaseCode = this.admin.ocode;
        if(navigator.appName == "Microsoft Internet Explorer" && 
            navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
            this.IE = true;  
            return
        } 
    },
	data () {
		return {
            delProptImg:false,
            IE:false,
            uploadSrc:'',
            checkIEData:{
                size:2,
                Proportion:1,
                minWidth:360,
                minHeight:360,
                needPropt:false
            },
            checkIEDataList:{
                size:2,
                width:1100,
                needPropt:false
            },
            uploadDate:[],
            imgListUpload:[              //IE9图片墙
                {number:0, src:'', isShow:false, mask:false},
            ],
            produceTypeDefault:'',
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            addDefaultRegion:'',
            addDefaultPlease:'',
            setOptionData:{},
            admin:{},
            produceType:[
                {regionName:'全部',  number:-1},
                {regionName:'待上架', number:0},
                {regionName:'审核中', number:1},
                {regionName:'已上架', number:2},
                {regionName:'已下架', number:3}
            ],
            fluteType:[
                {regionName: 'BC瓦', value: 1},
                {regionName: 'BE瓦', value: 2},
                {regionName: '单C瓦', value: 3},
                {regionName: '单B瓦', value: 4},
                {regionName: '单E瓦', value: 5},
                {regionName: 'EBC瓦', value: 6}
            ],
            numberOf:[
                {regionName: '7', value: 7},
                {regionName: '6', value: 6},
                {regionName: '5', value: 5},
                {regionName: '4', value: 4},
                {regionName: '3', value: 3},
                {regionName: '2', value: 2}
            ],
            isDel:false,
            listLength:0,
            showPagelist:5,
            currentPage:1,
            codeName:'',
            pleaseCode:3303,                //区域拆分键
            produceDetail:false,
            addNewProduce:false,
            currentPages:'productManger',
            imageUrl:'',
            fmarketproductname:'',          //市场名称
            fproductname:'',                //产品名称
            fmaterialid:'',                 //用料名称
            fmaxorderlength:'',             //最大接单长
            fmaxorderwidth:'',              //最大接单宽
            fmateriallengthplus:'0.0',        //落料长加
            frecommendindustry:'',          //推荐行业
            fsuitindustry:'',               //适用行业
            flogistics:'',                  //物流
            fminorderlength:'',             //最小接单长
            fminorderwidth:'',              //最小接单宽
            fmaterialwidthplus:'2.0',         //落料宽加
            flayer:'',                      //层数
            fflutetype:'',                  //楞型
            fflutetypeDf:'',                //楞型默认
            fsupplierid:'',                 //合作方ID
            fparterId:'',                   //添加-合作方ID
            fid:'',                         //产品ID
            editPartName:'',                //编辑合作方名称
            delFid:'',                      //删除Fid
            fgroupareaProvice:'',           //销售区域省
            fgroupareaCity:'',              //销售区域市
            fgroupareaid:'',                //销售区域ID
            imgFid:'',                      //主产品图fid
            fcreatorid:'',                  //创建人ID
