	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //市name
        getPlease(please){
            this.codeName = please;
        },
        //切换省时的操作
        getRegion(){
            this.codeName = ''
            this.tableData = [];
        },
        //合作方选择
        selectParter(datas){
            if(datas.id == undefined && datas.code == undefined){
                return
            }
            this.fsupplierid = datas.id;
            this.pleaseCode = datas.code;
            this.partnerName = datas.name;
            this.currentPage = 1;
            this.searchFunc();
            this.tableData = [];
            this.produceTypeDefault = '';
        },
        //添加合作方选择
        selectAddParter(data){
           if(data.id == undefined || data.code == undefined){
               return
           }
           this.fparterId = data.id;
           this.editPartName = data.name;
           this.uuId = data.id;
           this.key = '';
           this.addDefaultRegion = '';
           this.addDefaultPlease = '';
           setTimeout(()=>{
                this.key = data.code;
           },10)
        },
        //产品状态查询
        getType(name){
           this.produceTypeDefault = name;
           if(name == ''){
               return
           }
           for(let i=0; i<this.produceType.length; i++){
               if(name === this.produceType[i].regionName){
                   var type = this.produceType[i].number;
               }
           }
           let data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',
                fsupplierid:this.fsupplierid + '',
                pagenum:this.currentPage - 1 + '',
                pagesize:this.showPagelist + ''
            }
            if(this.partnerName == ''){
                this.$message({
                    message: '请选择区域合作方！',
                    type: 'warning',
                    duration: 2000
                });
                return
            }
            if(type !== -1){
               data.fonoffstatus = type + ''
               this.searchList(data);
               return
            }
            this.searchList(data);  
        },
        //切换页面
        changePages(page){
            this.currentPage = page;
            this.searchFunc()
        },
        //切换页面条数
        changSize(pageLength){
            this.showPagelist = pageLength;
            this.searchFunc()
        },
        //获取输入框的值
        getValue(){
            let ref = this.$refs;
            this.fmarketproductname = ref.fmarketproductname.input; //市场名称
            this.fproductname = ref.fproductname.input;//产品名称
            this.fmaterialid = ref.fmaterialid.input; //用料名称
            this.frecommendindustry = ref.frecommendindustry.input; //推荐行业
            this.fsuitindustry = ref.fsuitindustry.input; //适用行业
            this.flogistics = ref.flogistics.input; //物流

            this.errorAry[0].error = checkNumber(ref.fdeliveryday.input);
            if(!this.errorAry[0].error){
                if(Number(ref.fdeliveryday.input) > 999){
                    this.errorAry[0].error = true;
                }else{
                    this.errorAry[0].error = false;
                }
            }
            this.fdeliveryday = ref.fdeliveryday.input//交期天数

            this.errorAry[1].error = checkDecimal(ref.fmaxorderlength.input, 500, false);
            this.fmaxorderlength = ref.fmaxorderlength.input; //最大接单长

            this.errorAry[2].error = checkDecimal(ref.fminorderlength.input, 500, false);
            this.fminorderlength = ref.fminorderlength.input; //最小接单长

            this.errorAry[3].error = checkDecimal(ref.fmaxorderwidth.input, 500, false);
            this.fmaxorderwidth = ref.fmaxorderwidth.input; //最大接单宽

            this.errorAry[4].error = checkDecimal(ref.fminorderwidth.input, 500, false);
            this.fminorderwidth =  ref.fminorderwidth.input; //最小接单宽

            this.errorAry[5].error = checkDecimal(ref.fmateriallengthplus.input, 100, false);
            this.fmateriallengthplus = ref.fmateriallengthplus.input; //落料长加

            this.errorAry[6].error = checkDecimal(ref.fmaterialwidthplus.input, 100, false);
            this.fmaterialwidthplus = ref.fmaterialwidthplus.input; //落料宽加
            
            
        },
        //校验产品列表主图
        beforeAvatarUpload(files) {
            var file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error('上传图片只能是 JPG 或者 PNG 格式!');
                return 
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width < 360 || img.height < 360 || img.width !== img.height){
                    q.$message.error('图片宽高不小于360! 且比例为1:1');
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.pleaseCode);
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        q.imageUrl = files.url;
                        q.figures.ffileid = res.data.data.fileid;
                        q.figures.furl = res.data.data.fileurl;
                        q.figures.fuserid = q.fsupplierid;
                        q.figures.fkeyarea = q.pleaseCode;
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //新增产品接口
        addNewProduct(){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            //IE9专用
            this.imgListUpload = [              //IE9图片墙
                {number:0, src:'', isShow:false, mask:false}
            ];
            this.uploadSrc = '';
            
            this.addNewProduce = true;
            this.fmarketproductname = '';          //市场名称
            this.fproductname = '';                //产品名称
            this.fmaterialid =  '';                //用料名称
            this.fmaxorderlength = '';             //最大接单长
            this.fmaxorderwidth = '';              //最大接单宽
            this.fmateriallengthplus = '0.0';          //落料长加
            this.frecommendindustry = '';          //推荐行业
            this.fsuitindustry = '';               //适用行业
            this.flogistics = '';                  //物流
            this.fminorderlength = '';             //最小接单长
            this.fminorderwidth = '';              //最小接单宽
            this.fmaterialwidthplus = '2.0';           //落料宽加
            this.flayer = '';                      //层数
            this.fflutetype = '';                  //楞型
            this.fflutetypeDf = '';                //楞型默认
            this.addDefaultRegion = '';
            this.addDefaultPlease = '';
            this.endTime = '';                     //交期时间
            this.fdeliveryday = '';                //交期天数
            if(this.admin.ucompany == 100){
                this.editPartName = '';
                 this.uuId = '';
                 this.key = '';
                 setTimeout(()=>{
                    this.editPartName = this.partnerName;
                    this.uuId = this.fsupplierid;
                    this.key = this.pleaseCode;
                 },100)
                this.fparterId = this.fsupplierid; //添加-合作方Id
            }else{
                this.key = '';             //合作方拆分键
                this.uuId = '';
                this.editPartName = '';
                setTimeout(()=>{
                    this.key = this.admin.ocode;             //合作方拆分键
                    this.uuId = this.admin.ucompany;
                    this.editPartName = this.admin.oname;
                },10)
            }
            this.detailsfigure = [];
            this.delImgAry = [];
            this.figures = {
                ftype: 1
            }
            this.imageUrl = '';
            this.fileList = [];
            this.fid = '';
            this.imgFid = '';
            
        },
        //校验照片墙
        checkPicture(files, fileList){
            if(fileList.length == 6){
               fileList.pop();
               this.$message.warning('上传图片不得超过5张!');
               return
            }
            let file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error('上传图片只能是 JPG 或者 PNG 格式!');
                fileList.pop();
                return 
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
                fileList.pop();
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width !== 1100){
                    q.$message.error('图片宽度为1100');
                    fileList.pop();
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.pleaseCode);
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        let imgObj={
                            ftype:2,
                            fuserid:q.fsupplierid,
                            fkeyarea:q.pleaseCode
                        }
                        imgObj.ffileid = res.data.data.fileid;
                        imgObj.furl = res.data.data.fileurl;
                        q.detailsfigure.push(simpleClone(imgObj));
                        imgObj.uid = files.uid;
                        q.delImgAry.push(simpleClone(imgObj))
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //申请上下架
        applayShelves(data, num){
            let datas = {
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fid:data.fid
            }
            this.$http.post('/product/applyeExamineProduct.do', datas).then(res => {
                if(res.data.success){
                    if(num === 0){
                       this.$message({
                            message: '产品上架申请提交成功，请耐心等待后台审核吆！',
                            type: 'success',
                            duration: 2000
                        });
                    }else{
                        this.$message({
                            message: '产品下架申请提交成功，请耐心等待后台审核吆！',
                            type: 'success',
                            duration: 2000
                        });
                    }
                    this.searchFunc()
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                    console.log(error);
            });
            
        },
        //撤销申请
        revoke(data){
           let datas = {
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fid:data.fid
            }
            this.$http.post('/product/revokeApplyProduct.do', datas).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '撤销申请成功',
                        type: 'success',
                        duration: 2000
                    });
                    this.searchFunc()
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                    console.log(error);
            });
        },
        //删除
        delData(){
            let imgData ={
                fkeyarea:this.pleaseCode,
                fproductid:this.delFid
            }
            this.$http.post('/banner/queryBannerOfproduct.do ', imgData).then(res => {
                if(res.data.success){
                    if(res.data.data.length != 0){
                       this.delProptImg = true;
                       this.isDel = false;
                    }else{
                        let data = {
                            version:VERSION,
                            fkeyarea:this.pleaseCode,
                            fid:this.delFid
                        }
                        this.$http.post('/product/deleteProduct.do', data).then(res => {
                            if(res.data.success){
                                this.isDel = false;
                                this.$message({
                                    message: '删除成功',
                                    type: 'success',
                                    duration: 1500
                                });
                                this.searchFunc()
                            }else{
                                this.$message({
                                    message: res.data.msg,
                                    type: 'error',
                                    duration: 2000
                                });
                            }
                        }).catch(function (error) {
                            console.log(error);
                        });
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //删除弹框
        delPropt(data){
          this.isDel = true;
          this.delFid = data.fid;
        },
        //保存
        save(num){
            var data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',                  //区域拆分键
                fattributename:'属性名称',                            //属性名称
                forder:'9999',                                   //显示顺序
                fonoffstatus:num,                               //上下架状态0或1
                fid:this.fid,                                   //产品编号
                fsupplierid:this.fparterId,                     //添加-合作方ID
                fsuppliername:this.editPartName,                //添加-合作方名称
                fmarketproductname:this.fmarketproductname,     //市场名称
                fproductname:this.fproductname,                 //产品名称
                fmaterialid:this.fmaterialid,                   //用料名称
                fmaxorderlength:this.fmaxorderlength,           //最大接单长
                fmaxorderwidth:this.fmaxorderwidth,             //最大接单宽
                fmateriallengthplus:this.fmateriallengthplus,   //落料长加
                frecommendindustry:this.frecommendindustry,     //推荐行业
                fsuitindustry:this.fsuitindustry,               //适用行业
                flogistics:this.flogistics,                     //物流
                fminorderlength:this.fminorderlength,           //最小接单长
                fminorderwidth:this.fminorderwidth,             //最小接单宽
                fmaterialwidthplus:this.fmaterialwidthplus,     //落料宽加
                flayer:this.flayer,                             //层数
                fflutetype:this.fflutetype,                     //楞型
                figure:this.figures,                            //主产品图
                detailsfigure:this.detailsfigure,               //产品详情图
                fgrouparea:this.fgroupareaProvice + '-' + this.fgroupareaCity,//销售区域
                fgroupareaid:this.fgroupareaid,                 //销售区域ID
                fcreatorid:this.fcreatorid,                     //创建人 
                fdeliverytime:getBranch(this.endTime),          //交期时间
                fdeliveryday:this.fdeliveryday                  //交期天数 
           }
           for(let i=0; i<this.checkAry.length; i++){
               if(data[this.checkAry[i]].toString() == ''){
                   this.$message({
                        message: '字段不能为空',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
           }
           for(let i=0; i<this.errorAry.length; i++){
               if(this.errorAry[i].error){
                   this.$message({
                        message: '请填写正确的字段',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
           }
           if(!this.figures.ffileid || this.detailsfigure.length == 0){
               this.$message({
                    message: '请添加图片！',
                    type: 'error',
                    duration: 2000
                });
               return
           }
           if(Number(data.fmaxorderlength) <= Number(data.fminorderlength)){
               this.$message({
                    message: '最大接单长不得小于等于最小接单长！',
                    type: 'warning',
                    duration: 2000
               });
               return
           }
           if(Number(data.fmaxorderwidth) <= Number(data.fminorderwidth)){
               this.$message({
                    message: '最大接单宽不得小于等于最小接单宽！',
                    type: 'warning',
                    duration: 2000
               });
               return
           }
           this.$http.post('/product/addOrEditProducts.do', data).then(res => {
               if(res.data.success){
                    this.addNewProduce = false;
                    if(data.fid == ''){
                            this.$message({
                                message: '添加成功！',
                                type: 'success',
                                duration: 2000
                            });
                    }else{
                            this.$message({
                                message: '修改成功！',
                                type: 'success',
                                duration: 2000
                            });
                    }
                    this.detailsfigure = [];
                    this.searchFunc();
               }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
               }
           }).catch(function (error) {
                console.log(error);
           });
        },
        //获取层数
        getNumber(name){
           for(let i=0; i < this.numberOf.length; i++){
               if(name === this.numberOf[i].regionName){
                   this.flayer = this.numberOf[i].value;
               }
           }
        },
        //获取楞型
        getFlute(name){
            for(let i=0; i < this.fluteType.length; i++){
               if(name === this.fluteType[i].regionName){
                   this.fflutetype = this.fluteType[i].value;
                   this.fflutetypeDf = name;
               }
            }
        },
        //IE9删除图片墙
        removeImg(num){
            this.detailsfigure = [];
            this.imgListUpload = [];
            setTimeout(()=> {
                this.imgListUpload = [
                    {number:0 , src:'', isShow:false, mask:false}
                ];
            }, 1);
            // this.detailsfigure.splice(num,1);
            // this.imgListUpload.splice(num,1);
            // this.imgListUpload.splice(this.imgListUpload.length - 1, 1);
            // setTimeout(()=> {
            //     for(let i = num; i < this.imgListUpload.length; i++){
            //         this.imgListUpload[i].number = this.imgListUpload[i].number - 1;
            //     }
            //     this.imgListUpload.push({number:this.imgListUpload.length , src:'', isShow:false, mask:false})
            //     console.log(JSON.stringify(this.imgListUpload))
            // }, 100);
            // this.searchFunc();
        },
        //查询列表
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.tableData = res.data.data;
                    this.listLength = res.data.total;
                    for(let i=0; i<this.tableData.length; i++){
                        this.tableData[i].number = this.currentPage * this.showPagelist - this.showPagelist + i + 1;
                        if(this.tableData[i].figure == undefined || this.tableData[i].figure == '' || this.tableData[i].figure == {} ){
                            this.tableData[i].fimg = '';
                        }else{
                            this.tableData[i].fimg = IMGPATH + this.tableData[i].figure.furl;
                        }
                        this.tableData[i].type = this.tableData[i].fauditstatus == 1 ?  '审核中' :  this.typeArry[this.tableData[i].fonoffstatus];
                        this.tableData[i].moneyType = this.tableData[i].fgrouptype == 1 ? '限量团购' : this.tableData[i].fgrouptype ==  2 ? '限时团购' : '';
                        this.tableData[i].flimitedtime = this.tableData[i].fgrouptype == 1 ? '/' : this.tableData[i].flimitedtime;
                        this.tableData[i].fmaxarea = this.tableData[i].fgrouptype ==  2 ? '/' : this.tableData[i].fmaxarea;
                        if(this.tableData[i].moneyType == ''){
                            this.tableData[i].fmaxarea = '/';
                            this.tableData[i].moneyType = '/';
                            this.tableData[i].flimitedtime = '/';
                            this.tableData[i].fgroupprice = '/';
                        }
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //编辑
        editProduct(data){
            this.fileList = [];
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.imgFid = data.figure.fid;
            this.addNewProduce = true;
            this.fmarketproductname = data.fmarketproductname;     //市场名称
            this.fproductname = data.fproductname;                 //产品名称
            this.fmaterialid =  data.fmaterialid;                  //用料名称
            this.fmaxorderlength = data.fmaxorderlength;           //最大接单长
            this.fmaxorderwidth = data.fmaxorderwidth;             //最大接单宽
            this.fmateriallengthplus = data.fmateriallengthplus;   //落料长加
            this.frecommendindustry = data.frecommendindustry;     //推荐行业
            this.fsuitindustry = data.fsuitindustry;               //适用行业
            this.flogistics = data.flogistics;                     //物流
            this.fminorderlength = data.fminorderlength;           //最小接单长
            this.fminorderwidth = data.fminorderwidth;             //最小接单宽
            this.fmaterialwidthplus = data.fmaterialwidthplus;     //落料宽加
            this.flayer = data.flayer;                             //层数
            this.fflutetype = data.fflutetype;                     //楞型
            this.fflutetypeDf = this.flutetypeArry[data.fflutetype];//楞型默认
            this.fparterId = data.fsupplierid;                      //添加-合作方Id
            this.imageUrl = data.fimg;                             //主产品图
            // IE9
            this.uploadSrc = data.fimg;
            this.editPartName = '';
            this.imgListUpload = [];
            setTimeout(()=> {
                this.editPartName = data.fsuppliername;                //合作方名称
                this.fileList = data.detailsfigure;                    //照片墙
                for(let i=0; i<this.fileList.length; i++){
                    //IE9
                    let ieObj = {number: i, src:IMGPATH + this.fileList[i].furl , isShow:true, mask:false}
                    this.imgListUpload.push(simpleClone(ieObj));
                    //
                    this.fileList[i].url = IMGPATH + this.fileList[i].furl;
                }
                //IE9
                if(data.detailsfigure.length < 5){
                    this.imgListUpload.push({number:data.detailsfigure.length, src:'', isShow:false, mask:false})
                }
            }, 10);
            this.fid = data.fid;
            this.fgroupareaid = data.fgroupareaid;                 //销售区域
            this.uuId = '';
            this.key = '';
            this.fdeliveryday = data.fdeliveryday;                 //交期天数
            this.endTime = parserDate("1992-08-22" + ' ' + data.fdeliverytime);//交期时间
            var index = data.fgrouparea.indexOf('-');              //销售区域拆分显示
            setTimeout(()=> {
                 this.uuId = data.fsupplierid;
                 this.key = data.fkeyarea;
                 this.addDefaultRegion = data.fgrouparea.slice(0,index);
            }, 1);
            setTimeout(()=> {
                this.addDefaultRegion = '';
            }, 10);
            setTimeout(()=> {
                this.addDefaultRegion = data.fgrouparea.slice(0,index);
            }, 200);
            this.addDefaultPlease = data.fgrouparea.slice(index + 1, data.fgrouparea.length);
            this.fgroupareaProvice = this.addDefaultRegion;
            this.fgroupareaCity = this.addDefaultPlease;
            this.figures = data.figure;
            this.detailsfigure = [];
            for(let i=0; i<data.detailsfigure.length; i++){
                this.detailsfigure.push(data.detailsfigure[i]);  
            }
        },
        //获取销售区域市
        getPleaseName(data){
           this.fgroupareaCity = data.name;
           this.fgroupareaid = data.saleCode;
           this.addDefaultPlease = data.name;
        },
        //获取销售区域省
        getRegionName(data){
           this.fgroupareaProvice = data;
           this.addDefaultRegion = data;
        },
        //查询方法
        searchFunc(){
            let data = {
                version:VERSION,
                fkeyarea:this.pleaseCode + '',
                fsupplierid:this.fsupplierid + '',
                pagenum:this.currentPage - 1 + '',
                pagesize:this.showPagelist + ''
            }
            if(this.pleaseCode == undefined){
                return
            }
            this.searchList(data);
        },
        //选择方案
        changePage(data){
            this.$router.push({name: 'productManger', query: { plan: 'otherPage' }})
            this.currentPages = 'optionPricesCheme';
            this.setOptionData = data;
        },
        //查看
        seeDetal(data){
            this.produceDetail = true;
            this.parterDetal = data;
            this.parterDetal.url = IMGPATH +  this.parterDetal.figure.furl;
            var datas = this.parterDetal.detailsfigure;
            for(let i=0; i<datas.length; i++){
                datas[i].url = IMGPATH + datas[i].furl;
            }
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        },
        //IE9主图上传
        addSuccess(){
            this.uploadDate=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.pleaseCode}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                console.log(str)
                var res = JSON.parse(str);
                if(res.success){
                    this.figures.ffileid = res.data.fileid;
                    this.figures.furl = res.data.fileurl;
                    this.figures.fuserid = this.fsupplierid;
                    this.figures.fkeyarea = this.pleaseCode;
                }else{
                    this.$message.error('上传失败，请重新上传!');
                }
            }, 500);
        },
        //IE9列表图上传
        addSuccessList(index){
            this.imgListUpload[index].isShow = true;
            this.$set(this.imgListUpload, this.imgListUpload[index].isShow, true);
            this.uploadDate=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.pleaseCode}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm" + index).submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                console.log(str)
                var res = JSON.parse(str);
                if(res.success){
                    let imgObj={
                        ftype:2,
                        fuserid:this.fsupplierid,
                        fkeyarea:this.pleaseCode
                    }
                    imgObj.ffileid = res.data.fileid;
                    imgObj.furl = res.data.fileurl;
                    this.detailsfigure.push(imgObj);
                }else{
                    this.$message.error('上传失败，请重新上传!');
                }
                if(this.imgListUpload.length == 5 ){
                        return
                }
                this.imgListUpload.push({number:index + 1, src:'', isShow:false, mask:false})
            }, 500);
        }
    },
    watch:{
        '$route' (to, from) {
             if(this.$route.fullPath == '/home/goods/productManger'){
                 this.currentPages = 'productManger';
             }
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        pageing,
        commonInput,
        optionPrices,
        propt,
        uploadIe
    }  
}
</script>



