<template>
    <div :style="windowStyle" class="commonWindow-warp" v-show="commonWindowShow">
        <div class="commonWindow-titleWarp">
            <h3><slot name="title"></slot></h3>
        </div>
        <span class="commonWindow-closeBtn" @click="commonWindowClose()"></span>
        <slot name="content"></slot>
    </div>
</template>
<script>
    export default{
        props:['commonWindowShow', 'windowStyle'],
        data(){
            return{

            }
        },
        methods: {
            commonWindowClose(){
                this.$emit('close')
            }
        }
    }
</script>
<style>
    .commonWindow-warp{
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 9000;
    }
    .commonWindow-titleWarp{
        overflow: hidden;
    }
    .commonWindow-warp h3{
        font-size: 16px;
        height: 50px;
        line-height: 50px;
        padding-left: 20px;
        margin-bottom: 25px;
        box-shadow:0px 6px 10px #efefef;
    }
    .commonWindow-closeBtn {
        position: absolute;
        width: 26px;
        height: 26px;
        background: url(../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right: -8px;
        top: -8px;
        cursor: pointer;
    }
</style>




