<template>
    <div class="partnerManage partner-propt">
        <tHeader>选择价格方案</tHeader>
        <div class='partner-optionPrice-bg'>
            <div class='partner-select-left'>
                <div class='partner-select-region'>
                    <regional-choice
                        @getPleaseProvice='getPlease'
                        @getRegionProvice='getRegion'
                        :parterCode='-1'
                        :defaultPlease='defaultPlease'
                        :defaultRegion='defaultRegion'
                        :isDisableds='admin.ucompany == 100 && getProduct == undefined ? false : true'
                    >
                    </regional-choice>
                </div>
                <div class='partner-select-Prices'>
                    <partner
                       @getSelect='getPatnerId'
                        :codeName='codeName'
                        :partName='partnerName'
                        :isDisableds='admin.ucompany == 100 && getProduct == undefined ? false : true'
                    ></partner>
                </div>
                <div class='prchase-list partner-table partner-select-option partner-table-height'>
                    <el-table :data="leftTable" style="width: 100%" ref='leftTable'
                    @row-click='searchRight'
                    :highlight-current-row='true'
                    >
                        <el-table-column prop="fgrouparea" label="区域" width='110'>
                        </el-table-column>
                        <el-table-column prop="fsuppliername" label="合作方名称" >
                        </el-table-column>
                        <el-table-column prop="fproductname" label="产品名称" >
                        </el-table-column>
                    </el-table>
                </div>
            </div>
            <div class='partner-select-right'>
                <div class='partner-select-font'>价格方案</div>
                <span class='partner-select-span'>(最多可新增10条价格方案)</span>
                <div class='partner-select-btn'>
                     <el-button type="primary" @click='addNewPrice' :disabled='rightTable.length == 10 || isAdd ? true : false'>新增价格方案</el-button>
                </div>
                <div class='partner-table prchase-list partner-select-table'>
                <el-table ref="multipleTable" :data="rightTable" border tooltip-effect="dark" 
                :row-class-name="tableRowClassName" style="width: 100%" 
                >
                    <el-table-column prop="number" label="序号" width="60">
                    </el-table-column>
                    <el-table-column prop="fmarketingname" label="价格方案名称" width='130'>
                    </el-table-column>
                    <el-table-column prop="viewType" label="审核状态" width='100'>
                    </el-table-column>
                    <el-table-column prop="type" label="价格方案类型" width="120">
                    </el-table-column>
                    <el-table-column prop="fgroupprice" label="团购价格" >
                    </el-table-column>
                    <el-table-column prop="fmaxarea" label="限量结束" >
                    </el-table-column>
                    <el-table-column prop="flimitedtime" label="限时结束" >
                    </el-table-column>    
                    <el-table-column label="操作" width='200' fixed="right">
                        <template scope="scope">
                            <div class='partner-table-checked'> 
                                <el-checkbox v-model="scope.row.checked" @change='changCheck(scope.row)' v-if="scope.row.fauditstatus == '2' ? true : false" :disabled="scope.row.disabled">生效</el-checkbox>
                            </div> 
                            <el-button type="text" 
                            :disabled='scope.row.checked' 
                            size="small" v-show="scope.row.fauditstatus != '1' ? true : false"
                            @click='edit(scope.row)'
                            >
                                编辑
                            </el-button>
                            <el-button type="text" 
                            :disabled='scope.row.checked' 
                            size="small" v-show="scope.row.fauditstatus != '1' ? true : false"
                            @click='delpropt(scope.row)'
                            >
                                删除
                            </el-button>
                            <el-button type="text" 
                            size="small"
                             v-show="scope.row.fauditstatus == '0' || scope.row.fauditstatus == '3' ? true : false"
                             @click='examine(scope.row)'
                             >
                                申请审核
                            </el-button>
                            <el-button type="text" 
                            size="small" v-show="scope.row.fauditstatus == '1' ? true : false"
                            @click='revoke(scope.row)'
                            >
                                撤销审核
                            </el-button>  
                        </template>
                    </el-table-column>
                </el-table>
                </div>
            </div>    
        </div>
        <div class='commodity-propt partner-product-propt' v-if='newPrice'>
            <el-dialog title="新增价格方案" :visible.sync="newPrice" :show-close='false'  @open='openDialog' 
            @close='closeDialog' :modal-append-to-body='false' :close-on-click-modal='false'>
                <img src='../../../../../static/images/publicImages/close.png' class='partaner-product-close' @click='newPrice = false'>
                <div class='partner-product-bg'>
                    <div class='partner-price-top'>
                         <common-input textName='价格方案名称:' @setValue='getValue' ref='fmarketingname' :defaultValue='fmarketingname' defaultName='请输入名称，不超过30个字符' :maxlength='30' ></common-input>
                         <div class='partner-price-div'>
                             <span class='partner-price-span'><i class='partner-icon'>*</i>方案类型:</span>
                             <el-radio class="radio" v-model="radio" label="1">限量团购</el-radio>
                             <el-radio class="radio" v-model="radio" label="2">限时团购</el-radio>
                         </div>
                    </div>
                    <div class='partner-plan-font partner-plan-margin'>方案信息
                        <span>(</span>
                        <i class='partner-icon'>*</i>
                        <span>
                            为必填项)
                        </span>
                    </div>
                    <div class='partner-input-left'>
                        <common-input textName='起步单量:' :error='errorAry[0].error' :errorPropt='errorAry[0].errorPropt' @setValue='getValue' defaultName='请输入单量(整数)' ref='fminarea' :defaultValue='fminarea'></common-input><span class='partner-input-company'>平方米</span>
                        <div v-show="radio == '1' ? true : false "><common-input textName='总量限购:' :error='errorAry[1].error' :errorPropt='errorAry[1].errorPropt' @setValue='getValue' defaultName='请输入限购总量(整数)' ref='fmaxarea' :defaultValue='fmaxarea' :maxlength='10'></common-input><span class='partner-input-total'>平方米</span></div>
                        <div class="block partner-selectTime " v-if="radio == '2' ? true : false ">
                            <i>*</i>
                            <span class="demonstration">限时特价:</span>
                            <el-time-select
                                v-model="endTime"
                                :picker-options="{
                                    start: startTime,
                                    step: '00:15',
                                    end: endTimes
                                }"
                                placeholder="选择结束时间">
                                </el-time-select>
                        </div>
                        <div class='partner-option-error'>
                            <common-input textName='市场价:' :error='errorAry[3].error' :errorPropt='errorAry[3].errorPropt' @setValue='getValue' defaultName='请输入价格(大于0小于100)' ref='funitprice' :defaultValue='funitprice' :maxlength='6'></common-input><span class='partner-input-market'>元/平方米</span>
                            <common-input textName='小单价:' :error='errorAry[4].error' :errorPropt='errorAry[4].errorPropt' @setValue='getValue' defaultName='请输入价格(大于0小于100)' ref='fsmallprice' :defaultValue='fsmallprice' :maxlength='6'></common-input><span class='partner-input-smallPrice'>元/平方米</span>
                        </div>
                    </div>
                    <div class='partner-input-right'>
                        <div class='partner-right-one'>
                            <common-input textName='正常单量:' :error='errorAry[2].error' :errorPropt='errorAry[2].errorPropt' @setValue='getValue' defaultName='请输入单量(整数)' ref='fnormalarea' :defaultValue='fnormalarea'></common-input><span class='partner-input-company'>平方米</span>
                        </div>
                        <div class='partner-right-two'>
                        <common-input textName='团购价格:':error='errorAry[5].error' :errorPropt='errorAry[5].errorPropt' @setValue='getValue' defaultName='请输入价格(大于0小于100)' ref='fgroupprice' :defaultValue='fgroupprice' :maxlength='6'></common-input><span class='partner-input-group'>元/平方米</span>
                        </div>
                    </div>
                    <div class='partner-plan-font'>图片信息
                        <span>(</span>
                        <i class='partner-icon'>*</i>
                        <span>
                            为必填项)
                        </span>
                    </div>
                    <div class='partner-img-two'>
                        <div class='partner-plan-img partner-price-img'>
                            <p class='partner-add-img'>
                                <i class='partner-icon'>*</i>&nbsp;价格列表主图:
                            </p>
                            <div class='partner-upload partner-upload-first' v-if='!IE'>
                                <div class='partner-upload-font-first' >
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式,大小 < 2M </p>
                                    <p>宽高比1:1</p>
                                </div>
                                <el-upload 
                                    class="avatar-uploader"
                                    :show-file-list="false" 
                                    :auto-upload='false'
                                    action=''
                                    :on-change="pricelist"
                                    >
                                    <img v-if="productUrl" :src="productUrl" class="avatar" >
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload IE-option' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDateFirst' 
                                :uploadSrc='uploadSrcFirst'
                                @addImgSuccess='addFirstSuccess' 
                                uploadFormsId='uploadForm'
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>宽高比1:1</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                        <div class='partner-plan-endImg partner-price-img'>
                            <p class='partner-add-img'>
                                <i class='partner-icon'>*</i>&nbsp;团购结束显示图:
                            </p>
                            <div class='partner-upload partner-upload-first' v-if='!IE'>
                                <div class='partner-upload-font-first' >
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>360px*360px</p>
                                </div>
                                <el-upload 
                                    class="avatar-uploader"
                                    :show-file-list="false" 
                                    :auto-upload='false'
                                    action=''
                                    :on-change="endGroup"
                                    >
                                    <img v-if="endGroupUrl" :src="endGroupUrl" class="avatar" >
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>
                            </div>
                            <div class='IE-upload IE-option' v-if='IE'>
                                <upload-ie :checkData='checkIEData' 
                                :uploadDate='uploadDateTwo' 
                                :uploadSrc='uploadSrcTwo'
                                @addImgSuccess='addTwoSuccess' 
                                uploadFormsId='uploadFormsId'
                                action='djcrm/product/uploadFigure.do'
                                ></upload-ie>
                                <div class='partner-upload-font-first'>
                                    <p>支持jpg/png格式</p>
                                    <p>RGB模式，大小 < 2M </p>
                                    <p>360*360</p>
                                </div>
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                            </div>
                        </div>
                    </div>
                </div>
                <div class='partner-btn-propt partner-new-price'>
                    <el-button @click='newPrice = false'>取消</el-button>
                    <el-button type="primary" @click="save('0')">保存</el-button>
                    <el-button type="primary" @click="save('1')">申请审核</el-button>
                </div>
            </el-dialog>
        </div>
        <propt
            :dialogVisible = 'isDel'
            text='是否确认删除'
            :needCancel = 'true'
            @closeVisible='isDel = false'
            @sure='delData'
        ></propt>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import commonInput from './input'
import {VERSION, IMGPATH} from '@/util/constant.js'
import propt from '@/components/public/propt' 
import uploadIe from '@/components/public/uploadIE'
import {clippingProvice, clippingCity, checkNumber, checkDecimal} from '@/util/common.js'
export default {
    props:['getProductData'],
    created(){
        this.admin = this.$store.getters.doneAdmin;
        this.fcreator = this.admin.ucompany;
        this.parterId = this.admin.ucompany;    //合作方id
        //区域拆分键
        this.fkeyarea = this.admin.ocode;
        //非东京账号登录
        if(this.admin.ucompany != 100){
           this.defaultRegion = clippingProvice(this.admin.oprovince);
           this.defaultPlease = clippingCity(this.admin.ocity);
           setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
           }, 1);
           this.searchLeft();
        }
        if(this.getProduct != undefined){
            this.parterId = this.getProduct.fsupplierid;
            this.fkeyarea = this.getProduct.fkeyarea;
            var index = this.getProduct.fgrouparea.indexOf('-');             
            this.defaultRegion = this.getProduct.fgrouparea.slice(0,index);
            this.defaultPlease =this.getProduct.fgrouparea.slice(index + 1, this.getProduct.fgrouparea.length);
            setTimeout(()=> {
                this.partnerName = this.getProduct.fsuppliername;
            }, 1);
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                pagenum:0,
                pagesize:1000
            }
            this.searchList(data);
            this.searchRight(this.getProduct);
        }
        if(navigator.appName == "Microsoft Internet Explorer" && 
            navigator.appVersion .split(";")[1].replace(/[ ]/g,"") == "MSIE9.0"){
            this.IE = true;  
            return
        } 
        let data = {
            version:VERSION,
            fkeyarea:this.fkeyarea
        }
        //获取已经设置的开团时间
        this.$http.post('/product/getGroupOpenCycle.do', data).then(res => {
            if(res.data.success){
                let data = res.data.data
                this.startTime = data.fstarttime;
                this.endTimes = data.fendtime 
            }else{
                 this.$message({
                    message: res.data.msg,
                    type: 'error',
                    duration: 2000
                 });
             }
        }).catch(function (error) {
             console.log(error);
        });
    },
	data () {
		return {
            startTime:'',
            endTimes:'',
            IE:false,
            uploadSrcFirst:'',
            uploadSrcTwo:'',
            checkIEData:{
                size:2,
                Proportion:1,
                minWidth:360,
                minHeight:360,
                needPropt:false
            },
            uploadDateFirst:[],
            uploadDateTwo:[],
            errorAry:[
                {
                    error:false,
                    errorPropt:'请输入整数',
                },
                {
                    error:false,
                    errorPropt:'请输入整数',
                },
                {
                    error:false,
                    errorPropt:'请输入整数',
                },
                {
                    error:false,
                    errorPropt:'请输入价格(大于0小于100),只能保留二位小数',
                },
                {
                    error:false,
                    errorPropt:'请输入价格(大于0小于100),只能保留二位小数',
                },
                {
                    error:false,
                    errorPropt:'请输入价格(大于0小于100),只能保留二位小数',
                }
            ],
            showMasksed:false,
            showMask:false,
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            getProduct:this.getProductData,
            admin:{},
            isDel:false,
            newPrice:false,
            radio:'1',
            codeName:'',               //查询合作方市
            fkeyarea:'',               //区域拆分键
            parterId:'',               //合作方ID
            productUrl: '',            //主产品图
            endGroupUrl:'',            //团结束图
            endTime:'',                //限时特价
            fmarketingname:'',         //价格方案名称     
            fminarea:'',               //起步单量
            fmaxarea:'',               //总限购量
            funitprice:'',             //市场价
            fsmallprice:'',            //小单价
            fnormalarea:'',            //正常单量
            fgroupprice:'',            //团购价格
            isPricelist:true,          //是否主产品图
            fmarketingplanid:'',       //方案ID
            fgroupgoodid:'',           //产品ID
            fcreator:'',               //创建人ID
            fid:'',                    //产品ID
            deleteData:{},             //删除时获取到的数据
            imgEndFid:'',
            imgStartFid:'',
            rightTable: [],
            leftTable: [],
            startfigure:{},
            endfigure:{},
            typeArry:{
                '0':'待审核',
                '1':'审核中',
                '2':'已通过',
                '3':'已驳回'
            },
            isAdd:true,
            checkAry:[
                "fmarketingname",
                'fminarea',
                'fnormalarea',
                'funitprice',
                'fsmallprice',
                'fgroupprice'
            ]
		}
	},
	methods:{
        //样式
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //区域code
        getPlease(please){
             this.codeName = please;
             this.rightTable = [];
             this.leftTable = [];
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
            this.rightTable = [];
            this.leftTable = [];
        },
        //合作方选择
        getPatnerId(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            this.searchLeft();
            this.rightTable = [];
        },
        //获取输入框的值
        getValue(){
           var ref = this.$refs
           this.fmarketingname = ref.fmarketingname.input//价格方案名称

          
           this.errorAry[0].error = checkNumber(ref.fminarea.input);
           this.errorAry[1].error = checkNumber(ref.fmaxarea.input);
           this.errorAry[2].error = checkNumber(ref.fnormalarea.input);

           this.fminarea = ref.fminarea.input//起步单量
           this.fmaxarea = ref.fmaxarea.input//总限购量
           this.fnormalarea = ref.fnormalarea.input//正常单量

     
           this.errorAry[3].error = checkDecimal(ref.funitprice.input, 100, true);
           this.errorAry[4].error = checkDecimal(ref.fsmallprice.input, 100, true);
           this.errorAry[5].error = checkDecimal(ref.fgroupprice.input, 100, true);
           
           this.funitprice = ref.funitprice.input//市场价
           this.fsmallprice = ref.fsmallprice.input //小单价
           this.fgroupprice = ref.fgroupprice.input //团购价格
        },
        //校验产品列表主图
        pricelist(files){
           this.isPricelist = true;
           this.beforeAvatarUpload(files);
        },
        //团购结束显示图
        endGroup(files){
           this.isPricelist = false;
           this.beforeAvatarUpload(files);
        },
        //上传图片
        beforeAvatarUpload(files) {
            var file = files.raw;
            var img = new Image();
            img.src = files.url;
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG && !isPNG) {
                this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
                return 
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
                return
            }
            var q = this;
            img.onload = function(){
                if(img.width < 360 || img.height < 360 || img.width !== img.height){
                    q.$message.error('图片宽高不小于360! 且比例为1:1');
                    return
                }
                var imgData = new FormData();
                imgData.append('file', file);
                imgData.append('moduleid', 'P11000');
                imgData.append('version', VERSION);
                imgData.append('fkeyarea', q.fkeyarea);
                q.$http.post('/product/uploadFigure.do', imgData).then(res => {
                    if(res.data.success){
                        if(q.isPricelist){
                            q.productUrl = files.url;
                        }else{
                            q.endGroupUrl = files.url;
                        }
                        if(q.isPricelist){
                            q.startfigure={
                                fuserid: q.parterId,
                                ftype: 1,
                                ffileid: res.data.data.fileid,
                                furl: res.data.data.fileurl,
                                fkeyarea: q.fkeyarea
                            }
                        }else{
                            q.endfigure={
                                fuserid: q.parterId,
                                ftype: 2,
                                ffileid: res.data.data.fileid,
                                furl: res.data.data.fileurl,
                                fkeyarea: q.fkeyarea
                            }
                        }
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                    }
                }).catch(function (error) {
                        console.log(error);
                });
            }
        },
        //保存
        save(num){
            let data ={
                 version:VERSION,
                 fkeyarea:this.fkeyarea,
                 fmarketingplanid:this.fmarketingplanid,
                 fgroupgoodid:this.fmarketingplanid != '' ? '' : this.fgroupgoodid,
                 fmarketingname:this.fmarketingname,
                 funitprice:this.funitprice,
                 fsmallprice:this.fsmallprice,
                 fgroupprice:this.fgroupprice,
                 fnormalarea:this.fnormalarea,
                 fminarea:this.fminarea,
                 fgrouptype:this.radio,
                 fmaxarea:this.fmaxarea,
                 fsupplierid:this.parterId,
                 fcreator:this.fcreator,
                 flimitedtime:this.endTime,
                 startfigure:this.startfigure,
                 endfigure:this.endfigure,
                 fauditstatus:num,
            }
            for(let i=0; i<this.checkAry.length; i++){
               if(data[this.checkAry[i]] == '' || 
               this.radio == '1' && this.fmaxarea == '' || 
               this.radio == '2' && this.endTime == ''){
                   this.$message({
                        message: '字段不能为空',
                        type: 'error',
                        duration: 2000
                    });
                    return
               }
            }
            for(let i=0; i<this.errorAry.length; i++){
                if(this.errorAry[i].error){
                    this.$message({
                        message: '请填写正确的格式',
                        type: 'warning',
                        duration: 1500
                    });
                    return
                }
            }
            if(!this.startfigure.fuserid || !this.endfigure.fuserid){
                    this.$message({
                        message: '请上传图片',
                        type: 'warning',
                        duration: 1500
                    });
                return
            }
            console.log(this.endfigure.furl)
            this.$http.post('/product/addOrEditProgramme.do', data).then(res => {
                     if(res.data.success){
                        this.$message({
                            message: '添加成功',
                            type: 'success',
                            duration: 1500
                        });
                        this.newPrice =false;
                        let data={
                            version:VERSION,
                            fkeyarea:this.fkeyarea,
                            fid:this.fid,
                        }
                        this.searchRightList(data);
                     }else{
                         this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                     }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查询左侧产品列表方法
        searchLeft(){
            let data = {
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fsupplierid:this.parterId,
                pagenum:0,
                pagesize:1000
            }
            if(this.fkeyarea == undefined || this.parterId == undefined){
                return
            }
            this.searchList(data);
        },
        //查询产品列表
        searchList(data){
            this.$http.post('/product/loadProducts.do', data).then(res => {
                if(res.data.success) {
                    this.leftTable = res.data.data;
                    if(this.getProduct != undefined){
                        for(let i=0; i<this.leftTable.length; i++){
                            if(this.leftTable[i].fid == this.getProduct.fid){
                                setTimeout(()=> {
                                    this.$refs.leftTable.setCurrentRow(this.leftTable[i])
                                }, 10);
                            }
                        }
                    }
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //查询右侧列表方法
        searchRight(row){
            this.fgroupgoodid = row.fid;
            this.fcreator = row.fcreatorid;
            this.fid = row.fid;
            let data={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fid:row.fid,
            }
            this.searchRightList(data);
        },
        //查询右侧列表方法
        searchRightList(data){
           this.$http.post('/product/loadProgrammes.do', data).then(res => {
                if(res.data.success){
                    this.rightTable = res.data.data;
                    this.isAdd = false;
                    var disabledNumber = 0;
                    for(let i=0; i<this.rightTable.length; i++){
                        this.rightTable[i].number = i + 1;
                        this.rightTable[i].disabled = true;
                        this.rightTable[i].type = this.rightTable[i].fgrouptype == '1' ? '限量团购' : this.rightTable[i].fgrouptype == '2' ? '限时团购' :'';
                        this.rightTable[i].viewType = this.typeArry[this.rightTable[i].fauditstatus];
                        this.rightTable[i].checked = this.rightTable[i].feffect == '0' ? false : true; 
                        if(this.rightTable[i].checked){
                            this.rightTable[i].disabled = false;
                            disabledNumber = 1;
                        }
                        this.rightTable[i].flimitedtime = this.rightTable[i].fgrouptype == '1' ? '/' : this.rightTable[i].flimitedtime;
                        this.rightTable[i].fmaxarea = this.rightTable[i].fgrouptype == '2' ? '/' : this.rightTable[i].fmaxarea;
                    }
                    if(disabledNumber == 0){
                        for(let i=0; i<this.rightTable.length; i++){
                            this.rightTable[i].disabled = false;
                        }
                    }
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //编辑
        edit(datas){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.productUrl = '';
            this.endGroupUrl= '';
            this.uploadSrcFirst='';
            this.uploadSrcTwo='';
            let detalData ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
            var data
            this.$http.post('/product/loadProgrammesDetails.do', detalData).then(res => {
                if(res.data.success){
                    data=res.data.data;
                    this.newPrice = true;
                    this.fmarketingplanid = data.fmarketingplanid;
                    this.endTime= data.flimitedtime;            //限时特价
                    this.fmarketingname= data.fmarketingname;   //价格方案名称     
                    this.fminarea= data.fminarea;                //起步单量
                    this.fmaxarea= data.fmaxarea;                //总限购量
                    this.funitprice= data.funitprice;            //市场价
                    this.fsmallprice= data.fsmallprice;          //小单价
                    this.fnormalarea= data.fnormalarea;          //正常单量
                    this.fgroupprice= data.fgroupprice;          //团购价格
                    this.radio = data.fgrouptype + '';
                    this.productUrl = IMGPATH + data.startfigure.furl;      //主产品图
                    this.endGroupUrl = IMGPATH + data.endfigure.furl;        //团结束图
                    this.startfigure = data.startfigure;
                    this.endfigure = data.endfigure;
                    this.imgEndFid = data.endfigure.fid;
                    this.imgStartFid = data.startfigure.fid;
                    //IE9
                    this.uploadSrcFirst = IMGPATH + data.startfigure.furl;
                    console.log(this.uploadSrcFirst)
                    this.uploadSrcTwo = IMGPATH + data.endfigure.furl;
                    console.log(this.uploadSrcTwo)
                }
            }).catch(function (error) {
                console.log(error);
            });
            
        },
        //新增方案
        addNewPrice(){
            for(let i=0; i<this.errorAry.length; i++){
                this.errorAry[i].error = false;
            }
            this.newPrice = true;
            this.productUrl = '';      //主产品图
            this.endGroupUrl= '';         //团结束图
            this.endTime='';              //限时特价
            this.fmarketingname= '';    //价格方案名称     
            this.fminarea= '';                //起步单量
            this.fmaxarea= '';                //总限购量
            this.funitprice= '';            //市场价
            this.fsmallprice= '';          //小单价
            this.fnormalarea='';          //正常单量
            this.fgroupprice= '';          //团购价格
            this.radio  = '1';
            this.fmarketingplanid = '';    //方案ID
            this.imgStartFid = ''; 
            this.imgEndFid = '';
            this.startfigure = {};
            this.endfigure= {};
            this.uploadSrcFirst='';
            this.uploadSrcTwo='';
        },
        //是否生效
        changCheck(datas){
            if(datas.checked){
                datas.feffect = '1';
            }else{
                datas.feffect = '0';
            }
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid,
                fgroupgoodid:this.fgroupgoodid,
                operate:datas.checked
            }
            this.$http.post('/product/activateProgrammes.do', data).then(res => {
                if(res.data.success){
                    var text = datas.feffect == '0' ?  '方案已失效' : '方案已生效'
                    this.$message({
                        message: text,
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    if(res.data.msg == '已有生效方案'){
                        this.$message({
                            message: '请先取消已生效的方案',
                            duration: 1500
                        });
                    }
                    datas.feffect = '0';
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //申请审核
        examine(datas){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
           this.$http.post('/product/applyeExamineProgrammes.do', data).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '申请审核成功',
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
           }).catch(function (error) {
                console.log(error);
           });
        },
        //删除提示框显示
        delpropt(datas){
            this.isDel = true;
            this.deleteData ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
        },
        //删除
        delData(){
            this.$http.post('/product/deleteProgrammes.do', this.deleteData).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '删除成功',
                        type: 'success',
                        duration: 1500
                    });
                    this.isDel = false;
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //撤销申请
        revoke(datas){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fmarketingplanid:datas.fmarketingplanid
            }
            this.$http.post('/product/revokeApplyProgramme.do', data).then(res => {
                if(res.data.success){
                    this.$message({
                        message: '撤销审核成功',
                        type: 'success',
                        duration: 1500
                    });
                    let data={
                        version:VERSION,
                        fkeyarea:this.fkeyarea,
                        fid:this.fid,
                    }
                    this.searchRightList(data);
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error',
                        duration: 2000
                    });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //删除上传图片
        removeImg(num){
            if(this.imgStartFid == '' &&  this.imgEndFid == ''){
                if(num === '0'){
                   this.productUrl = '';            //主产品图
                }else{
                   this.endGroupUrl = '';            //团结束图
                }
                return
            }
            let data ={
                fid:num == '0' ? this.imgStartFid : this.imgEndFid,
                version:VERSION,
                fkeyarea:this.fkeyarea
            }
            this.$http.post('/product/deleteFigure.do', data).then(res => {
                if(res.data.success){
                    this.$message({
                            message: '删除成功',
                            type: 'success',
                            duration: 1500
                    });
                    if(num === '0'){
                        this.productUrl = '';            //主产品图
                        this.startfigure = {};
                    }else{
                        this.endGroupUrl = '';            //团结束图
                        this.endfigure= {};
                    }
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        },
        //IE9开始图上传
        addFirstSuccess(){
            this.uploadDateFirst=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.fkeyarea}
            ]
            setTimeout(()=> {
                document.getElementById("uploadForm").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                var res = JSON.parse(str);
                if(res.success){
                    this.startfigure={
                        fuserid: this.parterId,
                        ftype: 1,
                        ffileid: res.data.fileid,
                        furl: res.data.fileurl,
                        fkeyarea: this.fkeyarea
                    }
                }
            }, 500);
        },
        //IE9结束图上传
        addTwoSuccess(){
            this.uploadDateTwo=[
                {name:'moduleid', data:'P11000'},
                {name:'version', data:VERSION},
                {name:'fkeyarea', data:this.fkeyarea}
            ]
            setTimeout(()=> {
                document.getElementById("uploadFormsId").submit();
            }, 200);
            setTimeout(()=> {
                var str = document.getElementById("uploadFrame").contentWindow.document.body.textContent;
                console.log(str)
                var res = JSON.parse(str);
                console.log(res.success)
                if(res.success){
                    this.endfigure={
                        fuserid: this.parterId,
                        ftype: 2,
                        ffileid: res.data.fileid,
                        furl: res.data.fileurl,
                        fkeyarea: this.fkeyarea
                    }
                }
            }, 500);
        }
    },
    watch:{
        getProductData(obj){
            this.getProduct = obj;
        }
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        commonInput,
        propt,
        uploadIe
    }  
}
</script>



