<template>
    <div id="areaConfig">
        <tHeader>区域分配</tHeader>
        <div class="main">
            <div class="name">
                合作方名称:&nbsp;&nbsp;&nbsp;&nbsp;{{oname}}
            </div>
            <ul class="clearfix search">
                <li>
                    添加区域:&nbsp;&nbsp;&nbsp;&nbsp;
                    <el-select v-model="province" filterable :loading="pLoading" placeholder="请选择省" @change="provinceCode(province)">
                        <el-option
                            v-for="item in provinceTable"
                            :key="item.fprovincecode"
                            :label="item.fprovincename"
                            :value="item.fprovincecode">
                        </el-option>
                    </el-select>
                    <span class="line"></span>
                    <el-select v-model="city" filterable :loading="cLoading" placeholder="请选择市" @change="cityCode(city)">
                        <el-option
                            v-for="item in cityTable"
                            :key="item.fcitycode"
                            :label="item.fcityname"
                            :value="item.fcitycode">
                        </el-option>
                    </el-select>
                    <span class="line"></span>
                    <el-select v-model="county" filterable :loading="aLoading" placeholder="请选择区">
                        <el-option
                            v-for="item in countyTable"
                            :key="item.fcountycode"
                            :label="item.fcounty"
                            :value="item.fcountycode">
                        </el-option>
                    </el-select>
                </li>
                <li>
                    <div class="btns">
                        <el-button type="primary" @click="addArea">添加区域</el-button>
                    </div>
                </li>
            </ul>
            <el-table
                :data="tableData"
                border
                style="width: 70%">
                <el-table-column
                    type="index"
                    label="序号"
                    align="center"
                    width="80"
                >
                </el-table-column>
                <el-table-column
                    label="区域"
                    align="center"
                >
                    <template scope="scope">
                        {{scope.row.provinceName}}-{{scope.row.cityName}}-{{scope.row.countyName}}
                    </template>
                </el-table-column>
                <el-table-column
                    align="center"
                    label="操作"
                    width="150"
                >
                    <template scope="scope">
                        <el-button type="text" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="back">
                <el-button @click="back">返回</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import tHeader from '@/components/public/tHeader'
    export default {
        name: 'areaConfig',
        data() {
            return {
                //默认初始
                pLoading: true,
                cLoading: false,
                aLoading: false,
                pid: '',    //合作商id
                oname: '',  //合作方名字
                keyArea: '',    //区域拆分件
                oaid: '',       //oaid

                province: '',
                city: '',
                county: '',

                provinceTable: [],
                cityTable: [],
                countyTable: [],
                tableData: []
            }
        },
        components: {
            tHeader
        },
        created() {
            let query = this.$route.query
            this.pid = query.pid                //id
            this.oname = query.oname
            this.keyArea = query.keyArea        //区域拆分件
            this.oaid = query.oaid

            //查询合作商区域列表
            this.$http.post('partnersArea/findAllArea.do', { pid: this.pid, keyArea: this.keyArea }).then( res => {
                if(res.data.success) {
                    this.tableData = res.data.data
                }
            }).catch( error => {
                this.$message({
                    message: '获取合作商区域列表失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log('查询合作商区域列表:' + error)
            })
            //获取省
            this.$http.post('/address/findProvince.do', { code: "0", status: "1" }).then( res => {
                if(res.data.success) {
                    this.provinceTable = res.data.data
                }
            }).catch( error => {
                this.$message({
                    message: '获取地区信息失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log('区域配置获取省:' + error)
            })
        },
        methods: {
            /**
             * 获取地区
             */
            provinceCode(fprovincecode) {
                this.$http.post('/address/findCity.do', {code: fprovincecode, status: 1}).then(res => {
                    if(res.data.success) {
                        this.cityTable = res.data.data
                    }
                }).catch(function (error) {
                    this.$message({
                        message: '获取地区信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('区域配置获取市:' + error)
                });
            },
            cityCode(fcitycode) {
                this.$http.post('/address/findDistrictc.do', {code: fcitycode, status: 1}).then(res => {
                    if(res.data.success) {
                        this.countyTable = res.data.data
                    }
                }).catch(function (error) {
                    this.$message({
                        message: '获取地区信息失败',
                        duration: 1000,
                        type: 'error'
                    });
                    console.log('区域配置获取区:' + error)
                });
            },
            handleDelete(index, row) {
                //删除合作方区域
                this.$confirm('确认删除此区域吗？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.post('partnersArea/delPartnersArea.do', { aid: row.aid, fkeyarea: this.keyArea }).then( res => {
                        if(res.data.success) {
                            this.$http.post('partnersArea/findAllArea.do', { pid: this.pid, keyArea: this.keyArea }).then( res => {
                                if(res.data.success) {
                                    this.tableData = res.data.data
                                    this.$message({
                                         type: 'success',
                                        duration: 1000,
                                        message: '删除成功!'
                                     });
                                }
                            }).catch( error => {
                                this.$message({
                                    type: 'error',
                                    duration: 1000,
                                    message: '获取合作商区域列表失败!'
                                });
                                console.log('查询合作商区域列表:' + error)
                            })
                        }
                    }).catch( error => {
                        console.log('删除合作方区域:' + error)
                    })
                });
            },
            back() {
                this.$router.push({name: 'partner'})
            },
            addArea() {
                this.$confirm('确认添加此区域吗？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    //添加合作方区域
                    if(this.province && this.city && this.county) {
                        let query = this.getAreaCodeName()
                        let str = query.provinceName + query.cityName + query.countyName
                        for(let item of this.tableData) {
                            if(str == item.provinceName + item.cityName + item.countyName) {
                                this.$message({
                                    message: '请不要重复添加相同的地址',
                                    duration: 1000,
                                    type: 'warning'
                                });
                                return false
                            }
                        }
                        this.$http.post('partnersArea/addPartnersArea.do', query).then( res => {
                            if(res.data.success) {
                                //更新合作方列表数据
                                let parameter = {
                                    pid: this.pid,
                                    keyArea: this.keyArea
                                }
                                this.$http.post('partnersArea/findAllArea.do', parameter).then( res => {
                                    if(res.data.success) {
                                        this.tableData = res.data.data
                                        this.$message({
                                            type: 'success',
                                            duration: 1000,
                                            message: '区域添加成功!'
                                        });
                                    }
                                    else {
                                        this.$message({
                                            message: '区域添加失败',
                                            duration: 1000,
                                            type: 'error'
                                        });
                                    }
                                }).catch( error => {
                                    this.$message({
                                        message: '区域添加错误',
                                        duration: 1000,
                                        type: 'error'
                                    });
                                    console.log('查询合作商区域列表:' + error)
                                })

                            }
                        }).catch( error => {
                            console.log('区域配置添加合作方区域:' + error)
                        })
                    }
                    else {
                        this.$message({
                            message: '请选择完整区域信息后再点击添加',
                            duration: 1000,
                            type: 'warning'
                        });
                    }
                });
            },
            //区域的code和名字
            getAreaCodeName() {
                let provinceCode = this.province
                let provinceName = ''
                let cityCode = this.city
                let cityName = ''
                let countyCode = this.county
                let countyName = ''
                let pid = this.pid
                let keyarea = this.keyArea
                let oaid = this.oaid
                for(let item of this.provinceTable) {
                    if(provinceCode == item.fprovincecode) {
                        provinceName = item.fprovincename
                        break
                    }
                }
                for(let item of this.cityTable) {
                    if(cityCode == item.fcitycode) {
                        cityName = item.fcityname
                        break
                    }
                }
                for(let item of this.countyTable) {
                    if(countyCode == item.fcountycode) {
                        countyName = item.fcounty
                        break
                    }
                }
                return { pid, oaid, keyarea, provinceCode, cityCode, countyCode, provinceName, cityName, countyName }
            }
        },

        watch: {
            provinceTable(val) {
                this.pLoading = false
            },
            province(val) {
                this.city = ''
                this.county = ''
                this.cLoading = true
                this.countyTable = []
            },
            cityTable(val) {
                this.cLoading = false
            },
            city() {
                this.county = ''
                this.aLoading = true
            },
            countyTable(val) {
                this.aLoading = false
            }
        }
    }
</script>

<style lang="scss" scoped>
    #areaConfig {
        padding: 30px 19px 0 40px;
        box-sizing: border-box;
        font-size: 14px;
        .main {
            background-color: #fff;
            box-sizing: border-box;
            padding-bottom: 80px;
            .name {
                padding-left:60px;
                height: 70px;
                line-height: 70px;
            }
            ul.search {
                line-height: 32px;
                padding-left: 74px;
                li {
                    float: left;
                }
                li:nth-child(2) {
                    margin-left: 39px;
                }
                li:last-child {
                    margin-left: 40px;
                }
                .line {
                    display: inline-block;
                    width: 13px;
                    background: #8c8c8c;
                    height: 1px;
                    margin-bottom: 5px;
                }
                /*element search change*/
                .el-select {
                    width: 150px;
                    height: 32px;
                }
                .el-input {
                    width: 200px;
                    height: 32px;
                }
                .el-button {
                    width: 110px;
                    height: 36px;
                    padding-left:30px;
                }
            }
            .btns {
                margin-bottom: 19px;
                .el-button {
                    width: 110px;
                    height: 36px;
                    background: url("../../../../../static/images/add.png") #20a0ff no-repeat 10px 9px;
                }
            }
            .el-table {
                margin-left: 75px;
            }
            .back {
                padding-left: 790px;
                padding-top: 60px;
                button {
                    width: 160px;
                    height: 44px;
                }
            }
        }
    }
</style>



