/**
 * 产品管理的路由
 */
import commodity from '@/components/common/goods/partnerManger/commodity.vue'
import productManger from '@/components/common/goods/partnerManger/productManger.vue'
import productShelfAudit from '@/components/common/goods/partnerManger/ProductShelfAudit.vue'
import productOffShelf from '@/components/common/goods/partnerManger/ProductOffShelf.vue'
import pricePlanReview from '@/components/common/goods/partnerManger/pricePlanReview.vue'
import optionPricesCheme from '@/components/common/goods/partnerManger/optionPricesCheme.vue'
import productCycle from '@/components/common/goods/partnerManger/productCycle.vue'
import openTime from '@/components/common/goods/partnerManger/openTime.vue'

var goods = [
    { path:'goods', name: 'goods', redirect: { name: 'commodity' } },
    { path:'goods/commodity', name:'commodity', component: commodity},
    { path:'goods/productCycle', name:'productCycle', component: productCycle},
    { path:'goods/pricePlanReview', name:'pricePlanReview', component: pricePlanReview},
    { path:'goods/productShelfAudit', name:'productShelfAudit', component: productShelfAudit},
    { path:'goods/optionPricesCheme', name:'optionPricesCheme', component: optionPricesCheme},
    { path:'goods/productOffShelf', name:'productOffShelf', component: productOffShelf},
    { path:'goods/productManger', name:'productManger', component: productManger},
    { path:'goods/openTime', name:'openTime', component: openTime},
]

//do other arr model router

export default goods.concat([])



