<template>
    <div class="partnerManage">
        <tHeader>积分商城入口位管理</tHeader>
        <div class='common-bg'>
            <regional-choice
            @getPlease = 'getPlease'
            :isDisableds='true'
            >
            </regional-choice>
            <table-carouse :tableData='imgTable' 
            :pleaseCodes='pleaseCode'
            :fbooths='fbooth'
            @saveUpImg='saveUpImg'
            :shopDetals='shopDetal'
            :appLinkTypes='appLinkType'
            ></table-carouse>
        </div>
    </div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import tableCarouse from './tebleCarouse'
import regionalChoice from '@/components/public/regionalChoice'
import {VERSION, IMGPATH} from '@/util/constant.js'
export default {
    created(){
        //初始化获取积分商城列表，获取之后在初始化轮播图
        this.getListShop();
    },
	data () {
		return {
            imgTable: [],
            appLinkType:[
                {regionName:'商城列表' , number:3},
                {regionName:'商城商品详情' , number:5, disabled:true}
            ],
            pleaseCode:3303,
            fbooth:5,
            shopDetal:''
		}
	},
	methods:{
        //获取市的code码
        getPlease(please){
           this.pleaseCode = please.code;
           this.getListShop();
        },
        //查询市对应的轮播图
        findImg(getData){
            this.$http.post('/banner/loadBanner.do', getData).then(res => {
                if (res.data.success) {
                    this.imgTable = res.data.data;
                    if(this.imgTable.length == 0){
                        this.init();
                        return
                    }
                    for(let i=0; i<this.imgTable.length; i++){
                         this.imgTable[i].number = i + 1;
                         this.imgTable[0].fname = 'APP首页积分商城第一排左';
                         this.imgTable[1].fname = 'APP首页积分商城第一排右';
                         this.imgTable[2].fname = 'APP首页积分商城第二排左';
                         this.imgTable[3].fname = 'APP首页积分商城第二排右';
                         this.imgTable[i].fimgpath = IMGPATH + this.imgTable[i].fimgpath;
                         if(this.imgTable[i].fjumpposition == 2){
                             this.imgTable[i].linkType = '团购列表'
                         }
                         if(this.imgTable[i].fjumpposition == 1){
                             this.imgTable[i].linkType = '普通网页'
                         }
                         if(this.imgTable[i].fjumpposition == 3 ){
                             this.imgTable[i].linkType = '商城列表-商城首页'
                         }
                         if(this.imgTable[i].fjumpposition == 5){
                            for(let l=0; l<this.shopDetal.length; l++){
                                 if(this.imgTable[i].fproductid === this.shopDetal[l].id){
                                       this.imgTable[i].linkType = '商城商品详情-' + this.shopDetal[l].regionName
                                 }
                             }
                         }
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //上传成功后刷新界面
        saveUpImg(){
            var upImg={ 
             farea:this.pleaseCode, 
             fclient:2, 
             fbooth:5, 
             fkeyarea:this.pleaseCode + ''
           }
           this.findImg(upImg);
        },
        //获取积分商城列表
        getListShop(){
           this.$http.post('/productController/searchAllGoods.do').then(res => {
               if(res.data.success){
                   console.log('获取积分列表成功')
                   this.shopDetal = res.data.data;
                    for(let i=0; i<this.shopDetal.length; i++){
                        this.shopDetal[i].regionName = this.shopDetal[i].goodName;
                        this.shopDetal[i].id = this.shopDetal[i].goodId;
                    }
                    if(this.shopDetal.length == 0){
                        this.appLinkType[1].disabled = true;
                    }else{
                        this.appLinkType[1].disabled = false;
                    }
               }
               //初始化轮播图
               this.saveUpImg();
           }).catch(function (error) {
                console.log(error);
           });
        },
        //初始化积分商城广告位
        init(){
            let data ={
                fbooth:5,
                fkeyarea:this.pleaseCode,
                farea:this.pleaseCode
            }
            this.$http.post('/banner/initializeAdPosition.do', data).then(res => {
                if (res.data.success) {
                    //初始化轮播图
                    this.saveUpImg();
                }
            }).catch(function (error) {
                console.log(error);
            });
        }
    },
    components:{
        tHeader,
        tableCarouse,
        regionalChoice
    }
}
</script>



