<template>
    <ul class="topNav-nav clearfix">
        <li v-for="(item,index) in listData">
            <span :class="{active:item.navState}"  @click="tab(index)">{{item.navContent}}</span>
        </li>
    </ul>
</template>
<script>
    /**
     * Created by qxl on 2017/9/11.
     */
    export default{
        props:['listData'],
        data(){
            return {

            }
        },
        methods:{
            tab(index){
                this.$emit('tab', index)
            }
        }
    }
</script>
<style>
    .topNav-nav{
        height:50px;
        font-size:16px;
        border-bottom:2px solid #b0b0b0;
        padding-left:10px;
    }
    .topNav-nav li{
        float:left;
        height:50px;
        line-height:50px;
        padding:0px 22px;
    }
    .topNav-nav li span{
        cursor: pointer;
    }
    .topNav-nav li span.active{
        color:#38acff;
    }
</style>



