<template>
    <div class="partnerManage">
        <tHeader>{{titleName}}</tHeader>
        <div class='common-bg'>
            <regional-choice
              @getPlease = 'getPlease'
              :isDisableds='fbooth == 3 ? true : false'
            >
            </regional-choice>
            <div class='carouse-type-select'>
                <carouse-select :region='fbooth == 2 ? others : other' textName='客户端' @getSelect='getSelect' regionName='网页端'></carouse-select>
            </div>
            <div class='carouse-time-setting'>
                <span>停留时间:</span>
                <input v-model='timeStop' @input='getTimeX' style="ime-mode:disabled" class='carouse-time-input' @blur='setTime'  maxlength='2'>
                <span>秒</span>
            </div>
            <div class='carouse-time-setting'>
                <span>位置滑动时间:</span>
                <input v-model='timeMove' @input='getTimeY' style="ime-mode:disabled" class='carouse-time-input' @blur='setTime'  maxlength='4'>
                <span>秒</span>
            </div>
            <div class='carouse-btn'>
                <el-button type="primary" @click="addUpImg">
                    <i class='carous-add'></i>
                    <i class='carous-adds'></i>
                    添加</el-button>
                <el-button @click='deletePropt = true' :disabled= 'isDisabled' >删除</el-button>
            </div>
            <div class='carouse-table'>
                <span class='carouse-table-num'></span>
                <el-table ref="multipleTable" 
                    :data="imgTable" border tooltip-effect="dark" 
                    :row-class-name="tableRowClassName"
                    style="width: 100%" 
                    @select='selectList'
                    >
                    <el-table-column type="selection" width="60">
                    </el-table-column>
                    <el-table-column prop="number" label="位置" width="100">
                    </el-table-column>
                    <el-table-column  label="图片" width="400" >
                         <template scope="scope">
                             <img :src="scope.row.fimgpath" class='carous-table-img' >
                         </template>
                    </el-table-column>
                    <el-table-column prop="linkType" label="链接类型" >
                    </el-table-column>
                    <el-table-column  label="操作" width="100">
                        <template scope="scope">
                            <el-button type="text" size="small" @click="editUpImg(scope.row)">编辑图片</el-button>
                            <el-button type="text" size="small" v-show="scope.row.number != '1'" @click='upMove(scope.row.fid,scope.row.number)'>上移</el-button>
                            <el-button type="text" size="small" v-show="scope.row.number != imgTable.length" @click='nextMove(scope.row.fid,scope.row.number)'>下移</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div>
            <editImg 
                :editImg='editImg'
                @closeEditImg='editImg = false'
                @saveUpImg='saveUpImg'
                :fids='fid'
                :pleaseCodes='pleaseCode'
                :webPageTypes='webPageType'
                :fbooths='fbooth'
                :ffileIds='ffileId'
                :uploadUrls='fimgpath'
                :webClass='webClass'
                :nums='nums'
                :groupNums='groupNums'
                :upLoadInputs='upLoadInputs'
                :files='files'
                :pcLinkTypeNames='pcLinkTypeNames'
                :fproductids='fproductids'
                :buyDetals='buyDetal'
                :shopDetals='shopDetal'
                :appLinkTypes='appLinkTypes'
                >
            </editImg>
            <propt
                text='确认要删除吗？'
                :dialogVisible='deletePropt'
                :needCancel='needCancel = true'
                @closeVisible='deletePropt = false'
                @sure='deletList'
            ></propt>
            <propt
                text='位置最多可以添加5个!'
                :dialogVisible='showPropt'
                @closeVisible='showPropt = false'
                @sure='showPropt = false'
            ></propt>
        </div>
    </div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import propt from '../../../public/propt'
import editImg from './editImg'
import carouseSelect from '@/components/public/select'
import {VERSION,IMGPATH} from '@/util/constant.js'
export default {
    created(){
        var initImg={ 
            farea:3303, 
            fclient:1, 
            fbooth:this.fbooth, 
            fkeyarea:'3303' 
        }
        //获取初始化浙江温州轮播图
        this.findImg(initImg);
        var data ={
            fbooth:this.fbooth,
            fkeyarea:'3303'
        }
        //初始化时间
        this.getinitTime(data);
        //初始化获取团购列表
        this.getListGrpup(); 
        //获取积分商城列表
        this.getListShop();
    },
    props:['titleName','fbooths'],
	data () {
		return {
            appLinkTypes:[
                {regionName:'普通网页' , number:1}, 
                {regionName:'团购列表' , number:2},
                {regionName:'商城列表' , number:3},
                {regionName:'团购商品详情' , number:4, disabled:true},
                {regionName:'商城商品详情' , number:5, disabled:true}
            ],
            fproductids:'',
            pcLinkTypeNames:'',
            files:{},
            upLoadInputs:'',
            other:[
                {regionName:'网页端', number:1},  //客户端选择
                {regionName:'移动端', number:2}
            ],
            others:[
                {regionName:'网页端', number:1},  //客户端选择
            ],
            webClass:'',
            pleaseCode:3303,     //区域拆分键
            webPageType:1,       //网页端或者移动端
            timeStop:'5',
            timeMove:'0.3',
            editImg:false,       //编辑图片的界面变量
            upLoadInput:'',      //存放内嵌网页链接
            deletePropt:false,   //显示提示框
            needCancel:true,     //是否需要取消按钮
            showPropt:false,     //显示位置最多5个提示
            showLinkType:false,  //连接类型未选择
            isDisabled:true,     //是否禁用按钮
            imgTable: [],        //表单数据
            delNumber:'',        //删除index
            fid:'',              //每行对应的id
            fbooth:this.fbooths, //页面值
            ffileId:'',          //文件ID
            delId:'',            //删除的ID
            fimgpath:'../../../../../static/images/authentication/upload.png',     //获取编辑图片路径
            arry:{
                0:'全部列表',
                1:'限量抢购列表',
                2:'爆款特价列表',
                3:'商城首页'
            },
            nums:'',
            groupNums :'',
            buyDetal:'',
            shopDetal:''          
		}
	},
	methods:{
      //table样式
      tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
      },
      //获取市code码
      getPlease(please){
        this.pleaseCode = please.code;
        var data ={
            fbooth:this.fbooth,
            fkeyarea:this.pleaseCode + ''
        }
        this.appLinkTypes = [
            {regionName:'普通网页' , number:1}, 
            {regionName:'团购列表' , number:2},
            {regionName:'商城列表' , number:3},
            {regionName:'团购商品详情' , number:4, disabled:true},
            {regionName:'商城商品详情' , number:5, disabled:true}
        ]
        //切换城市获取团购列表
        this.getListGrpup();
        //获取积分商城列表
        this.getListShop();
        //初始化时间
        this.getinitTime(data);
        //更新轮播图
        this.upDataImg();
      },
      //删除
      deletList(){
          var delData ={
              fid:this.delId,
              fkeyarea:this.pleaseCode + ''
          }
          this.$http.post('/banner/deleteBanner.do', delData).then(res => {
                if (res.data.success) {
                    for(let i=0; i<this.imgTable.length; i++){
                        if(this.delNumber == this.imgTable[i].number){
                            this.imgTable.splice(i, 1);
                        }
                    }
                    for(let i=0; i<this.imgTable.length; i++){
                       this.imgTable[i].number = i + 1;
                    }
                    this.isDisabled = true;
                    this.$message({
                        message: '删除成功',
                        type: 'success',
                        duration: 2000
                    });
                    this.deletePropt = false;
                }
            }).catch(function (error) {
                console.log(error);
            });
      },
      //单选
      selectList(obj){
          if(obj.length !== 0){
            if(obj.length > 1){
                obj[0] = obj[1];
                obj = obj.splice(1,1);  
            }
            this.delId = obj[0].fid;
            this.delNumber = obj[0].number;
          }
          this.isDisabled = false;
          if(obj.length === 0){
              this.isDisabled = true;
          }
      },
      //停留时间
      getTimeX(){
         var check = new RegExp('(^[0-9]{1}[0-9]*$)');
            if(check.test(this.timeStop)){
                if(this.timeStop > 10){
                    this.timeStop = 10
                }
                if(this.timeStop == 0){
                    this.timeStop = '';
                }
            }else{
                if(this.timeStop.length === 1){
                   this.timeStop = ''
                }else if(this.timeStop.length >= 2){
                    var arry = this.timeStop.split('')
                    var getNum = []
                    for(var i=0; i<arry.length; i++){
                      if(check.test(arry[i])){
                         getNum.push(arry[i])
                      }
                    }
                    this.timeStop = getNum.join("")
                }
            }
      },
      //位置滑动
      getTimeY(){
            var arry = this.timeMove.toString().split("")
            if(arry.length > 4){
              arry = arry.slice(0,4);
            }
            var isNumber = new RegExp(/^\d+(\.\d+)?$/)
            var check = new RegExp (/^\d(\.[0-9][0-9]?)?$/)
            var newArry = []
            for(var i=0; i<arry.length; i++){
                 if(isNumber.test(arry[i]) || arry[1] === '.'){
                     newArry.push(arry[i])
                 } 
            }
            var resoult = newArry.join("")
            if(check.test(resoult)){
                if(resoult > 1.99){
                    resoult = 1.99
                }
            }else{
                if(resoult === "0." || resoult === "1."){
                    this.timeMove = resoult
                    return
                }
                resoult = ''
            }
            this.timeMove = resoult
      },
      //查询市对应的轮播图
      findImg(getData){
            this.$http.post('/banner/loadBanner.do', getData).then(res => {
                if (res.data.success) {
                    this.imgTable = res.data.data;
                    for(let i=0; i<this.imgTable.length; i++){
                         this.imgTable[i].number = i + 1;
                         this.imgTable[i].fimgpath = IMGPATH + this.imgTable[i].fimgpath; 
                         if(this.imgTable[i].fjumpposition == 2){
                             this.imgTable[i].linkType = '团购列表' + '-' + this.arry[this.imgTable[i].ffunctiontype];
                         }
                         if(this.imgTable[i].fjumpposition == 1){
                             this.imgTable[i].linkType = '普通网页' + '-' + this.imgTable[i].furl;
                         }
                         if(this.imgTable[i].fjumpposition == 3){
                             this.imgTable[i].linkType = '商城列表' + '-' + this.arry[this.imgTable[i].ffunctiontype];
                         }
                         if(this.imgTable[i].fjumpposition == 4){
                             for(let l=0; l<this.buyDetal.length; l++){
                                 if(this.imgTable[i].fproductid === this.buyDetal[l].id){
                                       this.imgTable[i].linkType = '团购商品详情-' + this.buyDetal[l].regionName;
                                 }
                             }
                         }
                         if(this.imgTable[i].fjumpposition == 5){
                             for(let l=0; l<this.shopDetal.length; l++){
                                 if(this.imgTable[i].fproductid === this.shopDetal[l].id){
                                       this.imgTable[i].linkType = '商城商品详情-' + this.shopDetal[l].regionName;
                                 }
                             }
                         }
                    }
                }else{
                    this.imgTable = [];
                }
            }).catch(function (error) {
                console.log(error);
            });
      },
      //获取网页类型
      getSelect(data){
        for(let i=0; i<this.other.length; i++){
            if(data == this.other[i].regionName){
                this.webPageType = this.other[i].number;
            }
        }
        this.upDataImg();
      },
      //上移动
      upMove(fid,number){
        var data={
            move:0,
            fid:fid,
            fkeyarea:this.pleaseCode + ''
        }
        this.move(data,number);
      },
      //下移动
      nextMove(fid,number){
        var data={
            move:1,
            fid:fid,
            fkeyarea:this.pleaseCode + ''
        }
        this.move(data,number);
      },
      //移动接口
      move(data,number){
          this.$http.post('/banner/moveBanner.do', data).then(res => {
                if (res.data.success) {
                    this.upDataImg();
                }
            }).catch(function (error) {
                console.log(error);
            });
      },
      //刷新页面
      upDataImg(){
        var searchImg={ 
            farea:this.pleaseCode,        //区域编码
            fclient:this.webPageType,     //客户端
            fbooth:this.fbooth,           //1.首页轮播图管理，2.纸板团购轮播图管理，3.积分商城轮播图管理，4.纸板团购广告位管理，5.积分商城广告位管理，6。APP启动页管理
            fkeyarea:this.pleaseCode + '' //区域拆分键
        }
        this.findImg(searchImg);
      },
      //获取停留时间和滑动时间
      getinitTime(data){
          this.$http.post('/banner/loadBannerTime.do ', data).then(res => {
                if (res.data.success) {
                    if(res.data.data.length == 0){
                        this.timeStop = '5'
                        this.timeMove = '0.3'
                        return
                    }
                    this.timeStop = res.data.data[0].fdwelltime
                    this.timeMove = res.data.data[0].fsliptime
                }
            }).catch(function (error) {
                console.log(error);
            });
      },
      //设置时间
      setTime(){
          var timeData = {
              fbooth:this.fbooth,
              fdwelltime:this.timeStop,
              fsliptime:this.timeMove,
              fkeyarea:this.pleaseCode + ''
         }
         this.$http.post('/banner/updateBannerTime.do', timeData).then(res => {
                if (res.data.success) {
                    console.log('时间设置成功')
                }
            }).catch(function (error) {
                console.log(error);
            });
      },
      //上传成功后刷新界面
      saveUpImg(){
          this.editImg = false;
          this.setTime();
          this.upDataImg();
      },
      //编辑图片fid
      editUpImg(data){
         console.log(data)
         this.editImg = false;
         setTimeout(()=> {
            this.editImg = true;
         }, 1);
         this.fid = '';
         this.ffileId = '';
         this.fimgpath = '';
         this.nums = '';
         this.groupNums = ''; 
         this.upLoadInputs = '';
         this.webClass = '';
         this.files = '1';
         this.fproductids = ''
         if(data.fjumpposition == undefined && data.ffunctiontype == 0){
                this.pcLinkTypeNames = '1';
                setTimeout(()=> {
                    this.pcLinkTypeNames = '';
                }, 1);
         }
         setTimeout(()=> {
             this.fproductids = data.fproductid;
             this.webClass = 'carouse-web-img';
             this.upLoadInputs = data.furl;
             this.fid = data.fid;
             this.ffileId = data.ffileId;
             this.fimgpath = data.fimgpath;
             this.nums = data.fjumpposition;
             this.groupNums = data.ffunctiontype; 
             this.files = '';
         }, 1);
         
      },
      //添加按钮
      addUpImg(){
          this.fid='';
          this.ffileId = '';
          this.nums = '-1';
          this.upLoadInputs = '1';
          this.uploadUrls = '1'
          this.fimgpath = '';
          this.webClass = '1';
          this.pcLinkTypeNames='1'
          setTimeout(()=> {
              this.pcLinkTypeNames = '';
              this.nums = '';
              this.upLoadInputs = '';
              this.webClass = '';
              this.fimgpath = '../../../../../static/images/authentication/upload.png';
          }, 10);
          if(this.imgTable.length == 5){
              this.showPropt = true
          }else{
              this.editImg = false;
                setTimeout(()=> {
                    this.editImg = true;
                }, 1);
          }
          
          
      },
      //获取团购商品列表
      getListGrpup(){
           let data ={
                version:VERSION,
                fkeyarea:this.pleaseCode,
                fgroupareaid:this.pleaseCode,
                pagesize:1000,
                pagenum:0
            }
           this.$http.post('/product/loadListsForAddres.do', data).then(res => {
               if(res.data.success){
                   this.buyDetal = res.data.data;
                    if(this.buyDetal.length == 0){
                        return
                    }
                    for(let i=0; i<this.buyDetal.length; i++){
                        this.buyDetal[i].regionName = this.buyDetal[i].fproductname;
                        this.buyDetal[i].id = this.buyDetal[i].fid;
                    }
                    this.appLinkTypes[3].disabled = false;
               }
           }).catch(function (error) {
                console.log(error);
           });
      },
      //获取积分商城列表
      getListShop(){
           console.log('获取积分列表')
           this.$http.post('/productController/searchAllGoods.do').then(res => {
               if(res.data.success){
                    this.shopDetal = res.data.data;
                    if(this.shopDetal.length == 0){
                        return
                    }
                    for(let i=0; i<this.shopDetal.length; i++){
                        this.shopDetal[i].regionName = this.shopDetal[i].goodName;
                        this.shopDetal[i].id = this.shopDetal[i].goodId;
                    }
                    this.appLinkTypes[4].disabled = false;
               }
           }).catch(function (error) {
                console.log(error);
           });
       }
    },
    components:{
        tHeader,
        regionalChoice,
        propt,
        editImg,
        carouseSelect,
    }  
}
</script>


