import { Commodity_all, Commodity_add } from '@/components/common/integral/commodity/commodity_all'
import Commodity_sell from '@/components/common/integral/commodity/commodity_sell'

//仓库发货类
import warehouse from '@/components/common/integralMall/commodityDelivery/warehouse.vue'
//商品发货
import delivery from '@/components/common/integralMall/commodityDelivery/delivery.vue'
//外部采购
import purchase from '@/components/common/integralMall/commodityDelivery/purchase.vue'
//账户充值
import recharge from '@/components/common/integralMall/commodityDelivery/recharge.vue'
//消费凭证
import voucher from '@/components/common/integralMall/commodityDelivery/voucher.vue'
//上门服务
import service from '@/components/common/integralMall/commodityDelivery/service.vue'

var integral = [
    { path: 'integral', name: 'integral_shop', redirect: { name: 'commodity_all' } },
]
var commodity = [
    { path: 'integral_shop/commodity_all', name: 'commodity_all', component: Commodity_all },
    { path: 'integral_shop/commodity_all/commodity_add', name: 'commodity_add', component: Commodity_add },
    { path: 'integral_shop/commodity_sell', name: 'commodity_sell', component: Commodity_sell }
]

let commodityDelivery = [
    { path:'integral_shop/warehouse',name: 'warehouse',component: warehouse},
    { path:'integral_shop/delivery',name: 'delivery',component: delivery},
    { path:'integral_shop/purchase',name: 'purchase',component: purchase},
    { path:'integral_shop/recharge',name: 'recharge',component: recharge},
    { path:'integral_shop/voucher',name: 'voucher',component: voucher},
    { path:'integral_shop/service',name: 'service',component: service}
]

export default integral.concat(commodity, commodityDelivery)



