<template>
    <div>
        <div class='carouse-table'>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" :row-class-name="tableRowClassName" style="width: 100%">
                <el-table-column prop="number" label="序号" width="150">
                </el-table-column>
                <el-table-column prop="fname" label="展位名称" width="150">
                </el-table-column>
                <el-table-column label="图片" width="400">
                    <template scope="scope">
                        <img :src="scope.row.fimgpath" class='carous-table-img'>
                    </template>
                </el-table-column>
                <el-table-column prop="linkType" label="链接类型" >
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template scope="scope">
                        <el-button type="text" size="small" @click='editUpImg(scope.row, scope.$index)'>编辑图片</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <editImg 
            :editImg='editImg'
            @closeEditImg='editImg = false'
            @saveUpImg='saveUpImg'
            :fids='fid'
            :pleaseCodes='pleaseCode'
            :webPageTypes='webPageType'
            :fbooths='fbooths'
            :ffileIds='ffileId'
            :uploadUrls='fimgpath'
            :webClass='webClass'
            :nums='nums'
            :groupNums='groupNums'
            :fproductids='fproductids'
            :files='files'
            :buyDetals='buyDetal'
            :shopDetals='shopDetal'
            :appLinkTypes='appLinkType'
            :selectImg = 'selectImg'
            >
        </editImg>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import editImg from './editImg'
export default {
    props:['tableData','pleaseCodes','fbooths','buyDetals','shopDetals','appLinkTypes'],
	data () {
		return {
            files:{},
            editImg:false,
            pleaseCode:this.pleaseCodes,
            fid:'',
            webPageType:2,
            ffileId:'',
            fimgpath:'../../../../../static/images/authentication/upload.png',
            webClass:'carouse-web-img',
            nums:'',
            groupNums :'',
            fproductids:'',
            buyDetal:this.buyDetals,
            shopDetal:this.shopDetals,
            appLinkType:this.appLinkTypes,
            selectImg:''
		}
	},
	methods:{
        tableRowClassName(row, index) {
            if (((index + 1) % 2) === 0) {
                return 'info-row';
            }
            return '';
        },
        //刷新界面
        saveUpImg(){
           this.editImg = false;
           this.$emit('saveUpImg')
        },
        //获取对应的ID
        editUpImg(data, index){
            this.selectImg = index;
            this.nums = '-2';
            this.groupNums = '';
            this.fid = '';
            this.ffileId ='';
            this.editImg = false;
            this.fproductids = '-1';
            this.files = '1';
            this.fimgpath = '';
            setTimeout(()=> {
                this.nums = data.fjumpposition;
                this.groupNums = data.ffunctiontype;
                this.fimgpath = data.fimgpath;
                this.fproductids = data.fproductid;
                this.editImg = true;
                this.fid = data.fid;
                this.ffileId = data.ffileId;
                this.files = '';
            }, 1);
            
        }
    },
    components:{
        carouseSelect,
        editImg
    },
    watch:{
        //获取对应市code
        pleaseCodes(code){
           this.pleaseCode = code;
        },
        //团购列表
        buyDetals(ary){
            this.buyDetal = ary; 
        },
        //积分商城列表
        shopDetals(ary){
            this.shopDetal = ary;
        },
        //链接类型
        appLinkTypes(ary){
            this.appLinkType = ary;
        }
    }  
}
</script>


