<template>
    <div class='partner-select'>
        <carouse-select
             :textName='textName'
             :region='partnerArry'
             :regionName='partnerName'
             @getSelect = 'getSelect'
             :isDisableds='isDisabled'
        ></carouse-select>
    </div>
</template>

<script>
import carouseSelect from './select'
export default {
    props:['codeName', 'partName','isDisableds', 'isCommday'],
    created(){
        if(this.isCommday){
            //商品管理页面获取合作方
            this.getPartnerCommpage(name);
            return
        }
        this.getpartnerAry(this.codeName);
    },
	data () {
		return {
            textName:'合作方:',
            partnerArry:[],
            partnerName:this.partName,
            code:this.codeName,
            isDisabled:this.isDisableds
        }
	},
	methods:{
        //获取合作方ID
        getSelect(name){
            for(let i=0; i<this.partnerArry.length; i++){
                if(name == this.partnerArry[i].regionName){
                    var id = this.partnerArry[i].id;
                    var ocode = this.partnerArry[i].ocode;
                }
            }
            let data ={
                id:id,
                name:name,
                code:ocode
            }
            this.partnerName = name;
            this.$emit('getSelect', data);
        },
        //获取合作方
        getpartnerAry(code){
            this.partnerName = '';
            this.$http.post('/partners/findOption.do', {city:code}).then(res => {
                    if(res.data.success) {
                        this.partnerArry = res.data.data;
                        for(let i=0; i < this.partnerArry.length; i++){
                            this.partnerArry[i].regionName = this.partnerArry[i].oname;
                            this.partnerArry[i].id = this.partnerArry[i].id;
                        }
                    }
            }).catch(function (error) {
                console.log(error);
            });
        },
        //商品管理获取合作方
        getPartnerCommpage(code){
            this.partnerName = '';
            this.$http.post('/partners/findPartnersByCityName.do', {cityName:code}).then(res => {
                    if(res.data.success) {
                        this.partnerArry = res.data.data;
                        for(let i=0; i < this.partnerArry.length; i++){
                            this.partnerArry[i].regionName = this.partnerArry[i].oname;
                            this.partnerArry[i].id = this.partnerArry[i].id;
                        }
                    }
            }).catch(function (error) {
                console.log(error);
            });
        }
    },
    components:{
       carouseSelect
    },
    watch:{
        codeName(name){
            if(this.isCommday){
                //商品管理页面获取合作方
                this.getPartnerCommpage(name);
                return
            }
            this.getpartnerAry(name);
        },
        //是否禁用
        isDisableds(val){
            this.isDisabled = val;
        },
        //默认合作方名称
        partName(val){
            this.partnerName = val;
        }
    }
}
</script>



