<template>
    <div class="debitWindow-warp" v-if="debitWindowShow">
        <div class="debitWindow-titleWarp">
            <h3>扣款</h3>
        </div>
        <span class="debitWindow-closeBtn" @click="debitWindowClose()"></span>
        <div class="debitWindow-table-warp">
            <table class="debitWindow-table">
	            <tr>
	                <td>手机号<span class="debitWindow-red">*</span>：</td>
	                <td> <input type="text" class="debitWindow-adjustText" v-model="phone" @input="phoneInput"></td>
	            </tr>
	            <tr>
	                <td>认证名：</td>
	                <td><input type="text" class="debitWindow-adjustText" disabled v-model="certificateName"></td>
	            </tr>
	            <tr>
	                <td>余额：</td>
	                <td><input type="text" class="debitWindow-adjustText" disabled v-model="balance"></td>
	            </tr>
	            <tr>
	                <td>资金类型：</td>
	                <td>
	                    <el-select v-model="fundType" placeholder="请选择" clearable @visible-change="visible">
	                        <el-option v-for="item,index in fundTypeList" :key="index" :label="item.label" :value="item.value">
	                        </el-option>
	                    </el-select>
	                </td>
	            </tr>
	            <tr>

	                <td>金额(元)<span class="debitWindow-red">*</span>：</td>
	                <td class="debitWindow-adjustWarp">
	                    <span class="debitWindow-jian">-</span>
	                    <input type="text" class="debitWindow-adjustText" v-model="amount" @input="uppercase"/>
	                </td>
	            </tr>
	            <tr>
	                <td>大写金额：</td>
	                <td>
	                    <input type="text" class="debitWindow-adjustText" disabled v-model="uppercaseAmount">
	                </td>
	            </tr>
	            <tr>
	                <td>备注：</td>
	                <td>
	                    <textarea  class="debitWindow-remarks" @input="remarksInput()" placeholder="限100字内"  v-model="remark" >
	                    </textarea>
	                </td>
	            </tr>
	        </table>
        </div>
        <div class="debitWindow-btnGroup">
            <button class="debitWindow-yesBtn" @click="debitSure()" :disabled="debitDisabled">申请审核</button>
        </div>
    </div>
</template>
<script>
    import {VERSION} from '@/util/constant.js'
    import {convertCurrency,selectToHighIndex} from '@/util/common.js'
    export default{
        props:['debitWindowShow'],
        data(){
            return{
                orderId:'', //订单号
                keyArea:'', //区域拆分键
                userId:'', //用户id
                balance:'', // 余额
                certificateName:'', // 用户名
                faccount:'',
                phone:'', //手机号
                amount:'', // 调整数
                uppercaseAmount:'', // 大写
                fundType:'', // 资金类型
                remark:'', //备注
                fundTypeList: [ // 资金类型下拉框
                    {
                        value:1,
                        label:'现金'
                    },{
                        value:2,
                        label:'承兑'
                    },{
                        value:3,
                        label:'电汇'
                    },{
                        value:4,
                        label:'积分'
                    },{
                        value:0,
                        label:'其他'
                    }
                ],
                debitDisabled:false
            }
        },
        methods: {
            phoneInput(){
                //输入手机号 查询
              this.phone = this.phone.replace(/\D/g,'');
              if(this.phone.length == 11){
                  //查询用户名
                  this.$http.post('/rechargeOrder/selectPhones.do',{
                      version:VERSION,
                      phone:this.phone
                  }).then(res => {
                      res = res.data;
                      if(res.success){
                          if(res.data[0]){
                              this.certificateName = res.data[0].fcertificateName;
                              this.userId = res.data[0].fuserId;
                              this.keyArea = res.data[0].fkeyArea;
                              this.faccount = res.data[0].faccount;
                          }else{
                              this.certificateName = '';
                              this.userId = '';
                              this.keyArea = '';
                              this.faccount = '';
                          }
                          //查询余额
                          this.$http.post('/balance/getBalance.do',{
                              userId:this.userId,
                              keyArea:this.keyArea,
                              version:VERSION
                          }).then(res => {
                              res = res.data;
                              if(res.success){
                                  this.balance = res.data.famount;
                              }else{
                                  this.balance = '';
                              }
                          })
                      }
                  })
              }else{
                  this.certificateName = '';
                  this.userId = '';
                  this.keyArea = '';
                  this.balance = '';
                  this.faccount = '';
              }
            },
            //关闭窗口
            debitWindowClose(){
                this.$emit('debitWindowClose');
                Object.assign(this.$data, this.$options.data());
            },
            //备注输入
            remarksInput(){
                this.remark = this.remark.replace(/\s/g,'');
            },
            //转大写
            uppercase(){
                this.amount = this.amount.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
                this.amount = this.amount.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的
                this.amount = this.amount.replace(".","$#$").replace(/\./g,"").replace("$#$",".");
                this.amount = this.amount.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数
                if(this.amount.indexOf(".")< 0 && this.amount !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                    this.amount= parseFloat(this.amount);
                }
                if(parseFloat(this.amount)> 10000000){
                    this.amount = 10000000;
                }
                this.uppercaseAmount = convertCurrency(this.amount)
            },
            //保存
            debitSure(){
                if(this.phone === '' || this.amount === '' || this.fundType === ''){
                    this.msg('填写完整!','error');
                    return;
                }
                if(this.phone.length != 11){
                    this.msg('手机号必须11位!','error');
                    return;
                }
                if((this.certificateName === ''|| this.certificateName == undefined)
                    &&(this.faccount === '' || this.faccount == undefined)){
                    this.msg('用户未认证!','error');
                    return;
                }
                if(this.remark.length > 100){
                    this.msg('备注不大于100字!','error');
                    return;
                }
                if(parseFloat(this.amount)>10000000){
                    this.msg('金额小于10000000!','error');
                    return;
                }
                if(parseFloat(this.amount) == 0){
                    this.msg('金额不能为零!','error');
                    return;
                }
                if(parseFloat(this.amount) > parseFloat(this.balance)){
                    this.msg('金额需≤余额!','error');
                    return;
                }
                this.debitDisabled = true;
                this.$http.post('/rechargeOrder/save.do',this.saveData()).then(res => {
                    res = res.data;
                    if(res.success){
                        this.$emit('debitSure');
                        Object.assign(this.$data, this.$options.data());
                    }else{
                        this.msg('操作失败!','error');
                    }
                    this.debitDisabled = false;
                })
            },
            saveData(){
                return {
                    operator:this.$store.getters.doneAdmin.uname, //操作人
                    orderId:this.orderId, //订单号
                    keyArea:this.keyArea, //区域拆分键
                    status:0, //状态
                    orderType:2, // 类别
                    userId:this.userId, //用户id
                    phone:this.phone, // 手机
                    amount:parseFloat(`-${this.amount}`), // 金额
                    uppercaseAmount: this.uppercaseAmount == '' ? null : this.uppercaseAmount, //大写金额
                    fundType:this.fundType, //资金类型
                    remark: this.remark==''?null:this.remark // 备注
                }
            },
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            visible(a){
               if(a){
                   selectToHighIndex();
               }
            }
        }
    }
</script>
<style>
    .debitWindow-warp{
        width:460px;
        height: 530px;
        background:#fff;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        border-radius: 4px;
        z-index: 9000;
    }
    .debitWindow-table-warp{
       padding-left:50px;
    }
    .debitWindow-table{
        display: block;
    }
    .debitWindow-table tr td:nth-child(1){
        padding-right: 20px;
    }
     .debitWindow-red{
        color:red;
    }
    .debitWindow-table tr:nth-child(1),
    .debitWindow-table tr:nth-child(2),
    .debitWindow-table tr:nth-child(3){
        height: 45px;
    }
    .debitWindow-table tr:nth-child(4),
    .debitWindow-table tr:nth-child(5),
     .debitWindow-table tr:nth-child(6){
        height: 52px;
    }
    .debitWindow-titleWarp{
        overflow: hidden;
    }
    .debitWindow-warp h3{
        font-size: 16px;
        height: 50px;
        line-height: 50px;
        padding-left: 48px;
        margin-bottom: 25px;
        box-shadow:0px 6px 10px #efefef;
    }
    .debitWindow-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .debitWindow-btnGroup{
        margin-top: 20px;
        text-align: center;
    }
    .debitWindow-btnGroup button{
        width:80px;
        height: 36px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        cursor:pointer;
        outline: none;
    }
    .debitWindow-adjustText{
        width: 280px;
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    .debitWindow-table .el-input__inner{
        height: 34px;
        line-height: 34px;
    }
    .debitWindow-table .el-select{
        width: 100%;
    }
    .debitWindow-notice{
        line-height: 26px;
        padding-left: 110px;
        color:red;
        background: url("../../../../../../static/images/publicImages/notice.png") no-repeat 90px 8px;
    }
    .debitWindow-remarks{
        height: 56px;
        display: block;
        resize: vertical;
        padding: 5px 7px;
        line-height: 1.5;
        width: 100%;
        color: #1f2d3d;
        border: 1px solid #bfcbd9;
        border-radius: 4px;
        width:264px;
        resize: none;
    }
    .debitWindow-adjustWarp{
        position: relative;
    }
    .debitWindow-adjustWarp span{
        font-size: 14px;
        position:absolute;
        left:7px;
        top:15px;
    }
    .debitWindow-adjustWarp input{
        padding-left: 12px;
    }
</style>



