<template>
    <div id="tag">
        <tHeader>标签管理</tHeader>
        <div class="main">
            <div class="content">
                <div class="header">
                    <el-button type="primary" style="width: 110px;" @click="addLabel"><i class="fa fa-plus-circle" style="margin-right:10px; font-size: 16px;"></i>添加标签</el-button>
                    <el-button type="primary" style="width: 110px;" @click="save"><i class="fa fa-floppy-o" style="margin-right: 10px;font-size: 16px;"></i>保存</el-button>
                </div>
                <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                    <el-table-column
                        type="index"
                        label="序号"
                        align="center"
                        width="198">
                    </el-table-column>
                    <el-table-column
                        label="标签名称"
                        prop="labelName"
                        align="center"
                        width="522">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        label="操作"
                        width="195">
                        <template scope="scope">
                            <el-button type="text" size="small" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                            <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        components: {
            tHeader() {
                return import('@/components/public/tHeader.vue')
            }
        },
        data() {
            return {
                tableData: []
            }
        },
        created() {
            this.$http.post('/productController/initLabel.do').then( res => {
                if(res.data.success) {
                    this.tableData = res.data.data.labelList
                }
                else {
                    this.$message({
                        message: '数据加载失败',
                        duration: 1000,
                        type: 'error'
                    });
                }
            }).catch( error => {
                this.$message({
                    message: '操作失败',
                    duration: 1000,
                    type: 'error'
                });
                console.log(error)
            })
        },
        methods: {
            addLabel() {
                this.$prompt('请输入值,最多6个字,开头结尾不能为空格', '新增', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValidator: (value) => {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 6) {
                            return '不能超过6个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                        for(let item of this.tableData) {
                            if(value === item.labelName) {
                                return '名称不能重复'
                            }
                        }
                    }
                }).then(({ value }) => {
                    let o = {
                        id: '',
                        labelName: value,
                        order: 1,
                        auth: true
                    }
                    this.tableData.unshift(o)
                }).catch(() => {});
            },
            handleDelete(index, row) {
                this.$confirm('删除的标签将不能恢复，请确认是否删除？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(row.auth) {
                        this.tableData.splice(index, 1)
                        this.$message({
                            message: '删除成功',
                            duration: 1000,
                            type: 'success'
                        });
                    }
                    else {
                        this.$http({
                            url: '/productController/delLabel.do',
                            method: 'post',
                            params: {
                                id: row.id,
                            }
                        }).then( res => {
                            if(res.data.success) {
                                this.tableData.splice(index, 1)
                                this.$message({
                                    message: '删除成功',
                                    duration: 1000,
                                    type: 'success'
                                });
                            }
                            else {
                                this.$message({
                                    message: '此标签正被使用，请确认不被使用后再进行删除',
                                    duration: 1000,
                                    type: 'info'
                                });
                            }
                        }).catch( error => {
                            this.$message({
                                message: '操作失败',
                                duration: 1000,
                                type: 'error'
                            });
                            console.log(error)
                        })
                    }

                }).catch(() => {

                });
            },
            handleEdit(index, row) {
                this.$prompt('请输入值,最多6个字,开头结尾不能为空格', '编辑', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue: row.labelName,
                    inputValidator: (value) => {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 6) {
                            return '不能超过6个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                        for(let i=0; i<this.tableData.length; i++) {
                            if(i == index) {
                                continue
                            }
                            else {
                                if(value === this.tableData[i].labelName) {
                                    return '名称不能重复'
                                }
                            }
                        }
                    }
                }).then(({ value }) => {
                    row.labelName = value
                }).catch(() => {

                });
            },
            save() {
                this.$confirm('是否确认保存', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.lastLabelList()
                    this.$http.post('productController/saveLabel.do', this.tableData).then( res => {
                        if(res.data.success) {
                            this.tableData = res.data.data
                            this.$message({
                                message: '保存成功',
                                duration: 1000,
                                type: 'success'
                            });
                        }
                        else {
                            this.$message({
                                message: '保存失败',
                                duration: 1000,
                                type: 'info'
                            });
                        }
                    }).catch( error => {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })
                }).catch(() => {

                });
            },
            lastLabelList() {
                this.tableData.forEach(function (element, index) {
                    element.order = index + 1
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    #tag {
        padding: 30px 30px 44px;
        .main {
            background: #fff;
            border-width: 0 1px 2px 1px;
            border-style: solid;
            border-color: #ededed;
            border-bottom-color: #e2e2e2;
            border-radius: 0 0 6px 6px;
            padding-bottom: 179px;
            .content {
                width: 918px;
                margin: 0 auto;
                .header {
                    padding-top: 14px;
                    height: 50px;
                    text-align: right;
                }
            }
        }
    }
</style>




