<template>
    <div id="userManage-warp">
        <div class="userManage-warp-padding">
            <div class="useManage-radius">
                <tHeader>用户管理</tHeader>
                <div class="userManage-tableQuery-padding">
                    <!--查询start-->
                    <ul class="clearfix userManage-Query">
                        <li>
                            账号状态：
                            <el-select   v-model="userManageQuery.mainUser.feffect" placeholder="请选择" clearable>
                                <el-option v-for="item in accountStateOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </li>
                        <li>
                            认证状态：
                            <el-select  v-model="userManageQuery.mainUser.fcertification" placeholder="请选择" clearable>
                                <el-option v-for="item in authentStateOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </li>
                        <li class="userManage-input" :class="{telError:telError}">
                            手机号码：
                            <input  v-model="userManageQuery.mainUser.fphone"  class="userManage-tel" @blur="telBlur()" @input="telInput()"></input>
                            <span>手机号位数最多11位数</span>
                        </li>
                        <li>
                            <el-button type="primary" icon="search" @click="query">搜索</el-button>
                        </li>
                    </ul>
                    <!--查询end-->
                    <!--表格start-->
                    <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  :row-class-name="tableRowClassName" height="700">
                        <el-table-column  label="序号" width="70" align="center">
                            <template scope="scope">
                                <span>{{scope.$index + userManageQuery.pageSize*userManageQuery.pageNo+1}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="faccount" label="用户名" align="center">
                        </el-table-column>
                        <el-table-column property="fphone" label="手机号"  align="center">
                        </el-table-column>
                        <el-table-column label="是否启用" align="center">
                            <template scope="scope">
                                <span  v-show="scope.row.feffect == 0" class="userManage-disableBtn" >禁用</span>
                                <span  v-show="scope.row.feffect == 1" class="userManage-enableBtn">启用</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fcreateTime" label="创建时间" align="center" width="145">
                        </el-table-column>
                        <el-table-column  label="认证类型" align="center" >
                            <template scope="scope">
                                <!--<span  v-show="scope.row.fcertificationType == 0" >未认证</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 10" >个人认证认证中</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 11" >个人认证已认证</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 12" >个人认证已驳回</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 20" >企业认证认证中</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 21" >企业认证已认证</span>-->
                                <!--<span  v-show="scope.row.fcertificationType == 22" >企业认证已驳回</span>-->
                                <!--10 和 12 是个人认证未通过的状态，20  22  是企业认证未通过的状态-->
                                <span  v-show="scope.row.fcertificationType == 0" >/</span>
                                <span  v-show="scope.row.fcertificationType == 10" >/</span>
                                <span  v-show="scope.row.fcertificationType == 11" >个人</span>
                                <span  v-show="scope.row.fcertificationType == 12" >/</span>
                                <span  v-show="scope.row.fcertificationType == 23" >个人</span>
                                <span  v-show="scope.row.fcertificationType == 25" >个人</span>
                                <span  v-show="scope.row.fcertificationType == 20" >/</span>
                                <span  v-show="scope.row.fcertificationType == 21" >企业</span>
                                <span  v-show="scope.row.fcertificationType == 22" >/</span>
                                <span  v-show="scope.row.fcertificationType == 26" >企业</span>
                                <span  v-show="scope.row.fcertificationType == 27" >企业</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fcertificationName" label="认证名称" align="center">
                        </el-table-column>
                        <!--<el-table-column property="fbalance" label="余额" align="center" >
                        </el-table-column>
                        <el-table-column property="fcredit" label="积分" align="center" >
                        </el-table-column>-->
                        <el-table-column label="操作" align="center" >
                            <el-table-column  label="查询详情" align="center" width="80">
                                <template scope="scope">
                                    <span class="userManage-seeBtn" @click="seeInfo(scope.$index,scope.row)">查看</span>
                                </template>
                            </el-table-column>
                            <el-table-column  label="账号启用/禁用" width="100" align="center">
                                <template scope="scope">
                                    <span class="userManage-enableBtn" v-show="scope.row.feffect == 0" @click="accountDisable(scope.$index,scope.row)">启用</span>
                                    <span class="userManage-disableBtn" v-show="scope.row.feffect == 1" @click="accountEnable(scope.$index,scope.row)">禁用</span>
                                </template>
                            </el-table-column>
                            <el-table-column  label="重置密码" align="center"  width="80">
                                <template scope="scope">
                                    <span class="userManage-resetBtn" @click="resetPwd(scope.$index,scope.row)">密码重置</span>
                                </template>
                            </el-table-column>
                        </el-table-column>
                    </el-table>
                    <!--表格end-->
                </div>
            </div>
            <!--重置窗口start-->
            <div class="userManage-resetWarp" v-show="resetWarpShow">
                <h3>
                    密码重置
                </h3>
                <div class="userManage-resetBtnWarp">
                    <div>
                        重置登录密码<button v-if="iseffect" :class="{'userManage-disabledBtn':!reset.resetLoginBtn}" @click="resetLoginPwd()" :disabled="!reset.resetLoginBtn">{{reset.resetLoginBtn?reset.loginContent:reset.loginCountDown+reset.seconds}}</button>
                        <button v-if="!iseffect" class="userManage-disabledBtn" >重置</button>
                    </div>
                    <div>
                        重置支付密码<button v-if="iseffect" :class="{'userManage-disabledBtn':!reset.resetPayBtn}" @click="resetPayPwd()" :disabled="!reset.resetPayBtn">{{reset.resetPayBtn?reset.payContent:reset.payCountDown+reset.seconds}}</button>
                        <button v-if="!iseffect" class="userManage-disabledBtn" >重置</button>
                    </div>
                </div>
                <div class="userManage-resetWarp-closeBtn" @click="closeReset"></div>
            </div>
            <!--重置窗口end-->
            <!--查看信息start-->
            <userManageSee :userbaseinfo="userBaseInfo" :otherInfo="otherInfo":seeData="seeData" :contentShow="contentType" :infoShow="infoShow" @closeInfo="closeInfo()" :topdata="topData" ></userManageSee>
            <!--查看信息end-->
        </div>
        <div class="userManage-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange" :current-page="userManageQuery.pageNo + 1"
                           :page-size="userManageQuery.pageSize"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
        <div class="userManage-masker" v-show="masker"></div>
        <!--提示窗口start-->
        <userManageWindow :info="infoMsg" @windowClose="windowClose()" :windowShow="windowShow" @close="windowShow = false" @sure="sure()"></userManageWindow>
        <!--提示窗口end-->
    </div>
</template>

<script>
    import userManageWindow from  './userManage-window.vue'
    import userManageSee from './userManage-see.vue'
    import tHeader from '../../../public/tHeader.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        created(){
            // this.masker = true;
            /*this.tableData = this.paging(0,this.size);*/
            this.listInit(true);
        },
        components:{
            tHeader,
            userManageWindow,
            userManageSee
        },
        data() {
            return {
                /*查询条件*/
                userManageQuery: {
                    mainUser:{
                        feffect: '',
                        fcertification: '',
                        fphone:''
                    },
                    pageSize:15,
                    pageNo:0,
                    version:VERSION
                },
                total:0,
                queryJson:{

                },
                /*下拉框的值*/
                accountStateOptions: [
                    {
                        value:'0',
                        label:'禁用'
                    },{
                        value:'1',
                        label:'启用'
                    }
                ],
                /*
                 认证状态
                 * */
                authentStateOptions: [
                    {
                        value:0,
                        label:'/'
                    },{
                        value:11,
                        label:'个人'
                    },{
                        value:21,
                        label:'企业'
                    }
                ],

                /*
                 表里数据
                 */
                tableData: [],
                //查看类型：
                contentType:-999,
                //当前操作的id
                currentId:-999,
                currentType:-999,
                currentIndex:-999,
                //一页显示数据
                //重置窗口
                resetWarpShow:false,
                //电话号码
                currentFphone:'', //当前查看的手机
                telError:false,
                telErrorTimer:null,
                //提示弹窗的状态
                windowShow:false,
                infoMsg:'',
                //重置信息
                reset:{
                    loginContent:'重置',
                    payContent:'重置',
                    loginCountDown:300,
                    payCountDown:300,
                    seconds:'s',
                    resetLoginBtn:true,
                    resetPayBtn:true,
                    loginTimer:null,
                    payTimer:null
                },
                //遮罩层
                masker:false,
                //详细信息
                infoShow:false,
                //查看数据
                seeData:{}, // 查看信息
                otherInfo:{}, // 其他信息
                topData:{}, //头部信息
                userBaseInfo:{}, //基础信息
                iseffect:true //是否禁用
            }
        },
        methods:{
            /*
             查询
             2017年7月5日11:22:49
             */
            query(){
                if(this.userManageQuery.mainUser.fphone.length > 11){
                    this.telError = true;
                    clearInterval(this.telErrorTimer);
                    var _this  = this;
                    this.telErrorTimer = setTimeout(function(){
                        _this.telError = false;
                    },3000)
                    return;
                }else{
                    this.telError = false;
                }
                this.userManageQuery.pageNo = 0;
                this.listInit(true);
            },
            /*
             表格行不同颜色
             2017年6月26日15:50:22
             */
            tableRowClassName(row, index){
                if (index%2 == 1) {
                    return 'gray-row';
                }
                return '';
            },
            /*
             表格重置按钮
             2017年6月22日14:01:03
             */
            resetPwd(index,row){
                this.currentId = row.fuserId;
                this.currentFphone = row.fphone;
                this.resetWarpShow = true;
                this.masker = true;
                if(row.feffect == 1){
                    this.iseffect = true;
                }else{
                    this.iseffect = false;
                }
            },
            /*
             关闭重置窗口
             2017年6月22日14:00:17
             */
            closeReset(){
                this.resetWarpShow = false;
                this.masker = false;
                /*clearInterval(this.reset.loginTimer);
                 this.reset.resetLoginBtn = true;
                 this.reset.loginCountDown =300;
                 clearInterval(this.reset.payTimer);
                 this.reset.payCountDown =300;
                 this.reset.resetPayBtn = true;*/
            },
            /*
             关闭详细信息
             2017年6月22日15:43:30
             */
            closeInfo(){
                this.infoShow = false;
                this.masker = false;
                this.seeData  = {};
                this.userBaseInfo = {};
                this.otherInfo  = {};
                this.topData = {};
            },
            /*
             登录重置
             2017年6月22日14:00:17
             */
            resetLoginPwd(){
                this.reset.resetLoginBtn = false;
                var _this = this;
                this.reset.loginTimer=setInterval(function(){
                    if(_this.reset.loginCountDown <= 1){
                        clearInterval(_this.reset.loginTimer);
                        _this.reset.resetLoginBtn = true;
                        _this.reset.loginCountDown =300;
                    }
                    _this.reset.loginCountDown--;
                },1000)
                this.$http.post('/outerUser/remakePasswd.do',{
                    version:VERSION, // 版本号
                    passwordType:'password', // 识别什么密码类型
                    userId:this.currentId, // 用户id
                    fphone:this.currentFphone // 手机号
                }).then(res => {
                    var res = res.data;
                    if(res.success){
                        _this.msg('恭喜亲爱哒，登录密码重置成功！','success');
                    }else{
                        clearInterval(this.reset.loginTimer);
                        this.reset.resetLoginBtn = true;
                        this.reset.loginCountDown =300;
                        _this.msg('对不起亲爱哒，登录密码重置失败！','error');
                    }
                })
            },
            /*
             支付密码重置
             2017年6月22日14:10:41
             */
            resetPayPwd(){
                this.reset.resetPayBtn = false;
                var _this = this;
                this.reset.payTimer=setInterval(function(){
                    _this.reset.payCountDown--;
                    if(_this.reset.payCountDown <= 1){
                        clearInterval(_this.reset.payTimer);
                        _this.reset.payCountDown = 300;
                        _this.reset.resetPayBtn = true;
                    }
                },1000)
                this.$http.post('/outerUser/remakePasswd.do',{
                    version:VERSION, // 版本号
                    passwordType:'payPassword', // 识别什么类型的密码
                    userId:this.currentId, // 当前的id
                    fphone:this.currentFphone // 用户手机
                }).then(res => {
                    var res = res.data;
                    if(res.success){
                        _this.msg('恭喜亲爱哒，支付密码重置成功！','success');
                    }else{
                        _this.msg('对不起亲爱哒，支付密码重置失败！','error');
                        clearInterval(this.reset.payTimer);
                        this.reset.payCountDown =300;
                        this.reset.resetPayBtn = true;
                    }
                })
            },
            /*
             查看详细信息
             2017年6月22日15:45:58
             */
            seeInfo(index,row){
                this.$http.post('/outerUser/getOuterDetailInfo.do',{
                    userId:row.fuserId, // 用户id
                    version:VERSION //版本号
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.infoShow = true;
                        this.masker = true;
                        // 判断是否为空
                        if(JSON.stringify(res.data) != '{}'){
                            if(res.data.certificationInfo){
                                // 判断是不是个人用户
                              this.seeData = res.data.certificationInfo;
                            }
                            if(res.data.userBaseInfo){
                                // 基础信息里面积分余额需要这个字段
                                this.userBaseInfo = res.data.userBaseInfo;
                            }
                            if(res.data.otherInfo){
                                this.otherInfo = res.data.otherInfo; // 其他信息
                            }
                        }
                        this.topData = row;
                        if(row.fcertificationType == 0 ||row.fcertificationType == 10 ||
                            row.fcertificationType == 12 || row.fcertificationType == 20 || row.fcertificationType == 22){
                            this.contentType = 3; //<!--未认证-->
                        }else if(row.fcertificationType == 11 || row.fcertificationType == 23 || row.fcertificationType == 25 ){
                            this.contentType = 1; //<!-- 个人-->
                        }else if(row.fcertificationType == 21 || row.fcertificationType == 26 || row.fcertificationType == 27){
                            this.contentType = 2;  //<!-- 企业-->
                        }
                    }else{
                        this.msg('查看失败！','error')
                    }
                })
            },
            telInput(){
                this.userManageQuery.mainUser.fphone = this.userManageQuery.mainUser.fphone.replace(/\D/g,'');
            },
            /*失去焦点判断*/
            telBlur(){
                if(this.userManageQuery.mainUser.fphone.length > 11){
                    this.telError = true;
                    clearInterval(this.telErrorTimer);
                    var _this  = this;
                    this.telErrorTimer = setTimeout(function(){
                        _this.telError = false;
                    },3000)
                }else{
                    this.telError = false;
                }
            },
            /*账号禁用*/
            /*
             * index：行的索引
             * row 行数据集合
             * return
             * */
            accountInit(index,row){
                this.windowShow = true;
                this.currentId = row.fuserId;
                this.currentIndex = index;
                this.currentType = row.feffect;
                this.masker = true;
            },
            accountDisable(index,row){
                this.infoMsg='是否要启用！';
                this.accountInit(index,row);
            },
            /*账号启用*/
            accountEnable(index,row){
                this.infoMsg='是否要禁用！';
                this.accountInit(index,row);
            },
            // 子组件的确定按钮
            sure(){
                // 禁用启用
                this.$http.post(this.currentType == 0 ?'/outerUser/enableAccount.do':'/outerUser/disableAccount.do',{
                    version:VERSION,userId:this.currentId
                }).then(res => {
                        var res = res.data;
                        if(res.success){
                            if(this.tableData[this.currentIndex].feffect){
                                this.tableData[this.currentIndex].feffect = 0;
                                this.msg('恭喜亲爱哒，禁用成功！','success');
                            }else{
                                this.tableData[this.currentIndex].feffect = 1;
                                this.msg('恭喜亲爱哒，启用成功！','success');
                            }
                            this.windowShow = false;
                            this.masker = false;
                        }else{
                            this.msg('操作失败！','error');
                        }
                    })
            },
            //子组件关闭
            windowClose(){
                this.masker = false;
                this.windowShow = false;
            },
            /*
             * msg：消息信息
             * type icon的class名
             * return
             * */
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            //点击翻页
            handleCurrentChange(pageNum){
                // 页面从零开始 要减到1
                this.userManageQuery.pageNo = pageNum - 1;
                this.listInit(false);
            },
            handleSizeChange(size){
                this.userManageQuery.pageNo = 0;
                this.userManageQuery.pageSize = size;
                this.listInit(false);
            },
            listInit(bFalg){
                // 根据后台的要求空值要为 - 1 ； fuck
                if(bFalg){
                    this.queryJson = this.deepCopy(this.userManageQuery);
                }
                if(this.queryJson.mainUser.feffect === '' ){
                    this.queryJson.mainUser.feffect = -1;
                }else{
                    this.queryJson.mainUser.feffect = parseFloat(this.queryJson.mainUser.feffect);
                }
                if(this.queryJson.mainUser.fcertification === '' ){
                    this.queryJson.mainUser.fcertification = -1;
                }else{
                    this.queryJson.mainUser.fcertification = parseFloat(this.queryJson.mainUser.fcertification);
                }
                this.queryJson.pageNo = this.userManageQuery.pageNo;
                this.queryJson.pageSize = this.userManageQuery.pageSize;
                this.$http.post('/outerUser/loadLst.do',this.queryJson).then(res => {
                    var res = res.data;
                    if(res.success){
                        if(JSON.stringify(res.data) != '{}'){
                            this.tableData = res.data.result;
                            this.total = res.data.total;
                        }
                    }else{
                        this.msg('获取列表失败！','error');
                    }
                })
            },
            //对象的copy；
            deepCopy(obj){
                if(typeof obj != 'object'){
                    return obj;
                }
                var newobj = {};
                for( var attr in obj){
                    newobj[attr] = this.deepCopy(obj[attr]);
                }
                return newobj;
            }
        }
    }
</script>

<style>
    /*用户管理*/
    .userManage-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .useManage-radius{
        padding-bottom: 30px;
        background: #fff;
        border-radius:8px;
        overflow: hidden;
    }
    .userManage-tableQuery-padding{
        padding: 0 10px;
    }
    #userManage-warp{
        width: 100%;
        height:100%;
        padding-bottom:50px;
    }
    #userManage-warp .gray-row {
        background: #f4f5fb;
    }
    /*userManage 查询*/
    .userManage-Query {
        padding: 20px 0;
    }
    .userManage-Query li {
        float: left;
    }
    .userManage-Query .el-select {
        width: 150px;
    }
    .userManage-Query .el-input .el-input__inner,
    .userManage-Query .userManage-input .el-input__inner {
        height: 32px;
        font-size: 12px;
    }
    .telError input{
        border-color: red;
    }
    .telError{
        position: relative;
    }
    .userManage-Query .telError span{
        display: block;
        position: absolute;
        left: 70px;
        bottom:-22px;
        color: red;
    }
    .userManage-Query .userManage-input {
        width: 280px;
    }
    .userManage-input span{
        display: none;
    }
    .userManage-tel{
        border-radius: 4px;
        border: 1px solid #bfcbd9;
        box-sizing: border-box;
        color: #1f2d3d;
        font-size: inherit;
        height: 32px;
        line-height: 1;
        padding: 3px 10px;
    }
    .userManage-Query li:nth-child(1),
    .userManage-Query li:nth-child(2)
    {
        width: 240px;
    }
    .userManage-Query .userManage-input .el-input {
        width: 200px;
    }
    /*表格*/
    .el-table__body-wrapper,
    .el-table__header-wrapper {
        font-size: 12px;
    }
    #userManage-warp .userManage-seeBtn,
    #userManage-warp .userManage-resetBtn
    {
        color:#38adff;
    }
    #userManage-warp .el-table_1_column_10_column_11 .cell,
    #userManage-warp .el-table_1_column_10_column_12 .cell,
    #userManage-warp .el-table_1_column_10_column_13 .cell{
        font-weight: normal;
        color:#333;
    }
    #userManage-warp .userManage-disableBtn{
        color: red;
    }
    #userManage-warp .userManage-enableBtn{
        color: green;
    }
    #userManage-warp .userManage-seeBtn,
    #userManage-warp .userManage-disableBtn,
    #userManage-warp .userManage-resetBtn,
    #userManage-warp .userManage-enableBtn
    {
        cursor:pointer;
    }
    /*重置窗口*/
    .userManage-resetWarp{
        width:400px;
        height: 180px;
        padding: 20px 0 0 20px;
        background: #fff;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        z-index: 6010;
        border-radius: 4px;
    }
    .userManage-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 6000;
    }
    .userManage-resetWarp h3{
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 28px;
    }
    .userManage-resetWarp-closeBtn{
        position:absolute;
        width:26px;
        height: 26px;
        background: url(../../../../../static/images/publicImages/close.png) no-repeat 0 0;
        border-radius: 50%;
        right:-8px;
        top:-8px;
        cursor:pointer;
    }
    .userManage-resetWarp .userManage-resetBtnWarp div{
        padding-left: 102px;
    }
    .userManage-resetWarp button{
        width:80px;
        height: 34px;
        color:#fff;
        border: 0;
        background: #38adff;
        border-radius: 4px;
        margin:0 0 20px 20px;
        cursor:pointer;
    }
    .userManage-resetWarp .userManage-disabledBtn{
        background:#eef1f6;
        color:#afb1b5;
        outline: none;
    }
    /*
        分页
    */
    .userManage-pagination{
        text-align: center;
    }
    .userManage-pagination .el-pagination button,
    .userManage-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .userManage-pagination .el-pager li.btn-quicknext,
    .userManage-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .userManage-pagination .el-pagination .el-select .el-input input,
    .userManage-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .userManage-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }

    #userManage-warp .el-table .cell, .el-table th>div{
        padding-left: 0px;
        padding-right: 0px;
    }
</style>



