<template>
    <div class="zbtgReport-warp">
        <query-list @query="query" :timeType="timeType" :timeSearch="true"></query-list>
        <el-table border ref="singleTable" highlight-current-row :data="tableData" style="width: 100%"  height="700">
            <el-table-column property="phone" label="手机号" align="center">
            </el-table-column>
            <el-table-column property="customerName" label="认证名" align="center">
            </el-table-column>
            <el-table-column property="productName" label="产品名称" align="center">
            </el-table-column>
            <el-table-column property="orderId" label="订单号" align="center" width="200">
            </el-table-column>
            <el-table-column label="下单时间" align="center">
                <template scope="scope">
                    {{scope.row.createTime.substring(0,scope.row.createTime.length -2)}}
                </template>
            </el-table-column>
            <el-table-column property="fproductarea" label="出库面积(㎡)" align="center">
            </el-table-column>
            <el-table-column property="money" label="出库金额(元)" align="center">
            </el-table-column>
            <el-table-column property="refundGround" label="退款面积(㎡)" align="center">
            </el-table-column>
            <el-table-column  label="退款金额(元)" align="center">
                <template scope="scope">
                    <span  v-show="scope.row.status == 1" >
                        {{scope.row.exceptionRefundMoney==0?'':scope.row.exceptionRefundMoney}}
                    </span>
                    <span  v-show="scope.row.status == 2">
                        {{scope.row.money==0?'':scope.row.money}}
                    </span>
                </template>
            </el-table-column>
        </el-table>
        <div class="zbtgReport-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'下单时间',
                total:0,
                size:15,
                currentPage:0,
                tableData:[], // 列表容器
                startTime:'',
                endTime:'',
                userId:''
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.$http.post(`/reportForms/sellReportForms.do`,{
                    pageSize:this.size,
                    pageNo:this.currentPage,
                    userId:this.userId==''?null:this.userId,
                    startTime:this.startTime==''?null:this.startTime,
                    endTime:this.endTime==''?null:this.endTime,
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = res.data.total;
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                    }
                })
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.userId = queryJson.userId;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .zbtgReport-warp{
        position:relative;
    }
    .zbtgReport-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .zbtgReport-pagination .el-pagination button,
    .zbtgReport-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .zbtgReport-pagination .el-pager li.btn-quicknext,
    .zbtgReport-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .zbtgReport-pagination .el-pagination .el-select .el-input input,
    .zbtgReport-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .zbtgReport-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
</style>



