<template>
    <div id="rechargeManage-warp">
        <div class="rechargeManage-warp-padding">
            <div class="rechargeManage-radius">
                <tHeader>充值管理</tHeader>
                <div class="rechargeManage-content">
                    <ul class="rechargeManage-nav clearfix">
                        <li v-for="(item,index) in navList">
                            <span :class="{active:item.navState}"  @click="tab(index)">{{item.navContent}}</span>
                        </li>
                    </ul>
                    <div>
                        <recharge-manage-query @query="query"></recharge-manage-query>
                    </div>
                    <div class="rechargeManage-btn-warp">
                        <span @click="recharge">充值</span>
                        <span @click="debit">扣款</span>
                    </div>
                    <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  height="700">
                        <el-table-column  label="序号" width="70" align="center">
                            <template scope="scope">
                                <span>{{scope.$index + size*pageNo+1}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fphone" label="手机号" align="center" width="120">
                        </el-table-column>
                        <el-table-column property="customerName" label="认证名" align="center" width="120">>
                        </el-table-column>
                        <el-table-column label="状态" align="center" width="120">
                            <template scope="scope">
                                <span  v-show="scope.row.fstatus == 0" class="userManage-disableBtn">未审核</span>
                                <span  v-show="scope.row.fstatus == 1" class="userManage-disableBtn">已审核</span>
                                <span  v-show="scope.row.fstatus == 2" class="userManage-enableBtn">已驳回</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="forderType" label="类别" align="center" width="120">
                            <template scope="scope">
                                <span  v-show="scope.row.forderType == 1" class="userManage-disableBtn">充值</span>
                                <span  v-show="scope.row.forderType == 2" class="userManage-enableBtn">扣款</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="famount" label="金额(元)" align="center" width="120">
                        </el-table-column>
                        <el-table-column  label="资金类型" align="center" width="120">
                            <template scope="scope">
                                <span  v-show="scope.row.ffundType == 1" >现金</span>
                                <span  v-show="scope.row.ffundType == 2" >承兑</span>
                                <span  v-show="scope.row.ffundType == 3">电汇</span>
                                <span  v-show="scope.row.ffundType == 4" >积分</span>
                                <span  v-show="scope.row.ffundType == 0" >其他</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fremark" label="备注" align="center" width="250">
                        </el-table-column>
                        <el-table-column property="foperator" label="申请人" align="center" width="120">
                        </el-table-column>
                        <el-table-column  label="申请时间" align="center" width="180">
                            <template scope="scope">
                                <span v-if="scope.row.foperatingTime">{{scope.row.foperatingTime.substring(0,scope.row.foperatingTime.length -2)}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="fauditExplain" label="审核说明" align="center" width="180">
                        </el-table-column>
                        <el-table-column property="fauditor" label="审核人" align="center" width="120">
                        </el-table-column>
                        <el-table-column property="fauditTime" label="审核时间" align="center" width="180">
                            <template scope="scope">
                                <span v-if="scope.row.fauditTime">{{scope.row.fauditTime.substring(0,scope.row.fauditTime.length -2)}}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="rechargeManage-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false"
                           @current-change="handleCurrentChange"
                           :current-page="pageNo +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
        <debit-window @debitSure="debitSure" :debitWindowShow="debitWindowShow" @debitWindowClose="debitClose"></debit-window>
        <recharge-window @rechargeSure="rechargeSure" :rechargeWindowShow="rechargeWindowShow" @rechargeWindowClose="rechargeClose"></recharge-window>
        <div class="rechargeManage-masker" v-show="mask"></div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import debitWindow from './debitWindow.vue'
    import rechargeWindow from './rechargeWindow.vue'
    import rechargeManageQuery from './rechargeManageQuery.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        components:{
            tHeader,
            debitWindow,
            rechargeWindow,
            rechargeManageQuery
        },
        created(){
          this.listInit(false);
        },
        data(){
            return {
                //遮罩层
                mask:false,
                 //扣款的window
                debitWindowShow:false,
                //充值的window
                rechargeWindowShow:false,
                //导航的列表
                navList:[
                    {navContent:'已审核', navState:false},
                    {navContent:'未审核', navState:false},
                    {navContent:'已驳回', navState:false},
                    {navContent:'全部', navState:true}
                ],
                phone:'', //手机号
                endTime:'', // 结束时间
                startTime:'', //开始时间
                total:0, //总数
                size:15, //条数
                pageNo:0, // 当前页,
                orderType: '', //订单类别
                status: null, // 审核状态
                userId:'', //用户id
                tabIndex:3, // 导航的索引
                tableData:[] // 列表容器
            }
        },
        methods:{
            //充值打开
            recharge(){
                this.rechargeWindowShow = true;
                this.mask = true;
            },
             //充值关闭
            rechargeClose(){
                this.rechargeWindowShow = false;
                this.mask = false;
            },
            //充值确定
            rechargeSure(){
                this.rechargeWindowShow = false;
                this.mask = false;
                this.msg('保存成功!','success');
                this.listInit(true);
            },
            // 扣款保存
            debitSure(){
                this.debitWindowShow = false;
                this.mask = false;
                this.msg('保存成功!','success');
                this.listInit(true);
            },
            //扣款打开
            debit(){
                this.debitWindowShow = true;
                this.mask = true;
            },
            //扣款关闭
            debitClose(){
                this.debitWindowShow = false;
                this.mask = false;
            },
            // 一页多少条变化
            handleSizeChange(size){
                this.size = size;
                this.listInit(true);
            },
            //页码变化
            handleCurrentChange(page){
                this.pageNo = page - 1;
                this.listInit(false);
            },
            // 导航条变化
            tab(index){
                if(this.tabIndex == index){
                    return;
                }
                for(var i=0; i<this.navList.length; i++){
                    this.navList[i].navState = false;
                }
                this.navList[index].navState = true;
                switch (index) {
                    case 0:
                        this.status = 1;
                        break;
                    case 1:
                        this.status = 0;
                        break;
                    case 2:
                        this.status = 2;
                        break;
                    default:
                        this.status = null;
                        break;
                }
                this.tabIndex = index;
                this.listInit(true);
            },
            //查询
            query(queryJson){
                this.endTime = queryJson.endTime;
                this.startTime = queryJson.startTime;
                this.orderType = queryJson.orderType;
                this.phone = queryJson.phone;
                this.listInit(true);
            },
            // 查询的数据
            queryData(){
                return {
                    startTime:this.startTime == ''?null:this.startTime,
                    endTime:this.endTime == '' ?null:this.endTime,
                    orderType:this.orderType ==''?null:parseFloat(this.orderType),
                    status:this.status,
                    pageSize:this.size,
                    pageNo:this.pageNo,
                    version:VERSION,
                    phone:this.phone == ''?null:this.phone
                }
            },
            // 列表初始化
            listInit(sizechange){
                this.$http.post('/rechargeOrder/list.do',this.queryData()).then(res => {
                    res = res.data;
                    if(res.success){
                        this.tableData = res.data.list;
                        this.total = parseFloat(res.data.total);
                        if(sizechange){
                            this.pageNo = 0;
                        }
                    }
                })
            },
            // 消息
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            }
        }
    }
</script>

<style>
    #rechargeManage-warp{
        width: 100%;
        height:100%;
        padding-bottom:250px;
    }
    #rechargeManage-warp .el-table .cell,
    #rechargeManage-warp.el-table th>div{
        padding: 0px;
    }
    .rechargeManage-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .rechargeManage-radius{
        background: #fff;
        border-radius:8px;
        padding-bottom:100px;
    }
    .rechargeManage-content{
        padding:0px 10px;
    }
    .rechargeManage-nav{
        height:50px;
        font-size:16px;
        border-bottom:2px solid #b0b0b0;
        padding-left:10px;
    }
    .rechargeManage-nav li{
        float:left;
        height:50px;
        line-height:50px;
        padding:0px 22px;
    }
    .rechargeManage-nav li span{
        cursor: pointer;
    }
    .rechargeManage-nav li span.active{
        color:#38acff;
    }
    /*
        分页
    */
    .rechargeManage-pagination{
        text-align: center;
    }
    .rechargeManage-pagination .el-pagination button,
    .rechargeManage-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .rechargeManage-pagination .el-pager li.btn-quicknext,
    .rechargeManage-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .rechargeManage-pagination .el-pagination .el-select .el-input input,
    .rechargeManage-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .rechargeManage-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .rechargeManage-masker{
        position: fixed;
        left:0;
        top:0;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 7000;
    }
    .rechargeManage-btn-warp{
        padding:20px 0px;
    }
    .rechargeManage-btn-warp span{
        width:90px;
        height:30px ;
        text-align: center;
        cursor:pointer;
        line-height: 30px;
        display: inline-block;
        border-radius: 4px;
        margin: 0px 10px;
        background:#38acff;
        color:#fff;
    }
</style>



