/**
 * 基础设置
 */
import addressMagr from '@/components/common/basic/addressMagr.vue'
import Classification from '@/components/common/basic/classification'
import Businessmen from '@/components/common/basic/businessmen'
import Tag from '@/components/common/basic/tag'
import Specification from '@/components/common/basic/specification'

var basic = [
    { path:'basic', name: 'basic', redirect: { name: 'addressMagr' } },
    { path:'basic/addressMagr', name: 'addressMagr', component: addressMagr },
]

/**
 * 分类管理
 */
var classification = [
    { path: 'basic/classification', name: 'classification', component: Classification },
]
/**
 *商家管理
 */
var businessmen = [
    // { path: 'basic/businessmen', name: 'businessmen', component: Businessmen },
]
/**
 * 标签管理
 */
var tag = [
    { path: 'basic/tag', name: 'tag', component: Tag },
]
/**
 *规格管理
 */
var specification = [
    { path: 'basic/specification', name: 'specification', component: Specification },
]
export default basic.concat(classification, businessmen, tag, specification)



