<template>
    <div id="queryList-warp">
        <ul class="clearfix">
            <li v-if="timeSearch">
                {{timeType}} :
                <el-date-picker v-model="startTime" type="datetime" placeholder="开始日期"  format = "yyyy-MM-dd HH:mm:ss"  :picker-options="pickerStart">
                </el-date-picker>
                至
                <el-date-picker v-model="endTime" type="datetime" placeholder="结束日期"  format = "yyyy-MM-dd HH:mm:ss"  :picker-options="pickerEnd">
                </el-date-picker>
            </li>
            <li>
                <get-user-by-phone-or-name @getByPhone="getdata" @getByName="getdata" @clear="getdata"></get-user-by-phone-or-name>
            </li>
            <li><span class="queryList-query" @click="query()">查询</span></li>
            <li><span class="queryList-export">导出excel</span></li>
        </ul>
    </div>
</template>
<script>
    import { get3MonthBefor } from '@/util/common.js'
    import {VERSION} from '@/util/constant.js'
    import getUserByPhoneOrName from '../../../../public/getUserByPhoneOrName.vue'
    export default{
        components:{
            getUserByPhoneOrName
        },
        props:['timeType','timeSearch'],
        data(){
            return {
                pickerStart:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerEnd:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                endTime:'', // 结束时间
                startTime:'', //开始时间
                phone:'',  //用户手机号
                certificateName:'',
                userId:'' //
            }
        },
        methods:{
              //获取时间戳
            getTime(val){
                    return new Date(val).getTime();
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            useTime(value){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value);
                var year = time.getFullYear();
                var month = time.getMonth() + 1<10? '0'+ (time.getMonth() + 1):time.getMonth() + 1;
                var date = time.getDate() <10 ? '0'+ time.getDate():time.getDate();
                var hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours();
                var min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
                var sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                return `${year}-${month}-${date} ${hour}:${min}:${sec}`
            },
            //查询 向父组件传递当前输入的信息
            query(){
               if(this.getTime(this.endTime)<this.getTime(this.startTime)){
                    this.msg('对不起亲爱哒，结束日期必须大于开始日期！','error');
                    return;
                }
                if(this.phone.length > 11){
                    this.msg('手机号不能大于11位!','error');
                    return;
                }
                if(this.startTime=='' && this.endTime !=''){
                    this.msg('请填写开始时间再进行查询！','error');
                     return;
                }
                if(this.startTime !='' && this.endTime ==''){
                    this.msg('请填写结束时间再进行查询！','error');
                     return;
                }
                var queryJson ={
                    startTime:this.useTime(this.startTime),
                    endTime:this.useTime(this.endTime),
                    phone: this.phone,
                    certificateName:this.certificateName,
                    userId:this.userId
                };
                this.$emit('query', queryJson);
            },
            getdata(phone,certificateName,userId){
                this.phone = phone;
                this.certificateName = certificateName;
                this.userId = userId;
            }
        }
    }
</script>
<style>
    #queryList-warp ul li{
        float:left;
        margin-right:10px;
        margin-bottom: 10px;
    }
    #queryList-warp ul li:nth-child(3),
    #queryList-warp ul li:nth-child(4){
        margin-right:10px;
    }
    #queryList-warp >ul{
        padding:20px 0;
    }
    .queryList-query,
    .queryList-export{
        height:38px;
        border-radius:4px;
        cursor:pointer;
        color:#fff;
        display:inline-block;
        line-height:38px;
        font-size:14px;
    }
    .queryList-query{
        background: url(../../../../../../static/images/publicImages/search.png) no-repeat 26px 8px #38acff;
        padding-left:56px;
        width:56px;
    }
    .queryList-export{
        background: url(../../../../../../static/images/publicImages/export.png) no-repeat 10px 8px #38acff;
        padding-left:37px;
        width:75px;
    }
</style>



