<template>
    <div id="classification">
        <tHeader>分类管理</tHeader>
        <div class="list_class clearfix">
            <addClassList  :name="'one_list'" titleName="新增分类" :classifyList="one_list" :parameter="one_parameter" @nextList="oneNextList" @addClass="addOneClass" @removeClass="removeOneClass"></addClassList>
            <addClassList v-if="one_parameter !== ''"  :name="'two_list'" titleName="新增子分类" :classifyList="two_list" :parameter="two_parameter" @nextList="twoNextList" @addClass="addTwoClass" @removeClass="removeTwoClass"></addClassList>
            <div class="child_box fl" v-if="one_parameter !== ''">
                <ul class="child_list">
                    <li v-for="(item, index) in three_list">
                        <div class="single_sort" @click="editThreeClass(item)">
                            {{ item.catName }}
                            <i class="el-icon-circle-cross" @click.stop="removeThreeClass(item, index)"></i>
                        </div>
                    </li>
                    <li>
                        <div class="single_sort last_single_sort" @click="addThreeClass">
                            <i class="el-icon-plus"></i>
                            &nbsp;
                            <span>新增三级分类</span>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="foot">
                <el-button type="primary" @click="save" style="width: 110px;">
                    <i class="fa fa-floppy-o" style="margin-right: 10px; font-size: 16px;"></i>保存
                </el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                one_list: [],
                two_list: [],
                three_list: [],
                one_parameter: '',
                two_parameter: '',
            }
        },
        created() {
            this.$http.post('/productController/initClassify.do').then( res => {
                if(res.data.success) {
                    this.one_list = res.data.data.classifyList
                    this.defaultFirst()  //默认显示
                }
            }).catch( error => {
                console.log(error)
            })

        },
        components: {
            tHeader() {
                return import('@/components/public/tHeader.vue')
            },
            addClassList() {
                return import('./AddClass.vue')
            }
        },
        methods: {
            //1,2级类操作
            addOneClass() {
                if(this.one_list.length >= 15) {
                    this.$message({
                        message: '最多只能添加到15个',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.$prompt('请输入值,最多10个字,开头结尾不能为空格', '新增', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValidator(value) {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 10) {
                            return '不能超过10个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                    }
                }).then(({ value }) => {
                    this.$http({
                        url: '/numberController/getNumber.do',
                        method: 'post',
                        params: {
                            count: 3,
                        }
                    }).then( res => {
                        if(res.data.success) {
                            let dict = {
                                "catId": "" ,
                                "children":  [
                                    {
                                        "catId": "" ,
                                        "children":  [
                                            {
                                                "catId": "" ,
                                                "ispCat": 0 ,
                                                "catName": "请输入内容" ,
                                                "pCatId": "" ,
                                                "catLevel": 3 ,
                                                "order": 1
                                            }
                                        ],
                                        "ispCat": 1 ,
                                        "catName": "请输入内容" ,
                                        "pCatId": "" ,
                                        "catLevel": 2 ,
                                        "order": 1
                                    }
                                ],
                                "ispCat": 1 ,       //是否是父级
                                "catName": "请输入内容" ,
                                "pCatId": "0" ,
                                "catLevel": 1 ,
                                "order": 1
                            }
                            let list = res.data.data.numbers
                            list[0] = list[0] + new Date().getTime()
                            list[1] = list[1] + new Date().getTime()
                            list[2] = list[2] + new Date().getTime()
                            //1级
                            dict.catId = list[0]
                            dict.catName = value
                            dict.auth = true        //为了对自己添加的和原来就有数据删除时做判断，添加的点删除不请求接口
                            //2级
                            dict.children[0].catId = list[1]
                            dict.children[0].pCatId = list[0]
                            dict.children[0].catName = value
                            dict.children[0].auth = true
                            //3级
                            dict.children[0].children[0].catId = list[2]
                            dict.children[0].children[0].pCatId = list[1]
                            dict.children[0].children[0].catName = value
                            dict.children[0].children[0].auth = true

                            this.one_list.unshift(dict)
                        }
                        else {
                            this.$message.error('添加失败');
                        }
                    }).catch( error => {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })

                }).catch(() => {});
            },
            addTwoClass() {
                if(this.two_list.length >= 15) {
                    this.$message({
                        message: '最多只能添加到15个',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.$prompt('请输入值,最多10个字,开头结尾不能为空格', '新增', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValidator(value) {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 10) {
                            return '不能超过10个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                    }
                }).then(({ value }) => {
                    this.$http({
                        url: '/numberController/getNumber.do',
                        method: 'post',
                        params: {
                            count: 2,
                        }
                    }).then( res => {
                        if(res.data.success) {
                            let dict = {
                                "catId": "" ,
                                "children":  [
                                    {
                                        "catId": "" ,
                                        "ispCat": 0 ,
                                        "catName": "" ,
                                        "pCatId": "" ,
                                        "catLevel": 3 ,
                                        "order": 1
                                    }
                                ],
                                "ispCat": 1 ,
                                "catName": "" ,
                                "pCatId": "" ,
                                "catLevel": 2 ,
                                "order": 1
                            }
                            let list = res.data.data.numbers
                            list[0] = list[0] + new Date().getTime()
                            list[1] = list[1] + new Date().getTime()
                            //2级
                            dict.catId = list[0]
                            dict.pCatId = this.two_list[0].pCatId
                            dict.catName = value
                            dict.auth = true
                            //3级
                            dict.children[0].catId = list[1]
                            dict.children[0].pCatId = list[0]
                            dict.children[0].catName = value
                            dict.children[0].auth = true
                            this.two_list.unshift(dict)
                        }
                    }).catch( error => {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })

                }).catch(() => {});
            },
            oneNextList(item) {
                this.two_list = item.children
                this.three_list = item.children[0].children
                this.one_parameter = item.catId
                this.two_parameter = item.children[0].catId
            },
            twoNextList(item) {
                this.two_parameter = item.catId
                this.three_list = item.children
            },
            removeOneClass(dict) {
                if(this.one_list.length === 0) {
                    return false
                }
                this.one_list = dict.items
                this.two_list = dict.items[0].children        //删除后将第一个赋值
                this.three_list = dict.items[0].children[0].children
                this.one_parameter = dict.items[0].catId
                this.two_parameter = dict.items[0].children[0].catId
            },
            removeTwoClass(dict) {
                if(dict.items.length) {
                    this.three_list = dict.items[0].children
                    this.two_parameter = dict.items[0].catId
                }
                else {
                    for(let i=0, len=this.one_list.length; i<len; i++) {
                        if(this.one_list[i].catId == dict.item.pCatId) {
                            this.one_list.splice(i, 1)
                            break
                        }
                    }
                    this.defaultFirst()
                }
            },
            //3级类操作
            addThreeClass() {
                if(this.three_list.length >= 15) {
                    this.$message({
                        message: '最多只能添加到15个',
                        duration: 1000,
                        type: 'warning'
                    });
                    return
                }
                this.$prompt('请输入值,最多10个字,开头结尾不能为空格', '新增', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValidator(value) {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 10) {
                            return '不能超过10个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                    }
                }).then(({ value }) => {
                    this.$http({
                        url: '/numberController/getNumber.do',
                        method: 'post',
                        params: {
                            count: 1,
                        }
                    }).then( res => {
                        if(res.data.success) {
                            let dict = {
                                "catId": "" ,
                                "ispCat": 0 ,
                                "catName": "" ,
                                "pCatId": "" ,
                                "catLevel": 3 ,
                                "order": 1
                            }
                            let list = res.data.data.numbers
                            list[0] = list[0] + new Date().getTime()
                            dict.catId = list[0]
                            dict.pCatId = this.three_list[0].pCatId
                            dict.catName = value
                            dict.auth = true
                            this.three_list.unshift(dict)

                        }
                    }).catch( error =>  {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })

                }).catch(() => {
                });
            },
            editThreeClass(item) {
                this.$prompt('请输入值,最多10个字,开头结尾不能为空格', '编辑', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    inputValue: item.catName,
                    inputValidator(value) {
                        let bl = /(^\s)|(\s$)/.test(value)
                        if(!value.length) {
                            return '不能为空'
                        }
                        else if(value.length > 10) {
                            return '不能超过10个字'
                        }
                        else if(bl) {
                            return '开头结尾不能为空格'
                        }
                    }
                }).then(({ value }) => {
                    item.catName = value
                }).catch(() => {
                });
            },
            removeThreeClass(item, index) {
                this.$confirm('此操作将删除该项, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(item.auth) {
                        this.isRemoveThreeClass(item, index)
                    }
                    else {
                        this.$http({
                            url: '/productController/delClassify.do',
                            method: 'post',
                            params: {
                                catId: item.catId,
                            }
                        }).then( res => {
                            if(res.data.success) {
                                this.isRemoveThreeClass(item, index)
                            }
                            else {
                                this.$message({
                                    message: res.data.msg,
                                    duration: 1000,
                                    type: 'info'
                                });
                            }
                        }).catch( error => {
                            this.$message({
                                message: '操作失败',
                                duration: 1000,
                                type: 'error'
                            });
                            console.log(error)
                        })
                    }

                }).catch(() => {});
            },
            save() {
                this.$confirm('您是否确定保存?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let list = this.saveAllList()
                    this.$http.post('/productController/saveClassify.do', list).then( res => {
                        if(res.data.success) {
                            this.one_list.forEach(function(element, index) {
                                element.auth = false           //因为是本地新增的 所以保存的时候要让他变成 false  表示不是新增了  这样删除就会去请求删除接口
                                element.children.forEach(function(element, index) {
                                    element.auth = false
                                    element.children.forEach(function(element, index) {
                                        element.auth = false
                                    })
                                })
                            });
                            this.$message({
                                message: '保存成功',
                                duration: 1000,
                                type: 'success'
                            });
                        }
                        else {
                            this.$message({
                                message: '保存失败',
                                duration: 1000,
                                type: 'error'
                            });
                        }
                    }).catch( error => {
                        this.$message({
                            message: '操作失败',
                            duration: 1000,
                            type: 'error'
                        });
                        console.log(error)
                    })
                })
            },
            //默认选择都选择第一项
            defaultFirst() {
                if(this.one_list.length === 0) return false
                this.two_list = this.one_list[0].children
                this.three_list = this.one_list[0].children[0].children
                this.one_parameter = this.one_list[0].catId
                this.two_parameter = this.one_list[0].children[0].catId
            },
            //删除第3级内容时和1级 2级关联等的判断
            isRemoveThreeClass(item, index) {
                this.three_list.splice(index, 1)
                if(!this.three_list.length)  {
                    if(this.two_list.length == 1) {
                        //删除所有
                        let id = this.two_list[0].pCatId
                        for(let i=0, len=this.one_list.length; i<len; i++) {
                            if(this.one_list[i].catId == id) {
                                this.one_list.splice(i, 1)
                                break
                            }
                        }
                        this.defaultFirst()
                    }
                    else {
                        //删除第二级中的class
                        let id = item.pCatId
                        for(let i=0, len=this.two_list.length; i<len; i++) {
                            if(this.two_list[i].catId == id) {
                                this.two_list.splice(i, 1)
                                break
                            }
                        }
                        this.two_parameter = this.two_list[0].catId
                        this.three_list = this.two_list[0].children
                    }
                }
                this.$message({
                    message: '删除成功',
                    duration: 1000,
                    type: 'success'
                });
            },
            //保存时将树转为列表并排序
            saveAllList() {
                let one = []
                let two = []
                let three = []
                this.one_list.forEach(function(element, index) {
                    let item = {}
                    item.catId = element.catId
                    item.ispCat = 1
                    item.catName = element.catName
                    item.pCatId = 0
                    item.catLevel = 1
                    item.order = index + 1
                    one.push(item)
                    element.children.forEach(function(element, index) {
                        let item = {}
                        item.catId = element.catId
                        item.ispCat = 1
                        item.catName = element.catName
                        item.pCatId = element.pCatId
                        item.catLevel = 2
                        item.order = index + 1
                        two.push(item)

                        element.children.forEach(function(element, index) {
                            let item = {}
                            item.catId = element.catId
                            item.ispCat = 0
                            item.catName = element.catName
                            item.pCatId = element.pCatId
                            item.catLevel = 3
                            item.order = index + 1
                            three.push(item)
                        })
                    })
                });
               return [...one, ...two, ...three]
            }
        },
        watch: {
            one_list(newValue, oldValue) {
                if(this.one_list.length === 0) {
                    this.two_list = []
                    this.three_list = []
                    this.one_parameter = ''
                    this.two_parameter = ''
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    $list_class_width: 1600;
    @function getW($px, $divWidth) {
        @return percentage($px / $divWidth);
    }
    /*@media screen and (min-width: 1350px) {
        #classification .child_list li .single_sort {
            height: 52px;
            line-height: 52px;
        }
    }*/
    @media screen and (min-width: 1500px) {
        #classification .child_list li .single_sort {
            height: 68px !important;
            line-height: 68px !important;
        }
    }
    #classification {
        padding: 30px 30px 44px;
        .list_class {
            position: relative;
            background: #fff;
            height: 827px;
            border-bottom: 1px solid #d6d7dc;
            border-left: 1px solid #ededed;
            border-right: 1px solid #ededed;
            .child_box {
                height: 100%;
                width: 53%;
                padding: 56px 56px 0;
                box-sizing: border-box;
            }
            .child_list {
                height: 100%;
                overflow-y: auto;
                li {
                    width: 33.3%;
                    float: left;
                    .single_sort {
                        height: 52px;
                        line-height: 52px;
                        width: getW(225, 264);
                        margin: 20px getW(32, 264);
                        border: 1px solid #c6c6c6;
                        border-radius: 3px;
                        text-align: center;
                        cursor: pointer;
                        position: relative;
                        .el-icon-circle-cross {
                            position: absolute;
                            right: -5px;
                            top: -5px;
                            color: #c6c6c6;
                            display: none;
                        }
                        &:hover .el-icon-circle-cross {
                            display: block;
                        }
                    }
                    .last_single_sort {
                        color: #c6c6c6;
                        border-style: dashed;
                    }
                }
            }
        }
        .foot {
            position: absolute;
            top: 12px;
            right: 56px;
        }
    }
</style>



