<template>
    <div v-show="beforeShow">
        <el-table border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  :height="680">
            <el-table-column  label="序号" width="70" align="center">
                <template scope="scope">
                    <span>{{scope.$index + size*(currentPage - 1)+1}}</span>
                </template>
            </el-table-column>
            <el-table-column property="fphone" label="手机号" width="250" align="center">
            </el-table-column>
            <el-table-column property="fcustName" label="客户名称" align="center">
            </el-table-column>
            <el-table-column property="fintegral" label="积分余额" align="center">
            </el-table-column>
            <el-table-column property="fupdatetimeStr" label="更新时间" align="center">
            </el-table-column>
            <el-table-column label="操作" align="center">
                <el-table-column  label="查询明细" width="130" align="center">
                    <template scope="scope">
                        <span class="beforeTable-seeBtn" @click="seeInfo(scope.$index,scope.row)">查看</span>
                    </template>
                </el-table-column>
                <el-table-column  label="调整" width="130" align="center">
                    <template scope="scope">
                        <span class="beforeTable-adjustBtn"  @click="adjust(scope.$index,scope.row)">调整</span>
                    </template>
                </el-table-column>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
    export default{
        props:['tableData','currentPage','size','beforeShow'],
        data(){
            return {

            }
        },
        mounted(){

        },
        methods: {
            seeInfo(index,row){
                this.$emit('seeInfo',index,row);
            },
            adjust(index,row){
                this.$emit('adjust',index,row);
            }
        }
    }
</script>
<style>
    .beforeTable-seeBtn,.beforeTable-adjustBtn{
        color:#38adff;
        cursor: pointer;
    }
</style>



