/**
 * 销售管理路由
 */


//积分策略
import intergralSendList from '@/components/common/sell/intergral/intergralStrategy/intergralSendList.vue'
/**
 * 充值送积分
 */
import chargeIntergral from '@/components/common/sell/intergral/intergralStrategy/chargeIntergral.vue'
/**
 * 线下送积分
 */
import lineSendIntergral from '@/components/common/sell/intergral/intergralStrategy/lineSendIntergral.vue'



var sell = [
    { path: 'sell', name: 'sell', redirect: { name: 'intergralSendList' } }
]

/**
 * 积分策略
 */
let integralStrategy = [
    { path:'sell/intergralSendList',name: 'intergralSendList',component: intergralSendList},
    { path:'sell/chargeIntergral',name: 'chargeIntergral',component: chargeIntergral},
    { path:'sell/lineSendIntergral',name: 'lineSendIntergral',component: lineSendIntergral}
];
//do other arr model router


let integralMall = [
    { path:'sell/intergralSendList',name: 'intergralSendList',component: intergralSendList},
]


export default sell.concat(integralStrategy, integralMall)



