<template>
    <div id="first">
        <div class="marker">
            <div class="show-picture">
                <img src="../../../../static/images/first/t1.png" alt="" class="t1">
                <img src="../../../../static/images/first/t2.png" alt="" class="t2">
                <img src="../../../../static/images/first/t3.png" alt="" class="t3">
                <img src="../../../../static/images/first/t4.png" alt="" class="t4">
                <img src="../../../../static/images/first/t5.png" alt="" class="t5">
                <img src="../../../../static/images/first/t6.png" alt="" class="t6">
                <img src="../../../../static/images/first/t7.png" alt="" class="t7">
                <h1 class="title">欢迎登录CRM管理平台</h1>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'first'
    }
</script>

<style scoped lang="scss">
    $boxWidth: 958;
    $boxHeight: 1030;
    /*函数 用于响应式 的width height*/
    @function getW($px) {
        @return percentage($px / $boxWidth);
    }
    @function getH($px) {
        @return percentage($px / $boxHeight);
    }
    #first {
        /*覆盖层*/
        .marker {
            background: #fff;
            position: fixed;
            left: 0;
            top: 50px;
            z-index: 4000;
            width: 100%;
            height: calc(100% - 50px);
            -moz-height: calc(100% - 50px);
            -webkit-height: calc(100% - 50px);

            .show-picture {
                /*overflow: hidden;*/
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                width: percentage(958/1920);
                height: 100%;
                 /*图片定位*/
                .title {
                    width: 100%;
                    position: absolute;
                    left: 0;
                    font-size: 45px;
                    text-align: center;
                    top: getH(100);
                    animation: zoomInDown 3s;
                    animation-fill-mode: forwards;
                }
                .t1 {
                    width: getW(200);
                    position: absolute;
                    bottom: getH(120);
                    left: getW(120);
                    z-index: 11;
                    animation: bounceInLeft 2s cubic-bezier(0.215, 0.610, 0.355, 1.000);
                    animation-fill-mode: forwards;
                }
                .t2 {
                    width: getW(351);
                    position: absolute;
                    left: getW(170);
                    bottom: getH(120);
                    z-index: 10;
                    animation: rollIn 1.5s;
                    animation-fill-mode: forwards;
                }
                .t3 {
                    width: getW(502);
                    position: absolute;
                    left: getW(310);
                    bottom: getH(76);
                    z-index: 13;
                    animation: lightSpeedIn 1.5s cubic-bezier(.08,.73,.15,1.05);
                    animation-fill-mode: forwards;
                }
                .t4 {
                    width: getW(230);
                    position: absolute;
                    left: getW(584);
                    bottom: getH(120);
                    z-index: 11;
                    animation: rotateInDownRight 1.5s ease-in;
                    animation-fill-mode: forwards;
                }
                .t5 {
                    width: getW(944);
                    position: absolute;
                    left:0;
                    bottom: getH(120);
                    animation: bounceInDown 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000);
                    animation-fill-mode: forwards;
                }
                .t6 {
                    width: getW(890);
                    position: absolute;
                    left:getW(64);
                    bottom: getH(450);
                    animation: flipInX 5s;
                    animation-fill-mode: forwards;
                }
                .t7 {
                    position: absolute;
                    width:getW(878);
                    left: getW(70);
                    bottom: getH(120);
                    animation: bounceInRight 1.5s;
                    animation-fill-mode: forwards;
                }
            }
        }
    }
    /*
     *   动画
     */
            /*title*/
    @keyframes zoomInDown {
        from {
            opacity: 0;
            transform: scale3d(.1, .1, .1) translate3d(0, -1000px, 0);
            animation-timing-function: cubic-bezier(0.550, 0.055, 0.675, 0.190);
        }

        60% {
            opacity: 1;
            transform: scale3d(.475, .475, .475) translate3d(0, 60px, 0);
            animation-timing-function: cubic-bezier(0.175, 0.885, 0.320, 1);
        }
    }
            /*t1*/
    @keyframes bounceInLeft {
        0% {
            opacity: 0;
            transform: translate3d(-3000px, 2000px, 0);
        }

        60% {
            opacity: 1;
            transform: translate3d(25px, 0, 0);
        }

        75% {
            transform: translate3d(-10px, 0, 0);
        }

        90% {
            transform: translate3d(5px, 0, 0);
        }

        to {
            opacity: 1;
            transform: none;
            left: getW(120);
        }
    }
            /*t2*/
    @keyframes rollIn {
        from {
            opacity: 0;
            transform: translate3d(-100%, -200%, 0) rotate3d(0, 0, 1, -120deg);
            left: -1000px;
        }

        to {
            opacity: 1;
            transform: none;
            left: getW(170);
        }
    }
            /*t3*/
    @keyframes lightSpeedIn {
        from {
            transform: translate3d(100%, 0, 0) skewX(-30deg);
            opacity: 0;
            left: -1000px;
        }

        60% {
            transform: skewX(20deg);
            opacity: 1;
        }

        80% {
            transform: skewX(-5deg);
            opacity: 1;
        }

        to {
            transform: none;
            opacity: 1;
            left: getW(310);
        }
    }
            /*t4*/
    @keyframes rotateInDownRight {
        from {
            transform-origin: right bottom;
            transform: rotate3d(0, 0, 1, 45deg);
            opacity: 0;
        }

        to {
            transform-origin: right bottom;
            transform: none;
            opacity: 1;
        }
    }
            /*t5*/
    @keyframes bounceInDown {
        0% {
            top: -500px;
            opacity: 0;
            transform: translate3d(0, -3000px, 0);
        }

        60% {
        opacity: 1;
            transform: translate3d(0, 25px, 0);
        }

        75% {
            transform: translate3d(0, -10px, 0);
        }

        90% {
            transform: translate3d(0, 5px, 0);
        }

        to {
            transform: none;
            bottom: getH(120);
        }
    }
            /*t6*/
    @keyframes flipInX {
        from {
            transform: perspective(400px) rotate3d(1, 0, 0, 180deg);
            animation-timing-function: ease-in;
            opacity: 0;
        }

        40% {
            transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
            animation-timing-function: ease-in;
        }

        60% {
            transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
            opacity: 1;
        }

        80% {
            transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
        }

        to {
            transform: perspective(400px);
        }
    }
            /*t7*/
    @keyframes bounceInRight {
        from, 60%, 75%, 90%, to {
            animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        }

        from {
            opacity: 0;
            transform: translate3d(3000px, 0, 0);
        }

        60% {
            opacity: 1;
            transform: translate3d(-25px, 0, 0);
        }

        75% {
            transform: translate3d(10px, 0, 0);
        }

        90% {
            transform: translate3d(-5px, 0, 0);
        }

        to {
            transform: none;
        }
    }
</style>



