import Vue from 'vue'
import App from './App'
import router from './router'
//add magic-input.min.css
import '../node_modules/magic-input/dist/magic-input.min.css';

//add element-ui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(ElementUI)

//add css base
import './assets/style/base.css'
import './assets/style/changeElementUi.css'

//add vuex
import store from './store'

/**
 * add util
 */
import * as type from './util'
//add axios
Vue.prototype.$http = type.Axios
//cache
Vue.prototype.$cache = type.Cache

import '../static/css/font-awesome/css/font-awesome.min.css'

Vue.config.productionTip = false

type.Axios.get("ipConfig.json").then((result)=>{
    localStorage.setItem('ip',result.data.ip);
    console.log(localStorage.getItem('ip'));
    console.log('22222222222222222222222222222')
}).catch((error)=>{
    localStorage.setItem('ip',error.data.ip);
    console.log(localStorage.getItem('ip'));
    console.log('1111111111111111111111111111111')
});
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



