<template>
    <div class="hhssReport-warp">
        <query-list @query="query" :timeType="timeType" :timeSearch="true"></query-list>
        <div class="hhssReport-btn-warp">
            <span :class="{isactive:isActive}" @click="changeActive(0)">纸板团购订单</span>
            <span :class="{isactive:!isActive}" @click="changeActive(1)">积分商城订单</span>
        </div>
        <el-table border ref="singleTable" highlight-current-row :data="tableData" style="width: 100%"  height="700">
            <el-table-column property="phone" label="手机号" align="center">
            </el-table-column>
            <el-table-column label="认证名" align="center">
                <template scope="scope">
                    <span v-if="isActive">
                        {{scope.row.customerName}}
                    </span>
                    <span v-else>
                        {{scope.row.certificationName}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column  label="产品名称" align="center">
                <template scope="scope">
                    <span v-if="isActive">{{scope.row.productName}}</span>
                    <span v-else>{{scope.row.goodName}}</span>
                </template>
            </el-table-column>
            <el-table-column property="orderId" label="订单号" align="center"  width="200">
            </el-table-column>
            <el-table-column  label="订单类型" align="center">
                <!--property="orderStatus"-->
                <template scope="scope">
                    <span v-if="isActive">团购</span>
                    <span v-else>积分商城</span>
                </template>
            </el-table-column>
            <el-table-column label="下单时间" align="center">
                <template scope="scope">
                    <span v-if="isActive">{{scope.row.createTime.substring(0,scope.row.createTime.length -2)}}</span>
                    <span v-else>{{scope.row.orderDate.substring(0,scope.row.orderDate.length -3)}}</span>
                </template>
            </el-table-column>
            <el-table-column label="订单积分" align="center">
                <template scope="scope">
                    <span v-if="isActive">{{scope.row.giveintegral}}</span>
                    <span v-else>{{scope.row.integral}}</span>
                </template>
            </el-table-column>
            <el-table-column  label="订单金额(元)" align="center">
                <template scope="scope">
                    <span v-if="isActive">{{scope.row.money}}</span>
                    <span v-else>{{scope.row.goodPrice}}</span>
                </template>
            </el-table-column>
            <el-table-column label="退款原因" align="center">
                <template scope="scope">
                    <div v-if="isActive">
                        <span  v-show="scope.row.status == 1"  >异常订单退款</span>
                        <span  v-show="scope.row.status == 2" >订单取消退款</span>
                    </div>
                    <div v-else>
                        <span>{{scope.row.cancelReasons}}</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column  label="退还积分" align="center">
                <template scope="scope">
                    <div v-if="isActive">
                        <span >{{scope.row.integral}}</span>
                    </div>
                    <div v-else>
                        <span v-if="scope.row.status == 5">{{scope.row.integral}}</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column  label="退款金额(元)" align="center">
                <template scope="scope">
                    <div v-if="isActive">
                        <span  v-show="scope.row.status == 1">
                            {{scope.row.exceptionRefundMoney==0?'':scope.row.exceptionRefundMoney}}
                        </span>
                        <span  v-show="scope.row.status == 2">
                            {{scope.row.money==0?'':scope.row.money}}
                        </span>
                    </div>
                    <div v-else>
                        <span v-if="scope.row.status == 5">{{scope.row.goodPrice}}</span>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="hhssReport-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false" @current-change="handleCurrentChange"
                           :current-page="currentPage +1"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import queryList from './queryWarp.vue'
    import {VERSION} from '@/util/constant.js'
    export default{
        created(){
            this.getListData();
        },
        components:{
            queryList
        },
        data(){
            return {
                timeType:'下单时间',
                total:0,
                size:15,
                currentPage:0,
                tableData:[], // 列表容器
                startTime:'',
                endTime:'',
                userId:'',
                isActive:true
            }
        },
        methods:{
            handleSizeChange(size){
                this.size = size;
                this.currentPage = 0;
                this.getListData();
            },
            handleCurrentChange(page){
                this.currentPage = page - 1;
                this.getListData();
            },
            getListData(){
                this.$http.post(this.isActive?`/reportForms/mixReportForms.do`:'/reportForms/mixReportOfMallForms.do',{
                    pageSize:this.size,
                    pageNo:this.isActive?this.currentPage:this.currentPage+1,
                    userId:this.userId==''?null:this.userId,
                    startTime:this.isActive?(this.startTime==''?null:this.startTime):(this.startTime==''?null:new Number(new Date(this.startTime).getTime().toString().substring(0,10))),
                    endTime:this.isActive?(this.endTime==''?null:this.endTime):(this.endTime==''?null:new Number(new Date(this.endTime).getTime().toString().substring(0,10))+86400),
                    version:VERSION
                }).then(res => {
                    res = res.data;
                    if(res.success){
                        if(this.isActive){
                            this.total = res.data.total;
                            this.tableData = res.data.list;
                        }else{
                            this.total = res.data.orderReport.totalSize;
                            this.tableData = res.data.orderReport.content;
                        }
                    }else{
                        this.$message({
                          message: res.msg,
                          type: 'error'
                        });
                    }
                })
            },
            query(queryJson){
                this.startTime = queryJson.startTime;
                this.endTime =queryJson.endTime;
                this.userId = queryJson.userId;
                this.currentPage = 0;
                this.getListData();
            },
            changeActive(status){
                if(status === 0){
                    this.isActive = true;
                }else{
                    this.isActive = false;
                }
            }
        },
        watch:{
            isActive(){
                this.tableData = [];
                this.total = 0;
                this.currentPage = 0;
                this.getListData();
            }
        }
    }
</script>
<style>
    .hhssReport-warp{
        position:relative;
    }
    .hhssReport-pagination{
        text-align: center;
        position:absolute;
        bottom:-200px;
        width:100%;
        left:0px;
    }
    .hhssReport-pagination .el-pagination button,
    .hhssReport-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .hhssReport-pagination .el-pager li.btn-quicknext,
    .hhssReport-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .hhssReport-pagination .el-pagination .el-select .el-input input,
    .hhssReport-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .hhssReport-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    .hhssReport-btn-warp{
        padding: 20px;
        border-top:2px solid #c6c6c6;
    }
    .hhssReport-btn-warp span{
        display: inline-block;
        border: 1px solid #c6c6c6;
        color:#8c8c8c;
        width: 90px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor:pointer;
        border-radius: 2px;
    }
    .hhssReport-btn-warp span:nth-child(2){
        margin-left: 20px;
    }
    .hhssReport-btn-warp span.isactive{
        width: 92px;
        height: 32px;
        background: #38adff;
        line-height: 32px;
        color:#fff;
        border: 0px;
    }
</style>



