<template>
	<div class="partnerManage">
		<tHeader>认证审核信息</tHeader>
		<div class="partManage-bg partManage-position tax-position">
			<div :class="[selectImg === taxImgSmall ? 'partnerManage-Img tax-taxImg-top' :'partnerManage-Img' ]">
				<el-dialog :visible.sync="dialogFormVisible" :show-close="false" @open='openDialog' @close='closeDialog' :modal-append-to-body='false'>
				   <img :src="selectImg" :class="[selectImg === taxImgSmall ? 'tax-taxImg' : 'tax-taxImg-one']">
				</el-dialog>
			</div>
			<personalLeft :step01='step01'></personalLeft>
			<personalright
			     :stringArry='stringArry'
				 @getText='getText'
				 :image01='taxImgBug'
				 :image02='taxImgSmall'
				 :imgClass='imgClass'
				 :classImg='classImg'
				 @showImg='showImg'
				 :defalt='defalt'
				 :pass='pass'
				 :noPass='noPass'
				 :returnBtn='returnBtn'
				 @setNum = 'getRadio'
				 @completed='completed'
				 :isEdit='isEdit'
				 @goBack='goBack'
				>
				<div class='personal-write'>
					<div class='persoanl-type'>
						<span>类型:</span>
						<el-input v-model='type' :disabled='isDisableds'></el-input>
					</div>
					<div class='persoanl-capital'>
						<span>注册资本:</span>
						<el-input v-model='capital' :disabled='isDisableds'></el-input>
						<span>万元</span>
					</div>
					<div class='persoanl-taxName' v-show='isCommon'>
						<span>税字号:</span>
						<el-input v-model='taxName' :disabled='isDisableds'></el-input>
					</div>
					<div class='persoanl-birthTime'>
						<span>成立日期:</span>
						<div class="block">
							<el-date-picker v-model="birthTime" type="date" placeholder="选择日期" :ditable='false' :disabled='isDisableds'></el-date-picker>
						</div>
					</div>
					<div class='persoanl-term'>
						<span>营业期限:</span>
						<div class="block">
							<el-date-picker v-model="term" 
							type="date" 
							placeholder="选择日期" 
							:editable='false' 
							:clearable='false'
							:disabled='isDisableds'>
							</el-date-picker>
						</div>
						<span>至</span>
						<div class='pwesoanl-toTime'>
							<el-date-picker v-model="toTime"
							stype="date" 
							placeholder="选择日期" 
							:editable='false' 
							:disabled="isDisableds"
							:clearable='false'>
							</el-date-picker>
						</div>
					</div>
					<div class='persoanl-address'>
						<span >住址</span>
						<el-input class='personal-longInput' v-model='address' :disabled="isDisableds"></el-input>
					</div>
				</div>
			</personalright>
		</div>
	</div>
</template>


<script>
import tHeader from '../../../public/tHeader'
import personalright from './personalRight'
import personalLeft from './personalLeft'
import {getTimes} from '@/util/common.js'
import {VERSION} from '@/util/constant.js' 
export default {
	props:['getInitData'],
	created () {
		 let data = this.getInitData;
		 let type = data.num;
		 this.stringArry[0].information = data.fcompanyName;
	     this.stringArry[1].information = data.fregistrationNumber;
		 this.stringArry[2].information = data.frepresentative;
         this.id = data.fid;
		 this.userId = data.fuserId;
		 this.typeNum = data.ftype;
		 //普通双证
		 if(data.ftype == 2){
            this.isCommon = true;
			this.step01='营业执照/税务登记';
			this.taxImgSmall = '../../../static/images/authentication/sw.jpg';
			this.classImg = 'taxImg-taxSmall';
			if(type !== 0){
               this.taxName = data.ftaxNumber;
			}
			var imgData = {
				version:VERSION,	
				userId:data.fuserId,
				pictures:[{
					frelativeId:data.fid,
					ftype:13
				},
				{
					frelativeId:data.fid,
					ftype:14
				}]
			}
			this.getInitImg(imgData);	
		 }else{
            this.isCommon = false;
			this.step01='营业执照/五证合一';
			this.taxImgSmall = '';
			this.classImg = '';
			this.imgClass = 'taxImg-taxBig-five';
			var imgData = {
				version:VERSION,
				userId:data.fuserId,
				pictures:[{
					frelativeId:data.fid,
					ftype:12
				}]
			}
			this.getInitImg(imgData);
		 }
		//查看
		if(type === '-1'){
			this.defalt = false;
			this.pass = true;
			this.returnBtn = true;
			this.isDisableds = true;
		}
		//修改
		if(type === '-2'){
			this.defalt = false;
			this.pass = true;
			this.isEdit = true;
		}
		//查看赋值
		if(type !== 0){
			this.address = data.fcompanyAddress;
			this.capital = data.fregisteredCapital;
			this.type = data.fcompanyType;
			this.term = data.fbusinesstermStart;
			this.toTime = data.fbusinesstermEnd; 
			this.birthTime = data.festablishDate;
		}
	},
	data() {
		return {
			isDisableds:false,
			typeNum:1,                  //五证或者双证
			userId:'',
			id:'',
			type:'',                    //类型
			capital:'',                 //注册资本
			taxName:'',                 //税字号
			birthTime:'',               //成立日期
			term:'',                    //营业期限
			toTime:'',                  //营业期限
			address:'',                 //地址
			rejectReason:'',            //驳回理由
			radio:'1',                  //通过/驳回
			isCommon:true,              //是否是双证页面
			isEdit:false,               //是否修改
			stringArry:[
                {string:'企业名称:', information:''},
				{string:'统一社会信用代码/注册号:', information:''},
				{string:'法人代表:', information:''}
			],
			taxImgBug:'',
			taxImgSmall:'',
			imgClass:'taxImg-taxBig',
            classImg:'taxImg-taxSmall',
			selectImg:'',               //图片选择放大
			dialogFormVisible:false,    //图片遮罩
			defalt:true,    			//审核界面
			pass:false,     			//已通过界面
			noPass:false,   			//未通过界面
			returnBtn:false,			//是否显示返回列表按钮
			step01:'营业执照/税务登记'
		}
	},
	components: {
		tHeader,
		personalright,
		personalLeft
	},
	methods: {
	   //获取驳回信息
       getText(data){
          this.rejectReason = data;
	   },
	   //放大图片
	   showImg(imgNum){
          this.selectImg = this.taxImgSmall
		   if(imgNum === '1'){
			  this.selectImg = this.taxImgBug
		   }
           this.dialogFormVisible = true
	   },
	   //获取通过或者驳回
	   getRadio(num){
          this.radio = num
	   },
	   //审核完成
	   completed(){
		   var companyData={
			  fcompanyAddress:this.address,
			  fregisteredCapital:this.capital,
			  festablishDate:this.birthTime == '' ?  '' : getTimes(this.birthTime),
			  ftype:'1',
			  fbusinesstermStart:this.term == '' ?  '' : getTimes(this.term),
			  fbusinesstermEnd:this.toTime == '' ?  '' : getTimes(this.toTime),
			  ftaxNumber:this.taxName,
			  frejectReason:this.rejectReason,
			  fcompanyType:this.type,
			  fid:this.id,
              fuserId:this.userId,
			  fverifier:this.$store.getters.doneAdmin.uname //审核人取登录的账号
		   }
		   if(this.radio === '2'){
               companyData.fregisteredCapital = 0;
		   }
		   let data = {
              version:VERSION,
			  operationType:this.radio,
			  certificationType:'2',
			  companyCertification:companyData
		   }
		   if(this.radio === '2' && this.rejectReason === ''){
			   this.$message({
					message: '请输入驳回原因',
					type: 'error'
				});
			   return 
		   }
		   if(this.radio === '2' && this.rejectReason.length < 5){
				this.$message({
					message: '驳回理由不得少于5个字',
					type: 'error'
				});
				return
		   }
		   if(this.radio === '1' && !Number(this.capital)){
               this.$message({
					message: '注册资本必须为数字',
					type: 'warning'
				});
			   return
		   }
		   this.$http.post('/outerUser/userAudit.do', data).then(res => {
                if(res.data.success){
					this.$message({
					   message: '审核成功',
					   type: 'success'
				   });
                   this.$emit('goBack');
				}
            }).catch(function (error) {
                console.log(error);
            });
	   },
	   //获取图片
       getInitImg(data){
            this.$http.post('/outerUser/getPicture.do', data).then(res => {
                if (res.data.success) {
					  this.taxImgBug = res.data.data[0].furl;
					  if(res.data.data[1]){
                        this.taxImgSmall = res.data.data[1].furl;
					  }
                }
            }).catch(function (error) {
                console.log(error);
            });
		},
		//返回列表
		goBack(){
			this.$emit('goBack');
		},
		openDialog(){
            this.$store.commit('openZindexModel');
        },
        closeDialog(){
            this.$store.commit('closeZindexModel');
        }
	}
}

</script>

<style>

</style>



