<template>
    <div class="ab-query-warp">
        <ul class="clearfix">
            <li>
                创建时间 :
                <el-date-picker v-model="startTime" type="datetime" placeholder="开始日期"  :picker-options="pickerStart">
                </el-date-picker>
                至
                <el-date-picker v-model="endTime" type="datetime" placeholder="结束日期"   :picker-options="pickerEnd">
                </el-date-picker>
            </li>
            <li>用户手机号 : <input type="text" v-model="phone" @input="phoneCheck" placeholder="请输入手机号"></li>
            <li>
                认证名 : <input type="text" v-model="name" placeholder="请输入认证名">
            </li>
            <li>
                <el-button type="primary" icon="search" @click="query">搜索</el-button>
            </li>
        </ul>
    </div>
</template>
<script>
    /**
     * Created by qxl on 2017/8/28.
     */
    import {VERSION} from '@/util/constant.js'
    import { get3MonthBefor } from '@/util/common.js'
    export default{
        data(){
            return {
                pickerStart:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                pickerEnd:{
                    disabledDate(time) {
                        return time.getTime() < get3MonthBefor() || time.getTime() > Date.now() - 8.64e7 + 86400000;
                    }
                },
                endTime:'', // 结束时间
                startTime:'', //开始时间
                startGetTime:-999, //开始时间时间戳
                endGetTime:-999, //结束时间时间戳
                phone:'',  //用户手机号
                name:''
            }
        },
        methods:{
            phoneCheck(){
                this.phone = this.phone.replace(/\D/g,'');
            },
              //获取时间戳
            getTime(val){
                    return new Date(val).getTime();
            },
            //消息提示
            msg(msg,type){
                this.$message({
                    message: msg,
                    type: type
                });
            },
            useTime(value){
                if(value == '' || value == undefined){
                    return '';
                }
                var time = new Date(value);
                var year = time.getFullYear();
                var month = time.getMonth() + 1<10? '0'+ (time.getMonth() + 1):time.getMonth() + 1;
                var date = time.getDate() <10 ? '0'+ time.getDate():time.getDate();
                var hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours();
                var min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
                var sec = time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds();
                return `${year}-${month}-${date} ${hour}:${min}:${sec}`
            },
            //查询 向父组件传递当前输入的信息
            query(){
                if(this.getTime(this.endTime)<this.getTime(this.startTime)){
                    this.msg('对不起亲爱哒，结束日期必须大于开始日期！','error');
                    return;
                }
                if(this.phone.length > 11){
                    this.msg('手机号不能大于11位!','error');
                    return;
                }
                if(this.startTime=='' && this.endTime !=''){
                    this.msg('请填写开始时间再进行查询！','error');
                     return;
                }
                if(this.startTime !='' && this.endTime ==''){
                    this.msg('请填写结束时间再进行查询！','error');
                     return;
                }
                var queryJson ={
                    startTime:this.useTime(this.startTime),
                    endTime:this.useTime(this.endTime),
                    phone:this.phone,
                    name:this.name
                }
                this.$emit('query', queryJson)
            }
        }
    }
</script>
<style>
    .ab-query-warp ul li{
        float:left;
        margin-right:15px;
    }
    .ab-query-warp ul li:nth-child(1){
        margin-bottom: 5px;
    }
    .ab-query-warp ul{
        padding:24px 0;
    }
    .ab-query-warp ul li:nth-child(2) input,
    .ab-query-warp ul li:nth-child(3) input{
        width:175px;
        height:33px;
        border:1px solid #c6c6c6;
        padding-left: 5px;
        border-radius:4px;
    }

</style>



