<template>
    <div id="widthdrawCash-warp">
        <div class="widthdrawCash-warp-padding">
            <div class="widthdrawCash-radius">
                <tHeader>提现管理</tHeader>
                <div class="widthdrawCash-content">
                    <ul class="widthdrawCash-nav clearfix">
                        <li v-for="(item,index) in navList">
                            <span :class="{active:item.navState}"  @click="tab(index)">{{item.navContent}}</span>
                        </li>
                    </ul>
                    <div>
                        <widthdraw-cash-query @query="query"></widthdraw-cash-query>
                    </div>
                    <el-table  border ref="singleTable" :data="tableData" highlight-current-row  style="width: 100%"  height="700">
                        <el-table-column  label="序号" width="70" align="center">
                            <template scope="scope">
                                <span>{{scope.$index + size*(pageNo - 1)+1}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="f_payermobilenumber" label="手机号" align="center">
                        </el-table-column>
                        <el-table-column property="f_payername" label="认证名" align="center">
                        </el-table-column>
                        <el-table-column property="f_amount" label="金额(元)" align="center">
                        </el-table-column>
                        <el-table-column  label="业务时间" align="center">
                            <template scope="scope">
                                <span v-if="scope.row.f_createdatetime">{{scope.row.f_createdatetime.substring(0,scope.row.f_createdatetime.length -2)}}</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="状态" align="center">
                            <template scope="scope">
                                <span  v-show="scope.row.f_withdrawalstate ==0" >未提现</span>
                                <span  v-show="scope.row.f_withdrawalstate ==1 "> 已经提现</span>
                                <span  v-show="scope.row.f_withdrawalstate == 2">异常</span>
                            </template>
                        </el-table-column>
                        <el-table-column property="f_ordernumber" label="提现流水号" align="center" width="300">
                        </el-table-column>
                        <el-table-column label="提现时间" align="center">
                            <template scope="scope">
                                <span v-if="scope.row.f_modifydatetime">{{scope.row.f_modifydatetime.substring(0,scope.row.f_modifydatetime.length -2)}}</span>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </div>
        </div>
        <div class="widthdrawCash-pagination">
            <el-pagination layout="total, sizes,prev, pager, next, jumper" :total="total"  :small="false"
                           @current-change="handleCurrentChange"
                           :current-page="pageNo"
                           :page-size="size"
                           @size-change="handleSizeChange"
                           :page-sizes="[15, 20, 30, 100]">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import tHeader from '../../../../public/tHeader.vue'
    import widthdrawCashQuery from './withdrawCashQuery.vue'
    import {VERSION} from '@/util/constant.js'
    export default {
        components:{
            tHeader,
            widthdrawCashQuery
        },
        created(){
          this.listInit(false);
        },
        data(){
            return {
                navList:[ //导航容器
                    {navContent:'未提现', navState:false},
                    {navContent:'异常', navState:false},
                    {navContent:'已提现', navState:false},
                    {navContent:'全部', navState:true}
                ],
                username:'', //用户名
                mobileNumber:'', //手机号
                endTime:'', // 结束时间
                startTime:'', //开始时间
                total:0, //总数
                size:15, //条数
                pageNo:1, // 当前页,
                timetype: '', //类别
                withdrawalstate: '-1', // 提现状态
                userId:'', //用户id
                tabIndex:3, // 导航的索引
                tableData:[] // 列表容器
            }
        },
        methods:{
            // 一页多少条变化
            handleSizeChange(size){
                this.size = size;
                this.listInit(true);
            },
            //页码变化
            handleCurrentChange(page){
                this.pageNo = page;
                this.listInit(false);
            },
            // 导航条变化
            tab(index){
                if(this.tabIndex == index){
                    return;
                }
                for(var i=0; i<this.navList.length; i++){
                    this.navList[i].navState = false;
                }
                this.navList[index].navState = true;
                switch (index) {
                    case 0:
                        this.withdrawalstate = '0';
                        break;
                    case 1:
                        this.withdrawalstate = '2';
                        break;
                    case 2:
                        this.withdrawalstate = '1';
                        break;
                    default:
                        this.withdrawalstate = '-1';
                        break;
                }
                this.tabIndex = index;
                this.listInit(true);
            },
            //查询
            query(queryJson){
                this.endTime = queryJson.endTime; // 结束时间
                this.startTime = queryJson.startTime; //开始时间
                this.mobileNumber = queryJson.mobileNumber; // 手机号
                this.username= queryJson.username; // 认证名
                this.timetype = queryJson.timetype; // 类别
                this.listInit(true);
            },
            // 查询的数据
            queryData(){
                return {
                    startTime:this.startTime,
                    endTime:this.endTime,
                    timetype:this.timetype === ''? '-1':this.timetype,
                    withdrawalstate:this.withdrawalstate,
                    pageNumber:this.size+'',
                    currentPage:this.pageNo+'',
                    mobileNumber:this.mobileNumber,
                    username:this.username
                }
            },
            // 列表初始化
            listInit(bFlag){
                this.$http.post('/finance/payBankwithdrawallist.do',this.queryData()).then(res => {
                    res = res.data;
                    if(res.success === 'true'){
                        this.tableData = res.data.data;
                        this.total = parseFloat(res.data.total);
                        if(bFlag){
                            this.pageNo = 1;
                        }
                    }
                })
            },
            // 消息
            msg(msg,iconClass){
                this.$message({
                    message: msg,
                    iconClass: iconClass
                });
            }
        }
    }
</script>

<style>
    #widthdrawCash-warp{
        width: 100%;
        height:100%;
        padding-bottom:250px;
    }
    #widthdrawCash-warp .el-table .cell,
    #widthdrawCash-warp.el-table th>div{
        padding: 0px;
    }
    .widthdrawCash-warp-padding{
        padding:30px 30px 20px 30px;
    }
    .widthdrawCash-radius{
        background: #fff;
        border-radius:8px;
        padding-bottom:100px;
    }
    .widthdrawCash-content{
        padding:0px 10px;
    }
    .widthdrawCash-nav{
        height:50px;
        font-size:16px;
        border-bottom:2px solid #b0b0b0;
        padding-left:10px;
    }
    .widthdrawCash-nav li{
        float:left;
        height:50px;
        line-height:50px;
        padding:0px 22px;
    }
    .widthdrawCash-nav li span{
        cursor: pointer;
    }
    .widthdrawCash-nav li span.active{
        color:#38acff;
    }
    /*
        分页
    */
    .widthdrawCash-pagination{
        text-align: center;
    }
    .widthdrawCash-pagination .el-pagination button,
    .widthdrawCash-pagination .el-pagination li{
        width:38px;
        height:38px;
        margin-right: 5px;
        line-height: 38px;
    }
    .widthdrawCash-pagination .el-pager li.btn-quicknext,
    .widthdrawCash-pagination .el-pager li.btn-quickprev
    {
        line-height: 38px;
    }
    .widthdrawCash-pagination .el-pagination .el-select .el-input input,
    .widthdrawCash-pagination .el-pagination span{
        height: 38px;
        line-height: 38px;
    }
    .widthdrawCash-pagination .el-pagination__editor{
        line-height: 26px;
        width: 38px;
        height: 36px;
    }
    /*
      icon 背景图
     */
    .widthdrawCash-rightIcon{
        width: 40px;
        height: 40px;
        display: inline-block;
        margin-left: -13px;
        background: #48d1cc url(../../../../../../static/images/publicImages/info-right.png) no-repeat 7px 7px;
    }
    .widthdrawCash-errorIcon{
        width: 40px;
        height: 40px;
        display: inline-block;
        margin-left: -13px;
        background: #f57777 url(../../../../../../static/images/publicImages/info-error.png) no-repeat 7px 7px;
    }
</style>



