<template>
    <el-submenu
        :index="tree.menu.url"
        v-if="tree.childrenNode.length && tree.auth"
    >
        <template slot="title">
            <!--之所以是3 是因为 只有二级菜单有图标，对于二级菜单是  x-x的形式 3个字符 -->
            <i class="icon iconfont" v-if="tree.menu.icon" v-html="tree.menu.icon"></i>
            <span slot="title">{{ tree.menu.name }}</span>
        </template>
        <el-submenu
            :index="item.menu.url"
            v-for="(item, index) in tree.childrenNode"
            :key="index"
            v-if="item.childrenNode.length && item.auth"
        >
            <span slot="title">{{ item.menu.name }}</span>
            <!-- 回调 三四···级菜单 -->
            <el-menu-component v-for="(list, index) in item.childrenNode"  :key="index" :tree="list"></el-menu-component>
        </el-submenu>
        <el-menu-item :index="item.menu.url" v-else-if="item.auth" @click="toRouterView(item.menu.url)">
            <span slot="title">{{ item.menu.name }}</span>
        </el-menu-item>
    </el-submenu>

    <el-menu-item :index="tree.menu.url" v-else-if="tree.auth && getBarsState" @click="toRouterView(tree.menu.url)">
        <i class="icon iconfont" v-if="tree.menu.icon" v-html="tree.menu.icon"></i>
        <span slot="title">{{ tree.menu.name }}</span>
    </el-menu-item>
    <el-tooltip class="item" effect="dark" :content="tree.menu.name" placement="right" v-else-if="tree.auth && !getBarsState">
        <el-menu-item :index="tree.menu.url"  @click="toRouterView(tree.menu.url)">
            <i class="icon iconfont" v-if="tree.menu.icon" v-html="tree.menu.icon"></i>
            <span slot="title">{{ tree.menu.name }}</span>
        </el-menu-item>
    </el-tooltip>
</template>

<script>
    export default {
        name: 'elMenuComponent',
        props: ['tree', 'getBarsState'],
        data() {
            return {

            }
        },
        methods: {
            toRouterView(pathName) {
                this.$router.push({name: pathName})
            }
        }
    }
</script>

<style>

</style>



