<template>
    <div class="partnerManage">
        <tHeader>产品不开团日期设置</tHeader>
        <div class='commodity-bg'>
            <regional-choice
                 @getPleaseProvice='getPlease'
                 @getRegionProvice='getRegion'
                 :parterCode='-1'
                 :defaultPlease='defaultPlease'
                 :defaultRegion='defaultRegion'
                 :isDisableds='admin.ucompany == 100 ? false : true'
                 :isProduce='true'
            >
            </regional-choice>
            <div class='parter-select-top'>
                <partner
                    @getSelect='getSelect'
                    :codeName='codeName'
                    :partName='partnerName'
                    :isDisableds='admin.ucompany == 100 ? false : true'
                ></partner>
            </div>
            <div class='partner-cycle-name'>
                不开团周期选择<span>(请点选不开团日期)</span>
                <el-button type="primary" @click='save'>保存设置</el-button>
            </div>
            <div>
                <div class='partner-cycle-time'>
                     <datepicker @input='getTime' :min='minTimes' :max='maxTimes' :closeTime='closeTime'></datepicker>
                </div>
            </div>        
        </div>
    </div>
</template>

<script>
import carouseSelect from '@/components/public/select'
import tHeader from '@/components/public/tHeader'
import regionalChoice from '@/components/public/regionalChoice'
import partner from '@/components/public/Partner'
import commonInput from './input'
import datepicker from './Datepicker'
import {VERSION} from '@/util/constant.js'
import {clippingProvice, clippingCity} from '@/util/common.js'
export default {
    created(){
       this.admin = this.$store.getters.doneAdmin;
       this.parterId = this.admin.ucompany;    //合作方id
        //区域拆分键
       this.fkeyarea = this.admin.ocode;
       this.minTimes = this.year + '-' + (this.month + 1) + '-' + this.date;
       this.maxTimes = this.year + '-' + (this.month + 3) + '-' + this.date;
       //非东京账号登录
        if(this.admin.ucompany != 100){
            this.defaultRegion = clippingProvice(this.admin.oprovince);
            this.defaultPlease = clippingCity(this.admin.ocity);
            setTimeout(()=> {
               this.partnerName = this.admin.oname;    //合作方名称
            }, 1);
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fpartner:this.parterId,
                startHugh:this.minTimes,
                endHugh:this.maxTimes
            }
            this.getCloseTime(data);
        }
    },
	data () {
        let now = new Date()
		return {
            partnerName:'',
            defaultPlease:'',
            defaultRegion:'',
            admin:{},
            startTime:'',
            endTime:'',
            minTimes: '',
            maxTimes: '',
            year: now.getFullYear(),
            month: now.getMonth(),
            date: now.getDate(),
            closeTime:[],
            fkeyarea:'',          //区域拆分键
            codeName:'',
            parterId:'',
            fhughgrouptime:''
		}
	},
	methods:{
        //区域code
        getPlease(please){
            this.codeName = please;
            this.closeTime = [];
        },
        //切换省时的操作
        getRegion(){
            this.codeName = '';
            this.closeTime = [];
        },
        //合作方选择
        getSelect(data){
            if(data.id == undefined && data.code == undefined){
                return
            }
            this.parterId = data.id;
            this.fkeyarea = data.code;
            let datas ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fpartner:this.parterId,
                startHugh:this.minTimes,
                endHugh:this.maxTimes
            }
            this.getCloseTime(datas);
        },
        //获取不开团时间
        getTime(timeArry){
           var time = timeArry;
           var timeStr = '';
           var timeAry = [];
           for(let i=0; i < time.length; i++){
               var month = time[i].month + 1;
               if(month < 10){
                    month = '0' + month;
               }
               var date = time[i].date;
               if(date < 10){
                   date = '0' + date;
               }
               timeStr = time[i].year  + '-' + month + '-' + date;
               timeAry.push(timeStr);
           }
           this.fhughgrouptime = timeAry.join('#');
        },
        //获取不开团日期
        getCloseTime(data){
            this.$http.post('/product/getGroupHugh.do', data).then(res => {
                    if(res.data.success){
                        var timeAry = res.data.data;
                        this.closeTime = [];
                        if(timeAry.length == 0){
                            this.closeTime = [];
                            return
                        }
                        var timeObj={};
                        for(let i=0; i<timeAry.length; i++){
                            var timeObj={};
                            timeObj.year = Number(timeAry[i].fhughgrouptime.slice(0,4));
                            timeObj.month = Number(timeAry[i].fhughgrouptime.slice(5,7)) - 1;
                            timeObj.date = Number(timeAry[i].fhughgrouptime.slice(8,10));
                            this.closeTime.push(timeObj);
                        }
                        var timeStr = '';
                        var timeAry = [];
                        for(let i=0; i < this.closeTime.length; i++){
                            var month = this.closeTime[i].month + 1;
                            if(month < 10){
                                    month = '0' + month;
                            }
                            var date = this.closeTime[i].date;
                            if(date < 10){
                                date = '0' + date;
                            }
                            timeStr = this.closeTime[i].year  + '-' + month + '-' + date;
                            timeAry.push(timeStr);
                        }
                        this.fhughgrouptime = timeAry.join('#');
                    }
            }).catch(function (error) {
                    console.log(error);
            });
        },
        //保存设置不开团周期
        save(){
            let data ={
                version:VERSION,
                fkeyarea:this.fkeyarea,
                fpartner:this.parterId,
                fhughgrouptime:this.fhughgrouptime,
                startHugh:this.minTimes,
                endHugh:this.maxTimes
            }
            if(this.parterId == 100){
                this.$message({
                    message: '请选择区域合作方',
                    type: 'warning',
                    duration: 1500
                });
                return
            }
            this.$http.post('/product/setGroupHugh.do', data).then(res => {
                if(res.data.success){
                        this.$message({
                        message: '设置不开团日期成功',
                        type: 'success',
                        duration: 1500
                    });
                }else{
                    this.$message({
                            message: res.data.msg,
                            type: 'error',
                            duration: 2000
                        });
                }
            }).catch(function (error) {
                    console.log(error);
            });
        },
    },
    components:{
        carouseSelect,
        tHeader,
        regionalChoice,
        partner,
        commonInput,
        datepicker
    }  
}
</script>


